<template>
    <v-layout align-center justify-center>
        <v-flex xs12 sm8 md6 lg5 xl4>
            <div class="vld-parent">
                <loading :active.sync="isLoading" 
                    :can-cancel="true" 
                    :color="'#922B21 '"
                    :height="140"
                    :width="140"
                    :loader="'Dots'"
                    :on-cancel="onCancel"
                    :is-full-page="fullPage=true">
                </loading>
            </div>
            <v-card>
                <v-toolbar dark flat color="blue darken-2">
                    <v-toolbar-title>
                        INICIA SESION
                    </v-toolbar-title>
                </v-toolbar>
                <v-card-text>
                        <v-flex text-center>
                            <v-text-field v-model="usuario"   prepend-icon="person"  color="primary" label="USUARIO" required>
                            </v-text-field>
                            <v-text-field v-model="password"   prepend-icon="lock" type="password" color="primary" label="PASSWORD" required>
                            </v-text-field>
                            <v-flex class="red--text" v-if="error">
                                <h3> {{error}} </h3>
                            </v-flex>
                         </v-flex>
                </v-card-text>
                <v-card-actions class="px-3 pb-3">
                    <v-flex text-xs-right>
                        <div class="text-center">
                            <v-btn rounded color="error" @click="ingresar()">Ingresar</v-btn>
                        </div>
                    </v-flex>
                </v-card-actions>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
import axios from 'axios'
import Loading from 'vue-loading-overlay'
import 'vue-loading-overlay/dist/vue-loading.css';

export default {
    data(){
        return {
            isLoading: false,
            fullPage: true,
            usuario: '',
            password: '',
            error:null,
            click: 0
        }
    },
    components: {
        Loading
    },
    methods: {
        async ingresar(){
            try {
                let me = this;
                me.isLoading = true;
                me.click++;
                if(me.click == 1){
                    me.error=null;
                    const resp = await axios.post('api/Usuarios/LoginWeb', {'usuario': me.usuario, 'password': me.password});
                    let respuesta = resp.data;
                    me.$store.dispatch("guardarToken", respuesta.token);
                    me.$router.push({ name: 'home' });
                }
            } catch (err) {
                let me = this;
                if (err.response.status==400){
                    me.error="No es usuario v√°lido";
                } else if (err.response.status==404){
                    me.error="No existe el usuario o sus datos son incorrectos";
                }else{
                    me.error="Ocurri√≥ un error";
                }
                me.click=0;
                me.isLoading = false;
            }
            this.isLoading = false;
        },
        doAjax() {
            this.isLoading = true;
            setTimeout(() => {
                this.isLoading = false
            },3000)
        },
        onCancel() {
            console.log('User cancelled the loader.')
        }
    }    
}
</script> 

