<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar color="white">
                <v-toolbar-title>FACTURACION ELECTRONICA</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
                 <v-menu
                    v-model="menu1"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                    >
                    <template v-slot:activator="{ on }">
                    <v-text-field
                        v-model="fechaentrada"
                        label="FECHA EMISION"
                        prepend-icon="event"
                        readonly
                        v-on="on"
                    ></v-text-field>
                    </template>
                    <v-date-picker color="green lighten-1" v-model="fechaentrada" @input="menu1 = false"></v-date-picker>
                </v-menu>
                <v-btn color="primary" dark  @click="listarFacturas()">
                    BUSCAR VENTAS<v-icon dark >search</v-icon>
                </v-btn>
                <v-btn color="brown" dark @click="listarDetalle()" >
                   ENVIAR<v-icon dark >search</v-icon>
                </v-btn>
            </v-toolbar>
            <v-card>
                <v-container fluid grid-list-md >
                    <v-layout row wrap>
                        <v-flex xs12>
                        <v-card>
                            <v-card-title class="error" style="color: white;margin-top:3px"><h3>LISTA DE FACTURAS</h3></v-card-title>
                                <v-card-text>
                                      <v-container grid-list-md class="tabs">
                                       <v-layout>
                                            <v-flex xs9>
                                               
                                             </v-flex>
                                            <v-flex xs3>
                                          
                                            </v-flex>
                                        </v-layout>
                                      </v-container>
                                    <v-data-table
                                        :headers="cabeceraLista"
                                        :items="facturas"
                                        hide-actions
                                        class="elevation-1 margen">
                                        <template  slot="items" slot-scope="props">
                                            <td>{{ props.item.idVenta}}</td>
                                            <td>{{ props.item.fecha }}</td>
                                            <td>{{ props.item.serie }}</td>
                                            <td>{{ props.item.correlativo}}</td>
                                            <td>{{ props.item.cliente}}</td>
                                            <td>{{ props.item.documento}}</td>
                                            <td>{{ props.item.numDocumento}}</td>
                                            <td>{{ props.item.total}}</td>
                                        </template>
                                        <template slot="no-data">
                                            <span class="red--text">
                                                <h3> No hay facturas.</h3>
                                            </span>
                                        </template>
                                    </v-data-table>
                                </v-card-text>
                            <v-divider></v-divider>
                          </v-card>
                         </v-flex>
                    </v-layout>
                </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
import axios from 'axios';
import letras from './Letras.js'

export default {
    data() {
        return {
            menu1:null,
            fechaentrada:'',
            facturas:[],
            detalleFacturas:[],
            cabeceraLista: [
                { text: '#', value: 'opciones',sortable: false},
                { text: 'FECHA COMPRA', value: '',sortable: false},
                { text: 'SERIE', value: '',sortable: false},
                { text: 'CORRELATIVO', value: '',sortable: false},
                { text: 'CLIENTE ', width:800, value: '',sortable: false },
                { text: 'DOCUMENTO', value: '',sortable: false },
                { text: 'NUMERO DOCUMENTO', value: '', sortable: false  },    
                { text: 'TOTAL', value: '', sortable: false  },    
            ],
        }
    },
    created(){
      
    },
    computed: {
        
    },
    methods: {
        listarFacturas(){
            let me =this;
            axios.get('api/FacturasSunats/EnvioFacturas/'+this.fechaentrada).then(function(response){
                me.facturas=response.data;
            }).catch(function(error){
                console.log(error);
            });
        },
        crear(array =[], i){
            let me = this;
            let comprobantes = 
            {
                'ublVersion': '2.1',
                'tipoOperacion':'0101',
                'tipoDoc':'01',
                'serie': me.facturas[i].serie,
                'correlativo': me.facturas[i].correlativo,
                'fechaEmision': '2019-12-09T00:00:00-00:00',
                'client': {
                    'tipoDoc':'6',
                    'numDoc':me.facturas[i].numDocumento,
                    'rznSocial':me.facturas[i].cliente,
                    'address':{
                        'ubigueo':me.facturas[i].ubigeo,
                        'codigoPais':'PE',
                        'departamento':me.facturas[i].departamento,
                        'provincia':me.facturas[i].provincia,
                        'distrito':me.facturas[i].distrito,
                        'urbanizacion':'-',
                        'direccion':me.facturas[i].direccion,
                    }
                },
                'company': {
                    'ruc':'10710924967',
                    'razonSocial':'GERARDO RAFAEL GUEVARA ALLER',
                    'nombreComercial':'GERARDO RAFAEL GUEVARA ALLER',
                    'address':{
                        'ubigueo':'160101',
                        'codigoPais':'PE',
                        'departamento':'Loreto',
                        'provincia':'Maynas',
                        'distrito':'Iquitos',
                        'urbanizacion':'-',
                        'direccion':'ir√≥n Fitzcarrald 232'
                    }
                },
                'tipoMoneda': 'PEN',
                'sumOtrosCargos': 0,
                'mtoOperGravadas': 0,
                'mtoOperInafectas': 0,
                'mtoOperExoneradas': me.facturas[i].subTotal,
                'mtoOperExportacion': 0,
                'mtoIGV': 0,
                'mtoISC': 0,
                'mtoOtrosTributos': 0,
                'icbper': 0,
                'mtoImpVenta': me.facturas[i].subTotal,
                'subTotal': me.facturas[i].subTotal,
                'details': array,
                'legends':[{
                   'code':1000,
                   'value':'SON: '+letras(me.facturas[i].total)+'/100 SOLES'
                }],
                'sumDsctoGlobal': me.facturas[i].descuento,
                'valorVenta': me.facturas[i].total
            };
          
            me.enviarSunat(comprobantes);
          
        },
        async enviarSunat(comprobantes){
            console.log(comprobantes);
            await axios.post('http://localhost:81/facturacion/lycet/public/api/v1/invoice/send?token=123456', comprobantes).then(function(response){
                console.log(response.data)
            }).catch(function(err){
                console.log(err)
            })
        },
        async listarDetalle(){
            let me = this;
            for(var i=0;i<me.facturas.length;i++){
                let id = me.facturas[i].idVenta;
                await axios.get('api/FacturasSunats/DetalleVentaFacturas/'+id).then(function(response){
                    me.detalleFacturas = response.data;
                }).catch(function(error){
                    console.log(error);
                });
                me.crear(me.detalleFacturas, i);
            }
        }
    }
}
</script>
<style>
    .tabs{
        padding: 0%;
    }
    .margen{
        margin: 0px;
        padding: 0px;
        font-size: 12px;
    }
    td{
        font-size: 12px;
        width: auto;
    }
</style>