<template>
  <v-layout justify-center>
    <v-flex xs12>
      <v-toolbar color="white" >
        <v-toolbar-title>AREA PRODUCCION</v-toolbar-title>
      </v-toolbar>
      <v-card>
        <v-container fluid grid-list-md >
          <v-layout row wrap>
                <v-flex xs12>
                 <v-card>
                    <v-card-title class="error" style="color: white;"><h3>PLATOS FRIOS</h3></v-card-title>
                    <v-card-text>
                        <v-data-table
                            :headers="headers"
                            :items="cocina"
                            show-select="true"
                            hide-actions
                            class="elevation-1">
                            <template slot="items" slot-scope="props">
                              <tr v-if="props.item.time.substring(0,2) ==='00' && props.item.time.substring(3,5) >= (Number(props.item.tiempoPreparacion.substring(3,5))* 80)/ 100"  class="colores">
                                  <td>{{props.item.cantidad}}</td>
                                  <td>
                                      <h4>{{ props.item.plato }}</h4>
                                      <div class="">
                                          <h4><span class="primary--text">{{props.item.comentario}}</span></h4>
                                      </div>
                                  </td>
                                  <td>{{ props.item.nombreCompleto}}</td>
                                  <td>{{ props.item.descripcion }}</td>
                                  <td>
                                      <v-chip v-if="props.item.idEstadoPedido==1" color="primary" dark>SOLICITADO</v-chip>
                                      <v-chip v-if="props.item.idEstadoPedido==2" color="success" dark>PREPARANDO</v-chip>
                                  </td>
                                  <td>{{props.item.time.substring(0,8)}}</td>
                                  <td>
                                      <v-btn  v-if="props.item.idEstadoPedido==1" class="mx-2" fab dark small color="primary" @click="preparacion(props.item)">P</v-btn>
                                      <v-btn  v-if="props.item.idEstadoPedido==2" class="mx-2" fab dark small color="error"   @click="despachar(props.item)">E</v-btn>
                                  </td>
                              </tr>
                              <tr v-else-if="props.item.time.substring(0,2) === '00' && props.item.time.substring(3,5) < (Number(props.item.tiempoPreparacion.substring(3,5))* 80)/ 100 && props.item.time.substring(3,5) >= (Number(props.item.tiempoPreparacion.substring(3,5))* 50)/ 100 " class="coloramarillo">
                                <td>{{props.item.cantidad}}</td>
                                <td>
                                    <h4>{{ props.item.plato }}</h4>
                                     <div class="">
                                         <h4><span class="primary--text">{{props.item.comentario}}</span></h4>
                                    </div>
                                </td>
                                <td>{{ props.item.nombreCompleto}}</td>
                                <td>{{ props.item.descripcion }}</td>
                                <td>
                                     <v-chip v-if="props.item.idEstadoPedido==1" color="primary" dark>{{props.item.descripcionEstado}}</v-chip>
                                     <v-chip v-if="props.item.idEstadoPedido==2" color="success" dark>{{props.item.descripcionEstado}}</v-chip>
                                </td>
                                <td>{{props.item.time.substring(0,8)}}</td>
                                <td>
                                    <v-btn  v-if="props.item.idEstadoPedido==1" class="mx-2" fab dark small color="primary" @click="preparacion(props.item)">P</v-btn>
                                    <v-btn  v-if="props.item.idEstadoPedido==2" class="mx-2" fab dark small color="error"   @click="despachar(props.item)">E</v-btn>
                                </td>
                              </tr>
                              <tr v-else-if="props.item.time.substring(0,2) != '00'" class="colores">
                                <td>{{props.item.cantidad}}</td>
                                <td>
                                    <h4>{{ props.item.plato }}</h4>
                                     <div class="">
                                         <h4><span class="primary--text">{{props.item.comentario}}</span></h4>
                                    </div>
                                </td>
                                <td>{{ props.item.nombreCompleto}}</td>
                                <td>{{ props.item.descripcion }}</td>
                                <td>
                                     <v-chip v-if="props.item.idEstadoPedido==1" color="primary" dark>{{props.item.descripcionEstado}}</v-chip>
                                     <v-chip v-if="props.item.idEstadoPedido==2" color="success" dark>{{props.item.descripcionEstado}}</v-chip>
                                </td>
                                <td>Tiempo Excedido</td>
                                <td>
                                    <v-btn  v-if="props.item.idEstadoPedido==1" class="mx-2" fab dark small color="primary" @click="preparacion(props.item)">P</v-btn>
                                    <v-btn  v-if="props.item.idEstadoPedido==2" class="mx-2" fab dark small color="error"   @click="despachar(props.item)">E</v-btn>
                                </td>
                              </tr>
                              <tr v-else class="coloresnormales">
                                <td>{{props.item.cantidad}}</td>
                                <td>
                                    <h4>{{ props.item.plato }}</h4>
                                     <div class="">
                                         <h4><span class="primary--text">{{props.item.comentario}}</span></h4>
                                    </div>
                                </td>
                                <td>{{ props.item.nombreCompleto}}</td>
                                <td>{{ props.item.descripcion }}</td>
                                <td>
                                     <v-chip v-if="props.item.idEstadoPedido==1" color="primary" dark>{{props.item.descripcionEstado}}</v-chip>
                                     <v-chip v-if="props.item.idEstadoPedido==2" color="success" dark>{{props.item.descripcionEstado}}</v-chip>
                                </td>
                                <td>{{props.item.time.substring(0,8)}}</td>
                                <td>
                                    <v-btn  v-if="props.item.idEstadoPedido==1" class="mx-2" fab dark small color="primary" @click="preparacion(props.item)">P</v-btn>
                                    <v-btn  v-if="props.item.idEstadoPedido==2" class="mx-2" fab dark small color="error"   @click="despachar(props.item)">E</v-btn>
                                </td>
                              </tr>
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3> No hay productos agregados al detalle.</h3>
                                </span>
                            </template>
                        </v-data-table>
                    </v-card-text>
                   <v-divider></v-divider>
                 </v-card>
               </v-flex>
          </v-layout>
        </v-container>     
      </v-card>
    </v-flex>
  </v-layout>
</template>
<script>
import timerp from './Timer.vue'
import axios from 'axios'
import moment from 'moment';
moment.locale('es');

export default {
    data() {
        return {
            time:0,
            count:0,
            cocina:[],
            iddetalle:'',
            headers: [
            { text: 'CANT', value: 'cant',sortable: false },
            { text: 'PRODUCTO', value: 'producto' },
            { text: 'MOZO', value: 'mozo',sortable: false  },
            { text: 'PEDIDO', value: 'pedido', sortable: false  },
            { text: 'ESTADO', value: 'estado', sortable: false  },
            { text: 'TIMER', value: 'timer', sortable: false  },
            { text: 'OPCIONES', value: 'opciones', sortable: false  }                
         ],
         isRojo: true,
       }
    },
    mounted() {
      this.time_loop();
    },
    components:{
       timerp
    },
    filters: {
        uppercase: function(str){
            return str.toUpperCase()
        },
        lowercase:function(str){
            return str.toLowerCase()
        },
        capitalize: function(str){
            return str.charAt(0).toUpperCase() + str.slice(1)
        }
     },
    computed: {
            formTitle () {
            return this.editedIndex === -1 ? 'DETALLE PLATO' : 'EDITAR DETALLE PLATO'
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
           this.listarCocina();
        },
        methods:{
          time_loop(){
            this.listarCocina()
            setTimeout(this.time_loop,1000)         
          },
          since: function(d){
              return moment(d).fromNow();    
          },
          listarCocina() {
                let me=this;
                axios.get('api/DetallePedidos/ListarFrios').then(function(response){
                    me.cocina=response.data;
                }).catch(function(error){
                    console.log(error);
                });
          },
          preparacion(item){
                let me = this;
                this.iddetalle= item.idDetallePedido;
                axios.put('api/DetallePedidos/ActualizarPreparacion/'+this.iddetalle,{}).then(function(response){
                   me.listarCocina();
                }).catch(function(error){
                    alert(console.log(error));
                });
          },
          despachar(item){
                let me = this;
                this.iddetalle= item.idDetallePedido;
                axios.put('api/DetallePedidos/ActualizarDespachoFrios/'+this.iddetalle,{}).then(function(response){
                   me.listarCocina();
                }).catch(function(error){
                    alert(console.log(error));
                });
          }
      }
}
</script>

<style>
  .colores{
    background-color: #FF5252;
  }
  .coloresnormales{
    background-color: #fff;
  }
  .coloramarillo{
    background-color: #ffc107;
  }
  .colores:hover{
    background-color: #FF5252 !important;
    color: white;
  }
  .coloresnormales:hover{
    background-color: #fff !important;
  }
  .coloramarillo:hover{
    background-color: #ffc107 !important;
    /* font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif; */
    color: white;
  }
</style>