                            <v-select :items="tipocomprobante" v-model="idComprobante" chips prepend-icon="assignment" label="TIPO DOCUMENTO">
                            </v-select>
                        </v-flex>
                        <v-flex xs5 sm5 md5 lg5 xl5>
                            <v-text-field append-icon="search" placeholder="..................." v-model="searchventa"  class="text-xs-center vs"  label="Busqueda">
                            </v-text-field>
                        </v-flex>
                        <v-flex xs2 sm2 md2 lg2 xl2>
                            <v-btn round color="primary" dark @click="listarBoleta(idComprobante, searchventa)" class="bus">Buscar</v-btn>
                        </v-flex>
                    </v-layout>
                </v-flex>
                    <!-- <v-text-field v-model="search" append-icon="search" @keyup.enter="listarBoleta" type="number">
                    </v-text-field> -->
            </template>
      </v-toolbar>
      <v-dialog v-model="adModal" max-width="450" persistent>
            <v-card>
                <v-card-title class="headline info"  style="color: white;"><h4>IMPRIMIR COMPROBANTE</h4></v-card-title>
                <v-card-text>
                    <h2>¬øELEGIR PARA IMPRIMIR?</h2>
                </v-card-text>
                <v-card-actions>
                    <v-btn color="success" dark @click="verificarImpresion(1)">
                        <v-icon>append-icon</v-icon>
                        Imprmir A4</v-btn>
                    <v-btn color="error" dark @click="verificarImpresion(2)">Imprmir Ticket</v-btn>
                    <v-btn  color="error" dark @click="cerrarAnulacion()">Cancelar</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
      <v-card>
        <v-container fluid grid-list-md >
          <v-layout row wrap>
                <v-flex xs12>
                 <v-card>
                    <v-card-title class="error" style="color: white;"><h3>LISTADO</h3></v-card-title>
                    <v-card-text>
                        <v-data-table
                            :headers="headers"
                            :items="listado"
                            show-select="true"
                            class="elevation-1">
                            <template slot="items" slot-scope="props">
                                <td>{{props.item.idVenta}}</td>
                                <td>
                                    {{props.item.comprobante}}
                                </td>
                                <td>{{ props.item.serie}}</td>
                                <td>{{ props.item.correlativo }}</td>
                                <td>{{ since(props.item.fecha) }}</td>
                                <td>{{ props.item.hora }}</td>
                                <td>{{ props.item.cliente }}</td>
                                <td>{{ props.item.descripcion }}</td>
                                <td>{{ props.item.numeroDoc }}</td>
                                <td>S/.{{ props.item.totalPagar }}</td>
                                <td>
                                    <v-icon  color="red darken-2" small dark @click="abrirModal(props.item)">print</v-icon>
                                </td>
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3> No hay comprobantes.</h3>
                                </span>
                            </template>
                        </v-data-table>
                    </v-card-text>
                   <v-divider></v-divider>
                 </v-card>
               </v-flex>
          </v-layout>
        </v-container>     
      </v-card>
    </v-flex>
  </v-layout>
</template>
<script>
