                    Medida: data[i].medida,
                    Producto: data[i].descripcion,
                    Descripcion: data[i].descripcion,
                    Precio: data[i].mtoValorUnitario.toFixed(2),
                    Descuento: data[i].descuento,
                    SubTotal: subtotal.toFixed(2)
                })
            }
        },
