                        {
                            text: '----------------------------------------------------------------',alignment: 'center', fontSize: 6, bold: true, margin: [0, 5, 0, 2]
                        },
                        {text: 'SON ' + convertido + '/100 SOLES', fontSize:6, bold:true, margin:[2,2,0,2]},
                        {
                            text: 'OP. GRATUITAS    S/.     0.00',alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'OP. EXONERADA    S/.     '+ me.listado[0].totalPagar.toFixed(2),alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'OP. INEFACTA     S/.     0.00',alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'OP. GRAVADA      S/.     0.00',alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'IGV              S/.     0.00',alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'OP. TOTAL        S/.     '+me.listado[0].totalPagar.toFixed(2),alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'EFECTIVO         S/.     '+ me.listado[0].paga.toFixed(2),alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'VUELTO           S/. '+ me.listado[0].vuelto.toFixed(2),alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        [
                            {
                                columns:[
                                    {qr:qr, fit:'70', margin: [3, 3, 2, 3]},
                                    {text: 'Representaci√≥n impresa del comprobante electronico. Puede consultarlo en www.futura-electric.com/comprobante',alignment: 'center', fontSize: 7, bold: true, margin: [0, 3, 0, 2]},
                                ]
                            }
                        ],
                        {
                            text: 'Sistema Inform√°tico Desarrollado por Lervisoft - 2020',alignment: 'center', fontSize: 7, bold: true, margin: [0, 5, 0, 2]
                        },
                    ],
                };
                pdfMake.createPdf(docDefinition).print();
                me.ticketArray = [];
                me.arrayFinal = [];
                me.adModal = 0;
                me.idV = '';
                me.serie = '';
                me.correlativo = '';
                me.usuarioP = '';
                me.fecha = '';
                me.horaP = '';
                me.numDoc = '';
                me.total = '';
                me.comprobante = '';
                me.direc = '';
                /* me.listado = [];
                me.search = ''; */
        },
        arrayTicket(data){
            var subtotal = 0.0,p=0.0;
            for(var i = 0; i< data.length; i++){
            subtotal = (Number(data[i].mtoValorUnitario) * Number(data[i].cantidad)) - data[i].descuento;
                this.ticketArray.push({
                    Can: data[i].cantidad,
                    Cod: data[i].idProducto,
                    Med: data[i].medida,
                    Nombre: data[i].descripcion,
                    Des: data[i].descuento,
                    Prec: data[i].mtoValorUnitario,
                    SubTo: subtotal.toFixed(2)
                })
            }
        },
        abrirModal(item){
            this.adModal = 1;
            this.idV = item.idVenta;
            this.serie = item.serie;
            this.correlativo = item.correlativo;
            this.usuarioP = item.cliente;
            this.fechaP = this.since(item.fecha);
            this.horaP = item.hora;
            this.numDoc = item.numeroDoc;
            this.total = item.totalPagar.toFixed(2);
            this.comprobante = item.comprobante;
            this.direc = item.direccion;
        },
          cerrarAnulacion(){
            this.adModal = 0;
            this.error=null;
          },
          verificarImpresion(parametro){
              let me = this;
              me.enviar = [];
              axios.get('api/FacturasSunats/DetalleVentaFacturas/' + this.idV).then(function(response){
                  me.enviar = response.data;
                  if( parametro == 1){
                      me.pdfG(me.enviar);
                      return;
                  }else{
                      me.ticket(me.enviar)
                  }
              }).catch(function(errror){

                });
                /* axios.put('api/VentaProductos/Imprimir/'+id).then(function(response){
                }).catch(function(errror){

                }); */
          },
          since: function(d){
              return moment(d).format('L');  
           },
          listarBoleta(id, corre) {
              this.listado =[];
              if(this.searchventa.length > 0){
                let me=this;
                axios.get('api/VentaProductos/ListaVentaAnulacion/' + id +'/'+ corre).then(function(response){
                    me.listado = response.data;
                    me.validarQr(me.listado[0].numeroDoc);
                }).catch(function(error){
                    //this.$swal('Advertencia.', 'Debe aperturar caja, para proceder abrir mesa.', 'error');
                });
              }else{
                  this.searchventa='';
                  this.listado = [];
                  this.id ='';
              }
          },
          chequeo(){

          },
          validarQr(doc){
              let me = this;
              if(doc.length === 11){
                me.tipodocCli = '06';
                me.tipoComprobanteId = '01'
            }else if(doc.length === 8){
                me.tipodocCli = '01';
                me.tipoComprobanteId = '03'
            }else{
                return;
            }
          },
          pdfG(detalles = []){
            var qr = '';
            let me = this;
            qr = '20603507399|'+me.tipoComprobanteId+'|'+me.listado[0].serie+'|'+me.listado[0].correlativo+'|'+me.listado[0].fecha.substring(0,10)+'|'+'0.00'+'|'+me.total+'|'+me.tipodocCli+'|'+me.listado[0].numeroDoc;
            var convertido = numeroALetras(me.total, {
                plural: 'SOLES',
                singular: 'SOL',
                centPlural: 'CENTIMOS',
                centSingular: 'CENTIMOS'
            });
            if( detalles.length == 0){
                return;
            }
            me.crearArray(detalles);
            function buildTableBody(data, columns){
                    var body = [];
                    body.push(columns);
                    data.forEach(function(row){
                        var dataRow = [];
                        columns.forEach(function(column) {
                            dataRow.push(row[column].toString());
                        })
                        body.push(dataRow);
                    });
                    return body;
                };
    
                function tablelleno(data, columns) {
                    return {
                    alignment:'center',
                    width:'auto',
                    fontSize: 8,
                        table: {
                            style: 'tableHeader',
                            widths:[40,35,45,175,55,45,'*'],
                            headerRows: 1,
                            body:buildTableBody(data, columns)
                        },
                        layout: 'headerLineOnly',
                    };
                }
                var docDefinition = {
                    pageSize:'A4',
                    pageMargins: [40, 35, 40, 110],
                    content: [
                        {
                        columns:[
                            {
                            image:'data:image/jpeg;base64,/9j/4AAQSkZJRgABAQEAYABgAAD/       2wBDAAIBAQIBAQICAgICAgICAwUDAwMDAwYEBAMFBwYHBwcGBwcICQsJCAgKCAcHCg0KCgsMDAwMBwkODw0MDgsMDAz/ ',
                            alignment: 'left',
                            fit:[130,130]
                            },
                            [
                            {text: 'SELVATEC S.R.L.',alignment: 'center', fontSize: 16, bold: true, margin: [0, 3, 0, 3]},
                            {text: 'RUC 20603507399',alignment: 'center', fontSize: 10, bold: true, margin: [0, 3, 0, 3]},
                            {text: 'Calle Moore 1094/ 9 de Diciembre - Iquitos',alignment: 'center', fontSize: 9, bold: true, margin: [0, 3, 0, 3]},
                            ],
                            [
                                {text: ''+me.comprobante,alignment: 'right', fontSize: 13, bold: true, margin: [0, 3, 0, 3]},
                                {text: 'N¬∞ ' +me.listado[0].serie+' - ' + me.correlativo,alignment: 'right', fontSize: 13, bold: true, margin: [0, 3, 0, 3]},
                            ]
                        ],
                        },{
                            columns:[
                            {text: 'Cliente: ' + me.usuarioP, fontSize: 9, bold: true, margin: [0, 17, 0, 5]},
                            {text: 'RUC / DNI: '+ me.numDoc,alignment: 'center', fontSize: 9, bold: true, margin: [0, 17, 0, 5]},
                            ],
                        },{
                            columns:[
                            {text: 'Fecha: '+ me.fechaP, fontSize: 9, bold: true, margin: [0, 5, 0, 5]},
                            {text: 'Vendedor: '+me.$store.state.nombreUsuario.NombreCompleto,alignment: 'center', fontSize: 9, bold: true, margin: [0, 5, 0, 5]},
                        ],
                        },
                        {
                            columns:[
                            {text: 'Direcci√≥n: '+ me.direc, fontSize: 9, bold: true, margin: [0, 5, 0, 15]},
                        ],
                        },
                        tablelleno(me.arrayFinal, ['Cantidad','Codigo','Medida','Producto','Precio', 'Descuento','SubTotal'])
                        
                    ],
                    footer:{
                    margin:[40,0, 40, 15],
                    heigth:80,
                    columns:[
                        [
                        {qr:qr, fit:'100'},
                        ],
                        [
                        {text: 'SON ' + convertido + '/100 SOLES',alignment:'center', fontSize:8, bold:true, margin:[0,2,0,2]},
                        {text:'Representaci√≥n impresa del comprobante electronico',alignment:'center', fontSize:8, bold:true, margin:[0,2,0,2]},
                        {text: 'Puede ser consultado en: www.futura-electric.com/comprobante',alignment:'center', fontSize:8, bold:false, margin:[0,2,0,2]},
                        {text: 'Autorizado por RS. 203-2015/SUNAT',alignment:'center', fontSize:8, bold:true, margin:[0,2,0,2]},
                        {text: 'Desarrollado por LERVISOFT - 2019',alignment:'center', fontSize:8, bold:true, margin:[0,2,0,2]}
                        ],
                        [
                        {text: 'OP. GRATUITAS S/.    00.00',alignment:'right', fontSize:7, bold:true, margin:[0,2,0,2]},
                        {text: 'OP. EXONERADA S/.    '+me.listado[0].totalPagar.toFixed(2),alignment:'right', fontSize:7, bold:true, margin:[0,2,0,2]},
                        {text: 'OP. INAFECTA S/.    00.00',alignment:'right', fontSize:7, bold:true, margin:[0,2,0,2]},
                        {text: 'OP. GRAVADA S/.     00.00',alignment:'right', fontSize:7, bold:true, margin:[0,2,0,2]},
                        {text: 'IGV S/.    00.00',alignment:'right', fontSize:7, bold:true, margin:[0,2,0,2]},
                        {text: 'IMPORTE TOTAL S/:   '+ me.listado[0].totalPagar.toFixed(2),alignment:'right', fontSize:7, bold:true, margin:[0,2,0,2]},
                        {text: 'Efectivo S/.    ' + me.listado[0].paga.toFixed(2),alignment:'right', fontSize:7, bold:true, margin:[0,2,0,2]},
                        {text: 'Vuelto S/.    ' + me.listado[0].vuelto.toFixed(2),alignment:'right', fontSize:7, bold:true, margin:[0,2,0,2]},
                        ]
                    ]
                    }
                };
                pdfMake.createPdf(docDefinition).print();
                detalles = [];
                me.arrayFinal = [];
                me.adModal = 0;
                me.idV = '';
                me.serie = '';
                me.correlativo = '';
                me.usuarioP = '';
                me.fecha = '';
                me.horaP = '';
                me.numDoc = '';
                me.total = '';
                me.comprobante = '';
                me.direc = '';
                me.tipodocCli = '';
                me.tipoComprobanteId  = ''
        },
        selectComprobante(){
            let me=this;
            var comprobanteArray=[];
            axios.get('api/TipoComprobantes/Listar').then(function(response){
                comprobanteArray=response.data;
                comprobanteArray.map(function(x){
                    me.tipocomprobante.push({text: x.comprobante,value:x.idComprobante});
                    me.idComprobante=1;
                });
            }).catch(function(error){
                console.log(error);
            });
        },
      }
}
</script>

<style>
    .ma{
        padding-top: 3.5%;
        padding-left: 2%;
    }
</style>