<template>
  <v-layout justify-center>
    <v-flex xs12>
      <v-toolbar color="white" >
        <v-toolbar-title>PLATOS ANULADOS</v-toolbar-title>
        <v-spacer></v-spacer>
            <v-menu
                v-model="menu1"
                :close-on-content-click="false"
                :nudge-right="40"
                lazy
                transition="scale-transition"
                offset-y
                full-width
                min-width="290px"
                >
                <template v-slot:activator="{ on }">
                <v-text-field
                    v-model="fecha"
                    label="FECHA"
                    prepend-icon="event"
                    readonly
                    v-on="on"
                ></v-text-field>
                </template>
                <v-date-picker color="green lighten-1" v-model="fecha" @input="menu1 = false"></v-date-picker>
            </v-menu>
            <v-spacer></v-spacer>
            <v-btn color="info" @click="listarPlatosAnuladas"><v-icon>search</v-icon>Buscar</v-btn>
      </v-toolbar>
    <v-dialog v-model="verDetalles" max-width="900px" persistent> 
        <v-card>
            <v-card-title class="primary" style="color: white;"><h3>DETALLE VENTA</h3></v-card-title>
            <v-divider></v-divider>
            <v-card-text>
                <v-container grid-list-md>
                    <v-layout wrap>
                    <v-flex xs12 sm12 md12 lg12 xl12>
                        <template>
                            <v-data-table                           
                                :headers="headersDetalle"
                                :items="listadoDetalle"
                                :search="searching"
                                class="elevation-1">
                                    <template slot="items" slot-scope="props">
                                    <td>{{props.item.producto}}</td>
                                    <td>{{props.item.cantidad}}</td>
                                    <td>
                                        <v-chip color="orange" dark>S/. {{props.item.monto}}</v-chip>
                                    </td>
                                </template>
                                <template slot="no-data">
                                    <span class="red--text">
                                        <h3>No hay detalles para mostrar.</h3>
                                    </span>
                                </template>
                            </v-data-table> 
                        </template>
                    </v-flex>
                </v-layout>
            </v-container>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
                <v-btn dark color="warning darken" @click="ocultar()">
                    <v-icon>block</v-icon>
                    Cerrar
                </v-btn>
                <v-btn dark color="success darken" @click="platoCorrecta()">
                    <v-icon>check</v-icon>
                    Correcto
                </v-btn>
                <v-btn dark color="red darken" @click="platoIncorrecta()">
                    <v-icon>block</v-icon>
                    Incorrecto
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
      <v-card>
        <v-container fluid grid-list-md >
          <v-layout row wrap>
                <v-flex xs12>
                 <v-card>
                    <v-card-title class="error" style="color: white;"><h3>PLATOS ANULADOS</h3></v-card-title>
                    <v-card-text>
                        <v-data-table
                            :headers="headersPedidos"
                            :items="listado"
                            class="elevation-1">
                            <template slot="items" slot-scope="props" v-if="props.item.idVenta !=0">
                                <td>{{props.item.idAnulacion}}</td>
                                <td>{{props.item.producto}}</td>
                                <td>{{props.item.idVenta}}</td>
                                <td>
                                    {{props.item.usuario}}
                                </td>
                                <td>{{ since(props.item.fecha) }}</td>
                                <td>{{ props.item.hora }}</td>
                                <td>{{props.item.motivo}}</td>
                                <td>
                                    <v-icon
                                        small
                                        class="mr-2"
                                        color="success"
                                        @click="abrirDetalleTotal(props.item.idVenta,props.item.idAnulacion)"
                                        >
                                        edit
                                    </v-icon>
                                </td>
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                   <h3> No hay platos anulados.</h3>
                                </span>
                            </template>
                        </v-data-table>
                    </v-card-text>
                   <v-divider></v-divider>
                 </v-card>
               </v-flex>
          </v-layout>
          <v-snackbar
            v-model="snackbar"
            >
            {{ mensajeToast }}
            <v-btn
                color="pink"
                flat
                @click="snackbar = false"
            >
                Cerrar
            </v-btn>
            </v-snackbar>
        </v-container>     
      </v-card>
    </v-flex>
  </v-layout>
</template>
<script>
import axios from 'axios'
import moment from 'moment';
moment.locale('es');

export default {
    data() {
        return {
        listado:[],
        idAnulado:'',
        search: '',
        verDetalles: 0,
        searching: '',
        headersPedidos: [
            { text: 'CODIGO', value: 'idanulacion',sortable: false },
            { text: 'PRODUCTO/PLATOS', value: 'producto', sortable: false  },     
            { text: 'COD. VENTA', value: 'idVenta', sortable: false  }, 
            { text: 'USUARIO ANULA', value: 'usuario' },
            { text: 'FECHA', value: 'fecha', sortable: false  },
            { text: 'HORA', value: 'hora', sortable: false  }, 
            { text: 'MOTIVO', value: 'motivo', sortable: false  },   
            { text: 'OPCIONES', value: '#', sortable: false  },             
         ],
         headersDetalle: [
            { text: 'PRODUCTO', value: 'producto' },
            { text: 'CANTIDAD', value: 'cantidad', sortable: false},     
            { text: 'MONTO', value: 'monto',sortable: false},            
         ],
         error: null,
         mensajeToast:'',
         snackbar:false,
         id:'',
         fecha: '',
         menu1: null,
         listadoDetalle:[]
       }
    },
    mounted() {
    },
    components:{
    },
    filters: {
        uppercase: function(str){
            return str.toUpperCase()
        },
        lowercase:function(str){
            return str.toLowerCase()
        },
        capitalize: function(str){
            return str.charAt(0).toUpperCase() + str.slice(1)
        }
     },
    computed: {
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            
        },
        methods:{
          since: function(d){
              return moment(d).format('L');  
          },
          cerrarAnulacion(){
            this.anular = 0;
          },
          listarPlatosAnuladas() {
              let me = this;
              if(me.fecha ==''){
                  this.$swal('ADVERTENCIA.', 'Debe selecionar las fechas', 'error')
              }else{
                  axios.get('api/AnularDetallePedidos/DetallesProductosAnulados/'+me.fecha).then(function(response){
                      me.listado = response.data;
                  }).catch(function(error){
                  });
              }
          },
          platoCorrecta() {
              let me = this;
                axios.put('api/AnularDetallePedidos/Correcto/'+me.idAnulado).then(function(response){
                    me.listado= [];
                    me.id = 0;
                    me.idAnulado = 0;
                    me.snackbar = true;
                    me.mensajeToast = 'Plato Anulada Correctamente';
                    me.verDetalles = 0;
                    me.listarBoletaAnuladas(me.fecha);
                }).catch(function(error){
                    this.$swal('ADVERTENCIA.', 'Hubo un error al realizar el proceso', 'error')
                });
          },
          platoIncorrecta(){
              let me = this;
                axios.put('api/AnularDetallePedidos/Incorrecto/'+me.idAnulado).then(function(response){
                    me.listado= [];
                    me.id = 0;
                    me.idAnulado = 0;
                    me.snackbar = true;
                    me.mensajeToast = 'Plato Anulada Correctamente';
                    me.verDetalles = 0;
                    me.listarBoletaAnuladas(me.fecha);
                }).catch(function(error){
                    this.$swal('ADVERTENCIA.', 'Hubo un error al realizar el proceso', 'error')
                });
          },
          abrirDetalleTotal(id, anulado){
              let me = this;
              me.id = id;
              me.idAnulado = anulado;
              axios.get('api/VentaProductos/DetalleVenta/'+ me.id).then(function(response){
                  me.listadoDetalle = response.data;
                  me.verDetalles = 1;
              })
          },
          ocultar(){
              this.verDetalles = 0;
              this.searching = '';
              this.id = 0;
          }
      }
}
</script>