<template>
    <v-layout justify-center>
        <v-flex xs12>
            <v-toolbar color="white">
                <v-toolbar-title>VENTA DE ARTICULOS</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-btn color="brown" dark @click="abrirProductos()" v-if="verNuevo==0">
                    BUSCAR PRODUCTO<v-icon dark >search</v-icon>
                </v-btn>
                <v-btn color="green" dark @click="abrirBVenta()" v-if="verNuevo==0">
                    BUSCAR VENTA<v-icon dark >search</v-icon>
                </v-btn>

                <v-btn color="primary" dark  v-if="verNuevo==0" @click="abrirProforma()">
                    BUSCAR PROFORMA<v-icon dark >search</v-icon>
                </v-btn>
                <v-dialog v-model="modalNumeroProforma" max-width="300" persistent>
                    <v-card>
                        <v-card-title class="headline info"  style="color: white;"><h4>BUSCAR PROFORMA</h4></v-card-title>
                        <v-card-text>
                            <v-flex xs12 sm12 md6 lg12 xl12>
                                <v-text-field placeholder="0" v-model="codproforma" label="COD PROFROMA" type="number">
                                </v-text-field>
                            </v-flex>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-btn  color="error" dark @click="cerrarProforma()">Cerrar</v-btn>
                            <v-btn  color="info" dark @click="buscarProforma()">Buscar</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            
            <v-dialog v-model="verClientes" max-width="1150px" persistent>
                    <v-card>
                        <v-card-title class="primary" style="color: white;"><h3>LISTA DE CLIENTES</h3></v-card-title>
                        <v-divider></v-divider>
                        <v-card-text>
                            <v-container grid-list-md>
                            <v-layout wrap>
                                <v-flex xs12 sm12 md12 lg12 xl12>
                                    <v-text-field append-icon="search" placeholder="..................." v-model="searchbuscar" class="text-xs-center"  label="Busqueda" >
                                    </v-text-field>
                                    <template>
                                        <v-data-table                           
                                            :headers="cabeceraClientes"
                                            :items="cliente"
                                            :search="searchbuscar"
                                            class="elevation-1">
                                            <template slot="items" slot-scope="props">
                                                <td >
                                                    <v-icon
                                                    color="red darken-2"
                                                    small
                                                    @click="clienteAdd(props.item)"
                                                    class="mr-2">
                                                    add
                                                    </v-icon>
                                                </td>
                                                <td>{{props.item.idPersona}}</td>
                                                <td>{{props.item.numeroDocumento}}</td>
                                                <td>{{props.item.razonSocial}}</td>
                                                <td>{{props.item.direccion}}</td>
                                            </template>
                                            <template slot="no-data">
                                                <span class="red--text">
                                                    <h3>No hay clientes para mostrar.</h3>
                                                </span>
                                            </template>
                                        </v-data-table> 
                                    </template>
                                </v-flex>
                            </v-layout>
                        </v-container>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-btn dark color="red darken" @click="cerrarClientes()">
                                Cerrar
                            </v-btn>
                            <v-btn dark color="primary darken" @click="abrirNuevoCliente()">
                                    Nuevo
                            </v-btn>
                        </v-card-actions>
                    </v-card>
            </v-dialog>
            <v-dialog v-model="nuevoCliente" max-width="750px" persistent>
                        <v-card>
                            <v-card-title class="primary" style="color: white;">
                               <h3>NUEVO CLIENTE</h3>
                            </v-card-title>
                            <v-card-text>
                             <v-container grid-list-md>
                                <v-layout wrap>
                                    <v-flex xs6 sm6 md6>
                                        <v-select label="TIPO" v-model="tipoDoc" :items="tdocumento" prepend-icon="assignment"></v-select>
                                   </v-flex>
                                   <v-flex xs6 sm6 md6> 
                                        <v-text-field  type="number" v-model="numeroDocumentoReniec" label="NUMERO(RUC,DNI)" prepend-icon="looks_3" @keyup.enter="consultasDocuments()"></v-text-field>
                                   </v-flex>
                                   <v-flex xs12 sm16 md12> 
                                        <v-text-field  type="text" v-model="razonSocialReniec" label="RAZON SOCIAL"  prepend-icon="how_to_reg" ></v-text-field>
                                   </v-flex>
                                    <v-flex xs12 sm16 md12> 
                                        <v-text-field v-model="direccionReniec"  type="text" label="DIRECCION FISCAL"  prepend-icon="location_on"></v-text-field>
                                   </v-flex>
                                   <v-flex xs12 sm16 md12> 
                                        <v-text-field  v-model="estado" disabled type="text" label="ESTADO"  prepend-icon="assignment"></v-text-field>
                                   </v-flex>
                                    <v-flex xs6 sm6 md6>
                                        <v-text-field  type="number" v-model="telefono" label="CELULAR" prepend-icon="call" ></v-text-field>
                                    </v-flex>
                                    <v-flex xs6 sm6 md6> 
                                        <v-text-field  type="text" v-model="email" label="EMAIL" prepend-icon="contact_mail"></v-text-field>
                                   </v-flex>
                                    <v-flex xs12 sm12 md12 v-show="valida">
                                        <h4><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h4>
                                    </v-flex>
                                </v-layout>
                            </v-container>
                            </v-card-text>
                             <v-divider></v-divider>
                            <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="error" @click="cancelarCrearCliente()">Cancelar
                                <v-icon dark right>block</v-icon>
                            </v-btn>
                            <v-btn color="info" @click="guardarCliente()">Guardar
                                 <v-icon dark right>check_circle</v-icon>
                            </v-btn>
                            </v-card-actions>
                        </v-card>
                </v-dialog>
                <v-dialog v-model="verProductos" max-width="1250px" persistent>
                    <v-card>
                        <v-card-title class="primary" style="color: white;"><h3>LISTA DE PRODUCTOS - {{tipocliente}}</h3></v-card-title>
                        <v-divider></v-divider>
                        <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                <v-flex xs12 sm12 md12 lg12 xl12>
                                    <v-text-field append-icon="search" placeholder="..................." v-model="search" v-on:keyup="listarProductosporTipo(search)" class="text-xs-center"  label="Busqueda" >
                                    </v-text-field>
                                    <template>
                                        <v-data-table                           
                                            :headers="cabeceraProductos"
                                            :items="productos"
                                            :search="search"
                                            class="elevation-1">
                                                <template slot="items" slot-scope="props">
                                                <td>
                                                    <v-icon
                                                        color="red darken-2"
                                                        small
                                                        class="mr-2"  @click="agregarDetalle(props.item)">
                                                        add
                                                    </v-icon>
                                                </td>

                                                <td>{{props.item.codigo}}</td>
                                                <td>{{props.item.nombre}}</td>
                                                <td>{{props.item.categoria}}</td>
                                                <td>{{props.item.medida}}</td>
                                                <td>
                                                    <v-chip color="orange" dark>S/. {{props.item.precio.toFixed(2)}}</v-chip>
                                                </td>
                                                <td>
                                                    <v-chip color="primary" dark>{{props.item.stock}}</v-chip>
                                                </td>
                                            </template>
                                            <template slot="no-data">
                                                <span class="red--text">
                                                    <h3>No hay productos para mostrar.</h3>
                                                </span>
                                            </template>
                                        </v-data-table> 
                                    </template>
                                </v-flex>
                            </v-layout>
                        </v-container>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-btn dark color="red darken" @click="cerrarProductos()">
                                Cerrar
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
                <v-dialog v-model="buscarventa" max-width="1150px" persistent>
                    <v-card>
                        <v-card-title class="primary" style="color: white;"><h3>BUSCAR VENTAS</h3></v-card-title>
                        <v-divider></v-divider>
                        <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                <v-flex xs12 sm12 md12 lg12 xl12>
                                    <v-layout row wrap>   
                                        <v-flex xs5 sm5 md5 lg5 xl5>
                                            <v-select :items="tipocomprobante" v-model="idComprobante" chips prepend-icon="assignment" label="TIPO DOCUMENTO" @change="listaOrdenada(idComprobante)">
                                            </v-select>
                                        </v-flex>
                                        <v-flex xs5 sm5 md5 lg5 xl5>
                                            <v-text-field append-icon="search" placeholder="..................." v-model="searchventa"  class="text-xs-center vs"  label="Busqueda">
                                            </v-text-field>
                                        </v-flex>
                                        <v-flex xs2 sm2 md2 lg2 xl2>
                                            <v-btn round color="primary" dark @click="listraventasOk(idComprobante, searchventa)" class="bus">Buscar</v-btn>
                                        </v-flex>
                                    </v-layout>
                                    <template>
                                        <v-data-table                           
                                            :headers="cabeceraVentas"
                                            :items="ventasOk"
                                            class="elevation-1">
                                                <template slot="items" slot-scope="props">
                                                <td>
                                                    <v-icon
                                                        color="red darken-2"
                                                        small
                                                        class="mr-2"  @click="agregarDetalleVenta(props.item)">
                                                        add
                                                    </v-icon>
                                                </td>
                                                <td>{{props.item.idVenta}}</td>
                                                <td>{{props.item.documento}}</td>
                                                <td>{{props.item.serie}}</td>
                                                <td>{{props.item.correlativo}}</td>
                                                <td>{{props.item.razonSocial}}</td>
                                                <td>
                                                    <v-chip color="orange" dark>S/. {{props.item.total}}</v-chip>
                                                </td>
                                            </template>
                                            <template slot="no-data">
                                                <span class="red--text">
                                                    <h3>No hay productos para mostrar.</h3>
                                                </span>
                                            </template>
                                        </v-data-table> 
                                    </template>
                                </v-flex>
                            </v-layout>
                        </v-container>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-btn dark color="red darken" @click="cerrarBVenta()">
                                Cerrar
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
           
            </v-toolbar>
            <v-card>
                <v-container fluid grid-list-md  v-if="verNuevo==0">
                    <v-layout row wrap>
                        <v-flex xs12>
                        <v-card>
                            <v-card-title class="error" style="color: white;margin-top:3px"><h3>ITEM PRODUCTOS</h3></v-card-title>
                                <v-card-text>
                                      <v-container grid-list-md class="tabs">
                                       <v-layout>
                                             <v-flex xs6>
                                                <v-select :items="tipoc" @change="verTipo()" v-model="idTipoc" prepend-icon="assignment" label="TIPO CLIENTE">
                                                </v-select>
                                            </v-flex>
                                            <v-flex xs6>
                                                <v-text-field v-model="bcodBarras" type="number" v-on:keyup="buscarCodbarras()"  prepend-icon="looks_one" label="COD. BARRAS"></v-text-field>
                                            </v-flex>
                                        </v-layout>
                                      </v-container>
                                    <v-data-table
                                        :headers="cabeceraLista"
                                        :items="detalles"
                                        hide-actions
                                        class="elevation-1 margen">
                                        <template  slot="items" slot-scope="props">
                                            <td class="margen">
                                                <v-icon
                                                color="red darken-2"
                                                small  @click="eliminarDetalle(detalles,props.item)">
                                                delete
                                                </v-icon>
                                            </td>
                                            <td>{{ props.item.nombre }}</td>
                                            <td><v-text-field type="number" v-model="props.item.cantidad" class="margen"></v-text-field></td>
                                            <td>S/.{{ props.item.precioMinimo}}</td>
                                            <td><v-text-field type="number" @keyup.enter="validarPrecioMinimo(props.item)" class="margen" v-model="props.item.precio"></v-text-field></td>
                                            <td><v-text-field type="number" v-model="props.item.descuento" class="margen"></v-text-field></td>
                                            <td>S/. {{((props.item.cantidad * props.item.precio)-props.item.descuento).toFixed(2)}}</td>
                                        </template>
                                        <template slot="no-data">
                                            <span class="red--text">
                                                <h3> No hay productos agregados al detalle.</h3>
                                            </span>
                                        </template>
                                    </v-data-table>
                                    <v-flex class="text-xs-right">
                                        <h2>TOTAL VENTA: S/ {{subTotal=(calcularTotal).toFixed(2)}}</h2>
                                    </v-flex>
                                </v-card-text>
                            <v-divider></v-divider>
                          </v-card>
                         </v-flex>
                        <v-flex xs7>
                            <v-card>
                                <v-card-title><v-icon></v-icon><h3>INFORMACION CIERRE VENTA</h3></v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-container grid-list-md class="pegar">
                                            <v-layout wrap>
                                                <v-flex xs6>
                                                    <h3>
                                                    <v-select  prepend-icon="assignment"
                                                        :items="modalidadpago" v-model="idModalidadPago"
                                                        label="MODALIDAD" chips>
                                                    </v-select></h3>
                                                </v-flex>
                                                <v-flex xs6>
                                                    <h3>
                                                    <v-select :items="tipocomprobante" v-model="idComprobante" chips prepend-icon="assignment" label="TIPO DOCUMENTO">
                                                    </v-select></h3>
                                                </v-flex>  
                                                <v-flex>
                                                    <h3>
                                                    <v-text-field disabled type="number" v-model="numeroDocumento" prepend-icon="looks_one" label="DNI/RUC">
                                                    </v-text-field></h3>
                                                </v-flex>
                                                <v-btn  small fab dark color="teal" @click="mostrarClientes()">
                                                        <v-icon dark>list</v-icon>
                                                </v-btn>
                                                <v-flex xs12>
                                                    <h3>
                                                        <v-text-field disabled type="text" v-model="nombreCliente" prepend-icon="people" label="RAZON SOCIAL">
                                                        </v-text-field>
                                                    </h3>
                                                </v-flex>
                                                <v-flex xs6>
                                                    <h3>
                                                        <v-select  prepend-icon="assignment"
                                                            :items="tipopago" @change="limpiarEfectivo()" chips v-model="idTipoPago"
                                                            label="TIPO PAGO">
                                                        </v-select>
                                                    </h3>
                                                </v-flex>
                                                <v-flex xs6 v-if="idModalidadPago==1">
                                                    <h3>
                                                    <v-select :items="moneda"  v-model="idmoneda" chips prepend-icon="assignment" label="MONEDA">
                                                    </v-select></h3>
                                                </v-flex> 
                                            </v-layout>
                                        </v-container>
                                </v-card-text>
                            </v-card>
                        </v-flex>
                        <v-flex xs5>
                            <v-card>
                                <v-container fluid grid-list-lg class="cardcobrar">
                                    <v-layout row wrap>
                                        <v-flex xs6>
                                            <v-card color="info" dark>
                                                <v-card-title><h4>SUBTOTAL</h4></v-card-title>
                                                <v-card-text class="headline font-weight-bold" >
                                                     {{subtotal=(calcularTotal).toFixed(2)}} 
                                                </v-card-text>
                                            </v-card>
                                        </v-flex>

                                        <v-flex xs6>
                                            <v-card color="error" dark>
                                                <v-card-title><h4>DESCUENTO</h4></v-card-title>
                                                <v-card-text class="headline font-weight-bold" >
                                                   00.00
                                                </v-card-text>
                                            </v-card>
                                        </v-flex>
                                        <v-flex xs6>
                                            <v-card color="gray" dark>
                                                <v-card-title><h4>ICPBER</h4></v-card-title>
                                                <v-card-text class="headline font-weight-bold">
                                                     {{icbper.toFixed(2)}}
                                                </v-card-text>
                                            </v-card>
                                         </v-flex>
                                        <v-flex xs6>
                                            <v-card color="success" dark>
                                                <v-card-title><h4>TOTAL</h4></v-card-title>
                                                <v-card-text class="headline font-weight-bold">
                                                    {{totalVenta=((subtotal-des)+icbper).toFixed(2)}} 
                                                </v-card-text>
                                            </v-card>
                                        </v-flex>
                                        <v-flex xs6>
                                              <template v-if="idModalidadPago==1">
                                                <v-card v-if="idTipoPago==1">
                                                    <v-card-title><h4>EFECTIVO</h4></v-card-title>
                                                    <v-card-text class="headline font-weight-bold pegariii">
                                                        <v-text-field v-model="montoEfectivo" type="number" class="txt">
                                                        </v-text-field>
                                                    </v-card-text>
                                                </v-card>
                                              </template>
                                        </v-flex>
                                        <v-flex xs6>
                                              <template v-if="idModalidadPago==1">
                                                <v-card color="warning" dark v-if="idTipoPago==1" >
                                                    <v-card-title><h4>CAMBIO</h4></v-card-title>
                                                    <v-card-text class="headline font-weight-bold" v-if="montoEfectivo>0">
                                                        {{vuelto=(montoEfectivo-totalVenta).toFixed(2)}}
                                                    </v-card-text>
                                                    <v-card-text class="headline font-weight-bold" v-else>
                                                         0.00
                                                    </v-card-text>
                                                </v-card>
                                              </template>
                                        </v-flex>
                                    </v-layout>
                                </v-container>
                            </v-card>
                        </v-flex>
                        <v-flex xs12>
                            <v-card>
                            <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn color="red darken-1" dark large round class="mb-2" @click="pagarVenta(1)">
                                        REGISTRAR PAGO A4
                                        <v-icon class="alejar">shopping_basket</v-icon> 
                                        </v-btn>
                                    <v-btn color="green darken-1" dark large round class="mb-2" @click="pagarVenta(2)">
                                        REGISTRAR PAGO TICKET
                                        <v-icon class="alejar">shopping_cart</v-icon> 
                                        </v-btn>
                                </v-card-actions>
                            </v-card>
                    </v-flex>
                    </v-layout>
                </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>

<script>
import axios from 'axios';
import  numeroALetras from './numeroletra.js'
export default {
    data() {
        return {
            tipodocCli: '',
            tipoComprobanteId: '',
            direc: '',
            idVenta:0,
            nombreReniec:'',
            nuevoCliente:0,
            consultas: [],
            tipodedocumento: '',
            codproforma:'',
            modalNumeroProforma:0,
            verClientes:0,
            cliente:[],
            tipoDoc: '',
            numeroDocumentoReniec:'',
            razonSocialReniec:'',
            direccionReniec: '',
            estado:'',
            validaMensaje:[],
            valida:'',
            telefono:'',
            email:'',
            barra:[],
            bcodBarras:'',
            idTipoc:'',
            tipoc:[],
            vuelto:0.00,
            icbper:0,
            totalVenta:0,
            numeroDocumento:0,
            idCliente:0,
            des:0.00,
            montoEfectivo:0.00,
            nombreCliente:'',
            tipocomprobante:[],
            modalidadpago:[],
            tipopago:[],
            arrayFinal:[],
            moneda:[],
            ticketArray:[],
            tdocumento:[],
            idmoneda:0,
            buscarventa: 0,
            idTipoPago:0,
            idModalidadPago:0,
            idComprobante:0,
            errorProducto:null,
            verNuevo:0,
            subTotal:0,
            verProductos:0,
            search:'',
            searchbuscar:'',
            productos:[],
            listarDetalles:[],
            detalles:[],
            tipocliente:'',
            correlativoDefinitivo:'',
            serieFinal: '',
            searchventa: '',
            ventasOk: [],
            idMov: '',
            correrl:0,
            cabeceraLista: [
                    { text: '#', value: 'opciones',sortable: false},
                    { text: 'PRODUCTO', value: 'producto',sortable: false},
                    { text: 'CANTIDAD', value: 'cant',sortable: false},
                    { text: 'PRECIO MINIMO', value: '',sortable: false},
                    { text: 'PRECIO VENTA', value: 'precio',sortable: false},
                    { text: 'DESCUENTO', value: '',sortable: false },
                    { text: 'IMPORTE', value: 'total', sortable: false  },    
            ],
            cabeceraProductos: [
                    {text:  'SELECCIONAR', value: 'seleccionar', sortable: false },
                    { text: 'CODIGO', value: 'codigo',sortable: false},
                    { text: 'ARTICULO/PRODUCTOS', value: 'nombre',sortable: false},
                    { text: 'CATEGORIA', value: 'categoria',sortable: false },
                    { text: 'UNIDAD MEDIDA', value: 'medida' ,sortable: false},
                    { text: 'PRECIO VENTA', value: 'precioVenta' ,sortable: false},
                    { text: 'STOCK', value: 'stock', sortable: false  },    
            ],
            cabeceraClientes:[
                { text: 'SELECCIONAR', value: 'opcion',sortable: false},
                { text: '#', value: 'idPersona',sortable: false},
                { text: 'DOCUMENTO', value: 'numeroDocumento',sortable: false},
                { text: 'CLIENTE', value: 'razonSocial',sortable: false},
                { text: 'DIRECCION', value: 'direccion',sortable: false},
            ],
            cabeceraVentas:[
                { text: 'MARCAR', value: 'opcion',sortable: false},
                { text: '#', value: 'idVenta',sortable: false},
                { text: 'DOCUMENTO', value: 'documento',sortable: false},
                { text: 'SERIE', value: 'serie',sortable: false},
                { text: 'CORRELATIVO', value: 'correlativo',sortable: false},
                { text: 'CLIENTE/RAZON', value: 'razonSocial',sortable: false},
                { text: 'TOTAL', value: 'total',sortable: false},
            ]
        }
    },
    created(){
        //this.idTipoc=1;
        this.listarTipoCliente();
        this.tipocliente='PRECIO PUBLICO';
        //this.listaOrdenada(1);
        this.selectComprobante();
        this.selectModalidadPago();
        this.selecttipopago();
        this.selectMoneda();
        this.numeroDocumento='00000000';
        this.idCliente=9;
        this.nombreCliente='PUBLICO GENERAL';
        
    },
    computed: {
        calcularTotal:function(){
            var resultado=0.0;
            for(var i=0;i<this.detalles.length;i++){
                resultado=resultado+((this.detalles[i].precio*this.detalles[i].cantidad)-this.detalles[i].descuento);
            }
            return resultado;
        },
