        },
        clienteAdd(item){
            this.idCliente = item.idPersona;
            this.numeroDocumento = item.numeroDocumento;
            if(this.numeroDocumento.length==11)
            {
                this.idComprobante=2;
            }
            else if(this.numeroDocumento.length==8)
            {
                this.idComprobante=1;
            }
            this.nombreCliente = item.razonSocial;
            this.direc = item.direccion;
            this.verClientes=0;
            this.searchbuscar="";
        },
        listarTipoCliente(){
            let me=this;
            var TipocArray=[];
            axios.get('api/TipoClienteVentas').then(function(response){
                TipocArray=response.data;
                TipocArray.map(function(x){
                    me.tipoc.push({text: x.descripcion,value:x.idTipoClienteVenta});
                    me.idTipoc=1;
                   
                });
            }).catch(function(error){
                console.log(error);
            });
        },
        buscarProforma(){
            let me=this;
             axios.get('api/Proformas/ListarProformasEdicion/'+me.codproforma).then(function(response){

                me.idCliente = response.data[0].idCliente;
                me.nombreCliente = response.data[0].cliente;
                me.numeroDocumento = response.data[0].nurDocumento;
                me.direc = response.data[0].direccion;

                if(me.numeroDocumento.length==11){
                    me.idComprobante=2;
                }   
                if(me.numeroDocumento.length==8){
                     me.idComprobante=1;
                }
                axios.get('api/Proformas/DetalleProforma/'+me.codproforma).then(function(response){
                me.detalles=response.data;
                me.modalNumeroProforma=0;
                me.codproforma="";
                }).catch(function(error){
                    console.log(error);
                });
                    
            }).catch(function(error){
                console.log(error);
            });
        },
        selectMoneda(){
            let me=this;
            var monedadArray=[];
            axios.get('api/Monedas/Listar').then(function(response){
                monedadArray=response.data;
                monedadArray.map(function(x){
                    me.moneda.push({text: x.descripcion,value:x.idMoneda});
                    me.idmoneda=1;
                });
            }).catch(function(error){
                console.log(error);
            });
        },
        selectComprobante(){
            let me=this;
            var comprobanteArray=[];
            axios.get('api/TipoComprobantes/Listar').then(function(response){
                comprobanteArray=response.data;
                comprobanteArray.map(function(x){
                    me.tipocomprobante.push({text: x.comprobante,value:x.idComprobante});
                    me.idComprobante=1;
                });
            }).catch(function(error){
                console.log(error);
            });
        },
        cerrarBVenta(){
            this.buscarventa = 0;
        },
        selecttipopago(){
            let me=this;
            var tipoArray=[];
            axios.get('api/TipoPagoes/Listar').then(function(response){
                tipoArray=response.data;
                tipoArray.map(function(x){
                    me.tipopago.push({text: x.tipo,value:x.idTipoPago});
                    me.idTipoPago=1;
                });
            }).catch(function(error){
                console.log(error);
            });
        },
         selectTipo(){
            let me=this;
            var tipoArray=[];
            axios.get('api/TipoDocumento/Select').then(function(response){
                tipoArray=response.data;
                tipoArray.map(function(x){
                    me.tdocumento.push({text: x.descripcion,value:x.idTipoDocumneto});
                });
            }).catch(function(error){
                console.log(error);
            });
        },
        alertDisplayDNI() {
            this.$swal('ERROR', 'DNI no encontrado / No tiene conexion a internet...', 'error')
        },
        alertDisplayRUC() {
            this.$swal('ERROR', 'RUC no encontrado / No tiene conexion a internet...', 'error')
        },
        abrirNuevoCliente(){
            this.verClientes=0;
            this.nuevoCliente=1;
            this.selectTipo();
        },
        pagarVenta(id){
            if(this.idModalidadPago==1 && this.idTipoPago==1 && this.montoEfectivo==0){
                this.$swal('ADVERTENCIA', 'Ingrese monto efectivo.', 'warning');
                return;
            }
            if(this.idComprobante == 2  && this.numeroDocumento.length == 8 ){
                this.$swal('ADVERTENCIA', 'Debe buscar el cliente con RUC .', 'warning')
                return;
            }
            if(this.vuelto<0){
                this.$swal('ADVERTENCIA', 'El monto efectivo debe ser mayor al total a pagar .', 'warning')
                return;
            }
            if(this.detalles.length==0){
                this.$swal('ADVERTENCIA', 'No hay productos agregados al detalle.', 'warning')
                return;
            }
            let me=this;
            me.chequeoDocumento(id);
            axios.post('api/VentaProductos/Crear',{
                'idVenta':me.idVenta,
                'idUsuario': me.$store.state.nombreUsuario.IdUsuario,
                'idModalidadPago':me.idModalidadPago,
                'idComprobante':me.idComprobante,
                'idCliente': me.idCliente,
                'idTipoPago':me.idTipoPago,
                'idMoneda':me.idmoneda,
                'subtotal':me.subtotal,
                'descuento':me.des,
                'rc':0,
                'igv':0,
                'icbper':me.icbper,
                'totalPagar':me.totalVenta,
                'montoDolar':me.montoDolar,
                'montoEfectivo':me.montoEfectivo,
                'vuelto':me.vuelto,
                'tipoVenta': 'CAJA',
                'detalleVentas':me.detalles
            }).then(function(response){
                me.idMov = response.data;
                me.detalles=[];
                me.montoEfectivo=0;
                me.des=0;
                me.icbper=0;
                me.verNuevo=0;
                me.idVenta=0;
                me.nuevaVenta();
            }).catch(function(error){
                console.log(error);
            });
        },
        arrayTicket(data){
            var subtotal = 0.0,p=0.0;
            for(var i = 0; i< data.length; i++){
            subtotal = (Number(data[i].precio) * Number(data[i].cantidad)) - data[i].descuento;
                this.ticketArray.push({
                    Can: data[i].cantidad,
                    Cod: data[i].idProducto,
                    Med: data[i].medidas,
                    Nombre: data[i].nombre,
                    Des: data[i].descuento,
                    Prec: data[i].precio,
                    SubTo: subtotal.toFixed(2)
                })
            }
        },
        ticket(tipo, corre, serie, tipoCli, tipoCom){
            var fecha = new Date();
            var messs = fecha.getMonth() + 1;
            var nuevafecha = fecha.getDate() +'-'+ messs +'-'+fecha.getFullYear();
            var qr = '';
            let me = this;
            qr = '20603507399|'+tipoCom+'|'+serie+'|'+corre+'|'+nuevafecha+'|'+'0.00'+'|'+me.totalVenta+'|'+tipoCli+'|'+me.numeroDocumento;
            var convertido = numeroALetras(me.totalVenta, {
                plural: 'SOLES',
                singular: 'SOL',
                centPlural: 'CENTIMOS',
                centSingular: 'CENTIMOS'
            });
            if(me.detalles.length == 0){
                return;
            }
            var enviar = [];
            me.arrayTicket(me.detalles);
            function buildTableBody(data, columns){
                    var body = [];
                    body.push(columns);
                    data.forEach(function(row){
                        var dataRow = [];
                        columns.forEach(function(column) {
                            dataRow.push(row[column].toString());
                        })
                        body.push(dataRow);
                    });
                    return body;
                };
    
                function tablelleno(data, columns) {
                    return {
                    alignment:'center',
                    width:'auto',
                    bold: true,
                    fontSize: 5,
                        table: {
                            style: 'tableHeader',
                            margin: [5, 5, 5, 5],
                            widths:[10,10,32,13,18],
                            headerRows: 1,
                            layout: 'headerLineOnly',
                            body:buildTableBody(data, columns)
                        },
                    };
                }
                
                var mestodo =["Enero","Febrero","Marzo","Abril","Mayo","Junio","Julio","Agosto",
                "Septiembre","Otubre","Noviembre","Diciembre"];
                var diasemana =[
                "Domingo", "Lunes","Martes","Miercoles",
                "Jueves","Viernes","Sabado","Domingo"
                ];
                //Obtener fecha
                var fechahoy = diasemana[fecha.getDay()] + ", "+fecha.getDate() + " de " + mestodo[fecha.getMonth()]+
                " del " + fecha.getFullYear();
                var docDefinition = {
                    pageSize:{
                        width: 150,
                        height: 'auto'
                    },
                    /* pageOrientation:'landscape', */
                    pageMargins: [11, 11, 11, 11],
                    content: [
                        {
                        columns:[
                            {
                                image:'data:image/jpeg;base64,/9j/4AAQSkZJRgABAQEAYABgAAD/       2wBDAAIBAQIBAQICAgICAgICAwUDAwMDAwYEBAMFBwYHBwcGBwcICQsJCAgKCAcHCg0KCgsMDAwMBwkODw0MDgsMDAz/ ',
                                alignment: 'center',
                                fit:[110,110]
                            },
                        ],
                        },
                        {text: 'SELVATEC S.R.L.',alignment: 'center', fontSize: 10, bold: true, margin: [0, 2, 0, 3]},
                        {text: 'RUC 20603507399',alignment: 'center', fontSize: 8, bold: true, margin: [0, 2, 0, 3]},
                        ,{
                            columns:[
                                {text: 'Calle Moore 1094 / 9 de Diciembre - Iquitos',alignment: 'center', fontSize: 6, bold: true,width:'*', margin: [0, 3, 0, 3]},
                            ],
                        },
                        {text: ''+tipo,alignment: 'center', fontSize: 8, bold: true, margin: [0, 2, 0, 3], width:'*'},
                        {text: 'N¬∞ '+ serie + ' - ' + corre,alignment: 'center', fontSize: 8, bold: true, margin: [0, 2, 0, 3]},    
                        {
                            columns:[
                                {text: 'Cliente: '+ me.nombreCliente, fontSize: 6, bold: true, margin: [2, 3, 0, 2]},
                            {text: 'RUC / DNI: '+  me.numeroDocumento,alignment: 'center', fontSize: 6, bold: true, margin: [2, 3, 0, 2]},
                        ],
                        },
                        {
                            columns:[
                            {text: 'Direcci√≥n: '+ me.direc, fontSize: 6, bold: true, margin: [2, 3, 0, 2]},
                        ],
                        },
                        {
                            columns:[
                            {text: 'Fecha: '+ fechahoy, fontSize: 6, bold: true, margin: [2, 5, 0, 2]},
                            {text: 'Vendedor: '+me.$store.state.nombreUsuario.NombreCompleto,alignment: 'center', fontSize: 6, bold: true, margin: [2, 5, 0, 2]},
                        ],
                        },{
                            text: '----------------------------------------------------------------',alignment: 'center', fontSize: 6, bold: true, margin: [0, 5, 0, 2]
                        },
                            tablelleno(me.ticketArray, ['Can','Med','Nombre','Prec','SubTo']),
                        
                        {
                            text: '----------------------------------------------------------------',alignment: 'center', fontSize: 6, bold: true, margin: [0, 5, 0, 2]
                        },
                        {text: 'SON ' + convertido + '/100 SOLES', fontSize:6, bold:true, margin:[2,2,0,2]},
                        {
                            text: 'OP. GRATUITAS    S/.     0.00',alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'OP. EXONERADA    S/.     '+ me.totalVenta,alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'OP. INEFACTA     S/.     0.00',alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'OP. GRAVADA      S/.     0.00',alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'IGV              S/.     0.00',alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'OP. TOTAL        S/.     '+me.totalVenta,alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'EFECTIVO         S/.     '+ me.montoEfectivo,alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'VUELTO           S/. '+ me.vuelto,alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        [
                            {
                                columns:[
                                    {qr:qr, fit:'70', margin: [3, 3, 2, 3]},
                                    {text: 'Representaci√≥n impresa del comprobante electronico. Puede consultarlo en www.futura-electric.com/comprobante',alignment: 'center', fontSize: 7, bold: true, margin: [0, 3, 0, 2]},
                                ]
                            }
                        ],
                        {
                            text: 'Sistema Inform√°tico Desarrollado por Lervisoft - 2020',alignment: 'center', fontSize: 7, bold: true, margin: [0, 5, 0, 2]
                        },
                        
                    ]
                };
                var mensaje = 'Venta Registrada N¬∞'+ corre+' Con un total de ' + me.totalVenta;
                me.$swal('CONFORME',mensaje , 'success');
                pdfMake.createPdf(docDefinition).print();
                me.ticketArray =[];
                me.listarTipoCliente();
                me.selectComprobante();
                me.selectModalidadPago();
                me.selecttipopago();
                me.selectMoneda();
                me.numeroDocumento='000000000';
                me.idCliente=9;
                me.nombreCliente='PUBLICO GENERAL';
                me.arrayFinal = [];
                me.correrl=0;
                me.direc = '';
        },
    }
}
