<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>STOCK PRODUCTOS</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-select
                    @change="listar(filtro)"
                    :items="items"
                    v-model="filtro"
                    label="Filtro"
                    type="text"
                ></v-select>
                <v-spacer></v-spacer>
                <v-text-field class="text-xs-center vs" v-model="search" append-icon="search" label="B√∫squeda" single-line hide-details></v-text-field>
                <v-spacer></v-spacer>
                <v-snackbar
                  :color="color"
                  v-model="snackbar"
                 :timeout="timeout">
                 {{ text }}
                <v-btn
                    text
                    flat
                    @click="snackbar = false">
                    Cerrar
                </v-btn>
                </v-snackbar>
            </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="categorias"
                :search="search"
                class="elevation-1">
                <template slot="items" slot-scope="props">
                <td>{{ props.item.nombre }}</td>
                <td>{{ props.item.categoria }}</td>
                <td>{{ props.item.marca }}</td>
                <td>
                    <div v-if="filtro == ''">
                       <span>{{props.item.stock}}</span>
                    </div>
                    <div v-if="filtro == 'TODOS'">
                       <span class="green--text">{{props.item.stock}}</span>
                    </div>
                    <div v-if="filtro == 'STOCK MINIMO'">
                       <span class="red--text">{{props.item.stock}}</span>
                    </div>
                </td>

                </template>
                <template slot="no-data">
                    <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>
        </v-flex>
    </v-layout>
</template>
<script>
    import {misMixins} from './mixins.js'
    import {bus} from '../main'
    import axios from 'axios'
    
    export default {            
        data(){
            return {
                mixins:[misMixins],
                categorias:[],
                filtro: '',
                color: 'success',
                snackbar: false,
                text: 'REGISTRO GUARDADO EXITOSAMENTE.',
                timeout: 2000,
                dialog: false,
                headers: [
                    { text: 'NOMBRE', value: 'nombre'},
                    { text: 'CATEGORIA', value: 'categoria'},
                    { text: 'MARCA', value: 'marca'},
                    { text: 'ESTADO', value: 'estado', sortable: false }
               
                ],
                search: '',
                valida: 0,
                validaMensaje:[],
                adAccion:0,
                adNombre:'',
                adId:'',
                items: ['TODOS','STOCK MINIMO']
            }
        },
        computed: {
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            this.listar();
        },
        methods:{
            listar() {
                let me=this;
                me.categorias = [];
                if( me.filtro === '' || me.filtro === 'TODOS'){
                    let header={"Authorization" : "Bearer " + this.$store.state.token};
                    let configuracion= {headers : header};
                    axios.get('api/Productos/StockActualizado',configuracion).then(function(response){
                        me.categorias=response.data;
                        console.log(me.categorias);
                    }).catch(function(error){
                        console.log(error);
                    });
                }else if(me.filtro == 'STOCK MINIMO'){
                    let header={"Authorization" : "Bearer " + this.$store.state.token};
                    let configuracion= {headers : header};
                    axios.get('api/Productos/StockMinimo',configuracion).then(function(response){
                        me.categorias=response.data;
                        console.log(me.categorias);
                    }).catch(function(error){
                        console.log(error);
                    });
                }
            },
            close () {
                this.dialog = false;
                this.limpiar();
                this.valida=0;
            },
            limpiar(){
                this.search="";
            },
        }        
    }
</script>

<style>
    .vs{
        padding: 15px
    }
</style>