                    { text: 'CAJA', value: '',sortable: false  },
                    { text: 'FECHA', value: '', sortable: false  },
                    { text: 'HORA', value: '', sortable: false  },      
                    { text: 'MONTO', value: '', sortable: false  },                
                ],
                cabeceraPersonal: [
                    { text: 'TIPO', value: '',sortable: false },
                    { text: 'NOMBRE', value: '',sortable: false },
                    { text: 'TOTAL CREDITO PERSONAL', value: '',sortable: false  },             
                ],
                cabeceraTarjeta: [
                    { text: 'TIPO', value: '',sortable: false },
                    { text: 'CANTIDAD', value: '',sortable: false },
                    { text: 'TOTAL TARJETA', value: '',sortable: false  },             
                ],
                cabeceraClientes: [
                    { text: 'TIPO', value: '',sortable: false },
                    { text: 'NOMBRE', value: '',sortable: false },
                    { text: 'TOTAL CREDITO CLIENTES', value: '',sortable: false  },             
                ],
                cabeceraTarjetaMixto:[
                    { text: 'TIPO', value: '',sortable: false },
                    { text: 'CANTIDAD', value: '',sortable: false },
                    { text: 'TOTAL TARJETA', value: '',sortable: false  },         

                ]
            }
        },
        computed: {
            formTitle () {
                return this.editedIndex === -1 ? 'APERTURA CAJA' : 'EDITAR APERTURA CAJA';
            },
            calcularTotalTarjetas:function(){
                var resultado=0.0;
                for(var i=0;i<this.tarjeta.length;i++){
                    resultado=resultado+(this.tarjeta[i].totalTarjeta);
                }
                return resultado;
            },
            calcularTotalTarjetasMixtas:function(){
                var resultado=0.0;
                for(var i=0;i<this.pagoMixto.length;i++){
                    resultado=resultado+(this.pagoMixto[i].totalTarjetas);
                }
                return resultado;
            },
            calcularTotalCreditoPersonal:function(){
                var resultado=0.0;
                for(var i=0;i<this.personal.length;i++){
                    resultado=resultado+(this.personal[i].totalCreditoPersonal);
                }
                return resultado;
            },
            calcularTotalCreditoClientes:function(){
                var resultado=0.0;
                for(var i=0;i<this.clientes.length;i++){
                    resultado=resultado+(this.clientes[i].totalCreditoPersonal);
                }
                return resultado;
            },
            dolares100:function(){
                var resultado=0.0;
                resultado = this.d100 *100
                return resultado;
            }, 
            dolares50:function(){
                var resultado=0.0;
                resultado = this.d50 *50
                return resultado;
            }, 
            dolares20:function(){
                var resultado=0.0;
                resultado = this.d20 *20
                return resultado;
            }, 
            dolares10:function(){
                var resultado=0.0;
                resultado = this.d10 *10
                return resultado;
            }, 
            dolares5:function(){
                var resultado=0.0;
                resultado = this.d5 *5
                return resultado;
            }, 
            dolares1:function(){
                var resultado=0.0;
                resultado = this.d1 *1
                return resultado;
            },
            calcular200:function(){
                var resultado=0.0;
                resultado = this.c200 *200
                return resultado;
            },
            calcular100:function(){
                var resultado=0.0;
                resultado = this.c100 *100
                return resultado;
            },
            calcular50:function(){
                var resultado=0.0;
                resultado = this.c50 *50
                return resultado;
            },
            calcular20:function(){
                var resultado=0.0;
                resultado = this.c20 *20
                return resultado;
            },
            calcular10:function(){
                var resultado=0.0;
                resultado = this.c10 *10
                return resultado;
            },
            calcular5:function(){
                var resultado=0.0;
                resultado = this.c5 *5
                return resultado;
            },
            calcular2:function(){
                var resultado=0.0;
                resultado = this.c2 *2
                return resultado;
            },
            calcular1:function(){
                var resultado=0.0;
                resultado = this.c1 *1
                return resultado;
            },
           calcular050:function(){
                var resultado=0.0;
                resultado = this.c05 *0.5
                return resultado;
            },
            calcular020:function(){
                var resultado=0.0;
                resultado = this.c020 *0.2
                return resultado;
            },
            calcular010:function(){
                var resultado=0.0;
                resultado = this.c010 *0.1
                return resultado;
            },
            sumaDolares(){
                var resultado=0.0;
                resultado = this.dolares100 + this.dolares50 + this.dolares20 +this.dolares10 + this.dolares5 + this.dolares1  
                return resultado;
            },
            sumaSoles:function(){
                var resultado=0.0;
                resultado = this.calcular200 + this.calcular100 + this.calcular50 + this.calcular20 + this.calcular10 +
                            this.calcular5   + this.calcular2   + this.calcular1  + this.calcular050+ this.calcular020 + this.calcular010
                return resultado;
            }
        },
        watch: {
            
        },
        created () {
           this.listarAperturaCajaUsuario();
           this.cajaMaquina();
        },
        methods:{
            cerrar(){
               this.dialogPer=false;
               this.montoApertura="";
               this.valida=0;
            },
            listarAperturaCajaUsuario() {
                let me=this;
                axios.get('api/AperturaCajas/ListarAperturaCajaUsuario/'+me.$store.state.nombreUsuario.IdUsuario).then(function(response){
                   me.per=response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            alertDisplay() {
                this.$swal('Cancelled', 'Cajero no aperturo caja', 'error')
            },
            crearPDF(){
                var quotes = document.getElementById('factura');
                html2canvas(quotes).then(function (canvas) {
                    var imgData = canvas.toDataURL('image/png');
                    var imgWidth = 210;
                    var pageHeight = 295;
                    var imgHeight = canvas.height * imgWidth / canvas.width;
                    var heightLeft = imgHeight;
                    var doc = new jsPDF();
                    var position = 0;

                    doc.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);                    
                    doc.save('Liquidacion.pdf');
                });
            },
            liquidacionCaja(){
                
                let totalCredito;
                totalCredito = Number(this.totalTarjeta) + Number(this.totalTarjetasMixtas) + Number(this.totalCreditoPersonal) +Number(this.totalCreditoClientes);
            
                let me=this;
                axios.put('api/AperturaCajas/Actualizar',
                {
                    'idApertura': me.per[0].idApertura,
                    'montoEfectivo':me.totalSoles,
                    'montoDolares':me.totalDolares,
                    'montoDolaresSoles':me.conversionDolar,
                    'totalVentaCredito':totalCredito
                }).then(function(response){
                    me.reportePDF(me.per[0].idApertura);
                    me.verNuevo=0;
                    me.liquidacionCajaModel=1;
                    me.listarAperturaCajaUsuario();
                    me.alertCierreCaja();
                   
                }).catch(function(error){
                });
            },
            alertCierreCaja(){
               this.$swal('Exitoso', 'Caja cerrada con exito, proceda a salir del sistema, gracias.', 'success');
            },
            since: function(d){
              return moment(d).format('L');  
            },
            guardar(){
                let me = this;
                if(this.validar()){
                    return;
                }else{
                   axios.post('api/AperturaCajas/Crear',{
                       'idCaja':me.caja[0].idCaja,
                       'idUsuario':me.$store.state.nombreUsuario.IdUsuario,
                       'montoApertura':me.montoApertura
                   }).then(function(response){
                       me.listarAperturaCajaUsuario();
                       me.dialogPer=false;
                       me.montoApertura='';
                   }).catch(function(error){
                      console.log(error);
                   });
                }
            },
            informacionCajaVenta(ip){
              let me=this;
              axios.get('api/Equipos/ObtenerCaja/'+ip).then(function(response){
                me.caja=response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            ocultarComprobante(){
                this.liquidacionCajaModel=0;
            },
            cajaMaquina() {
                let me=this;
                axios.get('api/DetallePedidos/IPLocal').then(function(response){
                    me.ip=response.data;
                    me.informacionCajaVenta(me.ip);
                }).catch(function(error){
                    console.log(error);
                });
            },
            arqueoCaja(item){
                this.verNuevo=1;
                this.idApertura = item.idApertura;
                this.listarCreditoPersonal(this.idApertura);
                this.listarTotalTarjetas(this.idApertura);
                this.listarCreditoCliente(this.idApertura);
                this.listarPagoMixto(this.idApertura);
            },
            listarCreditoPersonal(id) {
                let me=this;
                axios.get('api/VentaProductos/ListarCreditosPersonal/'+id).then(function(response){
                   me.personal=response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            listarCreditoCliente(id) {
                let me=this;
                axios.get('api/VentaProductos/CreditoClientes/'+id).then(function(response){
                   me.clientes=response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            listarPagoMixto(id){
                let me=this;
                axios.get('api/PagoMixtos/ListarPagoMixtoTarjetas/'+id).then(function(response){
                   me.pagoMixto=response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            listarTotalTarjetas(id) {
                let me=this;
                 axios.get('api/VentaProductos/ListarTotalTarjetas/'+id).then(function(response){
                   me.tarjeta=response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            reportePDF(id) {
                let me=this;
                 axios.get('api/AperturaCajas/ListaInformacion/'+id).then(function(response){
                   me.arqueo=response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];
                if(this.montoApertura==0){
                    this.validaMensaje.push("El monto no debe ser 0.");
                }
                if(this.montoApertura.length==0 || this.montoApertura.length>100){
                    this.validaMensaje.push("Ingrese monto apertura.");
                }
                if(this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            }
        }       
    }
</script>

