                                                </v-icon>
                                            </td>
                                            <td>{{ props.item.nombre }}</td>
                                            <td><v-text-field type="number" v-model="props.item.cantidad" class="margen"></v-text-field></td>
                                            <td>{{ props.item.precioMinimo}}</td>
                                            <td><v-text-field type="number" @keyup.enter="validarPrecioMinimo(props.item)" class="margen" v-model="props.item.precio"></v-text-field></td>
