<script>
import axios from 'axios';
export default {
    data() {
        return {

            name: "Facturas",
            verboletas:0,
            fechaentrada:'',
            boletas:[],
            menu1:null,
            resumen:[],
            cabeceraLista: [
                { text: '#', value: 'opciones',sortable: false},
                { text: 'FECHA', value: '',sortable: false},
                { text: 'SERIE', value: '',sortable: false},
                { text: 'CORRELATIVO', value: '',sortable: false},
                { text: 'CLIENTE', value: '',sortable: false},
                { text: 'DOCUMENTO', value: '',sortable: false },
                { text: 'NUMERO DOCUMENTO', value: '', sortable: false  },    
                { text: 'TOTAL', value: '', sortable: false  },    
            ],
            cabeceraBoletas:[
                { text: '#', value: 'opciones',sortable: false},
                { text: 'FECHA', value: '',sortable: false},
                { text: 'SERIE', value: '',sortable: false},
                { text: 'CORRELATIVO', value: '',sortable: false},
                { text: 'CLIENTE', value: '',sortable: false},
                { text: 'DOCUMENTO', value: '',sortable: false },
                { text: 'NUMERO DOCUMENTO', value: '', sortable: false  },    
                { text: 'TOTAL', value: '', sortable: false  },    
            ]
        }
    },
    created(){
      
    },
    computed: {
        
    },
    methods: {
        listarFacturas(){
           
        },
        abrirBoletas(){
           this.$options.components.Facturas = require('./Facturas.vue').default
            //this.verboletas=1;
        },
        cerrarBoletas(){
            this.verboletas=0;
        },
        buscarBoletas(){
            let me =this;
            axios.get('api/BoletasSunats/EnvioBoletas/'+this.fechaentrada).then(function(response){
                me.boletas=response.data;
            }).catch(function(error){
                console.log(error);
            });
        },
        enviarSunat(){
           let resumen = {
                'correlativo': '2',
                'fecGeneracion':'2019-12-07T00:00:00-05:00',
                'fecResumen':'2019-12-07T00:00:00-05:00',
                'moneda': 'PEN',
                'company': {
                    'ruc':'10710924967',
                    'razonSocial':'GERARDO RAFAEL GUEVARA ALLER',
                    'nombreComercial':'',
                    'address':{
                        'ubigueo':'160101',
                        'codigoPais':'PE',
                        'departamento':'Loreto',
                        'provincia':'Maynas',
                        'distrito':'Iquitos',
                        'urbanizacion':'-',
                        'direccion':'ir√≥n Fitzcarrald 232'
                    }
                },
                'details':this.boletas
            };
            let me = this;
            axios.post('http://localhost:81/facturacion/lycet/public/api/v1/summary/send?token=123456', resumen).then(function(response){
               if(response.data.sunatResponse.success==false){
                     me.$swal('Error al enviar resumen diario..', ''+' '+response.data.sunatResponse.error.message, 'error')
                }
                else if(response.data.sunatResponse.success==true){
                     me.$swal('Resumen enviado..', ''+' '+response.data.sunatResponse.ticket, 'info')
                }
            }).catch(function(err){
                console.log(err)
            })
            //console.log(resumen)
        }
    }
}
</script>
