<template>
  <v-layout justify-center>
    <v-flex xs12>
      <v-toolbar color="white" >
        <v-toolbar-side-icon></v-toolbar-side-icon>
        <v-toolbar-title>TABLERO</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-dialog v-model="nuevoCliente" max-width="650px" persistent>
                        <v-card>
                            <v-card-title class="primary" style="color: white;">
                               <h3>NUEVO CLIENTE</h3>
                            </v-card-title>
                            <v-card-text>
                             <v-container grid-list-md>
                                <v-layout wrap>
                                    <v-flex xs6 sm6 md6>
                                        <v-select label="TIPO" v-model="tipoDoc" :items="tdocumento" prepend-icon="assignment" @change="limpiarConsultas()"></v-select>
                                   </v-flex>
                                   <v-flex xs6 sm6 md6> 
                                        <v-text-field  type="number" v-model="numeroDocumentoReniec" label="NUMERO(RUC,DNI)" prepend-icon="looks_3" @keyup.enter="consultasDocuments()"></v-text-field>
                                   </v-flex>
                                   <v-flex xs12 sm16 md12> 
                                        <v-text-field  type="text" v-model="razonSocialReniec" label="RAZON SOCIAL"  prepend-icon="how_to_reg" ></v-text-field>
                                   </v-flex>
                                    <v-flex xs12 sm16 md12> 
                                        <v-text-field v-model="direccionReniec"  type="text" label="DIRECCION FISCAL"  prepend-icon="location_on"></v-text-field>
                                   </v-flex>
                                   <v-flex xs12 sm16 md12> 
                                        <v-text-field  v-model="estado" disabled type="text" label="ESTADO"  prepend-icon="assignment"></v-text-field>
                                   </v-flex>
                                    <v-flex xs6 sm6 md6>
                                        <v-text-field  type="number" v-model="telefono" label="CELULAR" prepend-icon="call" ></v-text-field>
                                    </v-flex>
                                    <v-flex xs6 sm6 md6> 
                                        <v-text-field  type="text" v-model="email" label="EMAIL" prepend-icon="contact_mail"></v-text-field>
                                   </v-flex>
                                    <v-flex xs12 sm12 md12 v-show="valida">
                                        <h4><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h4>
                                    </v-flex>
                                </v-layout>
                            </v-container>
                            </v-card-text>
                             <v-divider></v-divider>
                            <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="error" @click="cancelarCrearCliente()">Cancelar
                                <v-icon dark right>block</v-icon>
                            </v-btn>
                            <v-btn color="info" @click="guardarCliente()">Guardar
                                 <v-icon dark right>check_circle</v-icon>
                            </v-btn>
                            </v-card-actions>
                        </v-card>
        </v-dialog>
        <v-dialog v-model="addReserva" max-width="480px" persistent>
            <v-card>
                <v-card-title class="headline warning"  style="color: white;"><h4>RESERVAR MESA</h4></v-card-title>
                <v-card-text>
                     <v-flex xs12 sm12 md12>
                            <v-text-field prepend-icon="person" v-model="nombresi" label="NOMBRE COMPLETO"></v-text-field>
                        </v-flex>
                        <v-flex xs12 sm12 md12>
                            <v-text-field
                                prepend-icon="phone"
                                v-model="celu"
                                label="CELULAR"
                                required
                                type="number"
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs12 sm12 md12>
                            <v-menu
                                v-model="menu2"
                                :close-on-content-click="false"
                                :nudge-right="40"
                                lazy
                                transition="scale-transition"
                                offset-y
                                full-width
                                min-width="290px"
                                >
                                <template v-slot:activator="{ on }">
                                <v-text-field
                                    v-model="fechita"
                                    label="FECHA"
                                    prepend-icon="event"
                                    readonly
                                    v-on="on"
                                ></v-text-field>
                                </template>
                                <v-date-picker color="green lighten-1" v-model="fechita" @input="menu2 = false"></v-date-picker>
                            </v-menu>
                        </v-flex>
                        <v-flex xs12 sm12 md12>
                            
                            <v-dialog
                                    ref="dialog"
                                    v-model="modal2"
                                    :return-value.sync="hora"
                                    persistent
                                    lazy
                                    full-width
                                    width="290px"
                                >
                                <template v-slot:activator="{ on }">
                                    <v-text-field
                                    v-model="hora"
                                    label="HORA"
                                    prepend-icon="access_time"
                                    readonly
                                    v-on="on"
                                    ></v-text-field>
                                </template>
                                <v-time-picker
                                    v-if="modal2"
                                    v-model="hora"
                                    full-width>
                                <v-spacer></v-spacer>
                                    <v-btn  color="error"   @click="modal2 = false">Cancel</v-btn>
                                    <v-btn  color="primary" @click="$refs.dialog.save(hora)">OK</v-btn>
                                </v-time-picker>
                            </v-dialog>
                        </v-flex>
                        <v-flex xs12 sm12 md12>
                            <v-text-field
                                prepend-icon="people"
                                v-model="personas"
                                label="CANTIDAD PERSONAS"
                                type="number"
                                required
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs12 sm12 md12>
                            <v-textarea v-model="moReserva"  prepend-icon="assignment" label="" placeholder="MOTIVO" row="1" auto-grow ></v-textarea>
                        </v-flex>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="error" @click="cerrarReserva()">CERRAR
                        <v-icon dark right>block</v-icon>
                    </v-btn>
                    <v-btn color="info" @click="guardarReserva()">GUARDAR
                        <v-icon dark right>check_circle</v-icon>
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model="adModal" max-width="450px" persistent>
            <v-card>
                <v-card-title class="success" style="color: white;" primary-title><v-icon dark>restaurant</v-icon><h3> MESA {{adNombre}}</h3></v-card-title>
                    <v-card-text>
                        <v-container grid-list-md>
                             <v-layout wrap>
                                 <v-flex xs12 sm10 md12 lg12 xl8>
                                 </v-flex>
                                 <v-flex xs3 sm6 md6 lg6 xl8>
                                    <v-text-field  prepend-icon="looks_one" label="CAPACIDAD" v-model="capacidad" type="number" disabled>
                                    </v-text-field>
                                 </v-flex>
                                 <v-flex xs3 sm6 md6 lg6 xl8>
                                    <v-text-field label="CANT PERSONAS" v-model="cantidadPersonas" type="number" prepend-icon="create">
                                    </v-text-field>
                                 </v-flex>
                                 <v-flex xs12 sm12 md12 v-show="valida">
                                   <h3>  <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h3>
                                </v-flex>
                             </v-layout>
                       </v-container>
                    </v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions>
                       <v-spacer></v-spacer>
                       <v-btn color="warning" dark class="mb-2" @click="abrirReserva()">RESERVAR</v-btn>
                       <v-btn color="info" class="mb-2" @click.native="nuevoOrden">ABRIR MESA</v-btn>
                       <v-btn color="red darken-1" dark class="mb-2" @click="activarDesactivarCerrar">CERRAR</v-btn>
                    </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model="Anulacion" max-width="450px" persistent>
            <v-card>
                <v-card-title class="error" style="color: white;" primary-title><v-icon dark>restaurant</v-icon><h3> ANULAR PRODUCTO {{nombreProducto}}</h3></v-card-title>
                    <v-card-text>
                        <v-container grid-list-md>
                             <v-layout wrap>
                                 <v-flex text-center>
                                    <v-text-field  prepend-icon="person"  v-model="nombreUsuario" color="primary" label="USUARIO" >
                                    </v-text-field>
                                    <v-text-field  prepend-icon="lock" v-model="password" type="password" color="primary" label="PASSWORD" >
                                    </v-text-field>
                                    <v-textarea  prepend-icon="assignment" v-model="motivo" label="MOTIVO" placeholder="" row="1" auto-grow ></v-textarea>
                                    <v-flex class="red--text" v-if="error">
                                        <h4> {{error}}</h4>
                                    </v-flex>
                         </v-flex>
                             </v-layout>
                       </v-container>
                    </v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions>
                       <v-spacer></v-spacer>
                       <v-btn color="red darken-1" dark class="mb-2" @click="cerrarAnulacion()" >Cerrar</v-btn>
                       <v-btn v-if="adAccion==0" color="info" class="mb-2" @click="anularDetalleSupervisor()">Confirmar</v-btn>
                    </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model="addComentario" max-width="450px" persistent>
          <v-card>
            <v-card-title class="success" style="color: white;"><h3>{{formTitle}}</h3></v-card-title>
                <v-card-text>
                    <v-container grid-list-md>
                            <v-layout wrap>
                                <v-flex xs12 sm10 md12 lg12 xl8>
                                   <h3>{{prod}}</h3>
                                   <h4 style="color: red;">PRECIO:S/.{{prec}}</h4>
                                </v-flex>
                                <v-flex xs12 sm10 md12 lg12 xl8>
                                    <template v-if="editedIndex == -1">
                                        <v-text-field  prepend-icon="looks_one" v-model="cant" label="CANTIDAD" type="number">
                                        </v-text-field>
                                    </template>
                                </v-flex>
                                <v-flex xs12 sm10 md12 lg12 xl8>
                                        <v-textarea  prepend-icon="assignment" label="" v-model="nota"  type="text" placeholder="COMENTARIO"  auto-grow ></v-textarea>
                                </v-flex>
                        </v-layout>
                    </v-container>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red darken-1" dark class="mb-2" @click="ocultarModalDetalle()">Cerrar</v-btn>
                    <v-btn color="info" class="mb-2"  @click="agregarDetalleComentarioSalon()">Confirmar</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model="addCambiarMesa" max-width="1050px" persistent>
          <v-card>
            <v-card-title  class="primary"  style="color: white;"><h3>CAMBIAR MESA</h3></v-card-title>
             <v-divider></v-divider>
                <v-card-text>
                    <v-container grid-list-md text-xs-center>
                        <v-layout  row wrap>
                            <v-flex v-for="item in mesas" :key="item.idMesa" xs1>
                                <v-btn  v-if="item.estado==1" color="success" fab large dark @click="cambiarMesa(item.idMesa)">
                                    {{item.nombre}}
                                </v-btn>
                                <v-btn  v-else-if="item.estado==2" color="error"  fab large dark @click="mesaOcupada()">
                                    <div>
                                        {{item.nombre}}
                                    </div>
                                </v-btn>
                                <v-btn  v-else  color="warning" fab large dark >
                                    {{item.nombre}}
                                </v-btn>
                            </v-flex>
                        </v-layout>
                    </v-container>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red darken-1" dark class="mb-2" @click="cerrarMesaCambiar()" >CERRAR</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model="addDescuento" max-width="450px" persistent>
          <v-card>
             <v-card-title class="info" primary-title>
                <h3><span style="color: white;" class="headline">APLICAR DESCUENTO</span></h3>
             </v-card-title>
             <v-divider></v-divider>
                <v-card-text>
                    <v-container grid-list-md>
                            <h3 style="color: brown;">SUBTOTAL: {{subtotal}}</h3>  
                            <v-layout wrap>
                                 <v-autocomplete @change="changedes()"
                                    :items="tipodes"
                                    prepend-icon="assignment"
                                    label="TIPO" chips
                                    required v-model="valor">
                                </v-autocomplete>
                                <v-text-field v-on:keyup="prueba()"  v-model="valodescuento"
                                    prepend-icon="message"
                                    label=" VALOR DESCUENTO"
                                    required
                                    type="number">
                                </v-text-field>
                                <v-textarea v-model="motivodes"  prepend-icon="assignment" label="" placeholder="MOTIVO" row="1" auto-grow ></v-textarea>
                            </v-layout>
                           <h3 style="color: brown;">DESCUENTO: {{predescuento}}</h3>  
                    </v-container>
                   
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red darken-1" dark class="mb-2" @click="cerrarAplicarDescuento()" >CERRAR</v-btn>
                    <v-btn color="info" class="mb-2" @click="confirmarDescuento()">CONFIRMAR</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model="verProductos" max-width="850px" persistent> 
            <v-card>
                <v-card-title class="primary" style="color: white;"><h3>LISTA DE PLATOS</h3></v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <v-container grid-list-md>
                        <v-layout wrap>
                        <v-flex xs12 sm12 md12 lg12 xl12>
                            <v-text-field append-icon="search" placeholder="..................." v-model="search" class="text-xs-center"  label="Busqueda platos" >
                            </v-text-field>
                            <template>
                                <v-data-table                           
                                    :headers="cabeceraProductos"
                                    :items="productos"
                                    :search="search"
                                    class="elevation-1">
                                        <template slot="items" slot-scope="props">
                                        <td >
                                                <v-icon
                                                color="red darken-2"
                                                small
                                                @click="comentarioRegistro(props.item)"
                                                class="mr-2">
                                                add
                                            </v-icon>
                                        </td>
                                        <td>{{props.item.areaProduccion}}</td>
                                        <td>{{props.item.nombre}}</td>
                                        <td>
                                            <v-chip color="orange" dark>S/{{props.item.precio}}</v-chip>
                                        </td>
                                    </template>
                                    <template slot="no-data">
                                        <span class="red--text">
                                            <h3>No hay insumos para mostrar.</h3>
                                        </span>
                                    </template>
                                </v-data-table> 
                            </template>
                        </v-flex>
                    </v-layout>
                </v-container>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                    <v-btn dark color="red darken" @click="ocultarProductos()">
                        Cerrar
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model="verClientes" max-width="850px" persistent>
                <v-card>
                    <v-card-title class="primary" style="color: white;"><h3>LISTA DE CLIENTES</h3></v-card-title>
                    <v-divider></v-divider>
                    <v-card-text>
                        <v-container grid-list-md>
                          <v-layout wrap>
                            <v-flex xs12 sm12 md12 lg12 xl12>
                                <v-text-field append-icon="search" placeholder="..................." v-model="search" class="text-xs-center"  label="Busqueda" >
                                </v-text-field>
                                <template>
                                    <v-data-table                           
                                        :headers="cabeceraClientes"
                                        :items="cliente"
                                        :search="search"
                                        class="elevation-1">
                                         <template slot="items" slot-scope="props">
                                            <td >
                                                 <v-icon
                                                  color="red darken-2"
                                                  small
                                                   @click="clienteAdd(props.item)"
                                                  class="mr-2">
                                                  add
                                                </v-icon>
                                            </td>
                                            <td>{{props.item.idPersona}}</td>
                                            <td>{{props.item.numeroDocumento}}</td>
                                            <td>{{props.item.razonSocial}}</td>
                                            <td>{{props.item.direccion}}</td>
                                            <td>{{props.item.montoCredito}}</td>
                                        </template>
                                        <template slot="no-data">
                                            <span class="red--text">
                                                <h3>No hay clientes para mostrar.</h3>
                                            </span>
                                        </template>
                                    </v-data-table> 
                                </template>
                            </v-flex>
                        </v-layout>
                    </v-container>
                    </v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions>
                        <v-btn dark color="red darken" @click="cerrarClientes()">
                            Cerrar
                        </v-btn>
                        <v-btn dark color="primary darken" @click="abrirNuevoCliente()">
                                Nuevo
                        </v-btn>
                    </v-card-actions>
                </v-card>
        </v-dialog>
        <v-dialog v-model="confirmar" max-width="400" persistent>
            <v-card>
                <v-card-title class="warning"  style="color: white;" primary-title><v-icon dark>restaurant</v-icon><h3>CONFIRMAR MESA {{adNombre}}</h3></v-card-title>
                <v-card-text>
                    <p>
                       DESEA CONFIRMAR LA RESERVA DE LA MESA <strong>{{adNombre}}</strong>
                       DEL CLIENTE <strong>{{listaReserva.cliente}}.</strong>
                    </p>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="error" dark class="mb-2" @click="cancelarConfirmarMesa()">Cancelar</v-btn>
                    <v-btn color="info"  class="mb-2" @click="saveConfirmar(adId)">Confirmar</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <template v-if="verNuevo==0" >
              <v-btn color="error" @click="actualizarMesas()">
                    <v-icon>check_circle</v-icon>ACTUALIZAR
             </v-btn>
        </template>
        <template v-if="verNuevo==1">
              <v-btn color="info" @click="volverMesas()">
                    <v-icon>check_circle</v-icon>REGRESAR
             </v-btn>
        </template>
        <template v-if="verNuevo==1">
              <v-btn color="gray" @click="cambiar()">
                    <v-icon>check_circle</v-icon>CAMBIAR MESA
             </v-btn>
        </template>
        <template v-if="verNuevo==2">
              <v-btn color="gray" @click="aplicarDescuento()" >
                    <v-icon>check_circle</v-icon>APLICAR DESCUENTO
             </v-btn>
        </template>
        <template v-if="verNuevo==2">
              <v-btn color="info" @click="volverSeleccionar()">
                    <v-icon>check_circle</v-icon>REGRESAR
             </v-btn>
             <h2 v-for="item in caja" :key="item.idEquipo">{{item.cajaNombre}}</h2>
        </template>
        </v-toolbar>
        <v-card>
        <v-container fluid grid-list-md  v-if="verNuevo==0">
          <v-layout row wrap>
               <v-flex xs12>
                 <v-card>
                    <v-card-title class="primary" style="color: white;"><h2>ITEM SALONES</h2></v-card-title>
                    <v-card-text>
                            <v-tabs color="white" grow>
                                <v-tabs-slider color="yellow"></v-tabs-slider>
                                <v-tab @click="verTodos">
                                    Todos
                                </v-tab>
                                <v-tab v-for="v in salones" :key="v.idSalon" @click="ver(v)">
                                    {{v.nombre}}
                                </v-tab>
                            </v-tabs>
                    </v-card-text>
                 </v-card>
               </v-flex>
               <v-flex xs12>
                 <v-card>
                   <v-card-title><h2>ITEM MESAS</h2></v-card-title>
                    <v-divider></v-divider>
                    <v-card-text>
                        <v-tabs-items>
                          <v-tab-item v-for="v in salones" :key="v.idSalon" :value="v.idSalon">
                                <v-container grid-list-md text-xs-center>
                                    <v-layout justify-start row wrap>
                                        <v-flex v-for="item in sal" :key="item.idMesa" xs1>
                                            <v-card color="success" dark v-if="item.estado==1" @click="abrirMesa(1,item)" class="border" >
                                                <v-card-text>
                                                    <h1>{{item.nombre}}</h1>
                                                    <h4>{{item.capacidad}} P </h4>
                                                </v-card-text>
                                            </v-card>
                                            <v-card color="error" dark v-else-if="item.estado==2" @click="mostrarNuevo(item)" class="border">
                                                <v-card-text>
                                                    <h1>{{item.nombre}}</h1>
                                                    <h4>{{item.capacidad}} P </h4>
                                                </v-card-text>
                                           </v-card>                  
                                           <v-card color="warning"  dark v-else @click="confirmarMesa(item)" class="border">
                                                <v-card-text>
                                                    <h1>{{item.nombre}}</h1>
                                                    <h4>{{item.capacidad}} P </h4>
                                                </v-card-text>
                                            </v-card>
                                        </v-flex>
                                    </v-layout>  
                                </v-container>
                            </v-tab-item>
                        </v-tabs-items>
                    </v-card-text>
                 </v-card>
               </v-flex>
          </v-layout>
         </v-container>
        </v-card>
        <v-card>
         <v-container fluid grid-list-md  v-if="verNuevo==1">
          <v-layout row wrap>
              <v-flex xs3>
                 <v-card>
                    <v-card-title class=""><h2>MESA: {{adNombre}}</h2></v-card-title>
                    <v-divider></v-divider>
                    <v-card-text v-for="item in pd" :key="item.idPedido">
                        <h5>CLIENTE:</h5><p>PUBLICO GENERAL</p>
                        <h5>FECHA:</h5><p>{{sinceInformacion(item.fecha)| uppercase}}</p>
                        <h5>HORA REGISTRO: </h5><p>{{item.hora }}</p>
                    </v-card-text>
                 </v-card>
               </v-flex>
               <v-flex xs9>
                 <v-card>
                    <v-card-title class="success" style="color: white;"><h3>ITEM PRODUCTOS</h3></v-card-title>
                        <v-flex xs12>
                            <v-btn color="teal" dark @click="mostrarProductos()">
                                <v-icon dark>search</v-icon>BUSCAR
                            </v-btn>
                            <v-btn color="primary" dark @click.native="guardar">
                                <v-icon dark>search</v-icon>REGISTRAR ORDEN
                            </v-btn>
                        </v-flex>
                    <v-card-text>
                         <v-data-table
                            :headers="cabeceraLista"
                            :items="listarDetalles"
                            hide-actions
                            class="elevation-1">
                            <template  slot="items" slot-scope="props">
                                 <td class="">
                                    <v-icon
                                    color="red darken-2"
                                    @click="eliminarDetalle(listarDetalles,props.item)"
                                    small>
                                    delete
                                    </v-icon>
                                    <v-icon
                                        color="green darken-2"
                                        small
                                        class="mr-2"
                                        @click="editItem(props.item)">
                                        edit
                                    </v-icon>
                                 </td>
                                 <td>
                                    {{ props.item.nombre }}
                                     <div class="">
                                         <span class="primary--text">{{props.item.comentario |uppercase}}</span>
                                    </div>
                                </td>
                                <td><v-text-field type="number" v-model="props.item.cantidad"></v-text-field></td>
                                <td>{{  props.item.precio }}</td>
                                <td>S/. {{props.item.cantidad * props.item.precio}}</td>
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3> No hay productos agregados al detalle.</h3>
                                </span>
                            </template>
                        </v-data-table>
                         <v-flex class="text-xs-right">
                            <h2>TOTAL PEDIDO: S/ {{totalPedido=(calcularTotalPedido).toFixed(2)}} </h2>
                        </v-flex>
                    </v-card-text>
                    <v-divider></v-divider>
                 </v-card>
               </v-flex>
               <v-flex xs12>
                <v-card>
                    <v-card-title class="error" style="color: white;"><h3>DETALLES PEDIDOS</h3></v-card-title>
                    <v-card-text>
                        <v-data-table
                            v-model="selected"
                            :headers="headers"
                            :items="detalles"
                            select-all
                            hide-actions
                            item-key="idDetallePedido"
                            class="elevation-1">
                            <template v-slot:headers="props">
                                <tr>
                                    <th>
                                        <v-checkbox
                                            :input-value="props.all"
                                            :indeterminate="props.indeterminate"
                                            primary
                                            hide-details
                                            color="error"
                                            @click.stop="toggleAll">
                                        </v-checkbox>
                                    </th>
                                    <th class="text-xs-left"
                                        v-for="header in props.headers"
                                        :key="header.text">
                                        {{ header.text }}
                                    </th> 
                                </tr>
                             </template>
                            <template slot="items" slot-scope="props">
                              <tr :active="props.selected" @click="props.selected = !props.selected">
                                <td>
                                    <v-checkbox :input-value="props.selected"  color="error" hide-details></v-checkbox>
                                </td>
                                <td>
                                    <strong>{{ props.item.producto }}</strong>
                                     <div class="">
                                          <strong><span class="primary--text">{{props.item.comentario}}</span></strong>
                                    </div>
                                </td>
                                <td> {{  props.item.usuario}}</td>
                                <td> {{  since(props.item.horaPedidoDate)| uppercase}}</td>
                                <td >{{  props.item.cantidad }}</td>
                                <td >{{  props.item.precio }}</td>
                                <td >S/. {{ (props.item.cantidad * props.item.precio).toFixed(2)}}</td>
                                <td >
                                    <v-chip v-if="props.item.idEstadoPedido==1"  color="primary" dark>SOLICITADO</v-chip>
                                    <v-chip v-if="props.item.idEstadoPedido==2"  color="orange" dark>PREPARANDO</v-chip>
                                    <v-chip v-if="props.item.idEstadoPedido==3"  color="purple" dark>DESPACHANDO</v-chip>
                                    <v-chip v-if="props.item.idEstadoPedido==4"  color="error" dark>ENTREGADO</v-chip>
                                </td>
                                <td>
                                     <v-icon  color="red darken-2" @click="anulardetalle(props.item)" small dark>delete</v-icon>
                                </td>
                              </tr>
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3> No hay productos agregados al detalle.</h3>
                                </span>
                            </template>
                        </v-data-table>
                         <v-flex class="text-xs-right">
                            <h2>TOTAL PARCIAL: S/ {{subtotal=(calcularTotal).toFixed(2)}} </h2>
                        </v-flex>
                    </v-card-text>  
                   <v-divider></v-divider>
                   <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="green darken-1" dark class="mb-2" @click="cobrar">COBRAR </v-btn>
                    </v-card-actions>
                 </v-card>
               </v-flex>
          </v-layout>
         </v-container>
        </v-card>
        <v-card>
          <v-container fluid grid-list-md  v-if="verNuevo==2">
           <v-layout row wrap>
               <v-flex xs7>
                 <v-card>
                    <v-card-title><v-icon>restaurant</v-icon><h3>INFORMACION CIERRE MESA {{adNombre}}</h3></v-card-title>
                    <v-divider></v-divider>
                    <v-card-text>
                         <v-container grid-list-md>
                                <v-layout wrap>
                                     <v-flex xs6>
                                        <h3>
                                        <v-select  @change="limpiarContado()" prepend-icon="assignment"
                                            :items="modalidadpago" v-model="idModalidadPago"
                                            label="MODALIDAD" chips> 
                                        </v-select>
                                        </h3>
                                    </v-flex>
                                    <v-flex xs6>
                                        <h3>
                                         <v-select :items="tipocomprobante" v-model="idComprobante"  chips  prepend-icon="assignment" label="TIPO DOCUMENTO">
                                        </v-select></h3>
                                    </v-flex>  
                                    <v-flex>
                                        <h3>
                                        <v-text-field type="number" v-model="numeroDocumento" prepend-icon="looks_one" label="DNI/RUC">
                                        </v-text-field></h3>
                                      </v-flex>
                                      <v-btn  small fab dark color="teal" @click="mostraClientes()">
                                            <v-icon dark>list</v-icon>
                                       </v-btn>
                                    <v-flex xs12>
                                        <h3>
                                        <v-text-field type="text"  v-model="nombreCliente" prepend-icon="people" label="RAZON SOCIAL">
                                        </v-text-field>
                                        </h3>
                                    </v-flex>
                                    <v-flex xs12>
                                        <h3 style="color: red;"><v-text-field  type="text" v-if="idModalidadPago==2" v-model="montoCredito" disabled prepend-icon="looks_one" label="CREDITO">
                                        </v-text-field></h3>
                                    </v-flex>
                                    <v-flex xs6 v-if="idModalidadPago==1">
                                        <h3>
                                        <v-select @change="limpiarEfectivo()" prepend-icon="assignment"
                                            :items="tipopago" chips v-model="idTipoPago"
                                            label="TIPO PAGO">
                                        </v-select>
                                        </h3>
                                    </v-flex>
                                    <v-flex xs6  v-if="idModalidadPago==1">
                                        <h3>
                                          <v-select  v-if="idTipoPago==1"  :items="moneda" @change="limpiarMoneda()" v-model="idmoneda" chips prepend-icon="assignment" label="MONEDA">
                                          </v-select>
                                          <v-select  v-else disabled :items="moneda" v-model="idmoneda" chips prepend-icon="assignment" label="MONEDA">
                                          </v-select>
                                        </h3>
                                    </v-flex> 
                                    <v-flex xs6>
                                        <h3>
                                            <v-text-field  v-on:keyup="onchange()" v-if="idmoneda==2" type="number"  prepend-icon="looks_one" label="DOLARES" v-model="montoDolar">
                                            </v-text-field>
                                        </h3>
                                    </v-flex>
                                    <v-flex xs6>
                                         <h3>
                                            <v-text-field v-if="idmoneda==2" disabled type="number"  prepend-icon="looks_one" label="CONVERSION SOLES" v-model="valorConversion">
                                            </v-text-field>
                                        </h3>
                                    </v-flex>  
                                    <template  v-if="idTipoPago==4">
                                        <v-flex xs6>
                                            <h3>
                                            <v-select prepend-icon="assignment"
                                                :items="tarjetas" v-model="idTarjeta"
                                                label="TARJETA">
                                            </v-select>
                                            </h3>
                                        </v-flex>

                                       <v-flex xs6>
                                            <h3><v-text-field type="number" v-model="mTarjeta" prepend-icon="looks_one" label="MONTO TARJETA">
                                            </v-text-field></h3>
                                       </v-flex>

                                       <v-flex xs6>
                                           <h3><v-text-field disabled value="EFECTIVO" prepend-icon="looks_one" label="EFECTIVO SOLES"></v-text-field></h3>
                                       </v-flex>

                                        <v-flex xs6>
                                           <h3><v-text-field type="number" v-model="mEfectivo" prepend-icon="looks_one" label="MONTO SOLES"></v-text-field></h3>
                                       </v-flex>

                                       <v-flex class="text-xs-right">
                                            <h2>TOTAL MIXTO: S/ {{montoEfectivo=(totalMixto).toFixed(2)}}</h2>
                                       </v-flex>
                                    </template>
                                </v-layout>
                            </v-container>
                    </v-card-text>
                 </v-card>
               </v-flex>
               <v-flex xs5>
                  <v-card>
                    <v-container fluid grid-list-lg>
                        <v-layout row wrap>
                                <v-flex xs6>
                                    <v-card color="info" dark>
                                        <v-card-title> <h4>SUBTOTAL</h4></v-card-title>
                                        <v-card-text class="headline font-weight-bold" >
                                        {{subtotal=(calcularTotal).toFixed(2)}} 
                                        </v-card-text>
                                    </v-card>
                                </v-flex>

                               <v-flex xs6>
                                <v-card color="error" dark>
                                    <v-card-title><h4>DESCUENTO</h4></v-card-title>
                                     <v-card-text class="headline font-weight-bold" v-model="des">
                                         {{des}}
                                    </v-card-text>
                                </v-card>
                             </v-flex>
                             <v-flex xs6>
                                 <v-card color="gray" dark>
                                    <v-card-title><h4>ICPBER</h4></v-card-title>
                                    <v-card-text class="headline font-weight-bold">
                                      {{icbper.toFixed(2)}}
                                    </v-card-text>
                                </v-card>
                             </v-flex>
                             <v-flex xs6>
                                 <v-card color="success" dark>
                                    <v-card-title><h4>TOTAL</h4></v-card-title>
                                    <v-card-text class="headline font-weight-bold">
                                       {{totalVenta=((subtotal-des)+icbper).toFixed(2)}} 
                                    </v-card-text>
                                </v-card>
                             </v-flex>
                             <v-flex xs6>
                                  <template v-if="idModalidadPago==1">
                                    <v-card v-if="idTipoPago==1" >
                                        <v-card-title><h4>EFECTIVO</h4></v-card-title>
                                        <v-card-text class="headline font-weight-bold">
                                            <v-text-field v-model="montoEfectivo" type="number">
                                            </v-text-field>
                                        </v-card-text>
                                    </v-card>
                                  </template>
                             </v-flex>
                             <v-flex xs6>
                                  <template v-if="idModalidadPago==1 ">
                                    <v-card color="warning" dark v-if="idTipoPago==1">
                                        <v-card-title><h4>CAMBIO</h4></v-card-title>
                                        <v-card-text class="headline font-weight-bold" v-if="montoEfectivo>0">
                                            {{vuelto=(calcularEfectivo).toFixed(2)}}
                                        </v-card-text>
                                        <v-card-text class="headline font-weight-bold" v-else>
                                         0.00
                                        </v-card-text>
                                    </v-card>

                                    <v-card color="warning" dark v-if="idTipoPago==4">
                                        <v-card-title><h4>CAMBIO</h4></v-card-title>
                                        <v-card-text class="headline font-weight-bold" >
                                            {{vuelto=(calcularVueltoMixto).toFixed(2)}}
                                        </v-card-text>
                                    </v-card>
                                  </template>
                             </v-flex>
                        </v-layout>
                    </v-container>
                  </v-card>
               </v-flex>
               <v-flex xs12>
                <v-card>
                  <v-card-text>
                  </v-card-text>
                  <v-divider></v-divider>
                   <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="green darken-1" dark class="mb-2" @click="registrarPago()">REGISTRAR PAGO</v-btn>
                    </v-card-actions>
                </v-card>
                </v-flex>
          </v-layout>
        </v-container>
        </v-card>
      </v-flex>
  </v-layout>
</template>
<script>
    import {misMixins} from './../mixins.js'
    import axios from 'axios'
    import jsPDF from 'jspdf'
    import autoTable from 'jspdf-autotable';
    import html2canvas from 'html2canvas';
    import toastr from 'toastr';
    import moment from 'moment';
    
    export default {
        data(){
            return {
               confirmar:0,
               tm:0,
               listaReserva:[],
               totalPedido:0,
               addReserva:0,
               mTarjeta:0,
               mEfectivo:0,
               idMesita:'',
               des:0,
               valor:'',
               addCambiarMesa:0,
               predescuento:0,
               valodescuento:0,
               motivodes:'',
               tipodes:['SOLES','PORCENTAJE'],
               montoDolar:0,
               addDescuento:0,
               valorDolar:3,
               valorConversion:0,
               ActEfectivo:false,
               montoEfectivo:0,
               totalVenta:0,
               descuento:[],
               id:'',
               tarjetas:[],
               idTarjeta:'',
               venta:[],
               cliente:[],  
               Anulacion:0,
               idCliente:'',
               numeroDocumento:'',
               nombreCliente:'',
               montoCredito:0,
               contador:1,
               contadorDes:1,
               subtotal:0,
               vuelto:0,
               selected: [],
               check:true,
               idmoneda:'',
               idModalidadPago:'',
               idTipoPago:'',
               idComprobante:'',
               tipocomprobante:[],
               moneda:[],
               tipopago:[],
               modalidadpago:[],
               ip:'',
               singleSelect: false,
               selected: [],
               editedIndex: -1,
               prod:'',
               prec:'',
               cant:1,
               idprod:'',
               nota:'',
               snack: false,
               snackColor: '',
               snackText: '',
               adId:'',
               addEditarComentario:0,
               addComentario:0,
               nombreProducto:'',
               adIdPedido:'',
               cantidadPersonas:'',
               pedidoMesa:[],
               pedido:[],
               salones:[],
               dolares:['100','50','20','10','5','1'],
               cuenta:[],
               caja:[],
               search: '',
               productos:[],
               comprobanteModal:0,
               idUsuario:'',
               listarDetalles:[],
               detalles:[],
               errorProducto:null,
               verNuevo:0,
               verProductos:0,
               verClientes:0,
               mesas:[],
               sal:[],
               adCobrar:0,
               adModal:0,
               adAccion:0,
               pd:[],
               selectSalon: "All",
               capacidad:'',
               adNombre:'',
               nombreUsuario:'',
               password:'',
               idDetallePedido:'',
               motivo:'',
               apertura:[],
               error:null,
               nombresi: '',
               fechita: null,
               celu:'',
               modal2: false,
               hora: null,
               moReserva:'',
               personas:'',
               menu2:null,
               telefono:'',
               email:'',
               estado:'',
               tdocumento:[],
               consultas:[],
               nombreReniec:'',
               apellido:'',
               razonSocialReniec:'',
               departamento:'',
               provincia:'',
               distrito:'',
               direccionReniec:'',
               valida: 0,
               validaMensaje:[],
               numeroDocumentoReniec:0,
               nuevoCliente:0,
               tipoDoc:'',
               icbper:0,
               headers: [
                    { text: 'PRODUCTOS /PLATOS', value: 'producto' },
                    { text: 'MOZO', value: 'mozo',sortable: false  },
                    { text: 'HORA PEDIDO ', value: '',sortable: false  },
                    { text: 'CANT', value: 'cantidad', sortable: false  },
                    { text: 'PRECIO', value: 'precio', sortable: false  },
                    { text: 'TOTAL', value: 'total', sortable: false  },
                    { text: 'ESTADO', value: 'idEstadoPedido', sortable: false  },
                    { text: 'OPCIONES', value: 'opciones', sortable: false  }                
                ],
                listapedidos:[
                    { text: 'CANTIDAD', value: 'cantidad',sortable: false},
                    { text: 'PRODUCTO', value: 'producto',sortable: false},
                    { text: 'TOTAL', value: 'total',sortable: false},

                ],
                cabeceraClientes:[
                    { text: 'SELECCIONAR', value: 'opcion',sortable: false},
                    { text: '#', value: 'idPersona',sortable: false},
                    { text: 'DOCUMENTO', value: 'numeroDocumento',sortable: false},
                    { text: 'CLIENTE', value: 'razonSocial',sortable: false},
                    { text: 'DIRECCION', value: 'direccion',sortable: false},
                    { text: 'CREDITO', value: 'montoCredito',sortable: false},

                ],
                cabeceraLista: [
                    { text: '#', value: 'opciones',sortable: false},
                    { text: 'PRODUCTO', value: 'producto'},
                    { text: 'CANTIDAD', value: 'cant' },
                    { text: 'PRECIO', value: 'precio' },
                    { text: 'TOTAL', value: 'total', sortable: false  },    
                ],
                cabeceraProductos: [
                    {text:  'SELECCIONAR', value: 'seleccionar', sortable: false },
                    {text:  'GRUPO', value: 'areaProduccion', sortable: false },
                    { text: 'PRODUCTO', value: 'nombre'},
                    { text: 'PRECIO', value: 'precio' ,sortable: false },
                ],
            }
        },
        mixins: [misMixins],

        filters: {
            uppercase: function(str){
                return str.toUpperCase()
            },
            lowercase:function(str){
                return str.toLowerCase()
            }
        },
        computed: {
            formTitle () {
            return this.editedIndex === -1 ? 'DETALLE PLATO' : 'EDITAR DETALLE  PLATO'
            },
            calcularTotal:function(){
                var resultado=0.0;
                for(var i=0;i<this.selected.length;i++){
                    resultado=resultado+(this.selected[i].precio*this.selected[i].cantidad);
                }
                return resultado;
            },
            totalMixto:function(){
                var r =0.0;
                r = Number(this.mTarjeta) + Number(this.mEfectivo);
                return r;
            },
            calcularEfectivo:function(){
                var resultado=0.0;
                resultado=(this.montoEfectivo-this.totalVenta);
                return resultado;
            },     
            calcularVueltoMixto:function(){
                var resultado=0.0;
                if(this.montoEfectivo=="0.00"){
                    return resultado =0.0;
                }   
                else{
                    resultado=(Number(this.montoEfectivo)-Number(this.totalVenta));
                    return resultado;
                    console.log('Prueba')
                }
            }       
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
           this.listarsalon();
           this.verTodos();
           this.cajaMaquina();
        },
        methods:{
            close () {
                this.addComentario = 0
            },
            cambiar(){
                this.addCambiarMesa=1;
            },
            confirmarDescuento(){
                let me=this;
                let array = [];
                array = this.descuento;
                if(me.descuento==0){
                   this.descuento.push(
                   {
                        "id":this.contadorDes,
                        "tipo":this.valor,
                        "subtotal":this.subtotal,
                        "valor":this.valodescuento,
                        "descuento": this.predescuento,
                        "motivo":this.motivodes,
                    })
                }else{
                    for( var i = 0; i < array.length; i++){ 
                        if(array[i].id == this.contadorDes) {
                            array[i].tipo =        this.valor;
                            array[i].subtotal =    this.subtotal;
                            array[i].valor =       this.valodescuento;
                            array[i].descuento =   this.predescuento;
                            array[i].motivo =      this.motivodes;
                            this.descuento = array;
                            this.addDescuento=0;
                        }
                    }      
                }
                this.addDescuento=0;
                this.des = this.descuento[0].descuento;
                this.motivodes="";
                this.predescuento=0;
            },
            //METODO CAMBIAR MESA
            cambiarMesa(idMesa){
                let me=this;
                axios.put('api/PedidoMesas/Actualizar',{
                    'idPedido': me.pd[0].idPedido,
                    'idMesa':idMesa,
                    'idMesaPedidoActualiza':me.pd[0].idMesa
                }).then(function(response){
                     me.exitosa();
                     me.cerrarMesaCambiar();
                     me.verNuevo=0;
                     me.verTodos();
                     me.detalles=[];

                }).catch(function(error){
              });
            },
            exitosa(){
                this.$swal('Informacion.', 'La mesa ha sido cambiado exitosamente.', 'success')
            },
            prueba: function(){
               var resultado=0.0,conversion=0.0;
               if(this.valor=='SOLES'){
                   resultado          =  this.valodescuento;
                   this.predescuento  =  resultado;
               }
               else if(this.valor=='PORCENTAJE'){
                   conversion         =  this.subtotal/100;
                   resultado          =  conversion * this.valodescuento;
                   this.predescuento  =  resultado;
               }
            },
            since: function(d){
                return moment(d).fromNow();    
            },
            changedes(){
               this.valodescuento=0; 
               this.predescuento=0;
               this.motivodes="";
            },
            fechasince:function(d){
                return moment(d).format("MMM Do YY")
            },
            aplicarDescuento(){
                let me=this;
                if(me.descuento==0){
                   this.addDescuento=1;
                }else{
                    this.addDescuento=1;
                    for(var i=0;i<me.descuento.length;i++){
                        this.valor = this.descuento[i].tipo;
                        this.valodescuento = this.descuento[i].valor;
                        this.motivodes = this.descuento[i].motivo;
                        this.predescuento = this.descuento[i].descuento;
                    }
                }
            },
            cerrarAplicarDescuento(){
               this.addDescuento=0;
               this.valodescuento=0;
               this.motivodes="";
               this.predescuento=0;
            },
            onchange(){
               this.valorConversion= this.montoDolar * this.valorDolar;
               this.montoEfectivo = this.valorConversion;
            },
            clienteAdd(item){
                this.idCliente = item.idPersona;
                this.numeroDocumento = item.numeroDocumento;
                this.nombreCliente = item.razonSocial;
                this.montoCredito = item.montoCredito;
                this.verClientes=0;
                this.search="";
            },
            toggleAll () {
                if (this.selected.length) this.selected = []
                else this.selected = this.detalles.slice()
            },
            mostraClientes(){
               this.verClientes=1;
               this.listarClientes();
            },
            cerrarClientes(){
                this.verClientes=0;
                this.search="";
            },
            mostrarProductos(){
              this.verProductos=1;
              this.listarProductos();
            },
            mostrarComprobante(){
               this.comprobanteModal=1;
            },
            ocultarComprobante(){
               this.comprobanteModal=0;
            },
            cerrarMesaCambiar(){
               this.addCambiarMesa=0;
            },
            ocultarProductos(){
                this.verProductos=0;
            },
            eliminarDetalle(arr,item){
                var i= arr.indexOf(item);
                if (i!==-1){
                    arr.splice(i,1);
                }
            },
            ocultarModalComentario(){
                this.prod="";
                this.prec="";
                this.cant=1;
                this.nota="";
                this.addEditarComentario=0;
           },
           cajaMaquina() {
                let me=this;
                axios.get('api/DetallePedidos/IPLocal').then(function(response){
                    me.ip=response.data;
                    me.informacionCajaVenta(me.ip);
                }).catch(function(error){
                    console.log(error);
                });
            },
            ocultarModalDetalle(){
                this.addComentario=0;
                this.editedIndex = -1;
            },
            editItem(item){
                this.id = item.id;
                this.prod = item.nombre;
                this.prec=  item.precio;
                this.cant = item.cantidad;
                this.nota=  item.comentario;
                this.cant=1;
                this.editedIndex=1
                this.addComentario= 1;
            },
            comentarioRegistro(item){
                this.ocultarProductos();
                this.idprod=item.idProducto;
                this.prod = item.nombre;
                this.prec=item.precio;
                this.addComentario=1;
                this.search="";
                this.nota="";
            },
            agregarPedidos(){
                let me=this;
                this.verNuevo=1;
                this.adCobrar=0;
                this.ListapedidoMesa(me.adId);
                this.listarDetails();
            },
            cerrarModalCobrar(){
               this.adCobrar=0;
            },
            mostrarNuevo(item){
                if(this.apertura.length==0){
                     this.$swal('Advertencia.', 'Debe aperturar caja, para proceder abrir mesa.', 'error');
                     return;
                }
                let me=this;
                this.verNuevo=1;
                this.adId =item.idMesa;
                this.adNombre=item.nombre;
                this.ListapedidoMesa(me.adId);
                this.listarDetails();
            },
            volverSeleccionar(){
                this.verNuevo=1;
                this.montoEfectivo=0;
                this.icbper=0;
            },
            cobrarAgregar(item){
                let me=this;
                this.adCobrar=1;
                this.adId =item.idMesa;
                this.adNombre=item.nombre;
            },
            informacionCajaVenta(ip){
              let me=this;
              axios.get('api/Equipos/ObtenerCaja/'+ip).then(function(response){
                 me.caja=response.data;
                 me.aperturaCaja(me.caja[0].idCaja)
                }).catch(function(error){
                   console.log(error);
                });
            },
            aperturaCaja(idCaja){
              let me=this;
              axios.get('api/AperturaCajas/ValidadAperturaCaja/'+idCaja).then(function(response){
                me.apertura =response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            encuentra(id){
               var sw=0;
               for(var i=0;i<this.listarDetalles.length;i++){
                    if (this.listarDetalles[i].idProducto==id){
                        sw=1;
                    }
                }
                return sw;
            },
            abrirMesa (accion,item) {
                if(this.apertura.length==0){
                     this.$swal('Advertencia.', 'Debe aperturar caja, para proceder abrir mesa.', 'error')
                }else{
                    this.adModal=1;
                    this.adId=item.idMesa;
                    this.adNombre = item.nombre;
                    this.capacidad = item.capacidad;
                }
            },
            alertDisplaP() {
                this.$swal('Advertencia.', 'Debe seleccionar los check para proceder el pago.', 'error')
            },
            mesaOcupada(){
                 this.$swal('Advertencia.', 'La mesa esta ocupada.', 'error')
            },
            alertDisplaCredito() {
                this.$swal('Advertencia.', 'No tiene credito disponible.', 'error')
            },
            cobrar(){
                let me=this;
                var resultado=0.0;
                if(this.selected.length>0) {
                    for(var i=0;i<this.selected.length;i++){
                         if(this.selected[i].idProducto==2622){
                             resultado = resultado + this.selected[i].cantidad;
                             this.icbper = resultado * 0.1;
                             console.log(this.icbper);
                         }
                     }
                     this.verNuevo=2;
                     this.numeroDocumento="0";
                     this.idCliente=9;
                     this.nombreCliente="PUBLICO GENERAL";
                     this.selectMoneda();
                     this.selecttipopago();
                     this.selectModalidadPago();
                     this.selectComprobante();
                     this.selectTarjetas();
                }else{
                   this.alertDisplaP()
                }
            },
            nuevoOrden(){
                let me=this;
                if(this.validar()){
                    return;
                }
                axios.post('api/Pedidos/CrearPedidoMesa',{
                    'idUsuario':me.$store.state.nombreUsuario.IdUsuario,
                    'pedidoMesa':{
                        'idMesa':me.adId,
                        'cantidadPersonas':me.cantidadPersonas
                    }
                }).then(function(response){
                     me.valida=0;
                     me.activarDesactivarCerrar();
                     me.verNuevo=1;
                     me.ListapedidoMesa(me.adId);
                     me.cantidadPersonas="";
                }).catch(function(error){
                    console.log(error);
                });
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];

                if(this.cantidadPersonas.length==0 || this.cantidadPersonas.length>100){
                    this.validaMensaje.push("Ingrese numero de personas.");
                }
                if(this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
            listarmesas() {
                let me=this;
                axios.get('api/mesas/activas').then(function(response){
                    me.mesas=response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            actualizarMesas(){
                let me=this;
                me.verTodos();
            },
            listarDetails(){
                let me=this;
                axios.get('api/DetallePedidos/Mostrar/'+me.adId).then(function(response){
                me.detalles=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            guardar(){  
                let me=this;
                axios.post('api/Pedidos/CrearPedidoDetalleMesa',{
                    'idMesa':me.adId,
                    'detallePedidos':me.listarDetalles
                }).then(function(response){
                     me.listarDetalles=[];
                     me.listarDetails();
                     me.cant=1;
                }).catch(function(error){
                    console.log(error);
                });
            },
            activarDesactivarCerrar:function(){
                this.valida=0;
                this.adModal=0;
            },
            sinceInformacion: function(d){
              return moment(d).format('L');  
            },  
            registrarPago(){
                if(this.idModalidadPago==2 && this.montoCredito==0){
                    this.alertDisplaCredito();
                    return;
                }
                else if(this.idModalidadPago==1 && this.idTipoPago==1 && this.montoEfectivo==0){
                    this.$swal('Advertencia.', 'Ingrese monto efectivo, para efectuar la venta.', 'error')
                    return;
                }
                else if(this.vuelto<0){
                    this.$swal('Advertencia.', 'EL monto debe ser menor .', 'error')
                    return;
                }
                else if(this.idModalidadPago==2 &&  this.totalVenta> this.montoCredito){
                    this.$swal('Advertencia.', 'No tiene el credito suficiente para efectuar la venta, gracias.', 'error')
                    return;
                }
                else if(this.idTipoPago==4){
                    if(!this.idTarjeta) {
                         this.$swal('Advertencia.', 'Seleccione tarjeta para efectuar la venta, gracias.', 'error')
                         return;
                    }
                    else if(this.vuelto<0){
                         this.$swal('Advertencia.', 'El monto es menor a la que debe cobrar, gracias.', 'error')
                         return;
                    }
                }
                let me=this;
                axios.post('api/VentaProductos/Crear',{
                    'idUsuario': me.$store.state.nombreUsuario.IdUsuario,
                    'idPedido':me.pd[0].idPedido,
                    'idCaja': me.caja[0].idCaja,
                    'idModalidadPago':me.idModalidadPago,
                    'idComprobante':me.idComprobante,
                    'idCliente': me.idCliente,
                    'idTipoPago':me.idTipoPago,
                    'idMoneda':me.idmoneda,
                    'subtotal':me.subtotal,
                    'descuento':me.des,
                    'rc':0,
                    'igv':0,
                    'icbper':me.icbper,
                    'totalPagar':me.totalVenta,
                    'montoDolar':me.montoDolar,
                    'montoEfectivo':me.montoEfectivo,
                    'vuelto':me.vuelto,
                    'tipoVenta': 'MESA'+' '+ me.adNombre,
                    'detalleVentas':me.selected
                }).then(function(response){
                    me.montoEfectivo=0;
                    me.venta = response.data;    
                    me.vuelto=0;
                    me.icbper=0;
                    
                    if(me.idModalidadPago==2){
                        me.guardarCreditoPersonal(me.venta.idCliente,me.venta.idVenta,me.venta.totalPagar);
                    }
                    else if(me.idTipoPago==4){
                        me.pagoMixto(me.venta.idVenta,me.idTarjeta,me.mTarjeta,me.mEfectivo,me.venta.totalPagar);
                        me.limpiarMixto();
                    }
                    else if(me.des>0){
                        axios.post('api/DescuentoVentas/Crear',{
                            'idVenta':me.venta.idVenta,
                            'idUsuario':me.$store.state.nombreUsuario.IdUsuario,
                            'tipo':me.descuento[0].tipo,
                            'subTotal':me.venta.subtotal,
                            'valor':me.descuento[0].valor,
                            'descuento':me.venta.descuento,
                            'totalPagar':me.venta.totalPagar,
                            'motivo':me.descuento[0].motivo
                        }).then(function(response){

                        }).catch(function(error){
                            console.log(error);
                        });
                    }
                    me.descuento = [];
                    me.des=0;
                    me.venta =[];
                    axios.get('api/DetallePedidos/Mostrar/'+me.adId).then(function(response){
                    me.detalles=response.data;
                        if(me.detalles.length>0){
                            me.volverSeleccionar();
                        }else{
                            me.actualizaEstadoMesa(me.adId);
                            me.volverMesas();
                            me.actualizaPedidoMesaEstado(me.adId);
                        }
                    }).catch(function(error){
                         console.log(error);
                    });
                }).catch(function(error){
                    console.log(error);
                });
                me.detalles=[];
                me.selected=[];
                me.montoDolar=0;
                me.valorConversion=0;
            },
            limpiarAnulacion(){
                this.nombreUsuario="";
                this.password="";
                this.motivo="";
            },
            cerrarAnulacion(){
                this.Anulacion=0;
                this.error=null;
            }
        }
    }
</script>

<style>
 .border{
     border-radius: 50%;
     width: 90px;
     height: 90px;
 }
</style>