<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>BUSCAR VENTAS</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
                <v-menu
                    v-model="menu1"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                    >
                    <template v-slot:activator="{ on }">
                    <v-text-field
                        v-model="fechaentrada"
                        label="FECHA INICIO"
                        prepend-icon="event"
                        readonly
                        v-on="on"
                    ></v-text-field>
                    </template>
                    <v-date-picker color="green lighten-1" v-model="fechaentrada" @input="menu1 = false"></v-date-picker>
                </v-menu>
                <v-menu
                    v-model="menu2"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                    >
                    <template v-slot:activator="{ on }">
                    <v-text-field
                        v-model="fechasalida"
                        label="FECHA SALIDA"
                        prepend-icon="event"
                        readonly
                        v-on="on"
                    ></v-text-field>
                    </template>
                    <v-date-picker color="green lighten-1" v-model="fechasalida" @input="menu2 = false"></v-date-picker>
                </v-menu>
                <v-spacer></v-spacer>
                <v-btn color="info" @click="listar">BUSCAR VENTAS <v-icon dark >search</v-icon></v-btn>
                <v-btn color="success" dark class="mb-2">
                        <export-excel
                        class   = "btn btn-default"
                        :data   = "categorias"
                        :fields = "json_fields"
                        worksheet = "LISTA DE COMPROBANTES"
                        title="VENTAS"
                        name    = "VentasXFecha.xls"> Excel
                        </export-excel> 
                    <v-icon dark right>cloud_download</v-icon>
                </v-btn> 
            </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="categorias"
                :search="search"
                class="elevation-1">
                <template slot="items" slot-scope="props">
                    <td>{{ props.item.documento }}</td>
                    <td>{{props.item.serie}}</td>
                    <td>{{props.item.correlativo}}</td>
                    <td>{{props.item.razonSocial}}</td>
                     <td>{{props.item.numDoc}}</td>
                    <td>{{since(props.item.fecha)}}</td>
                    <td>{{props.item.total}}</td>
                </template>
                <template slot="no-data">
                    <v-btn color="primary" @click="listar">
                        Resetear
                    </v-btn>
                </template>
            </v-data-table>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    import moment from 'moment';
    moment.locale('es');

    export default {
        data(){
            return {
                json_fields: {
                    'DOCUMENTO': 'documento',
                    'SERIE': 'serie',
                    'CORRELATIVO': 'correlativo',
                    'CLIENTE': 'razonSocial',
                    'NUM DOCUMENTO':'numDoc',
                    'FECHA': 'fecha',
                    'TOTAL': 'total',
                },
                categorias:[],
                search:'',
                color: 'success',
                snackbar: false,
                text: 'Registro guardado Exitosamente.',
                timeout: 2000,
                dialog: false,
                headers: [
                    { text: 'DOCUMENTO', value: 'documento'},
                    { text: 'SERIE', value: 'serie', sortable: false },
                    { text: 'CORRELATIVO', value: 'correlativo', sortable: false },
                    { text: 'CLIENTE', value: 'razonSocial', sortable: false },
                    { text: 'NRO DOC', value: 'numDoc', sortable: false },
                    { text: 'FECHA', value: 'fecha', sortable: false },
                    { text: 'TOTAL', value: 'total', sortable: false }
                ],
                valida: 0,
                validaMensaje:[],
                adAccion:0,
                adNombre:'',
                adId:'',
                menu1:null,
                menu2: null,
                fechaentrada: '',
                fechasalida:''
            }
        },
        computed: {
        },

        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            //this.listar();
        },
        methods:{

            since: function(d){
                return moment(d).format('L');
            },
            listar(){
            if(!this.fechaentrada || !this.fechasalida){
                    this.$swal('Advertencia.', 'Debe seleccionar ambas fechas para poder realizar la b√∫squeda', 'error')
                }else{
                    let me=this;
                    let header={"Authorization" : "Bearer " + this.$store.state.token};
                    let configuracion= {headers : header};
                    axios.get('api/VentaProductos/ReportesporFechas/'+ this.fechaentrada+'/'+this.fechasalida,configuracion).then(function(response){
                        me.categorias=response.data;
                        console.log(me.categorias);
                    }).catch(function(error){
                        console.log(error);
                    });
                }
            },
            limpiar(){
                this.fechasalida="",
                this.fechaentrada="";
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];

                
                if(this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
        }        
    }
</script>