<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>PRODUCTOS MAS VENDIDOS</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-menu
                    v-model="menu1"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                    >
                    <template v-slot:activator="{ on }">
                    <v-text-field
                        v-model="fechaentrada"
                        label="FECHA INICIO"
                        prepend-icon="event"
                        readonly
                        v-on="on"
                    ></v-text-field>
                    </template>
                    <v-date-picker color="green lighten-1" v-model="fechaentrada" @input="menu1 = false"></v-date-picker>
                </v-menu>
                <v-menu
                    v-model="menu2"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                    >
                    <template v-slot:activator="{ on }">
                    <v-text-field
                        v-model="fechasalida"
                        label="FECHA FIN"
                        prepend-icon="event"
                        readonly
                        v-on="on"
                    ></v-text-field>
                    </template>
                    <v-date-picker color="green lighten-1" v-model="fechasalida" @input="menu2 = false"></v-date-picker>
                </v-menu>
                <v-spacer></v-spacer>
                <v-btn color="info" @click="listar">Buscar<v-icon dark >search</v-icon></v-btn>
                <v-btn color="success" dark class="mb-2">
                        <export-excel
                        class   = "btn btn-default"
                        :data   = "ventas"
                        :fields = "json_fields"
                        worksheet = "Lista de Productos mas Vendidos"
                        title="Productos"
                        name    = "ProductosMVendidos.xls"> EXCEL
                        </export-excel> 
                    <v-icon dark right>cloud_download</v-icon>
                </v-btn> 
            </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="ventas"
                :search="search"
                class="elevation-1">
                <template slot="items" slot-scope="props">
                    <td>{{ props.item.producto }}</td>
                    <td>{{props.item.cantidad}}</td>
                    <td>S/.{{props.item.total}}</td>
                </template>
                <template slot="no-data">
                    <v-btn color="primary" @click="listar">
                        Resetear
                    </v-btn>
                </template>
            </v-data-table>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    import moment from 'moment';
    moment.locale('es');

    export default {
        data(){
            return {
                json_fields: {
                    'PRODUCTO': 'producto',
                    'CANTIDAD': 'cantidad',
                    'TOTAL': 'total',
                },
                ventas:[],
                search:'',
                color: 'success',
                snackbar: false,
                text: 'Registro guardado Exitosamente.',
                timeout: 2000,
                dialog: false,
                headers: [
                    { text: 'PRODUCTO', value: 'producto'},
                    { text: 'CANTIDAD', value: 'cantidad', sortable: false},
                    { text: 'TOTAL', value: 'total', sortable: false },
                ],
                valida: 0,
                validaMensaje:[],
                adAccion:0,
                adNombre:'',
                adId:'',
                menu1:null,
                menu2: null,
                fechaentrada: '',
                fechasalida:''
            }
        },
        computed: {
        },

        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
        },
        methods:{
            since: function(d){
                return moment(d).format('L');
            },
            listar() {
                if(!this.fechaentrada || !this.fechasalida){
                    this.$swal('Advertencia.', 'Debe seleccionar ambas fechas para poder realizar la b√∫squeda', 'error')
                }else{
                    let me=this;
                    let header={"Authorization" : "Bearer " + this.$store.state.token};
                    let configuracion= {headers : header};
                    axios.get('api/VentaProductos/ProductosMasVendidos/'+ this.fechaentrada+'/'+this.fechasalida,configuracion).then(function(response){
                        me.ventas=response.data;
                        console.log(me.categorias);
                    }).catch(function(error){
                        console.log(error);
                    });
                }
            },
            limpiar(){
                this.fechasalida="",
                this.fechaentrada="";
            }
        }        
    }
</script>