<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar color="white">
                <v-toolbar-title>REGISTRO DE PROVEEDORES</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="1050px" persistent>
                        <v-btn slot="activator" color="info" dark class="mb-2">Nuevo
                             <v-icon dark right>check_circle</v-icon>
                        </v-btn>
                        <v-card>
                            <v-card-title class="primary" style="color: white;">
                               <h3>{{formTitle}}</h3>
                            </v-card-title>
                            <v-card-text>
                             <v-container grid-list-md>
                                <v-layout wrap>
                                   <v-flex xs12 sm12 md16> 
                                        <v-text-field  type="number" v-model="numeroDocumento" label="INGRESE (RUC)" prepend-icon="looks_3" @keyup.enter="consultasDocuments()"></v-text-field>
                                   </v-flex>
                                   <v-flex xs12 sm12 md12> 
                                        <v-text-field  type="text" v-model="razonSocial" label="RAZON SOCIAL"  prepend-icon="how_to_reg" ></v-text-field>
                                   </v-flex>
                                    <v-flex xs12 sm16 md12> 
                                        <v-text-field v-model="direccion"  type="text" label="DIRECCION ACTUAL"  prepend-icon="location_on"></v-text-field>
                                   </v-flex>
                                    <v-flex xs6 sm6 md6>
                                        <v-text-field  type="number" v-model="telefono" label="CELULAR" prepend-icon="call" ></v-text-field>
                                    </v-flex>
                                    <v-flex xs6 sm6 md6> 
                                        <v-text-field  type="text" v-model="email" label="EMAIL" prepend-icon="contact_mail"></v-text-field>
                                    </v-flex>
                                    <v-flex xs12 sm12 md12 v-show="valida">
                                     <h3><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h3>
                                    </v-flex>


                                </v-layout>
                            </v-container>
                            </v-card-text>
                             <v-divider></v-divider>
                            <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="error" @click.native="close">Cancelar
                                <v-icon dark right>block</v-icon>
                            </v-btn>
                            <v-btn color="info" @click="guardarCliente()">Guardar
                                 <v-icon dark right>check_circle</v-icon>
                            </v-btn>
                            </v-card-actions>
                        </v-card>
                </v-dialog>
            </v-toolbar>
            <v-card>
            <v-container fluid grid-list-md v-if="verNuevo==0">
            <v-layout row wrap>
                <v-flex xs12>
                    <v-card>
                        <v-card-title class="primary" style="color: white;"><h3>LISTADO DE PROVEEDORES</h3></v-card-title>
                        <v-card-text>
                            <v-flex xs12>
                                <v-text-field class="text-xs-center" v-model="search" append-icon="search" placeholder="................" label="B√∫squeda"></v-text-field>
                            </v-flex>
                            <template>
                               <v-data-table
                                :headers="headers"
                                :items="cliente"
                                :search="search"
                                class="elevation-1">
                                
                                <template slot="items" slot-scope="props">
                                    <td>
                                        <v-icon
                                        small
                                        class="mr-2"
                                        @click="editItem(props.item)"
                                        >
                                        edit
                                        </v-icon>
                                    </td>
                                    <td>{{ props.item.idPersona}}</td>
                                    <td>{{ props.item.documento}}</td>
                                    <td>{{ props.item.numeroDocumento }}</td>
                                    <td>{{ props.item.razonSocial }}</td>
                                    <td>{{ props.item.direccion }}</td>
                                    <td>{{ props.item.celular }}</td>
                                    <td>{{ props.item.email }}</td>
                                    <td>
                                    </td>
                                </template>
                            <template slot="no-data">
                                 <v-btn color="primary" @click="listarProveedor">Resetear</v-btn>
                            </template>
                            </v-data-table>
                            </template>
                        </v-card-text>
                    </v-card>
                </v-flex>
            </v-layout>
            </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    export default {
        data(){
            return {
                tipoArray: [],
                idRazonSocial:'',
                idArea:'',
                area:[],
                empresa:[],
                fecha: new Date().toISOString().substr(0, 10),
                idPersona:'',
                menu2: false,
                menu3: false,
                search: '',
                fecha:'',
                fechaIngreso:'',
                montoCredito:'',
                validaMensaje:[],
                valida:0,
                telefono:'',
                email:'',
                verNuevo:0,
                tipoDoc:'',
                numeroDocumento:'',
                cliente:[],
                razonSocial:'',
                direccion:'',
                dialog: false,
                tabs: null,
                headers: [
                { text: 'OPCIONES', value: 'opciones', sortable: false },
                { text: '#', value: 'idPersona', sortable: false },
                { text: 'TIPO DOCUMENTO', value: 'documento', sortable: false },
                { text: 'NUMERO DOCUMENTO', value: 'numeroDocumento', sortable: false },
                { text: 'RAZON SOCIAL', value: 'razonSocial', sortable: false },
                { text: 'DIRECCION PROVEEDOR', value: 'direccion', sortable: false },
                { text: 'CELULAR ', value: 'celular', sortable: false },
                { text: 'CORREO ELECTRONICO', value: 'email', sortable: false },
                ],
                search: '',
                editedIndex: -1,
                adModal:0,
                adAccion:0,
            }
        },
        computed: {
                formTitle () {
                return this.editedIndex === -1 ? 'NUEVO PROVEEDOR' : 'ACTUALIZAR PROVEEDOR'
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () { 
            this.listarProveedor();
        },
        methods:{
            close (){
                this.dialog = false;
                this.limpiar();
                this.valida=0;

            },
            cerrarModalCliente(){
                this.adModal=0;
            },
            editItem (item) {
                this.idPersona = item.idPersona;
                this.numeroDocumento=item.numeroDocumento;
                this.direccion=item.direccion;
                this.telefono=item.celular;
                this.email=item.email;
                this.razonSocial = item.razonSocial;
                this.editedIndex=1;
                this.dialog = true
            },
            nuevoCliente(){

                this.verNuevo=1;
            },
            mostrar(item){
              this.adModal=1;
              this.idPersona=item.idPersona;
              this.montoCredito=item.montoCredito
            },
            guardarCliente () {
                let me = this;
                if(me.validar()){
                    return
                }
                if(me.editedIndex == -1){
                    console.log(me.numeroDocumento,me.telefono,me.direccion, me.email, me.razonSocial);
                    axios.post('api/Personas/CrearProveedor',{
                        'numeroDocumento':me.numeroDocumento,
                        'telefono':me.telefono,
                        'email':me.email,
                        'direccion':me.direccion,
                        'empresa':me.razonSocial
                    }).then(function(response){
                        me.dialog=false;
                        me.limpiar();
                        me.listarProveedor();
                    }).catch(function(error){
                        console.log(error);
                    });
                }else{
                    axios.put('api/Personas/ActualizarProveedor',{
                       'idPersona': me.idPersona,
                       'numeroDocumento': me.numeroDocumento,
                       'telefono': me.telefono,
                       'email':me.email,
                       'direccion':me.direccion,
                       'empresa':me.razonSocial,
                    }).then(function(response){
                        me.dialog=false;
                        me.limpiar();
                        me.listarProveedor();
                    }).catch(function(error){
                        console.log(error);
                    });
                }
            },
            limpiar(){
                this.numeroDocumento="";
                this.razonSocial="";
                this.direccion="";
                this.telefono="";
                this.email="";
                this.editedIndex=-1;
            },
            listarProveedor() {
                let me=this;
                axios.get('api/Personas/ListarProveedor').then(function(response){
                    me.cliente=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];

                if(this.numeroDocumento.length<10){
                    this.validaMensaje.push("Ingrese numero documento.");
                }
                if(this.direccion.length<3){
                    this.validaMensaje.push("Ingrese direccion.");
                }
                if(this.telefono.length<3){
                    this.validaMensaje.push("Ingrese telefono.");
                }
                if(this.razonSocial.length < 1){
                    this.validaMensaje.push("Seleccione una empresa.");
                }
                if(this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
            alertDisplayRUC() {
                this.$swal('ERROR','RUC no encontrado / No tiene conexion a internet...', 'error')
            },
            consultasDocuments() {
                let me=this;
                axios.get('http://localhost:8080/consultasdoc/peru-consult-api/public/api/v1/ruc/'+me.numeroDocumento+'?token=abcxyz').then(function(response){
                me.consultas=response.data;
                me.razonSocial = me.consultas.razonSocial;
                
                if(me.consultas.direccion=="-"){
                    me.direccion = '-';
                    me.departamento ='';
                    me.provincia ='';
                    me.distrito = '';
                }else{
                    me.direccion = me.consultas.direccion;
                    me.departamento = me.consultas.departamento;
                    me.provincia = me.consultas.provincia;
                    me.distrito = me.consultas.distrito;
                }
                }).catch(err => {
                    me.alertDisplayRUC();
                    this.numeroDocumento="";
                });
                
            }
        }        
    }
</script>