<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>MESAS</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
                <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="B√∫squeda" single-line hide-details></v-text-field>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="550px">
                        <v-btn slot="activator" color="info" dark class="mb-2">Nuevo
                            <v-icon dark right>check_circle</v-icon>
                        </v-btn>
                        <v-card>
                            <v-card-title class="primary" style="color: white;">
                               <h3>{{ formTitle }}</h3>
                            </v-card-title>
                            <v-card-text>
                            <v-container grid-list-md>
                              <v-layout wrap>
                                <v-flex xs6 sm6 md6>
                                    <v-text-field v-model="nombre" type="number" label="NOMBRE"></v-text-field>
                                </v-flex>
                                <v-flex xs6 sm16 md6>
                                    <v-select v-model="idSalon"
                                      :items="salones" label="SALON">
                                    </v-select>
                                </v-flex>

                                <v-flex xs12 sm12 md12>
                                    <v-text-field type="number" v-model="capacidad" label="CAPACIDAD"></v-text-field>
                                </v-flex>

                                <v-flex xs12 sm12 md12 v-show="valida">
                                     <h3><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h3>
                                </v-flex>
                                </v-layout>
                            </v-container>
                            </v-card-text>
                            <v-divider></v-divider>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="error" @click.native="close">CANCELAR
                                    <v-icon dark right>block</v-icon>
                                </v-btn>
                                <v-btn color="info" @click.native="guardar">GUARDAR
                                    <v-icon dark right>check_circle</v-icon>
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                </v-dialog>

                <v-dialog v-model="adModal" max-width="300">
                    <v-card>
                        <v-card-title class="headline info"  style="color: white;" v-if="adAccion==0">¬øActivar Item?</v-card-title>
                        <v-card-title class="headline error"  style="color: white;" v-if="adAccion==1">¬øDesactivar Item?</v-card-title>
                        <v-card-text>
                            Estas a Punto de
                            <span v-if="adAccion==0">Activar</span>
                            <span v-if="adAccion==1">Desactivar</span>
                            Mesa {{adNombre}}
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="green darken-1" dark class="mb-2" @click="activarDesactivarCerrar">CANCELAR</v-btn>
                            <v-btn v-if="adAccion==0" color="info"  class="mb-2" @click="activar">ACTIVAR</v-btn>
                            <v-btn v-if="adAccion==1" color="error" class="mb-2" @click="desactivar">DESACTIVAR</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>

            </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="mesas"
                :search="search"
                class="elevation-1">
                <template slot="items" slot-scope="props">
                <td class="">
                    <v-icon
                    color="green darken-2"
                    small
                    class="mr-2"
                    @click="editItem(props.item)"
                    >
                    edit
                    </v-icon>

                    <template v-if="props.item.condicion">
                        <v-icon
                            color="red darken-2"
                            small
                            @click="activarDesactivarMostrar(1,props.item)">
                            block
                        </v-icon>
                    </template>

                    <template v-else>
                        <v-icon
                            color="red darken-2"
                            small
                            @click="activarDesactivarMostrar(0,props.item)">
                            check
                        </v-icon>
                    </template>
                </td>
                <td>{{ props.item.salon }}</td>
                <td>{{ props.item.nombre }}</td>
                <td>{{ props.item.capacidad }}</td>
                <td>
                    <div v-if="props.item.condicion">
                       <span class="blue--text">Activo</span>
                    </div>
                    <div v-else>
                       <span class="red--text">Inactivo</span>
                    </div>
                </td>

                </template>
                <template slot="no-data">
                    <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    export default {
        data(){
            return {
                mesas:[],
                dialog: false,
                headers: [
                { text: 'OPCIONES', value: 'opciones', sortable: false },
                { text: 'SALON', value: 'salon'},
                { text: 'MESA', value: 'nombre', sortable: false },
                { text: 'CAPACIDAD', value: 'capacidad', sortable: false },
                { text: 'ESTADO', value: 'condicion', sortable: false },
                ],
                search: '',
                editedIndex: -1,
                id:'',
                idSalon:'',
                nombre:'',
                capacidad:'',
                salones:[],
                valida: 0,
                validaMensaje:[],
                adModal:0,
                adAccion:0,
                adNombre:'',
                adId:''  
            }
        },
        computed: {
            formTitle () {
            return this.editedIndex === -1 ? 'NUEVA MESA' : 'ACTUALIZAR MESA'
            }
        },

        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            this.listar();
            this.select();
        },
        methods:{
            listar() {
                let me=this;
                axios.get('api/Mesas/Listar').then(function(response){
                   me.mesas=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            select(){
                let me=this;
                var mesasArray=[];
                axios.get('api/Salones/Select').then(function(response){
                    mesasArray=response.data;
                    mesasArray.map(function(x){
                        me.salones.push({text: x.nombre,value:x.idSalon});
                    });
                }).catch(function(error){
                    alert(console.log(error));
                });
            },
            editItem (item) {
                this.id = item.idMesa;
                this.idSalon= item.idSalon;
                this.nombre = item.nombre;
                this.capacidad = item.capacidad;
                this.editedIndex=1;
                this.dialog = true
            },
            deleteItem (item) {
                const index = this.desserts.indexOf(item)
                confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1)
            },
            close () {
                this.dialog = false;
                this.limpiar();
                this.valida=0;
            },
            limpiar(){
                this.id="",
                this.idSalon="";
                this.nombre=""
                this.capacidad="",
                this.editedIndex=-1;
            },
            guardar () {
                if(this.validar()){
                    return;
                }
                if (this.editedIndex > -1) {
                   let me = this;
                   axios.put('api/Mesas/Actualizar',{
                      'idMesa': me.id,
                      'idSalon':me.idSalon,
                      'nombre':me.nombre,
                      'capacidad':me.capacidad
                   }).then(function(response){
                       me.close();
                       me.listar();
                       me.limpiar();
                   }).catch(function(error){
                      console.log(error);
                   });
                } else {
                   let me = this;
                   axios.post('api/Mesas/Crear',{
                     'nombre':me.nombre,
                     'idSalon':me.idSalon,
                     'capacidad':me.capacidad
                   }).then(function(response){
                       me.close();
                       me.listar();
                       me.limpiar();
                   }).catch(function(error){
                      console.log(error);
                   });
                }
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];

                if (this.nombre.length==0 || this.nombre.length>50){
                    this.validaMensaje.push("Ingrese numero de mesa.");
                }
                if (!this.idSalon){
                    this.validaMensaje.push("Seleccione un salon.");
                }
                if (!this.capacidad || this.capacidad==0){
                    this.validaMensaje.push("Ingrese capacidad de la mesa.");
                }
                if (this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
            activarDesactivarMostrar(accion,item){
              this.adModal=1;
              this.adNombre= item.nombre;
              this.adId=item.idMesa;
              
              if(accion==0){
                this.adAccion=0;
              }
              else if(accion==1){
                this.adAccion=1;
              }
              else{
                  this.adModal=0;
              }
            },
            activar(){
                let me = this;
                axios.put('api/Mesas/Activar/'+this.adId,{}).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();
                }).catch(function(error){
                    alert( console.log(error));
                });
            },
            desactivar(){
                let me = this;
                axios.put('api/Mesas/Desactivar/'+this.adId,{}).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();
                }).catch(function(error){
                    alert( console.log(error));
                   
                });
            },
            activarDesactivarCerrar() {
                this.adModal=0;
            }
        }        
    }
</script>