<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>VENTAS EN LINEA</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
                
            </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="ventas"
                :search="search"
                class="elevation-1">
                <template slot="items" slot-scope="props">
                <td>{{ props.item.usuario }}</td>
                <td>{{ props.item.caja }}</td>
                <td>{{ props.item.montoTotal }}</td>
                </template>
                <template slot="no-data">
                    <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    export default {
        data(){
            return {
                ventas:[],
                color: 'success',
                snackbar: false,
                text: 'REGISTRO GUARDADO EXITOSAMENTE.',
                timeout: 2000,
                dialog: false,
                headers: [
                    { text: 'USUARIO', value: 'usuario', sortable: false },
                    { text: 'CAJA', value: 'caja'},
                    { text: 'TOTAL', value: 'montoTotal', sortable: false }
               
                ],
                search: '',
                editedIndex: -1,
                id:'',
                nombre:'',
                valida: 0,
                validaMensaje:[],
                adModal:0,
                adAccion:0,
                adNombre:'',
                adId:''  
            }
        },
        computed: {
            formTitle () {
             return this.editedIndex === -1 ? 'NUEVA CATEGORIA' : 'ACTUALIZAR CATEGORIA'
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            this.listar();
        },
        methods:{
            listar() {
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/VentaProductos/VentasEnLinea',configuracion).then(function(response){
                    me.ventas=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            }
        }        
    }
</script>