<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>HISTORIAL</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
                <v-menu
                    v-model="menu1"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                    >
                    <template v-slot:activator="{ on }">
                    <v-text-field
                        v-model="fechaentrada"
                        label="FECHA"
                        prepend-icon="event"
                        readonly
                        v-on="on"
                    ></v-text-field>
                    </template>
                    <v-date-picker color="green lighten-1" v-model="fechaentrada" @input="menu1 = false"></v-date-picker>
                </v-menu>

                 <v-text-field  prepend-icon="person" v-model="ruc" label="RUC / NOMBRE" type="text">
                 </v-text-field>
                
                <v-spacer></v-spacer>
                <v-btn color="info" @click="listar">
                    <v-icon>search</v-icon>
                    BUSCAR</v-btn>
            </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="categorias"
                :search="search"
                class="elevation-1">
                <template slot="items" slot-scope="props">
                    <td>{{ props.item.correlativo }}</td>
                    <td>{{props.item.cliente}}</td>
                    <td>{{since(props.item.fecha)}}</td>
                    <td>{{props.item.hora}}</td>
                    <td>S/.{{ props.item.descuento.toFixed(2) }}</td>
                    <td>S/.{{props.item.subtotal.toFixed(2)}}</td>
                    <td>S/.{{props.item.totalPagar.toFixed(2)}}</td>
                    <td>
                        <v-icon  color="red darken-2" dark @click="abrirCorreo(props.item)">mail</v-icon>
                        <v-icon  color="green darken-2" dark @click="abrirWhatsapp(props.item)">person</v-icon>
                    </td>
                </template>
                <template slot="no-data">
                    <v-btn color="primary" @click="listar">
                        Resetear
                    </v-btn>
                </template>
            </v-data-table>
        </v-flex>
        <v-dialog v-model="modalCorreo" max-width="400" persistent>
            <v-card>
                <v-card-title class="headline info"  style="color: white;"><h4>INGRESE CORREO</h4></v-card-title>
                <v-card-text>
                    <v-flex xs12 sm12 md6 lg12 xl12>
                        <v-text-field  prepend-icon="email" v-model="correo" label="Email" type="email">
                        </v-text-field>
                    </v-flex>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn  color="error" dark @click="cerrarModalCorreo()">Cancelar</v-btn>
                    <v-btn  color="info" dark @click="enviarCorreo()">Confirmar</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model="modalWha" max-width="400" persistent>
            <v-card>
                <v-card-title class="headline info"  style="color: white;"><h4>INGRESE WHATSAPP</h4></v-card-title>
                <v-card-text>
                    <v-flex xs12 sm12 md6 lg12 xl12>
                        <v-text-field  prepend-icon="person" v-model="wha" label="WhatsApp" type="number">
                        </v-text-field>
                    </v-flex>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn  color="error" dark @click="cerrarModalWha()">Cancelar</v-btn>
                    <v-btn  color="info" dark @click="enviarWha()">Confirmar</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    import moment from 'moment';
    moment.locale('es');

    export default {
        data(){
            return {
                categorias:[],
                modalCorreo:0,
                correlativo:'',
                search:'',
                color: 'success',
                snackbar: false,
                text: 'Registro guardado Exitosamente.',
                timeout: 2000,
                dialog: false,
                headers: [
                    { text: 'CODIGO', value: 'documento'},
                    { text: 'RAZON', value: 'correlativo', sortable: false },
                    { text: 'FECHA', value: 'fecha', sortable: false },
                    { text: 'HORA', value: 'hora', sortable: false },
                    { text: 'DESCUENTO', value: 'descuento', sortable: false },
                    { text: 'SUBTOTAL', value: 'sub', sortable: false },
                    { text: 'TOTAL', value: 'total', sortable: false },
                    { text: 'OPCIONES', value: '', sortable: false }
                ],
                valida: 0,
                validaMensaje:[],
                adAccion:0,
                adNombre:'',
                adId:'',
                menu1:null,
                menu2: null,
                fechaentrada: '',
                fechasalida:'',
                correo:'',
                modalWha: 0,
                wha : '',
                ruc:''
            }
        },
        computed: {
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            //this.listar();
        },
        methods:{
            cerrarModalWha(){
                this.modalWha = 0;
                this.wha = ''
            },
            enviarWha(){
                let me=this;
                axios.post('api/Proformas/EnvioProformaWhatsapp/'+me.correlativo,{
                }).then(function(response){
                    me.modalWha=0;
                    me.numero=0;
                    me.w();
                }).catch(function(error){
                    me.$swal('ERROR', 'PDF no creado.', 'error');
                    console.log(error);
                });
            },
            w(){
              window.open('https://api.whatsapp.com/send?phone=51'+ this.wha, '_blank');
              this.wha='';
            },
            abrirWhatsapp(item){
                this.modalWha = 1;
                this.correlativo = item.correlativo;
            },
            cerrarModalCorreo(){
                this.modalCorreo = 0;
                this.correo = '';
            },
            enviarCorreo(){
                let me=this;
                axios.post('api/Proformas/EnvioProformaCorreo/'+me.correo+'/'+me.correlativo,{
                }).then(function(response){
                    me.$swal('CONFORME', 'Correo enviado correctamente.', 'success');
                    me.correo="";
                    me.modalCorreo=0;
                    me.numero='';
                }).catch(function(error){
                    me.$swal('ERROR', 'Correo no enviado.', 'error');
                    console.log(error);
                });
            },
            abrirCorreo(item){
                this.correlativo = item.correlativo;
                this.modalCorreo = 1;
            },
            since: function(d){
                return moment(d).format('L');
            },
            listar() {
            if(!this.fechaentrada || this.ruc.length==0){
                    this.$swal('Advertencia.', 'Debe seleccionar ambas fechas para poder realizar la b√∫squeda', 'error')
                }else{
                    let me=this;
                    let header={"Authorization" : "Bearer " + this.$store.state.token};
                    let configuracion= {headers : header};
                    axios.get('api/Proformas/ListarProformas/'+ this.fechaentrada+'/'+this.ruc,configuracion).then(function(response){
                        me.categorias=response.data;
                        console.log(me.categorias);
                    }).catch(function(error){
                        console.log(error);
                    });
                }
            },
            limpiar(){
                this.fechasalida="",
                this.fechaentrada="";
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];
                if(this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            }
        }        
    }
</script>