<template>
    <v-layout justify-center>
        <v-flex xs12>
            <v-toolbar color="white">
                <v-toolbar-title>TABLERO LLEVAR PEDIDO</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="B√∫squeda" single-line hide-details v-if="verNuevo==0"></v-text-field>
                <v-divider
                    class="mx-2"
                    inset
                    vertical>
                </v-divider>
                <v-dialog v-model="Anulacion" max-width="450px" persistent>
