                                <v-container grid-list-md>
                                    <v-layout wrap>
                                        <v-flex text-center>
                                            <v-text-field  prepend-icon="person"  v-model="nombreUsuario" color="primary" label="USUARIO" >
                                            </v-text-field>
                                            <v-text-field  prepend-icon="lock" v-model="password" type="password" color="primary" label="PASSWORD" >
                                            </v-text-field>
                                            <v-textarea  prepend-icon="assignment" v-model="motivo" label="MOTIVO" placeholder="" row="1" auto-grow ></v-textarea>
                                            <v-flex class="red--text" v-if="error">
                                                <h4> {{error}}</h4>
                                            </v-flex>
                                        </v-flex>
                                    </v-layout>
                            </v-container>
                            </v-card-text>
                            <v-divider></v-divider>
                            <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="red darken-1" dark class="mb-2" @click="cerrarAnulacion()" >Cerrar</v-btn>
                            <v-btn v-if="adAccion==0" color="info" class="mb-2" @click="anularDetalleSupervisorDLL()">Confirmar</v-btn>
                            </v-card-actions>
                    </v-card>
                </v-dialog>
                <v-dialog v-model="nuevoCliente" max-width="650px" persistent>
                        <v-card>
                            <v-card-title class="primary" style="color: white;">
                               <h3>NUEVO CLIENTE</h3>
                            </v-card-title>
                            <v-card-text>
                             <v-container grid-list-md>
                                <v-layout wrap>
                                    <v-flex xs6 sm6 md6>
                                        <v-select label="TIPO" v-model="tipoDoc" :items="tdocumento" prepend-icon="assignment" @change="limpiarConsultas()"></v-select>
                                   </v-flex>
                                   <v-flex xs6 sm6 md6> 
                                        <v-text-field  type="number" v-model="numeroDocumentoReniec" label="NUMERO(RUC,DNI)" prepend-icon="looks_3" @keyup.enter="consultasDocuments()"></v-text-field>
                                   </v-flex>
                                   <v-flex xs12 sm16 md12> 
                                        <v-text-field  type="text" v-model="razonSocialReniec" label="RAZON SOCIAL"  prepend-icon="how_to_reg" ></v-text-field>
                                   </v-flex>
                                    <v-flex xs12 sm16 md12> 
                                        <v-text-field v-model="direccionReniec"  type="text" label="DIRECCION FISCAL"  prepend-icon="location_on"></v-text-field>
                                   </v-flex>
                                   <v-flex xs12 sm16 md12> 
                                        <v-text-field  v-model="estado" disabled type="text" label="ESTADO"  prepend-icon="assignment"></v-text-field>
                                   </v-flex>
                                    <v-flex xs6 sm6 md6>
                                        <v-text-field  type="number" v-model="telefono" label="CELULAR" prepend-icon="call" ></v-text-field>
                                    </v-flex>
                                    <v-flex xs6 sm6 md6> 
                                        <v-text-field  type="text" v-model="email" label="EMAIL" prepend-icon="contact_mail"></v-text-field>
                                   </v-flex>
                                    <v-flex xs12 sm12 md12 v-show="valida">
                                        <h4><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h4>
                                    </v-flex>
                                </v-layout>
                            </v-container>
                            </v-card-text>
                             <v-divider></v-divider>
                            <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="error" @click="cancelarCrearCliente()">Cancelar
                                <v-icon dark right>block</v-icon>
                            </v-btn>
                            <v-btn color="info" @click="guardarCliente()">Guardar
                                 <v-icon dark right>check_circle</v-icon>
                            </v-btn>
                            </v-card-actions>
                        </v-card>
                </v-dialog>
                <v-dialog v-model="verClientes" max-width="850px" persistent>
                    <v-card>
                        <v-card-title class="primary" style="color: white;"><h3>LISTA DE CLIENTES</h3></v-card-title>
                        <v-divider></v-divider>
                        <v-card-text>
                            <v-container grid-list-md>
                            <v-layout wrap>
                                <v-flex xs12 sm12 md12 lg12 xl12>
                                    <v-text-field append-icon="search" placeholder="..................." v-model="searchClientes" class="text-xs-center"  label="Busqueda" >
                                    </v-text-field>
                                    <template>
                                        <v-data-table                           
                                            :headers="cabeceraClientes"
                                            :items="cliente"
                                            :search="searchClientes"
                                            class="elevation-1">
                                            <template slot="items" slot-scope="props">
                                                <td >
                                                    <v-icon
                                                    color="red darken-2"
                                                    small
                                                    @click="clienteAdd(props.item)"
                                                    class="mr-2">
                                                    add
                                                    </v-icon>
                                                </td>
                                                <td>{{props.item.idPersona}}</td>
                                                <td>{{props.item.numeroDocumento}}</td>
                                                <td>{{props.item.razonSocial}}</td>
                                                <td>{{props.item.direccion}}</td>
                                                <td>{{props.item.montoCredito}}</td>
                                            </template>
                                            <template slot="no-data">
                                                <span class="red--text">
                                                    <h3>No hay clientes para mostrar.</h3>
                                                </span>
                                            </template>
                                        </v-data-table> 
                                    </template>
                                </v-flex>
                            </v-layout>
                        </v-container>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-btn dark color="red darken" @click="cerrarClientes()">
                                Cerrar
                            </v-btn>
                            <v-btn dark color="primary darken" @click="abrirNuevoCliente()">
                                Nuevo
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
                <v-btn color="info" dark class="mb-2" v-if="verNuevo==0" @click="abrirLLevarCliente()">Nuevo
                            <v-icon dark right>check_circle</v-icon>
                </v-btn>
                <v-dialog v-model="dialog" max-width="400px" persistent>
                        
                        <v-card>
                            <v-card-title class="headline info"  style="color: white;"><h4>NOMBRE CLIENTE</h4></v-card-title>
                            <v-card-text>
                                    <v-flex xs12 sm12 md12>
                                        <v-text-field prepend-icon="person" v-model="nombre" label="NOMBRE"></v-text-field>
                                    </v-flex>
                                    <v-flex xs12 sm12 md12 v-show="valida">
                                        <h4><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h4>
                                    </v-flex>
                            </v-card-text>
                            <v-divider></v-divider>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="error" @click.native="close">CERRAR
                                    <v-icon dark right>block</v-icon>
                                </v-btn>
                                <v-btn color="info"  @click.native="guardar">GUARDAR
                                    <v-icon dark right>check_circle</v-icon>
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>

                <v-dialog v-model="adModal" max-width="400" persistent>
                    <v-card>
                        <v-card-title class="headline info"  style="color: white;"><h4>COMPLETAR PEDIDO LLEVAR</h4></v-card-title>
                        <v-card-text>
                            <p>
                            Estas a punto de
                            completar
                            el item proceso de Llevar, por consiguiente el registro selecionado
                            sera quitado de la lista.</p>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="error"  @click="activarDesactivarCerrar">Cancelar</v-btn>
                            <v-btn color="info" @click="activar">Completar</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
                <v-dialog v-model="verProductos" max-width="850px" persistent>
                    <v-card>
                        <v-card-title class="primary" style="color: white;"><h3>LISTA DE PLATOS</h3></v-card-title>
                        <v-divider></v-divider>
                        <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                <v-flex xs12 sm12 md12 lg12 xl12>
                                    <v-text-field append-icon="search" placeholder="..................." v-model="search" class="text-xs-center"  label="Busqueda platos" >
                                    </v-text-field>
                                    <template>
                                        <v-data-table                           
                                            :headers="cabeceraProductos"
                                            :items="productos"
                                            :search="search"
                                            class="elevation-1">
                                                <template slot="items" slot-scope="props">
                                                <td>
                                                        <v-icon
                                                        color="red darken-2"
                                                        small
                                                        @click="comentarioRegistro(props.item)"
                                                        class="mr-2">
                                                        add
                                                    </v-icon>
                                                </td>
                                                <td>{{props.item.areaProduccion}}</td>
                                                <td>{{props.item.nombre}}</td>
                                                <td>
                                                    <v-chip color="orange" dark>S/{{props.item.precio}}</v-chip>
                                                </td>
                                            </template>
                                            <template slot="no-data">
                                                <span class="red--text">
                                                    <h3>No hay insumos para mostrar.</h3>
                                                </span>
                                            </template>
                                        </v-data-table> 
                                    </template>
                                </v-flex>
                            </v-layout>
                        </v-container>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-btn dark color="red darken" @click="ocultarProductos()">
                                Cerrar
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
                <v-dialog v-model="verDetallePedido" max-width="850px" persistent>
                    <v-card>
                        <v-card-title class="primary" style="color: white;"><h3>DETALLE DEL PEDIDO</h3></v-card-title>
                        <v-divider></v-divider>
                        <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                <v-flex xs12 sm12 md12 lg12 xl12>
                                    <template>
                                        <v-data-table                           
                                            :headers="detalleListar"
                                            :items="detalles"
                                            :search="searching"
                                            class="elevation-1" hide-actions>
                                                <template slot="items" slot-scope="props">
                                                <td>
                                                    <strong>{{ props.item.producto }}</strong>
                                                    <div class="">
                                                        <strong><span class="primary--text">{{props.item.comentario}}</span></strong>
                                                    </div>
                                                </td>
                                                <td> {{ props.item.usuario}}</td>
                                                <td> {{ props.item.cantidad}}</td>
                                                <td> {{ props.item.precio }}</td>
                                                <td> S/.{{ (props.item.cantidad * props.item.precio).toFixed(2)}}</td>
                                                <td>
                                                    <v-chip v-if="props.item.idEstadoPedido==1" color="primary" dark>SOLICITADO</v-chip>
                                                    <v-chip v-if="props.item.idEstadoPedido==2" color="success" dark>PREPARANDO</v-chip>
                                                    <v-chip v-if="props.item.idEstadoPedido==3" color="purple" dark>DESPACHANDO</v-chip>
                                                    <v-chip v-if="props.item.idEstadoPedido==4" color="error" dark>ENTREGADO</v-chip>
                                                </td>
                                            </template>
                                            <template slot="no-data">
                                                <span class="red--text">
                                                    <h3> No hay productos agregados al detalle.</h3>
                                                </span>
                                            </template>
                                        </v-data-table> 
                                    </template>
                                </v-flex>
                            </v-layout>
                        </v-container>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-btn dark color="red darken" @click="ocultarListar()">
                                Cerrar
                            </v-btn>

                        </v-card-actions>
                    </v-card>
                </v-dialog>
                <v-dialog v-model="addComentario" max-width="450px" persistent>
                    <v-card>
                        <v-card-title class="success" style="color: white;"><h3>{{formTitle}}</h3></v-card-title>
                            <v-card-text>
                                <v-container grid-list-md>
                                    <v-layout wrap>
                                        <v-flex xs12 sm10 md12 lg12 xl8>
                                        <h3>{{prod}}</h3>
                                        <h4 style="color: red;">PRECIO:S/.{{prec}}</h4>
                                        </v-flex>
                                        <template v-if="editedIndex == -1">
                                            <v-flex xs12 sm10 md12 lg12 xl8>
                                                <v-text-field  prepend-icon="looks_one" v-model="cant" label="CANTIDAD" type="number">
                                                </v-text-field>
                                            </v-flex>
                                        </template>
                                        <v-flex xs12 sm10 md12 lg12 xl8>
                                            <v-textarea  prepend-icon="assignment" label="" v-model="nota" type="text" placeholder="COMENTARIO"  auto-grow ></v-textarea>
                                        </v-flex>
                                    </v-layout>
                            </v-container>
                            </v-card-text>
                            <v-divider></v-divider>
                            <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="red darken-1" dark class="mb-2" @click="ocultarModalDetalle()">Cerrar</v-btn>
                            <v-btn color="info" class="mb-2"  @click="agregarDetalleComentario()">Confirmar</v-btn>
                            </v-card-actions>
                    </v-card>
                </v-dialog>
                <v-dialog v-model="addDescuento" max-width="450px" persistent>
                <v-card>
                    <v-card-title class="info" primary-title>
                         <h3><span style="color: white;" class="headline">APLICAR DESCUENTO</span></h3>
                    </v-card-title>
                    <v-divider></v-divider>
                        <v-card-text >
                            <v-container grid-list-md>
                                    <h3 style="color: brown;">SUBTOTAL: {{subtotal}}</h3>  
                                    <v-layout wrap>
                                       
                                        <v-autocomplete @change="changedes()"
                                            :items="tipodes"
                                            prepend-icon="assignment"
                                            label="TIPO" chips
                                            required v-model="valor">
                                        </v-autocomplete>
                                        <v-text-field v-on:keyup="prueba()"  v-model="valorDescuento"
                                            prepend-icon="message"
                                            label=" VALOR DESCUENTO"
                                            required
                                            type="number">
                                        </v-text-field>
                                        <v-textarea v-model="motivodes"  prepend-icon="assignment" label="" placeholder="MOTIVO" row="1" auto-grow ></v-textarea>
                                    </v-layout>
                                     <h3 style="color: brown;">DESCUENTO: {{predescuento}}</h3>  
                            </v-container>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="red darken-1" dark class="mb-2" @click="cerrarAplicarDescuento()">CERRAR</v-btn>
                            <v-btn color="info" class="mb-2" @click="confirmarDescuento()">CONFIRMAR</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
 
                <template v-if="verNuevo==2">
                    <v-btn color="gray" @click="aplicarDescuento()" >
                            <v-icon>check_circle</v-icon>APLICAR DESCUENTO
                    </v-btn>
                </template>
                <template v-if="verNuevo==2">
                    <v-btn color="success" @click="regresarLLevar()" >
                            <v-icon>check_circle</v-icon>REGRESAR
                    </v-btn>
                </template>
                <template v-if="verNuevo==2">
                    <h2 v-for="item in caja" :key="item.idEquipo">{{item.cajaNombre}}</h2>
                </template>
            </v-toolbar>
            <v-card>
                <v-container fluid grid-list-md  v-if="verNuevo==0">
                 <v-layout row wrap>
                  <v-flex xs12>
                      <v-card>
                        <v-card-title class="error" style="color: white;"><h3>PEDIDO LLEVAR PENDIENTE</h3></v-card-title>
                        <v-card-text>
                            <v-flex xs12 >
                                <v-data-table
                                    :headers="headers"
                                    :items="pedidoLLevar"
                                    :search="search"
                                    class="elevation-1" hide-actions>
                                    <template slot="items" slot-scope="props">
                                    <td>
                                        <v-icon
                                        color="red darken-2"
                                        small
                                        class="mr-2"
                                        @click="abrirActivar(props.item.idPedido)"
                                        >
                                        block
                                        </v-icon>
                                        <v-icon
                                        color="blue"
                                        small
                                        class="mr-2"
                                        @click="mostrarListar(props.item)"
                                        >
                                        edit
                                        </v-icon>
                                    </td>
                                    <td>{{ props.item.idPedido }}</td>
                                    <td>{{ since(props.item.fecha) }}</td>
                                    <td>{{ props.item.horaPedido }}</td>
                                    <td>{{ props.item.clienteReferencial }}</td>
                                    <td>
                                        <div v-if="props.item.estado === 1">
                                            <h4><span class="red--text">Pendiente</span></h4>
                                        </div>
                                    </td>
                                    </template>
                                    <template slot="no-data">
                                        <v-btn color="primary" @click="listar">Resetear</v-btn>
                                    </template>
                                </v-data-table>
                            </v-flex>
                        </v-card-text>
                      </v-card>
                  </v-flex>
                 </v-layout>
                </v-container>
            </v-card>
            <v-card>
              <v-container fluid grid-list-md  v-if="verNuevo==1">
                <v-layout row wrap>
                    <v-flex xs3>
                        <v-card>
                            <v-card-title class=""><h2>PEDIDO </h2></v-card-title>
                            <v-divider></v-divider>
                            <v-card-text v-for="item in pllevar" :key="item.idPedido">
                               <h5>CLIENTE:</h5><p>{{item.cliente}}</p>
                               <h5>FECHA:</h5><p>{{since(item.fecha)}}</p>
                               <h5>HORA REGISTRO: </h5><p>{{item.hora}}</p>
                            </v-card-text>
                        </v-card>
                    </v-flex>
                    <v-flex xs9>
                        <v-card>
                            <v-card-title class="success" style="color: white;"><h3>ITEM PRODUCTOS</h3></v-card-title>
                                <v-flex xs12>
                                    <v-btn color="teal" dark @click="mostrarProductos()">
                                        <v-icon dark >search</v-icon>BUSCAR
                                    </v-btn>
                                    <v-btn color="primary" dark @click.native="registrarDetalle">
                                        <v-icon dark>search</v-icon>REGISTRAR ORDEN
                                    </v-btn>
                                </v-flex>
                            <v-card-text>
                                <v-data-table
                                    :headers="cabeceraLista"
                                    :items="listarDetalles"
                                    hide-actions
                                    class="elevation-1">
                                    <template  slot="items" slot-scope="props">
                                        <td class="">
                                                <v-icon
                                                color="red darken-2"
                                                @click="eliminarDetalle(listarDetalles,props.item)"
                                                small>
                                                delete
                                                </v-icon>
                                                <v-icon
                                                    color="green darken-2"
                                                    small
                                                    class="mr-2"
                                                    @click="editItem(props.item)">
                                                    edit
                                                </v-icon>
                                        </td>
                                        <td>
                                            {{ props.item.nombre }}
                                            <div class="">
                                                <span class="primary--text">{{props.item.comentario}}</span>
                                            </div>
                                        </td>
                                        <td><v-text-field type="number" v-model="props.item.cantidad"></v-text-field></td>
                                        <td>{{ props.item.precio }}</td>
                                        <td>S/. {{props.item.cantidad * props.item.precio}}</td>
                                    </template>
                                    <template slot="no-data">
                                        <span class="red--text">
                                            <h3> No hay productos agregados al detalle.</h3>
                                        </span>
                                    </template>
                                </v-data-table>
                                <v-flex class="text-xs-right">
                                    <h2>TOTAL PEDIDO: S/ {{totalPedido=(calcularTotalPedido).toFixed(2)}}</h2>
                                </v-flex>
                            </v-card-text>
                            <v-divider></v-divider>
                        </v-card>
                    </v-flex>
                    <v-flex xs12>
                        <v-card>
                            <v-card-title class="error" style="color: white;"><h3>DETALLES PEDIDOS</h3></v-card-title>
                            <v-card-text>
                                <v-data-table
                                    v-model="selected"
                                    :headers="detalllePedidoLlevar"
                                    :items="detalles"
                                    hide-actions
                                    class="elevation-1">
                                    <template v-slot:headers="props">
                                    <tr>
                                        <th class="text-xs-left"
                                            v-for="header in props.headers"
                                            :key="header.text">
                                            {{ header.text }}
                                        </th> 
                                    </tr>
                                    </template>
                                    <template slot="items" slot-scope="props">
                                    <tr>
                                        <td>
                                            <strong>{{ props.item.producto }}</strong>
                                            <div class="">
                                                <strong><span class="primary--text">{{props.item.comentario}}</span></strong>
                                            </div>
                                        </td>
                                        <td>{{ props.item.usuario}}</td>
                                        <td >{{ props.item.cantidad }}</td>
                                        <td >{{ props.item.precio }}</td>
                                        <td >S/. {{ (props.item.cantidad * props.item.precio).toFixed(2)}}</td>
                                        <td>
                                            <v-icon  color="red darken-2" small dark @click="anulardetalle(props.item)">delete</v-icon>
                                        </td>
                                    </tr>
                                    </template>
                                    <template slot="no-data">
                                        <span class="red--text">
                                            <h3> No hay productos agregados al detalle.</h3>
                                        </span>
                                    </template>
                                </v-data-table>
                                <v-flex class="text-xs-right">
                                    <h2>TOTAL PARCIAL: S/ {{subtotal=(calcularTotal).toFixed(2)}} </h2>
                                </v-flex>
                            </v-card-text>  
                        <v-divider></v-divider>
                        <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="green darken-1" dark class="mb-2" @click="cobrar">COBRAR </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-flex>
                </v-layout>
                </v-container>
            </v-card>
            <v-card>
                <v-container fluid grid-list-md  v-if="verNuevo==2">
                    <v-layout row wrap>
                        <v-flex xs7>
                            <v-card>
                                <v-card-title><v-icon>restaurant</v-icon><h3>INFORMACION CIERRE LLEVAR {{adNombre}}</h3></v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-container grid-list-md>
                                            <v-layout wrap>
                                                <v-flex xs6>
                                                    <v-select @change="limpiarContado()" prepend-icon="assignment"
                                                        :items="modalidadpago" v-model="idModalidadPago"
                                                        label="MODALIDAD" chips>
                                                    </v-select>
                                                </v-flex>
                                                <v-flex xs6>
                                                    <v-select :items="tipocomprobante" v-model="idComprobante"  chips  prepend-icon="assignment" label="TIPO DOCUMENTO">
                                                    </v-select>
                                                </v-flex>  
                                                <v-flex>
                                                    <v-text-field type="number" v-model="numeroDocumento" prepend-icon="looks_one" label="DNI/RUC">
                                                    </v-text-field>
                                                </v-flex>
                                                <v-btn  small fab dark color="teal" @click="mostraClientes()">
                                                        <v-icon dark>list</v-icon>
                                                </v-btn>
                                                <v-flex xs12>
                                                    <v-text-field type="text" v-model="nombreCliente" prepend-icon="people" label="RAZON SOCIAL">
                                                    </v-text-field>
                                                </v-flex>
                                                <v-flex xs12>
                                                    <v-text-field type="text" v-if="idModalidadPago==2" disabled v-model="montoCredito"  prepend-icon="looks_one" label="CREDITO">
                                                    </v-text-field>
                                                </v-flex>
                                                <v-flex xs6 v-if="idModalidadPago==1">
                                                    <v-select @change="limpiarEfectivo()" v-if="idmoneda==2" disabled prepend-icon="assignment"
                                                        :items="tipopago" chips v-model="idTipoPago"
                                                        label="TIPO PAGO">
                                                    </v-select>
                                                    <v-select @change="limpiarEfectivo()" v-else prepend-icon="assignment"
                                                        :items="tipopago" chips v-model="idTipoPago"
                                                        label="TIPO PAGO">
                                                    </v-select>
                                                </v-flex>
                                                <v-flex xs6 v-if="idModalidadPago==1">
                                                    <v-select :items="moneda" @change="limpiarMoneda()" v-model="idmoneda" chips prepend-icon="assignment" label="MONEDA">
                                                    </v-select>
                                                </v-flex>    
                                                <v-flex xs6>
                                                    <v-text-field  v-on:keyup="onchange()" v-if="idmoneda==2" type="number"  prepend-icon="looks_one" label="DOLARES" v-model="montoDolar">
                                                    </v-text-field>   
                                                </v-flex>
                                                <v-flex xs6>
                                                    <v-text-field v-if="idmoneda==2" type="number" disabled prepend-icon="looks_one" label="CONVERSION SOLES" v-model="valorConversion">
                                                    </v-text-field>
                                                </v-flex>                               
                                            </v-layout>
                                        </v-container>
                                </v-card-text>
                            </v-card>
                        </v-flex>
                        <v-flex xs5>
                            <v-card>
                                <v-container fluid grid-list-lg>
                                    <v-layout row wrap>
                                        <v-flex xs6>
                                            <v-card color="info" dark>
                                                <v-card-title>SUBTOTAL</v-card-title>
                                                <v-card-text class="headline font-weight-bold" >
                                                   {{subtotal=(calcularTotal).toFixed(2)}} 
                                                </v-card-text>
                                            </v-card>
                                        </v-flex>

                                        <v-flex xs6>
                                            <v-card color="error" dark>
                                                <v-card-title>DESCUENTO</v-card-title>
                                                <v-card-text class="headline font-weight-bold" >
                                                    {{des}}
                                                </v-card-text>
                                            </v-card>
                                        </v-flex>
                                        <v-flex xs6>
                                            <v-card color="gray" dark>
                                                <v-card-title><h4>ICPBER</h4></v-card-title>
                                                <v-card-text class="headline font-weight-bold">
                                                {{icbper.toFixed(2)}}
                                                </v-card-text>
                                            </v-card>
                                         </v-flex>
                                        <v-flex xs6>
                                            <v-card color="success" dark>
                                                <v-card-title>TOTAL</v-card-title>
                                                <v-card-text class="headline font-weight-bold">
                                                   {{totalVenta=((subtotal-des)+icbper).toFixed(2)}} 
                                                </v-card-text>
                                            </v-card>
                                        </v-flex>
                                        <v-flex xs6>
                                              <template v-if="idModalidadPago==1">
                                                <v-card v-if="idTipoPago==1" >
                                                    <v-card-title>EFECTIVO</v-card-title>
                                                    <v-card-text class="headline font-weight-bold">
                                                        <v-text-field v-model="montoEfectivo" type="number">
                                                        </v-text-field>
                                                    </v-card-text>
                                                </v-card>
                                              </template>
                                        </v-flex>
                                        <v-flex xs6>
                                              <template v-if="idModalidadPago==1">
                                                <v-card color="warning" dark v-if="idTipoPago==1" >
                                                    <v-card-title>CAMBIO</v-card-title>
                                                    <v-card-text class="headline font-weight-bold" v-if="montoEfectivo>0">
                                                        {{vuelto=(montoEfectivo-totalVenta).toFixed(2)}}
                                                    </v-card-text>
                                                    <v-card-text class="headline font-weight-bold" v-else>
                                                       0.00
                                                    </v-card-text>
                                                </v-card>
                                              </template>
                                        </v-flex>
                                    </v-layout>
                                </v-container>
                            </v-card>
                        </v-flex>
                        <v-flex xs12>
                            <v-card>
                            <v-card-text>
                            </v-card-text>
                                <v-divider></v-divider>
                            <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn color="green darken-1" dark class="mb-2" @click="registrarPago()">REGISTRAR PAGO</v-btn>
                                </v-card-actions>
                            </v-card>
                    </v-flex>
                    </v-layout>
                </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios';
    import {misMixins} from './../mixins.js'
    import moment from 'moment';
    export default {
        data(){
            return {
                tipoDoc:'',
                icbper:0,
                des:0,
                totalVenta:0,
                descuento:[],
                contadorDes:1,
                subtotal:0,
                predescuento:0,
                valorDescuento:0,
                motivodes:'',
                tipodes:['SOLES','PORCENTAJE'],
                montoEfectivo:0,
                montoDolar:0,
                idPedidoCompletarPago:0,
                addDescuento:0,
                valorDolar:3,
                valor:'',
                valorConversion:0,
                pedidoLLevar:[],
                dialog: false,
                verNuevo:0,
                idAnu:0,
                nombreProducto:'',
                llevar:[],
                nombreUsuario: '',
                password: '',
                motivo: '',
                headers: [
                    { text: 'OPCIONES', value: 'opciones', sortable: false },
                    { text: '#', value: 'idPedido',sortable: false},
                    { text: 'FECHA', value: 'fecha', sortable: false},
                    { text: 'HORA', value: 'horaPedido', sortable: false},
                    { text: 'CLIENTE', value: 'clienteReferencial', sortable: false},
                    { text: 'ESTADO', value: 'estado', sortable: false },
                ],
                detalllePedidoLlevar: [
                    { text: 'PRODUCTO', value: 'producto' },
                    { text: 'USUARIO', value: 'mozo',sortable: false  },
                    { text: 'CANTIDAD', value: 'cantidad', sortable: false  },
                    { text: 'PRECIO', value: 'precio', sortable: false  },
                    { text: 'TOTAL', value: 'total', sortable: false  },
                    { text: 'OPCIONES', value: 'opciones', sortable: false  }                
                ],
                detalleListar: [
                    { text: 'PRODUCTO', value: 'producto' },
                    { text: 'USUARIO', value: 'mozo',sortable: false  },
                    { text: 'CANTIDAD', value: 'cantidad', sortable: false  },
                    { text: 'PRECIO', value: 'precio', sortable: false  },
                    { text: 'TOTAL', value: 'total', sortable: false  },  
                    { text: 'ESTADO', value: '', sortable: false  },          
                ],
                cabeceraLista: [
                    { text: '#', value: 'opciones',sortable: false},
                    { text: 'PRODUCTO', value: 'producto'},
                    { text: 'CANTIDAD', value: 'cant' },
                    { text: 'PRECIO', value: 'precio' },
                    { text: 'TOTAL', value: 'total', sortable: false  },    
                ],
                cabeceraProductos: [
                    {text:  'SELECCIONAR', value: 'seleccionar', sortable: false },
                    { text: 'GRUPO', value: 'areaProduccion'},
                    { text: 'PRODUCTO', value: 'nombre'},
                    { text: 'PRECIO', value: 'precio' ,sortable: false },
                ],
                cabeceraClientes:[
                    { text: 'SELECCIONAR', value: 'opcion',sortable: false},
                    { text: '#', value: 'idPersona',sortable: false},
                    { text: 'DOCUMENTO', value: 'numeroDocumento',sortable: false},
                    { text: 'CLIENTE', value: 'razonSocial',sortable: false},
                    { text: 'DIRECCION', value: 'direccion',sortable: false},
                    { text: 'CREDITO', value: 'montoCredito',sortable: false},

                ],
                listapedidos:[
                    { text: 'CANTIDAD', value: 'cantidad',sortable: false},
                    { text: 'PRODUCTO', value: 'producto',sortable: false},
                    { text: 'TOTAL', value: 'total',sortable: false},

                ],
                apertura:[],
                error: null,
                Anulacion:0,
                searchClientes:'',
                vuelto:0,
                nuevoCliente:0,
                verClientes:0,
                cliente:[],  
                search: '',
                searching: '',
                tipocomprobante:[],
                idComprobante:'',
                editedIndex: -1,
                id:'',
                pago:[],
                nombre:'',
                listarDetalles:[],
                detalles:[],
                adModal:0,
                adAccion:0,
                adNombre:'',
                productos: [],
                adId:'',
                selected:[],
                verProductos:0,
                addComentario:0,
                montoCredito:0,
                pd:[],
                pllevar:[],
                dolares:['100','50','20','10','5','1'],
                prod:'',
                caja:[],
                idprod:'',
                prec:'',
                cant:1,
                nota:'',
                contador:1,
                idPedidoListo: 0,
                verDetallePedido:0,
                modalidadpago:[],
                idModalidadPago:'',
                numeroDocumento:'',
                nombreCliente:'',
                idModalidadPago:'',
                tipopago:[],
                idTipoPago:'',
                moneda:[],
                idmoneda:'',
                comentarioEdit:'',
                ip:'',
                totalPedido:0,
                razonSocial:'',
                direccion:'',
                telefono:'',
                email:'',
                estado:'',
                tdocumento:[],
                consultas:[],
                nombreReniec:'',
                apellido:'',
                razonSocialReniec:'',
                departamento:'',
                provincia:'',
                distrito:'',
                direccionReniec:'',
                valida: 0,
                validaMensaje:[],
                numeroDocumentoReniec:0
            }
        },
        mixins: [misMixins],
        computed: {
            calcularTotal:function(){
                var resultado=0.0;
                for(var i=0;i<this.detalles.length;i++){
                    resultado=resultado+(this.detalles[i].precio*this.detalles[i].cantidad);
                }
                return resultado;
            },
            calcularTotalPedido:function(){
                var resultado=0.0;
                for(var i=0;i<this.listarDetalles.length;i++){
                    resultado=resultado+(this.listarDetalles[i].precio*this.listarDetalles[i].cantidad);
                }
                return resultado;
            },
            formTitle () {
            return this.editedIndex === -1 ? 'DETALLE PLATO' : 'EDITAR DETALLE  PLATO'
            },
        },
        watch: {
            dialog (val) {
                val || this.close();
                this.valida=0;
            }
        },
        created () {
            this.cajaMaquina();
            this.listar();
        },
        methods:{
            eliminarDetalle(arr,item){
                var i= arr.indexOf(item);
                if (i!==-1){
                    arr.splice(i,1);
                }
            },
            since: function(d){
              return moment(d).format('L');  
            },
            //APLICAR DESCUENTO
            cerrarAplicarDescuento(){
               this.addDescuento=0;
               this.valorDescuento=0;
               this.motivodes="";
               this.predescuento=0;
            },
            cerrarAnulacion(){
                this.Anulacion=0;
                this.error=null;
            },
            confirmarDescuento(){
                let me=this;
                let array = [];
                array = this.descuento;

                if(me.descuento==0){
                    this.descuento.push(
                   {
                        "id":this.contadorDes,
                        "tipoDescuento":this.valor,
                        "valorDescuento":this.valorDescuento,
                        "montoDescuento": this.predescuento,
                        "motivo":this.motivodes,
                    })
                }else{
                    for( var i = 0; i < array.length; i++){ 
                        if(array[i].id == this.contadorDes) {
                            array[i].motivo = this.motivodes;
                            array[i].tipoDescuento = this.valor;
                            array[i].valorDescuento = this.valorDescuento;
                            array[i].montoDescuento = this.predescuento;
                            this.descuento = array;
                            this.addDescuento=0;
                        }
                    }      
                }
                this.addDescuento=0;
                this.des = this.descuento[0].montoDescuento;
                this.valorDescuento=0;
                this.motivodes="";
                this.predescuento=0;
            },
            aplicarDescuento(){
                let me=this;
                if(me.descuento==0){
                   this.addDescuento=1;
                }else{
                    this.addDescuento=1;
                    for(var i=0;i<me.descuento.length;i++){
                        this.valor = this.descuento[i].tipoDescuento;
                        this.valorDescuento = this.descuento[i].valorDescuento;
                        this.motivodes = this.descuento[i].motivo;
                        this.predescuento = this.descuento[i].montoDescuento;
                    }
                }
            },
            prueba: function(){
               var resultado=0.0,conversion=0.0;
               if(this.valor=='SOLES'){
                   resultado          =  this.valorDescuento;
                   this.predescuento  =  resultado;
                  
               }else if(this.valor=='PORCENTAJE'){
                   conversion         =  this.subtotal/100;
                   resultado          =  conversion * this.valorDescuento;
                   this.predescuento  =  resultado;
               }
            },
            changedes(){
               this.valorDescuento=0; 
               this.predescuento=0;
               this.motivodes="";
            },
            //INFORMACION CIERRE DE MESA
            onchange() {
               this.valorConversion= this.montoDolar * this.valorDolar;
               this.montoEfectivo = this.valorConversion;
            },
            clienteAdd(item){
                this.idCliente = item.idPersona;
                this.numeroDocumento = item.numeroDocumento;
                this.nombreCliente = item.razonSocial;
                this.montoCredito = item.montoCredito;
                this.verClientes=0;
                this.searchClientes="";
            },
            cajaMaquina() {
                let me=this;
                axios.get('api/DetallePedidos/nombrePC').then(function(response){
                    me.ip=response.data;
                    me.informacionCajaVenta(me.ip);
                }).catch(function(error){
                    console.log(error);
                });
            },
            cerrarClientes(){
                this.verClientes=0;
                this.searchClientes=""
            },
            mostraClientes(){
               this.verClientes=1;
               this.listarClientes();
            },
            listarClientes() {
                let me=this;
                axios.get('api/Personas/Clientes').then(function(response){
                    me.cliente=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            aperturaCaja(idCaja){
              let me=this;
              axios.get('api/AperturaCajas/ValidadAperturaCaja/'+idCaja).then(function(response){
                me.apertura =response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            informacionCajaVenta(ip){
              let me=this;
              axios.get('api/Equipos/ObtenerCaja/'+ip).then(function(response){
                 me.caja=response.data;
                 me.aperturaCaja(me.caja[0].idCaja)
                }).catch(function(error){
                   console.log(error);
                });
            },
            ListapedidoLlevar(id){
                let me=this;
                axios.get('api/PedidoLlevars/Mostrar/'+id).then(function(response){
                    me.pllevar=response.data;
                 }).catch(function(error){
                    console.log(error)
                });
            },
            mostrarListar(item){
                this.verDetallePedido= 1;
                this.listarPedidoDetalles(item.idPedido);
                this.searching = '';
            },
            ocultarListar() {
                this.verDetallePedido= 0;
                this.searching = '';
                this.detalles=[];
            },
            ocultarModalComentario(){
                this.prod="";
                this.prec="";
                this.cant=1;
                this.nota="";
                this.addEditarComentario=0;
            },
            ocultarProductos(){
                this.verProductos=0;
            },
            cobrar(){

                if(this.detalles.length>0){
                    var resultado =0.0;
                     for(var i=0;i<this.detalles.length;i++){
                         if(this.detalles[i].idProducto==2622){
                             resultado = resultado + this.detalles[i].cantidad;
                             this.icbper = resultado * 0.1;
                         }
                    }
                    this.verNuevo=2;
                    this.numeroDocumento="0";
                    this.idCliente=9;
                    this.nombreCliente="PUBLICO GENERAL";
                    this.selectMoneda();
                    this.selecttipopago(); 
                    this.selectModalidadPago();
                    this.selectComprobante();
                }else{
                   this.$swal('Advertencia.', 'No hay registros de pedidos.', 'error')
                }
            },
            volver(){
                this.verNuevo=1;
            },
            abrirLLevarCliente(){
                 if(this.apertura.length==0){
                     this.$swal('ADVERTENCIA.', 'Debe aperturar caja.', 'error')
                      return;
                 }
                this.dialog=true;
            },
            selectComprobante(){
                let me=this;
                var comprobanteArray=[];
                axios.get('api/TipoComprobantes/Listar').then(function(response){
                    comprobanteArray=response.data;
                    comprobanteArray.map(function(x){
                        me.tipocomprobante.push({text: x.comprobante,value:x.idComprobante});
                        me.idComprobante=1;
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
            selectModalidadPago(){
                let me=this;
                var modalidadArray=[];
                axios.get('api/ModalidadPagoes/Listar').then(function(response){
                    modalidadArray=response.data;
                    modalidadArray.map(function(x){
                        me.modalidadpago.push({text:x.modalidad,value:x.idModalidadPago});
                        me.idModalidadPago=1;
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
            selecttipopago(){
                let me=this;
                var tipoArray=[];
                axios.get('api/TipoPagoes/Listar').then(function(response){
                    tipoArray=response.data;
                    tipoArray.map(function(x){
                        me.tipopago.push({text: x.tipo,value:x.idTipoPago});
                        me.idTipoPago=1;
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
            selectMoneda(){
                let me=this;
                var monedadArray=[];
                axios.get('api/Monedas/Listar').then(function(response){
                    monedadArray=response.data;
                    monedadArray.map(function(x){
                        me.moneda.push({text: x.descripcion,value:x.idMoneda});
                        me.idmoneda=1;
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
            alertDisplaP() {
                this.$swal('Advertencia.', 'Debe seleccionar los check para proceder el pago.', 'error')
            },
            ocultarModalDetalle(){
                this.addComentario=0;
                this.editedIndex = -1;
            },
            agregarDetalleComentario(){
                let me=this;
                let array = [];
                array = this.listarDetalles;
                
                if (this.editedIndex > -1) {
                    let aca = this;
                    for( var i = 0; i < array.length; i++){ 
                        if( array[i].id == this.id) {
                            array[i].comentario = this.nota;
                            this.listarDetalles = array;
                            this.ocultarModalDetalle();
                            console.log(this.listarDetalles)
                            return;
                        }
                    }
                }
                else
                {
                    this.listarDetalles.push
                    ({
                        "id":this.contador,
                        "idPedido": me.pllevar[0].idPedido,
                        "idUsuario":me.$store.state.nombreUsuario.IdUsuario,
                        "idProducto":this.idprod,
                        "descripcion":'LLEVAR',
                        "nombre":this.prod,
                        "comentario":this.nota,
                        "cantidad":this.cant,
                        "precio":this.prec
                    });
                   this.contador++
                   console.log(this.listarDetalles)
                }
                this.cant=1;
                this.nota='';
                this.ocultarModalDetalle();
                this.editedIndex = -1;
            },
            toggleAll () {
                if (this.selected.length) this.selected = []
                else this.selected = this.detalles.slice()
            },
            listar() {
                let me=this;
                axios.get('api/PedidoLlevars').then(function(response){
                   me.pedidoLLevar=response.data;
                   me.pedidoLLevar = me.pedidoLLevar.filter( filtro => filtro['estado'] === 1);
                   return me.pedidoLLevar;
                }).catch(function(error){
                    console.log(error);
                });
            },
            registrarDetalle(){  
                let me=this;
                axios.post('api/Pedidos/CrearPedidoDetalle',{
                    "detallePedidos":me.listarDetalles
                }).then(function(response){
                    me.listarDetalles=[];
                    me.listarDetails(me.pllevar[0].idPedido);
                    me.cant=1;
                }).catch(function(error){
                    console.log(error);
                });
            },
            limpiarAnulacion(){
                this.nombreUsuario="";
                this.password="";
                this.motivo="";
            },
            alertDisplaCredito(){
                this.$swal('ADVERTENCIA.', 'no tiene credito disponible.', 'error')
            },
            registrarPago(){
                if(this.idModalidadPago==2 && this.montoCredito==0){
                    this.alertDisplaCredito();
                    return;
                }
                else if(this.idModalidadPago==1 && this.idTipoPago==1 && this.montoEfectivo==0){
                    this.$swal('ADVERTENCIA.', 'Ingrese monto efectivo, para efectuar la venta.', 'error')
                    return;
                }
                else if(this.idModalidadPago==2 &&  this.totalVenta> this.montoCredito){
                    this.$swal('ADVERTENCIA.', 'No tiene el credito suficiente para efectuar la venta, gracias.', 'error')
                    return;
                }
                let me=this;
                axios.post('api/VentaProductos/Crear',{
                    'idUsuario': me.$store.state.nombreUsuario.IdUsuario,
                    'idPedido':me.pllevar[0].idPedido,
                    'idCaja': me.caja[0].idCaja,
                    'idModalidadPago':me.idModalidadPago,
                    'idComprobante':me.idComprobante,
                    'idCliente': me.idCliente,
                    'idTipoPago':me.idTipoPago,
                    'idMoneda':me.idmoneda,
                    'subtotal':me.subtotal,
                    'descuento':me.des,
                    'rc':0,
                    'igv':0,
                    'icbper':me.icbper,
                    'totalPagar':me.totalVenta,
                    'montoDolar':me.montoDolar,
                    'montoEfectivo':me.montoEfectivo,
                    'vuelto':me.vuelto,
                    'tipoVenta': 'LLEVAR',
                    'detalleVentas':me.detalles
                }).then(function(response){
                    me.montoEfectivo=0;
                    me.venta = response.data;    
                    me.descuento = [];
                    me.des=0;
                    me.icbper=0;
                    if(me.idModalidadPago==2){
                        me.guardarCreditoPersonal(me.venta.idCliente,me.venta.idVenta,me.venta.totalPagar)
                    }
                    me.verNuevo=0;
                }).catch(function(error){
                    console.log(error);
                });
                me.montoDolar=0;
                me.valorConversion=0;
            },
            guardarCreditoPersonal(idcliente,idventa,total){  
                let me=this;
                axios.post('api/CreditoPersonal/Crear',{
                    'idCliente':idcliente,
                    'idVenta':idventa,
                    'total':total
                }).then(function(response){
                }).catch(function(error){
                    console.log(error);
                });
            },
            regresarLLevar(){
                this.verNuevo=1;
            },
            mostrarProductos(){
                this.verProductos=1;
                this.listarProductos();
            },
            listarDetails(id){
               let me=this;
               axios.get('api/DetallePedidos/MostrarLlevar/'+id).then(function(response){
                   me.detalles=response.data;
                   }).catch(function(error){
                       console.log(error);
                   });
            },
            listarDetallesFaltaPagar(id){
               let me=this;
               axios.get('api/DetallePedidos/MostrarLlevar/'+id).then(function(response){
                   me.detalles=response.data;
                   }).catch(function(error){
                       console.log(error);
                   });
            },
            validarPago(id){
               let me=this;
               axios.get('api/DetallePedidos/ValidarPagoVenta/'+id).then(function(response){
                    me.pago=response.data;

                    if(me.pago.length>0){
                        me.verNuevo = 1;
                        me.ListapedidoLlevar(id);
                        me.listarDetallesFaltaPagar(id);
                    }else{
                        me.id = '';
                        me.id = id;
                        me.adModal=1;
                    }
                }).catch(function(error){
                    console.log(error);
                });
            },
            listarPedidoDetalles(idPedido){
               let me=this;
               axios.get('api/DetallePedidos/MostrarLlevar/'+idPedido).then(function(response){
                   me.detalles=response.data;
                   }).catch(function(error){
                       console.log(error);
                   });
            },
            listarProductos() {
                let me=this;
                axios.get('api/Productos/Activos').then(function(response){
                    me.productos=response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            editItem(item){
                this.id = item.id;
                this.prod = item.nombre;
                this.prec=  item.precio;
                this.cant = item.cantidad;
                this.nota=  item.comentario;
                this.editedIndex=1
                this.addComentario= 1;
            },
            close () {
                this.dialog = false;
                this.limpiar();
            },
            limpiar(){
                this.id="",
                this.nombre="",
                this.editedIndex=-1;
            },
            comentarioRegistro(item){
                this.ocultarProductos();
                this.idprod=item.idProducto;
                this.prod = item.nombre;
                this.prec=item.precio;
                this.addComentario=1;
                this.search="";
                this.nota = '';
            },
            guardar() {
                if(this.validar()){
                    return;
                }else{
                    let me = this;
                    axios.post('api/PedidoLlevars/CrearPedidoLlevar',{
                        'idUsuario': me.$store.state.nombreUsuario.IdUsuario,
                        'clienteReferencial':me.nombre
                    }).then(function(response){
                        me.close();
                        me.listar();
                        me.limpiar();
                        me.verNuevo = 1;
                        me.llevar = response.data;
                        me.idPedidoListo = me.llevar.idPedido;
                        me.detalles=[];
                        me.searching='';
                        me.search='';
                        me.ListapedidoLlevar(me.llevar.idPedido);
                        me.idPedidoCompletarPago=0;
                    }).catch(function(error){
                       console.log(error);
                    });
                }
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];

                if(this.nombre.length<5 || this.nombre.length>100){
                    this.validaMensaje.push("Ingrese nombre cliente.");
                }
                if(this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
            abrirActivar(id){
               let me = this;
               me.idPedidoCompletarPago = id;
               me.validarPago(me.idPedidoCompletarPago);
            },
            activar(){
                let me = this;
                axios.put('api/PedidoLlevars/ActualizarEstado/'+this.id,{}).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.id="";
                    me.listar();
                }).catch(function(error){
                    alert( console.log(error));
                });
            },
            activarDesactivarCerrar() {
                this.adModal=0;
                this.pago=[];
            }
        }        
    }
</script>