<template>
  <v-layout justify-center>
    <v-flex xs12>
      <v-toolbar color="white" >
        <v-toolbar-title>TABLERO DELIVERY</v-toolbar-title>
        <v-spacer></v-spacer>
          <v-text-field class="text-xs-center" v-model="searchDelivery" append-icon="search" label="Buscar" single-line hide-details v-if="verNuevo==0"></v-text-field>
            <v-divider
                    class="mx-2"
                    inset
                    vertical>
            </v-divider>
        <v-dialog v-model="Anulacion" max-width="450px" persistent>
            <v-card>
                <v-card-title class="error" style="color: white;" primary-title><v-icon dark>restaurant</v-icon><h3> ANULAR PRODUCTO {{nombreProducto}}</h3></v-card-title>
                    <v-card-text>
                        <v-container grid-list-md>
                             <v-layout wrap>
                                 <v-flex text-center>
                                    <v-text-field  prepend-icon="person"  v-model="nombreUsuario" color="primary" label="USUARIO" >
                                    </v-text-field>
                                    <v-text-field  prepend-icon="lock" v-model="password" type="password" color="primary" label="PASSWORD" >
                                    </v-text-field>
                                    <v-textarea  prepend-icon="assignment" v-model="motivo" label="MOTIVO" placeholder="" row="1" auto-grow ></v-textarea>
                                    <v-flex class="red--text" v-if="error">
                                        <h4> {{error}}</h4>
                                    </v-flex>
                         </v-flex>
                             </v-layout>
                       </v-container>
                    </v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions>
                       <v-spacer></v-spacer>
                       <v-btn color="red darken-1" dark class="mb-2" @click="cerrarAnulacion()" >Cerrar</v-btn>
                       <v-btn color="info" class="mb-2" @click="anularDetalleSupervisorDLL()">Confirmar</v-btn>
                    </v-card-actions>
            </v-card>
        </v-dialog>
        
        <v-dialog v-model="verDetallePedido" max-width="850px" persistent>
                    <v-card>
                        <v-card-title class="primary" style="color: white;"><h3>DETALLE DEL PEDIDO</h3></v-card-title>
                        <v-divider></v-divider>
                        <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                <v-flex xs12 sm12 md12 lg12 xl12>
                                    <template>
                                        <v-data-table                           
                                            :headers="detalleListar"
                                            :items="detallesPedidos"
                                            class="elevation-1" hide-actions>
                                                <template slot="items" slot-scope="props">
                                                <td>
                                                    <strong>{{ props.item.producto }}</strong>
                                                    <div class="">
                                                        <strong><span class="primary--text">{{props.item.comentario}}</span></strong>
                                                    </div>
                                                </td>
                                                <td>{{  props.item.usuario}}</td>
                                                <td >{{ props.item.cantidad }}</td>
                                                <td >{{ props.item.precio }}</td>
                                                <td >S/.{{ (props.item.cantidad * props.item.precio).toFixed(2)}}</td>
                                                <td>
                                                     <v-chip v-if="props.item.idEstadoPedido==1" color="primary" dark>SOLICITADO</v-chip>
                                                     <v-chip v-if="props.item.idEstadoPedido==2" color="orange" dark>PREPARANDO</v-chip>
                                                     <v-chip v-if="props.item.idEstadoPedido==3" color="purple" dark>DESPACHANDO</v-chip>
                                                     <v-chip v-if="props.item.idEstadoPedido==4" color="error" dark>ENTREGADO</v-chip>
                                                </td>
                                            </template>
                                            <template slot="no-data">
                                                <span class="red--text">
                                                    <h3> No hay productos agregados al detalle.</h3>
                                                </span>
                                            </template>
                                        </v-data-table> 
                                    </template>
                                </v-flex>
                            </v-layout>
                        </v-container>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-btn dark color="red darken" @click="cerrarDetalle()">
                                Cerrar
                            </v-btn>

                        </v-card-actions>
                    </v-card>
        </v-dialog>
        <v-dialog v-model="nuevoCliente" max-width="650px" persistent>
                        <v-card>
                            <v-card-title class="primary" style="color: white;">
                               <h3>NUEVO CLIENTE</h3>
                            </v-card-title>
                            <v-card-text>
                             <v-container grid-list-md>
                                <v-layout wrap>
                                    <v-flex xs6 sm6 md6>
                                        <v-select label="TIPO" v-model="tipoDoc" :items="tdocumento" prepend-icon="assignment" @change="limpiarConsultas()"></v-select>
                                   </v-flex>
                                   <v-flex xs6 sm6 md6> 
                                        <v-text-field  type="number" v-model="numeroDocumentoReniec" label="NUMERO(RUC,DNI)" prepend-icon="looks_3" @keyup.enter="consultasDocuments()"></v-text-field>
                                   </v-flex>
                                   <v-flex xs12 sm16 md12> 
                                        <v-text-field  type="text" v-model="razonSocialReniec" label="RAZON SOCIAL"  prepend-icon="how_to_reg" ></v-text-field>
                                   </v-flex>
                                    <v-flex xs12 sm16 md12> 
                                        <v-text-field v-model="direccionReniec"  type="text" label="DIRECCION FISCAL"  prepend-icon="location_on"></v-text-field>
                                   </v-flex>
                                   <v-flex xs12 sm16 md12> 
                                        <v-text-field  v-model="estado" disabled type="text" label="ESTADO"  prepend-icon="assignment"></v-text-field>
                                   </v-flex>
                                    <v-flex xs6 sm6 md6>
                                        <v-text-field  type="number" v-model="telefono" label="CELULAR" prepend-icon="call" ></v-text-field>
                                    </v-flex>
                                    <v-flex xs6 sm6 md6> 
                                        <v-text-field  type="text" v-model="email" label="EMAIL" prepend-icon="contact_mail"></v-text-field>
                                   </v-flex>
                                    <v-flex xs12 sm12 md12 v-show="valida">
                                        <h4><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h4>
                                    </v-flex>
                                </v-layout>
                            </v-container>
                            </v-card-text>
                             <v-divider></v-divider>
                            <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="error" @click="cancelarCrearCliente()">Cancelar
                                <v-icon dark right>block</v-icon>
                            </v-btn>
                            <v-btn color="info" @click="guardarCliente()">Guardar
                                 <v-icon dark right>check_circle</v-icon>
                            </v-btn>
                            </v-card-actions>
                        </v-card>
        </v-dialog>
        <v-dialog v-model="addDescuento" max-width="450px" persistent>
                <v-card>
                    <v-card-title class="info" primary-title>
                         <h3><span style="color: white;" class="headline">APLICAR DESCUENTO</span></h3>
                    </v-card-title>
                    <v-divider></v-divider>
                        <v-card-text >
                            <v-container grid-list-md>
                                    <h3 style="color: brown;">SUBTOTAL: {{subtotal}}</h3>  
                                    <v-layout wrap>
                                        <v-autocomplete @change="changedes()"
                                            :items="tipodes"
                                            prepend-icon="assignment"
                                            label="TIPO" chips
                                            required v-model="valor">
                                        </v-autocomplete>
                                        <v-text-field v-on:keyup="prueba()" v-model="valorDescuento"
                                            prepend-icon="message"
                                            label=" VALOR DESCUENTO"
                                            required
                                            type="number">
                                        </v-text-field>
                                        <v-textarea v-model="motivodes"  prepend-icon="assignment" label="" placeholder="MOTIVO" row="1" auto-grow ></v-textarea>
                                    </v-layout>
                                    <h3 style="color: brown;">DESCUENTO: {{predescuento}}</h3>  
                            </v-container>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="red darken-1" dark class="mb-2" @click="cerrarAplicarDescuento()">CERRAR</v-btn>
                            <v-btn color="info" class="mb-2" @click="confirmarDescuento()">CONFIRMAR</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>

        <v-dialog v-model="verClientes" max-width="850px" persistent>
                <v-card>
                    <v-card-title class="primary" style="color: white;"><h3>LISTA DE CLIENTES</h3></v-card-title>
                    <v-divider></v-divider>
                    <v-card-text>
                        <v-container grid-list-md>
                          <v-layout wrap>
                            <v-flex xs12 sm12 md12 lg12 xl12>
                                <v-text-field append-icon="search" placeholder="..................." v-model="searchClientes" class="text-xs-center"  label="Busqueda" >
                                </v-text-field>
                                <template>
                                    <v-data-table                           
                                        :headers="cabeceraClientes"
                                        :items="clienteArray"
                                        :search="searchClientes"
                                        class="elevation-1">
                                         <template slot="items" slot-scope="props">
                                            <td >
                                                 <v-icon
                                                  color="red darken-2"
                                                  small
                                                   @click="clienteAdd(props.item)"
                                                  class="mr-2">
                                                  add
                                                </v-icon>
                                            </td>
                                            <td>{{props.item.idPersona}}</td>
                                            <td>{{props.item.numeroDocumento}}</td>
                                            <td>{{props.item.razonSocial}}</td>
                                            <td>{{props.item.direccion}}</td>
                                            <td>{{props.item.montoCredito}}</td>
                                        </template>
                                        <template slot="no-data">
                                            <span class="red--text">
                                                <h3>No hay clientes para mostrar.</h3>
                                            </span>
                                        </template>
                                    </v-data-table> 
                                </template>
                            </v-flex>
                        </v-layout>
                    </v-container>
                    </v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions>
                        <v-btn dark color="red darken" @click="cerrarClientes()">
                            Cerrar
                        </v-btn>
                        <v-btn dark color="primary darken" @click="abrirNuevoCliente()">
                                Nuevo
                        </v-btn>
                    </v-card-actions>
                </v-card>
        </v-dialog>
        <v-dialog v-model="verMotorizado" max-width="850px" persistent>
            <v-card>
                <v-card-title class="primary" style="color: white;"><h3>ASIGNAR MOTORIZADO</h3></v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <v-container grid-list-md>
                        <v-layout wrap>
                        <v-flex xs12 sm12 md12 lg12 xl12>
                            <template>
                                <v-data-table                           
                                    :headers="cabeceraMotorizado"
                                    :items="motorizado"
                                     class="elevation-1">
                                        <template slot="items" slot-scope="props">
                                             <td>
                                                <v-icon @click="asignacion(props.item)"
                                                    color="red darken-2"
                                                    small
                                                    class="mr-2">
                                                    add
                                                </v-icon>
                                            </td>
                                            <td >{{ props.item.descr√≠pcion }}</td>
                                            <td >{{ props.item.motorizado }}</td>
                                        </template>
                                        <template slot="no-data">
                                            <span class="red--text">
                                                <h3> No hay motorizado disponibles.</h3>
                                            </span>
                                        </template>
                                </v-data-table> 
                            </template>
                        </v-flex>
                    </v-layout>
                </v-container>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                    <v-btn dark color="red darken" @click="cerrarModalMotorizado()">
                        Cerrar
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        
        <v-dialog v-model="addComentario" max-width="450px" persistent>
            <v-card>
                <v-card-title class="success" style="color: white;"><h3>{{formTitle}}</h3></v-card-title>
                    <v-card-text>
                        <v-container grid-list-md>
                             <v-layout wrap>
                                 <v-flex xs12 sm10 md12 lg12 xl8>
                                   <h3>{{prod}}</h3>
                                   <h4 style="color: red;">PRECIO:S/.{{prec}}</h4>
                                 </v-flex>
                                <v-flex xs12 sm10 md12 lg12 xl8>
                                   <template v-if="editedIndex == -1">
                                        <v-text-field  prepend-icon="looks_one" v-model="cant" label="CANTIDAD" type="number">
                                        </v-text-field>
                                    </template>
                                 </v-flex>
                                 <v-flex xs12 sm10 md12 lg12 xl8>
                                    <v-textarea  prepend-icon="assignment" label="" v-model="nota" type="text" placeholder="COMENTARIO"  auto-grow ></v-textarea>
                                 </v-flex>
                            </v-layout>
                       </v-container>
                    </v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions>
                       <v-spacer></v-spacer>
                       <v-btn color="red darken-1" dark class="mb-2" @click="ocultarModalDetalle()">Cerrar</v-btn>
                       <v-btn color="info" class="mb-2" @click="agregarDetalleComentarioDelivery()">Confirmar</v-btn>
                    </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model="verProductos" max-width="850px" persistent>
            <v-card>
                <v-card-title class="primary" style="color: white;"><h3> LISTA DE PLATOS</h3></v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <v-container grid-list-md>
                        <v-layout wrap>
                             <v-flex xs12 sm12 md12 lg12 xl12>
                            <v-text-field append-icon="search" placeholder="..................." v-model="search" class="text-xs-center"  label="Busqueda platos" >
                            </v-text-field>
                            <template>
                                <v-data-table
                                    :headers="cabeceraProductos"
                                    :items="productos"
                                    :search="search"
                                    class="elevation-1">
                                    <template slot="items" slot-scope="props">
                                        <td >
                                                <v-icon
                                                color="red darken-2"
                                                small
                                                @click="comentarioRegistro(props.item)"
                                                class="mr-2">
                                                add
                                            </v-icon>
                                        </td>
                                        <td>{{props.item.areaProduccion}}</td>
                                        <td>{{props.item.nombre}}</td>
                                        <td>
                                            <v-chip color="orange" dark>S/{{props.item.precio}}</v-chip>
                                        </td>
                                    </template>
                                    <template slot="no-data">
                                        <span class="red--text">
                                            <h3>No hay insumos para mostrar.</h3>
                                        </span>
                                    </template>
                                </v-data-table> 
                            </template>
                        </v-flex>
                    </v-layout>
                </v-container>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                    <v-btn dark color="red darken" @click="ocultarProductos()">
                        Cerrar
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-snackbar
            :color="color"
            v-model="snackbar"
            :timeout="timeout">
            {{ text }}
        <v-btn
            flat
            @click="snackbar = false">
            Cerrar
        </v-btn>
        </v-snackbar>
         <template v-if="verNuevo==0" >
            <v-btn color="info" @click="nuevoDelivery()">
                <v-icon>check_circle</v-icon>NUEVO
            </v-btn>
        </template>
        <template v-if="verNuevo==3">
            <v-btn color="gray" @click="aplicarDescuento()">
                    <v-icon>check_circle</v-icon>APLICAR DESCUENTO
            </v-btn>
        </template>
        <template v-if="verNuevo==3" >
            <v-btn color="success" @click="regresarDelivery()">
                <v-icon>check_circle</v-icon>VOLVER
            </v-btn>
        </template>
        <template v-if="verNuevo==3">
         <h2 v-for="item in caja" :key="item.idEquipo">{{item.cajaNombre}}</h2>
        </template>
        </v-toolbar>
        <v-card>
            <template>
             <v-container fluid grid-list-md v-if="verNuevo==0">
                  <v-layout row wrap>
                  <v-flex xs12>
                      <v-card>
                        <v-card-title class="error" style="color: white;"><h3>INFORMACION DELIVERY</h3></v-card-title>
                        <v-card-text>
                            <v-flex xs12 >
                                <v-data-table
                                    :headers="cabeceraDeliveryRegistro"
                                    :items="listadely"
                                    :search="searchDelivery"
                                    hide-actions
                                    class="elevation-1">
                                    <template slot="items" slot-scope="props">
                                    <td>
                                        <template>
                                            <v-icon
                                            color="red darken-2"
                                            small
                                           
                                            @click="asignarMotorizado(props.item)"
                                            >
                                            motorcycle
                                            </v-icon>
                                        </template>
                                        <template>
                                            <v-icon
                                            color="blue"
                                            small
                                            @click="abrirActivar(props.item.idPedido)">
                                            edit
                                            </v-icon>
                                        </template>
                                    </td>
                                    <td>{{ props.item.idPedido }}</td>
                                    <td>{{ props.item.nombre |uppercase}}</td>
                                    <td>{{ props.item.direccion |uppercase}}</td>
                                    <td>{{ props.item.referencia |uppercase}}</td>
                                    <td>{{ props.item.celular |uppercase }}</td>

                                    </template>
                                    <template slot="no-data">
                                        <v-btn color="primary">Resetear</v-btn>
                                    </template>
                                </v-data-table>
                            </v-flex>
                        </v-card-text>
                      </v-card>
                  </v-flex>
                 </v-layout>
            </v-container>
            </template>
        </v-card>
        <v-card>
         <v-container fluid grid-list-md  v-if="verNuevo==1">
          <v-layout row wrap>
               <v-flex xs7>
                 <v-card>
                    <v-card-title class="primary" style="color: white;"><h2> CLIENTE FRECUENTES</h2></v-card-title>
                    <v-divider></v-divider>
                    
                    <v-card-text>
                        <v-flex xs12 sm12 md12 lg12 xl12>
                            <v-text-field append-icon="search" type="number"
                                      class="text-xs-center" v-model="celular" @keyup.enter="listarDirecciones()"
                                      label="Buscar">
                            </v-text-field>
                        </v-flex>
                        <template>
                            <v-data-table
                                :headers="cabeceraDelivery"
                                :items="direcciones"
                                class="elevation-1">
                                <template slot="items" slot-scope="props">
                                    <td >
                                            <v-icon
                                            color="red darken-2"
                                            small
                                            @click="direccioDelivery(props.item)"
                                            class="mr-2">
                                            add
                                        </v-icon>
                                    </td>
                                    <td>{{props.item.cliente }}</td>
                                    <td>{{props.item.direccion}}</td>
                                    <td>{{props.item.referencia}}</td>
                                </template>
                                <template slot="no-data">
                                    <span class="red--text">
                                        <h3>No hay clientes para mostrar.</h3>
                                    </span>
                                </template>
                            </v-data-table> 
                        </template>
                    </v-card-text>
                 </v-card>
               </v-flex>
               <v-flex xs5>
                 <v-card>
                   <v-card-title class="success" style="color: white;"><h2>INFORMACION DELIVERY</h2></v-card-title>
                    <v-divider></v-divider>
                    <v-card-text>
                        <v-text-field
                            prepend-icon="message"
                            v-model="celular"
                            label=" NUMERO CELULAR"
                            required
                            type="text">
                        </v-text-field>
                        <v-text-field
                            prepend-icon="looks_one"
                            v-model="cliente"
                            type="text"
                            label="NOMBRE COMPLETO"
                            required>
                        </v-text-field>
                        <v-textarea  v-model="direccion" prepend-icon="assignment" label="" placeholder="DIRECCCION" row="1" auto-grow ></v-textarea>
                        <v-textarea  v-model="referencia" prepend-icon="assignment" label="" placeholder="REFERENCIA" row="1" auto-grow ></v-textarea>
                        <v-flex xs12 sm12 md12 v-show="valida">
                             <h3> <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h3>
                        </v-flex>
                    </v-card-text>
                    <v-divider></v-divider>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="error" outlined  class="ma-2">Cancelar</v-btn>
                            <v-btn color="info"  outlined  dark class="ma-2" @click.native="guardarPedidoDelivery">Confirmar</v-btn>
                        </v-card-actions>
                 </v-card>
               </v-flex>
          </v-layout>
         </v-container>
        </v-card>
         <v-card>
         <v-container fluid grid-list-md  v-if="verNuevo==2">
          <v-layout row wrap>
              <v-flex xs3>
                 <v-card>
                    <v-card-title class=""><h3> INFORMACION DELIVERY</h3></v-card-title>
                     <v-divider></v-divider>
                        <v-card-text v-for="item in delivery" :key="item.idPedido">
                            <h5>CELULAR:</h5><p>{{item.celular}}</p>
                            <h5>CLIENTE:</h5><p>{{item.nombre |uppercase}}</p>
                            <h5>DIRECCION: </h5><p>{{item.direccion |uppercase}}</p>
                            <h5>REFERENCIA:</h5><p>{{item.referencia |uppercase}}</p>
                        </v-card-text>
                 </v-card>
               </v-flex>
               <v-flex xs9>
                 <v-card>
                    <v-card-title class="success" style="color: white;"><h3> ITEM PRODUCTOS</h3></v-card-title>
                        <v-flex xs12>
                            <v-btn color="teal" dark @click="mostrarProductos()">
                                <v-icon dark >search</v-icon>BUSCAR
                            </v-btn>
                             <v-btn color="primary" dark @click.native="guardar">
                                <v-icon dark>search</v-icon>REGISTRAR ORDEN
                            </v-btn>
                        </v-flex>
                    <v-card-text>
                         <v-data-table
                            :headers="cabeceraLista"
                            :items="listarDetalles"
                            hide-actions
                            class="elevation-1">
                            <template  slot="items" slot-scope="props">
                                  <td class="">
                                        <v-icon
                                        color="red darken-2"
                                        @click="eliminarDetalle(listarDetalles,props.item)"
                                        small>
                                        delete
                                        </v-icon>

                                         <v-icon
                                            color="green darken-2"
                                            small
                                            class="mr-2"
                                            @click="editItem(props.item)"
                                            >
                                            edit
                                        </v-icon>
                                </td>
                                 <td>
                                    {{ props.item.nombre }}
                                     <div class="">
                                         <span class="primary--text">{{props.item.comentario}}</span>
                                    </div>
                                </td>
                                <td><v-text-field type="number" v-model="props.item.cantidad"></v-text-field></td>
                                <td>{{ props.item.precio }}</td>
                                <td>S/. {{props.item.cantidad * props.item.precio}}</td>
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3> No hay productos agregados al detalle.</h3>
                                </span>
                            </template>
                        </v-data-table>
                        <v-flex class="text-xs-right">
                            <h2>TOTAL PEDIDO: S/ {{totalPedido=(calcularTotalPedido).toFixed(2)}}  </h2>
                        </v-flex>
                    </v-card-text>
                    <v-divider></v-divider>
                 </v-card>
               </v-flex>
               <v-flex xs12>
                 <v-card>
                    <v-card-title  class="error" style="color: white;"><h3> DETALLES PEDIDOS </h3></v-card-title>
                    <v-card-text>
                        <v-data-table
                            :headers="headers"
                            :items="detalles"
                            hide-actions
                            class="elevation-1">
                            <template slot="items" slot-scope="props">
                                <td>{{props.item.idPedido}}</td>
                                <td>
                                    {{ props.item.producto }}
                                     <div class="">
                                         <span class="primary--text">{{props.item.comentario}}</span>
                                    </div>
                                </td>
                                <td>{{ props.item.usuario}}</td>
                                <td>{{ props.item.cantidad }}</td>
                                <td>{{ props.item.precio }}</td>
                                <td>S/. {{ (props.item.cantidad * props.item.precio).toFixed(2)}}</td>
                                <td>
                                     <v-icon  color="red darken-2" small dark @click="anulardetalle(props.item)">delete</v-icon>
                                </td>
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3> No hay productos agregados al detalle.</h3>
                                </span>
                            </template>
                        </v-data-table>
                        <v-flex class="text-xs-right">
                            <h2>TOTAL PARCIAL: S/ {{subtotal=(calcularTotal).toFixed(2)}} </h2>
                        </v-flex>
                    </v-card-text>
                   <v-divider></v-divider>
                   <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="green darken-1" dark class="mb-2" @click="cobrar()" >COBRAR </v-btn>
                    </v-card-actions>
                 </v-card>
               </v-flex>
          </v-layout>
         </v-container>
        </v-card>
        <v-card>
            <v-container fluid grid-list-md  v-if="verNuevo==3">
            <v-layout row wrap>
                <v-flex xs7>
                    <v-card>
                        <v-card-title><v-icon>restaurant</v-icon><h3>INFORMACION CIERRE DELIVERY</h3></v-card-title>
                        <v-divider></v-divider>
                        <v-card-text>
                            <v-container grid-list-md>
                                    <v-layout wrap>
                                        <v-flex xs6>
                                            <h3>
                                                <v-select @change="limpiarContado()" prepend-icon="assignment"
                                                    :items="modalidadpago" v-model="idModalidadPago"
                                                    label="MODALIDAD" chips>
                                                </v-select>
                                            </h3>
                                        </v-flex>
                                        <v-flex xs6>
                                            <h3>
                                                <v-select :items="tipocomprobante" v-model="idComprobante"  chips  prepend-icon="assignment" label="TIPO DOCUMENTO">
                                                </v-select>
                                            </h3>
                                        </v-flex>  
                                        <v-flex>
                                            <h3>
                                                <v-text-field type="number" v-model="numeroDocumento" prepend-icon="looks_one" label="DNI/RUC">
                                                </v-text-field>
                                            </h3>
                                        </v-flex>
                                        <v-btn  small fab dark color="teal" @click="mostraClientes()" >
                                                <v-icon dark>list</v-icon>
                                        </v-btn>
                                        <v-flex xs12>
                                            <h3>
                                                <v-text-field type="text" v-model="nombreCliente" prepend-icon="people" label="RAZON SOCIAL">
                                                </v-text-field>
                                            </h3>
                                        </v-flex>
                                        <v-flex xs12>
                                            <h3>
                                                <v-text-field type="text" v-if="idModalidadPago==2" v-model="montoCredito" disabled prepend-icon="looks_one" label="CREDITO">
                                                </v-text-field>
                                            </h3>
                                        </v-flex>
                                        <v-flex xs6 v-if="idModalidadPago==1">
                                            <h3>
                                                <v-select @change="limpiarEfectivo()" prepend-icon="assignment"
                                                    :items="tipopago" chips v-model="idTipoPago"
                                                    label="TIPO PAGO">
                                                </v-select>
                                            </h3>
                                        </v-flex>
                                        <v-flex xs6 v-if="idModalidadPago==1">
                                            <h3>
                                                <v-select  v-if="idTipoPago==1"  :items="moneda" @change="limpiarMoneda()" v-model="idmoneda" chips prepend-icon="assignment" label="MONEDA">
                                                </v-select>
                                                <v-select  v-else disabled :items="moneda" v-model="idmoneda" chips prepend-icon="assignment" label="MONEDA">
                                                </v-select>
                                            </h3>
                                        </v-flex> 
                                        <v-flex xs6>
                                            <h3>
                                                <v-text-field  v-on:keyup="onchange()" v-if="idmoneda==2" type="number"  prepend-icon="looks_one" label="DOLARES" v-model="montoDolar">
                                                </v-text-field>
                                            </h3>
                                        </v-flex>
                                        <v-flex xs6>
                                            <h3>
                                                <v-text-field v-if="idmoneda==2" type="number"  prepend-icon="looks_one" label="CONVERSION SOLES" disabled v-model="valorConversion">
                                                </v-text-field>
                                            </h3>
                                        </v-flex> 
                                        <template  v-if="idTipoPago==4">
                                            <v-flex xs6>
                                                <h3>
                                                    <v-select prepend-icon="assignment"
                                                        :items="tarjetas" v-model="idTarjeta"
                                                        label="TARJETA">
                                                    </v-select>
                                                </h3>
                                            </v-flex>
                                            <v-flex xs6>
                                                    <h3><v-text-field type="number" v-model="mTarjeta" prepend-icon="looks_one" label="MONTO TARJETA">
                                                    </v-text-field></h3>
                                            </v-flex>
                                            <v-flex xs6>
                                                <h3><v-text-field disabled value="EFECTIVO" prepend-icon="looks_one" label="EFECTIVO SOLES"></v-text-field></h3>
                                            </v-flex>
                                            <v-flex xs6>
                                                <h3><v-text-field type="number" v-model="mEfectivo" prepend-icon="looks_one" label="MONTO SOLES"></v-text-field></h3>
                                            </v-flex>
                                            <v-flex class="text-xs-right">
                                                    <h2>TOTAL MIXTO: S/ {{montoEfectivo=(totalMixto).toFixed(2)}}</h2>
                                            </v-flex>
                                     </template>                            
                                    </v-layout>
                                </v-container>
                        </v-card-text>
                    </v-card>
                </v-flex>
                <v-flex xs5>
                    <v-card>
                        <v-container fluid grid-list-lg>
                            <v-layout row wrap>
                                <v-flex xs6>
                                    <v-card color="info" dark>
                                        <v-card-title>SUBTOTAL</v-card-title>
                                        <v-card-text class="headline font-weight-bold" >
                                             {{subtotal=(calcularTotal).toFixed(2)}} 
                                        </v-card-text>
                                    </v-card>
                                </v-flex>

                                <v-flex xs6>
                                    <v-card color="error" dark>
                                        <v-card-title>DESCUENTO</v-card-title>
                                        <v-card-text class="headline font-weight-bold" >
                                             {{des}}                
                                        </v-card-text>
                                    </v-card>
                                </v-flex>
                                <v-flex xs6>
                                 <v-card color="gray" dark>
                                    <v-card-title><h4>ICPBER</h4></v-card-title>
                                    <v-card-text class="headline font-weight-bold">
                                      {{icbper.toFixed(2)}}
                                    </v-card-text>
                                </v-card>
                                </v-flex>
                                <v-flex xs6>
                                    <v-card color="success" dark>
                                        <v-card-title>TOTAL</v-card-title>
                                        <v-card-text class="headline font-weight-bold">
                                             {{totalVenta=((subtotal-des)+icbper).toFixed(2)}} 
                                        </v-card-text>
                                    </v-card>
                                </v-flex>
                                <v-flex xs6>
                                    <template v-if="idModalidadPago==1">
                                         <v-card v-if="idTipoPago==1" >
                                            <v-card-title>EFECTIVO</v-card-title>
                                            <v-card-text class="headline font-weight-bold">
                                                <v-text-field type="number" v-model="montoEfectivo">
                                                </v-text-field>
                                            </v-card-text>
                                        </v-card>
                                      </template>
                                </v-flex>
                                <v-flex xs6>
                                      <template v-if="idModalidadPago==1">
                                        <v-card color="warning" dark v-if="idTipoPago==1">
                                            <v-card-title>CAMBIO</v-card-title>
                                            <v-card-text class="headline font-weight-bold" v-if="montoEfectivo>0">
                                            {{vuelto=(montoEfectivo-totalVenta).toFixed(2)}}
                                            </v-card-text>
                                            <v-card-text class="headline font-weight-bold" v-else>
                                                0.00
                                            </v-card-text>
                                        </v-card>
                                        <v-card color="warning" dark v-if="idTipoPago==4">
                                            <v-card-title><h4>CAMBIO</h4></v-card-title>
                                            <v-card-text class="headline font-weight-bold" >
                                                {{vuelto=(calcularVueltoMixto).toFixed(2)}}
                                            </v-card-text>
                                        </v-card>
                                      </template>
                                </v-flex>
                            </v-layout>
                        </v-container>
                    </v-card>
                </v-flex>
                <v-flex xs12>
                    <v-card>
                    <v-card-text>
                    </v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="green darken-1" dark class="mb-2" @click="registrarPago()">REGISTRAR PAGO</v-btn>
                        </v-card-actions>
                    </v-card>
                    </v-flex>
            </v-layout>
            </v-container>
        </v-card>
      </v-flex>
  </v-layout>
</template>
<script>
    import axios from 'axios'
    import {misMixins} from './../mixins.js'
    export default {
        data(){
            return {
               Anulacion:0,
               error: null,
               icbper:0,
               tarjetas:[],
               tm:0,
               montoDolar:0,
               valorDolar:3,
               totalPedido:0,
               detallesPedidos:[],
               idPedidoMostrar:'',
               verDetallePedido:0,
               id:'',
               valida: 0,
               validaMensaje:[],
               pago:[],
               caja:[],
               totalVenta:0,   
               des:0,
               montoCredito:0,
               contadorDes:1,
               contador:1,
               tipodes:['SOLES','PORCENTAJE'], 
               descuento:[],
               clienteArray:[],
               verClientes:0,
               verMotorizado:0,
               dolares:['100','50','20','10','5','1'],
               numeroDocumento:'',
               idPedidoDelivery:'',
               valorConversion:0,
               idCliente:'',
               nombreCliente:'',
               tipocomprobante:[],
               moneda:[],
               apertura:[],
               tipopago:[],
               modalidadpago:[],
               idmoneda:'',
               idModalidadPago:'',
               idTipoPago:'',
               idComprobante:'',
               color: 'success',
               snackbar: false,
               text: 'ENVIDADO A COCINA EXITOSAMENTE.',
               timeout: 2000,
               vuelto:0,
               checkdir:false,
               idDelivery:'',
               direcciones:[],
               editedIndex: -1,
               celular:'',
               cliente:'',
               direccion:'',
               referencia:'',
               montoEfectivo:0,
               prod:'',
               prec:'',
               cant:1,
               idprod:'',
               nota:'',
               adId:'',
               addEditarComentario:0,
               addComentario:0,
               adIdPedido:0,
               pedido:[],
               ip:'',
               delivery:[],
               motorizado:[],
               subtotal:0,
               search: '',
               productos:[],
               idUsuario:'',
               listarDetalles:[],
               detalles:[],
               listadely:[],
               idAnu:0,
               errorProducto:null,
               verNuevo:0,
               verProductos:0,
               adModal:0,
               adAccion:0,
               addDescuento:0,
               valorDescuento:0,
               valor:'',
               motivodes:'',
               predescuento:0,
               idTarjeta:'',
               mTarjeta:0,
               nombreProducto:'',
               mEfectivo:0,
               nombreUsuario: '',
               password: '',
               motivo: '',
               detalleListar: [
                    { text: 'PRODUCTO', value: 'producto' },
                    { text: 'USUARIO', value: 'mozo',sortable: false  },
                    { text: 'CANTIDAD', value: 'cantidad', sortable: false  },
                    { text: 'PRECIO', value: 'precio', sortable: false  },
                    { text: 'TOTAL', value: 'total', sortable: false  },          
                    { text: 'ESTADO', value: '', sortable: false  },          
               ],
               headers: [
                    { text: '#', value: 'idPedido',sortable: false },
                    { text: 'PRODUCTO', value: 'producto' },
                    { text: 'MOZO', value: 'mozo',sortable: false  },
                    { text: 'CANTIDAD', value: 'cantidad', sortable: false  },
                    { text: 'PRECIO', value: 'precio', sortable: false  },
                    { text: 'TOTAL', value: 'total', sortable: false  },
                    { text: 'OPCIONES', value: 'opciones', sortable: false  }                
                ],
                cabeceraLista: [
                    { text: '#', value: 'opciones',sortable: false},
                    { text: 'PRODUCTO', value: 'producto'},
                    { text: 'CANTIDAD', value: 'cant' },
                    { text: 'PRECIO', value: 'precio' },
                    { text: 'TOTAL', value: 'total', sortable: false  },    
                ],
               cabeceraMotorizado: [
                    { text: 'SELECCIONAR', sortable: false}, 
                    { text: 'DESCRIPCION',sortable: false },
                    { text: 'MOTORIZADO', sortable: false },
               ],
               cabeceraDeliveryRegistro:[
                    { text: 'OPCION', value: 'opciones',sortable: false},
                    { text: '#', value: 'idPedido',sortable: false},
                    { text: 'CLIENTE', value: 'nombre',sortable: false},
                    { text: 'DIRECCION', value: 'direccion',sortable: false},
                    { text: 'REFERENCIA', value:'referencia',sortable: false},
                    { text: 'CELULAR', value:'celular',sortable: false},
               ],
               cabeceraDelivery: [
                    { text: 'OPCION', value: 'opciones',sortable: false},
                    { text: 'CLIENTE', value: 'producto',sortable: false},
                    { text: 'DIRECCION', value: 'cant',sortable: false},
                    { text: 'REFERENCIA', value:'cant',sortable: false},
                ],
                cabeceraProductos: [
                    {text:  'SELECCIONAR', value: 'seleccionar', sortable: false },
                    { text: 'GRUPO', value: 'areaProduccion'},
                    { text: 'PRODUCTO', value: 'nombre'},
                    { text: 'PRECIO', value: 'precio' ,sortable: false },
                ],
                cabeceraClientes:[
                    { text: 'SELECCIONAR', value: 'opcion',sortable: false},
                    { text: '#', value: 'idPersona',sortable: false},
                    { text: 'DOCUMENTO', value: 'numeroDocumento',sortable: false},
                    { text: 'CLIENTE', value: 'razonSocial',sortable: false},
                    { text: 'DIRECCION', value: 'direccion',sortable: false},
                    { text: 'CREDITO', value: 'montoCredito',sortable: false},

                ],
                tipoDoc:'',
                telefono:'',
                email:'',
                estado:'',
                tdocumento:[],
                consultas:[],
                nombreReniec:'',
                apellido:'',
                razonSocialReniec:'',
                departamento:'',
                provincia:'',
                distrito:'',
                direccionReniec:'',
                valida: 0,
                validaMensaje:[],
                numeroDocumentoReniec:0,
                nuevoCliente:0,
                searchDelivery:'',
                searchClientes:''
            }
        },
        mixins: [misMixins],
        computed: {
            calcularTotal:function(){
                var resultado=0.0;
                for(var i=0;i<this.detalles.length;i++){
                    resultado=resultado+(this.detalles[i].precio*this.detalles[i].cantidad);
                }
                return resultado;
            },
            calcularVueltoMixto:function(){
                var resultado=0.0;
                if(this.montoEfectivo=="0.00"){
                    return resultado =0.0;
                }
                else{
                    resultado=(Number(this.montoEfectivo)-Number(this.totalVenta));
                    return resultado;
                }
            } ,
            formTitle () {
            return this.editedIndex === -1 ? 'DETALLE PLATO' : 'EDITAR DETALLE PLATO'
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            this.cajaMaquina();
            this.listarDelivery();
        },
        filters: {
            uppercase: function(str){
                return str.toUpperCase()
            },
            lowercase:function(str){
                return str.toLowerCase()
            },
            capitalize: function(str){
                return str.charAt(0).toUpperCase() + str.slice(1)
            }
        },
        methods:{
            //LISTA DE INFORMACION DELIVERY
            listarDelivery(){
                let me=this;
                axios.get('api/PedidoDelivery/ListarPedidoDelivery').then(function(response){
                     me.listadely=response.data;
                  }).catch(function(error){
                    console.log(error);
                });
            },
            cerrarAnulacion(){
                this.Anulacion=0;
                this.error=null;
            },
            onchange(){
               this.valorConversion= this.montoDolar * this.valorDolar;
               this.montoEfectivo = this.valorConversion;
            },
            abrirActivar(id){
                let me = this;
                me.idPedidoMostrar = id;
                this.ListapedidoDeliery(me.idPedidoMostrar);
            },
            editItem(item){
                this.id = item.id;
                this.prod = item.nombre;
                this.prec=  item.precio;
                this.cant = item.cantidad;
                this.nota=  item.comentario;
                this.editedIndex=1
                this.addComentario= 1;
            },
            limpiarAnulacion(){
                this.nombreUsuario="";
                this.password="";
                this.motivo="";
            },
            ListapedidoDeliery(id){
                let me=this;
                axios.get('api/DetallePedidos/MostrarLlevar/'+id).then(function(response){
                    me.detallesPedidos=response.data;
                    me.verDetallePedido=1;
                 }).catch(function(error){
                    console.log(error)
                });
            },
            cerrarDetalle(){
                this.verDetallePedido=0;
            },
            asignacion(item){
                let me = this;
                axios.put('api/PedidoDelivery/Actualizar',{
                    'idPedidoDelivery': me.idPedidoDelivery,
                    'idMotorizado':item.idMotorizado
                }).then(function(response){
                    me.cerrarModalMotorizado();
                    me.listarDelivery();
                }).catch(function(error){
                    console.log(error);
                });
            },
            mostrarProductos(){
                this.listarProductos();
                this.verProductos=1;
                this.search = '';
            },
            nuevoDelivery(){
                if(this.apertura.length==0){
                     this.$swal('ADVERTENCIA.', 'Debe aperturar caja, para proceder abrir delivery.', 'error');
                     return;
                }
                this.verNuevo=1;
            },
            ocultarProductos(){
                this.verProductos=0;
            },
            eliminarDetalle(arr,item){
                var i= arr.indexOf(item);
                if (i!==-1){
                    arr.splice(i,1);
                }
            },
            ocultarModalDetalle(){
                this.addComentario=0;
                this.editedIndex = -1;
            },
            comentarioRegistro(item){
                this.ocultarProductos();
                this.idprod=item.idProducto;
                this.prod = item.nombre;
                this.prec=item.precio;
                this.addComentario=1;
            },
            guardar(){  
                let me=this;
                axios.post('api/Pedidos/CrearDetallePedidoDelivery',{
                    'detallePedidos':me.listarDetalles
                }).then(function(response){
                    me.snackbar=true;
                    me.listarDetalles=[];
                    me.listarDetails(me.delivery[0].idPedido);
                }).catch(function(error){
                    console.log(error);
                });
            },
            informacionDelivery(id){
                let me=this;
                axios.get('api/PedidoDelivery/ListaInformacionPedidoDelivery/'+id).then(function(response){
                     me.delivery=response.data;
                  }).catch(function(error){
                    console.log(error);
                });
            },
            nuevaDireccion(){
               this.checkdir=true;
               this.direccion="";
               this.referencia="";
            },
            limpiar(){
                this.direccion="";
                this.referencia="";
                this.cliente="";
                this.celular="";
                this.direcciones=[];
            },
            listarDetails(id){
               let me=this;
               axios.get('api/DetallePedidos/DetallePedidoDelivery/'+id).then(function(response){
                   me.detalles=response.data;
                   }).catch(function(error){
                        console.log(error);
                   });
            },
            direccioDelivery(item){
                
                this.idDelivery = item.idDelivery;
                this.cliente = item.cliente;
                this.cliente = this.cliente.toUpperCase();
                this.direccion=item.direccion;
                this.direccion= this.direccion.toUpperCase();
                this.referencia=item.referencia
                this.referencia = this.referencia.toUpperCase();
            },
            encuentra(id){
               var sw=0;
               for(var i=0;i<this.listarDetalles.length;i++){
                    if (this.listarDetalles[i].idProducto==id){
                        sw=1;
                    }
                }
                return sw;
            },
           cajaMaquina() {
                let me=this;
                axios.get('api/DetallePedidos/nombrePC').then(function(response){
                    me.ip=response.data;
                    me.informacionCajaVenta(me.ip);
                }).catch(function(error){
                    console.log(error);
                });
            },
            aperturaCaja(idCaja){
              let me=this;
              axios.get('api/AperturaCajas/ValidadAperturaCaja/'+idCaja).then(function(response){
                me.apertura =response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            informacionCajaVenta(ip){
              let me=this;
              axios.get('api/Equipos/ObtenerCaja/'+ip).then(function(response){
                 me.caja=response.data;
                 me.aperturaCaja(me.caja[0].idCaja)
                }).catch(function(error){
                   console.log(error);
                });
            },
            listarProductos() {
                let me=this;
                axios.get('api/Productos/Activos').then(function(response){
                    me.productos=response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            listarDirecciones(){
                let me=this;
                axios.get('api/Delivery/ListarDireccionDelivery/'+me.celular).then(function(response){
                    me.direcciones=response.data;
                }).catch(function(error){
                    alert(console.log(error));
                });
            },
            //INFORMACION COBRAR DELIVERY
            mostraClientes(){
               this.verClientes=1;
               this.listarClientes();
            },
            cerrarAplicarDescuento(){
               this.addDescuento=0;
               this.valorDescuento=0;
               this.motivodes="";
               this.predescuento=0;
            },
            aplicarDescuento(){
                let me=this;
                if(me.descuento==0){
                   this.addDescuento=1;
                }else{
                    this.addDescuento=1;
                    for(var i=0;i<me.descuento.length;i++){
                        this.valor = this.descuento[i].tipoDescuento;
                        this.valorDescuento = this.descuento[i].valorDescuento;
                        this.motivodes = this.descuento[i].motivo;
                        this.predescuento = this.descuento[i].montoDescuento;
                    }
                }
            },
            confirmarDescuento(){
                let me=this;
                let array = [];
                array = this.descuento;

                if(me.descuento==0){
                   this.descuento.push(
                   {
                        "id":this.contadorDes,
                        "tipoDescuento":this.valor,
                        "valorDescuento":this.valorDescuento,
                        "montoDescuento": this.predescuento,
                        "motivo":this.motivodes,
                    })
                }else{
                    for( var i = 0; i < array.length; i++){ 
                        if(array[i].id == this.contadorDes) {
                            array[i].motivo = this.motivodes;
                            array[i].tipoDescuento = this.valor;
                            array[i].valorDescuento = this.valorDescuento;
                            array[i].montoDescuento = this.predescuento;
                            this.descuento = array;
                            this.addDescuento=0;
                        }
                    }
                }
                this.addDescuento=0;
                this.des = this.descuento[0].montoDescuento;
                this.valorDescuento=0;
                this.motivodes="";
                this.predescuento=0;
            },
            registrarPago(){
                if(this.idModalidadPago==2 && this.montoCredito==0){
                    this.alertDisplaCredito();
                    return;
                }
                else if(this.idModalidadPago==1 && this.idTipoPago==1 && this.montoEfectivo==0){
                    this.$swal('Advertencia.', 'Ingrese monto efectivo, para efectuar la venta.', 'error')
                    return;
                }
                else if(this.vuelto<0){
                    this.$swal('Advertencia.', 'EL monto no debe ser menor .', 'error')
                    return;
                }
                else if(this.idModalidadPago==2 && this.totalVenta > this.montoCredito){
                    this.$swal('Advertencia.', 'No tiene el credito suficiente para efectuar la venta, gracias.', 'error')
                    return;
                }
                else if(this.idTipoPago==4){
                    if(!this.idTarjeta) {
                         this.$swal('Advertencia.', 'Seleccione tarjeta para efectuar la venta, gracias.', 'error')
                         return;
                    }
                    else if(this.vuelto<0){
                         this.$swal('Advertencia.', 'El monto es menor a la que debe cobrar, gracias.', 'error')
                         return;
                    }
                }
                let me=this;
                axios.post('api/VentaProductos/Crear',{
                    'idUsuario': me.$store.state.nombreUsuario.IdUsuario,
                    'idPedido':me.delivery[0].idPedido,
                    'idCaja': me.caja[0].idCaja,
                    'idModalidadPago':me.idModalidadPago,
                    'idComprobante':me.idComprobante,
                    'idCliente': me.idCliente,
                    'idTipoPago':me.idTipoPago,
                    'idMoneda':me.idmoneda,
                    'subtotal':me.subtotal,
                    'descuento':me.des,
                    'rc':0,
                    'igv':0,
                    'icbper':me.icbper,
                    'totalPagar':me.totalVenta,
                    'montoDolar':me.montoDolar,
                    'montoEfectivo':me.montoEfectivo,
                    'vuelto':me.vuelto,
                    'tipoVenta': 'DELIVERY',
                    'detalleVentas':me.detalles
                }).then(function(response){
                    me.montoEfectivo=0;
                    me.venta = response.data;    
                    me.descuento = [];
                    me.des=0;
                    me.icbper=0;
                    me.detalles=[];
                    if(me.idModalidadPago==2){
                        me.guardarCreditoPersonal(me.venta.idCliente,me.venta.idVenta,me.venta.totalPagar)
                    }
                    else if(me.idTipoPago==4){
                        me.pagoMixto(me.venta.idVenta,me.idTarjeta,me.mTarjeta,me.mEfectivo,me.venta.totalPagar)
                        me.limpiarMixto();
                    }
                    me.verNuevo=0;
                    me.listarDetalles=[];
                    me.delivery=[];
                }).catch(function(error){
                    console.log(error);
                });
                me.venta=[];
            },
            guardarCreditoPersonal(idcliente,idventa,total){  
                let me=this;
                axios.post('api/CreditoPersonal/Crear',{
                    'idCliente':idcliente,
                    'idVenta':idventa,
                    'total':total
                }).then(function(response){

                }).catch(function(error){
                    console.log(error);
                });
            },
            alertDisplaCredito() {
                this.$swal('Advertencia.', 'no tiene credito disponible.', 'error')
            },
            clienteAdd(item){
                this.idCliente = item.idPersona;
                this.numeroDocumento = item.numeroDocumento;
                this.nombreCliente = item.razonSocial;
                this.montoCredito = item.montoCredito;
                this.verClientes=0;
                this.searchClientes="";
            },
            listarClientes(){
                let me=this;
                axios.get('api/Personas/Clientes').then(function(response){
                    me.clienteArray=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            cerrarClientes(){
                this.verClientes=0;
                this.searchClientes="";
            },
            //INFORMACION MOTORIZADO
            listaMotorizado(){
               let me=this;
               axios.get('api/Motorizados/Select').then(function(response){
                   me.motorizado=response.data;
                   }).catch(function(error){
                        console.log(error);
                   });
            },
            regresarDelivery(){
                this.verNuevo=2;
            },
            asignarMotorizado(item){
                let me=this;
                me.adIdPedido = item.idPedido
                
                axios.get('api/DetallePedidos/ValidarPagoVenta/'+me.adIdPedido).then(function(response){
                    me.pago=response.data;
                    if(me.pago.length>0){
                       me.verNuevo = 2;
                       me.informacionDelivery(me.adIdPedido);
                       me.listarDetails(me.adIdPedido);
                    }else{
                        me.idPedidoDelivery = item.idPedidoDelivery;
                        me.listaMotorizado();
                        me.verMotorizado=1;
                    }
                }).catch(function(error){
                    console.log(error);
                });
            },
            cerrarModalMotorizado(){
                this.verMotorizado=0;
            },
            //INFORMACION PARA CERRAR CAJA
            cobrar(){
                if(this.detalles.length>0){
                     var resultado =0.0;
                     for(var i=0;i<this.detalles.length;i++){
                         if(this.detalles[i].idProducto==2622){
                             resultado = resultado + this.detalles[i].cantidad;
                             this.icbper = resultado * 0.1;
                         }
                    }
                    this.verNuevo=3;
                    this.numeroDocumento="0";
                    this.idCliente=9;
                    this.nombreCliente="PUBLICO GENERAL";
                    this.selectMoneda();
                    this.selecttipopago();
                    this.selectModalidadPago();
                    this.selectComprobante();
                    this.selectTarjetas();
                }else{
                    this.$swal('Advertencia.', 'No hay registros de pedidos.', 'error')
                }
            },
            changedes(){
               this.valorDescuento=0; 
               this.predescuento=0;
               this.motivodes="";
            },
            prueba: function(){
               var resultado=0.0,conversion=0.0;
               if(this.valor=='SOLES'){
                   resultado          =  this.valorDescuento;
                   this.predescuento  =  resultado;
               }
               else if(this.valor=='PORCENTAJE'){
                   conversion         =  this.subtotal/100;
                   resultado          =  conversion * this.valorDescuento;
                   this.predescuento  =  resultado;
               }
            },
            selectMoneda(){
                let me=this;
                var monedadArray=[];
                axios.get('api/Monedas/Listar').then(function(response){
                    monedadArray=response.data;
                    monedadArray.map(function(x){
                        me.moneda.push({text: x.descripcion,value:x.idMoneda});
                        me.idmoneda=1;
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
            selecttipopago(){
                let me=this;
                var tipoArray=[];
                axios.get('api/TipoPagoes/Listar').then(function(response){
                    tipoArray=response.data;
                    tipoArray.map(function(x){
                        me.tipopago.push({text: x.tipo,value:x.idTipoPago});
                        me.idTipoPago=1;
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
            selectComprobante(){
                let me=this;
                var comprobanteArray=[];
                axios.get('api/TipoComprobantes/Listar').then(function(response){
                    comprobanteArray=response.data;
                    comprobanteArray.map(function(x){
                        me.tipocomprobante.push({text: x.comprobante,value:x.idComprobante});
                        me.idComprobante=1;
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
            selectModalidadPago(){
                let me=this;
                var modalidadArray=[];
                axios.get('api/ModalidadPagoes/Listar').then(function(response){
                    modalidadArray=response.data;
                    modalidadArray.map(function(x){
                        me.modalidadpago.push({text:x.modalidad,value:x.idModalidadPago});
                        me.idModalidadPago=1;
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
            guardarPedidoDelivery(){  

                if(this.validar()){
                    return
                }
                let me=this;
                axios.post('api/Pedidos/CrearPedidoDelivery',{
                    'idUsuario':me.$store.state.nombreUsuario.IdUsuario,
                    'celular':me.celular,
                    'nombre':me.cliente,
                    'direccion':me.direccion,
                    'referencia':me.referencia
                }).then(function(response){
                     me.pedido=response.data;
                     me.limpiar();
                     me.verNuevo=2;
                     me.informacionDelivery(me.pedido.idPedido);
                     me.listarDelivery();
                }).catch(function(error){
                    console.log(error);
                });
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];

                if (this.celular.length!=9){
                    this.validaMensaje.push("Ingrese numero celular 9 digitos.");
                }
                if (this.cliente<3){
                    this.validaMensaje.push("Ingrese nombre cliente.");
                }
                if (this.direccion <3){
                    this.validaMensaje.push("Ingrese direccion.");
                }
                if (this.referencia <3){
                    this.validaMensaje.push("Ingrese referencia.");
                }
                if (this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            }
          }
        }
</script>