<template>
  <v-layout justify-center>
    <v-flex xs12>
      <v-toolbar color="white">
        <v-toolbar-side-icon></v-toolbar-side-icon>
        <v-toolbar-title>REPORTE DESCUENTO PERSONAL</v-toolbar-title>
        <v-spacer></v-spacer>
        <template v-if="verNuevo==0">
              <v-btn color="success"> 
                    <export-excel
                    class   = "btn btn-default"
                    :data   = "descuento"
                    :fields = "json_fields"
                    worksheet = "Lista Descuento de Personales"
                    title="DESCUENTOS DEL  PERSONAL CASA DE LAS ENCHILADAS"
                    name    = "descuento.xls"> Excel
                    </export-excel> 
                <v-icon dark right>cloud_download</v-icon>
             </v-btn>
        </template>
        <template v-if="verNuevo==2">
              <v-btn color="info" @click="regresarIndividual()">
                    <v-icon>check_circle</v-icon>REGRESAR
             </v-btn>
        </template>
        <template v-if="verNuevo==1">
            <v-btn color="warning" @click="enviarReportePagar()">
                    <v-icon>check_circle</v-icon>COBRAR
            </v-btn>
        </template>
        <template v-if="verNuevo==1">
            <v-btn color="info" @click="volver()">
                <v-icon>check_circle</v-icon>REGRESAR
            </v-btn>
        </template>
        <template v-if="verNuevo==1">
              <v-btn color="success"> 
                    <export-excel
                    class   = "btn btn-default"
                    :data   = "descuentoIndividual"
                    :fields = "json_fields2"
                    worksheet = "Reporte Individual"
                    title="DESCUENTOS DEL PERSONAL INDIVIDUAL CASA DE LAS ENCHILADAS"
                    name    = "descuentoindividual.xls"> Excel
                    </export-excel> 
                <v-icon dark right>cloud_download</v-icon>
             </v-btn>
        </template>       
        </v-toolbar>
        <v-card>
         <v-container fluid grid-list-md  v-if="verNuevo==0">
          <v-layout row wrap>
               <v-flex xs12>
                 <v-card>
                   <v-card-title class="primary" style="color: white;"><h3>LISTA DE CREDITO PERSONAL</h3></v-card-title>                        
                    <v-container>
                        <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="B√∫squeda" single-line hide-details></v-text-field>
                    </v-container>
                    <v-card-text>
                        <v-data-table
                            :headers="descuentoPersonal"
                            :items="descuento"
                            hide-actions
                            :search="search"
                            class="elevation-1">
                            <template slot="items" slot-scope="props">
                                <td> 
                                    <v-icon
                                         color="green darken-2"
                                         small
                                         class="mr-2"
                                         @click="reporteIndividual(props.item.idTrabajador, props.item.empleado)"
                                         >
                                        edit
                                    </v-icon>
                                    <v-icon
                                         color="red darken-2"
                                         small
                                         class="mr-2"
                                         @click="Individual(props.item)"
                                         >
                                        check
                                    </v-icon>
                                </td>
                                <td>{{ props.item.empleado}}</td>
                                <td>{{ props.item.cantidad }} </td>
                                <td>S/. {{ props.item.totalDescuentos}}</td>
                               
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3> No hay productos.</h3>
                                </span>
                            </template>
                        </v-data-table>
                         <v-flex class="text-xs-left">
                            <h2>TOTAL CREDITO: S/  {{totalCredito=(calcularTotalCredito).toFixed(2)}} </h2>
                        </v-flex>
                    </v-card-text>
                 </v-card>
               </v-flex>
          </v-layout>
         </v-container>
        </v-card>
        <v-card>
         <v-container fluid grid-list-md  v-if="verNuevo==1">
          <v-layout row wrap>
                <v-flex xs12>
                 <v-card>
                   <v-card-title class="primary" style="color: white;"><h3>REPORTE INDIVIDUAL {{trabajador}}</h3></v-card-title>                        
                    <v-card-text>
                        <v-data-table
                            v-model="selected"
                            :headers="creditosPersonalId"
                            :items="descuentoIndividual"
                            hide-actions
                            select-all
                            :search="search"
                            item-key="idReporte"
                            class="elevation-1">
                             <template v-slot:headers="props">
                                <tr>
                                    <th>
                                        <v-checkbox
                                            :input-value="props.all"
                                            :indeterminate="props.indeterminate"
                                            primary
                                            hide-details
                                            color="error"
                                            @click.stop="toggleAll"
                                           >
                                      </v-checkbox>
                                    </th>
                                    <th class="text-xs-left"
                                        v-for="header in props.headers"
                                        :key="header.text">
                                        {{ header.text }}
                                    </th> 
                                </tr>
                             </template>
                            <template slot="items" slot-scope="props">
                                <tr :active="props.selected" @click="props.selected = !props.selected">
                                <td>
                                    <v-checkbox :input-value="props.selected"  color="error" hide-details></v-checkbox>
                                </td>
                                <td>{{props.item.idTrabajador}}</td>
                                <td>{{props.item.empleado}}</td>
                                <td>{{ since(props.item.fecha) }} </td>
                                <td>{{ props.item.hora.substring(0,8)}}</td>
                                <td>{{ props.item.motivo}}</td>
                                <td>S/. {{ props.item.monto}}</td>
                             </tr>
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3> No hay Informacion.</h3>
                                </span>
                            </template>
                        </v-data-table>
                    </v-card-text>
                 </v-card>
               </v-flex>
                <v-flex xs12>
                    <v-card>
                        <v-card-text>
                            Total Check Seleccionado:S/   <strong> {{totalSelec=(calcularTotalSeleccionados).toFixed(2)}} </strong>
                            Total Consumo Personal:S/     <strong> {{totalCreditoIndividual=(calcularTotalCreditoIndividual).toFixed(2)}}  </strong>
                        </v-card-text>
                    </v-card>
                </v-flex>
          </v-layout>
         </v-container>
        </v-card>
        <Individual :nombre=trabajador :valor=id v-if="verNuevo==3"/>
      </v-flex>
  </v-layout>
</template>
<script>
    import axios from 'axios'
    import DetalleVenta  from './DetalleVenta.vue'
    import Individual  from './Individuales/IndividualPersonal.vue'
    import EventBus from '../../main'
    import moment from 'moment';

    export default {
        data(){
            return {
               json_fields: {
                    'NOMBRE COMPLETO': 'empleado',
                    'CANTIDAD': 'cantidad',
                    'TOTAL DESCUENTOS': 'totalDescuentos',
                },
                json_meta: [
                [
                    {
                      'key': 'charset',
                      'value': 'utf-8'
                    }
                 ]
               ],
                json_fields2: {
                    'NOMBRE COMPLETO': 'empleado',
                    'FECHA': 'fecha',
                    'HORA': 'hora',
                    'MOTIVO': 'motivo',
                    'TOTAL': 'monto',
                },
                json_meta2: [
                [
                    {
                      'key': 'charset',
                      'value': 'utf-8'
                    }
                 ]
               ],
               totalCredito:0,
               totalSelec:0,
               totalCreditoIndividual:0,
               editedIndex: -1,
               trabajador:'',
               verInsumos:0,
               idprod:'',
               descuentoIndividual:[],
               search: '',
               descuento:[],
               selected:[],
               id:0,
               errorProducto:null,
               verNuevo:0,
               descuentoPersonal: [
                    { text: 'OPCIONES', value: 'op',sortable: false  },
                    { text: 'NOMBRE COMPLETO', value: 'empleado',sortable: false },
                    { text: 'CANTIDAD', value: 'cantidad',sortable: false  },
                    { text: 'TOTAL DESCUENTO', value: 'totalDescuentos', sortable: false  },         
                ],
                creditosPersonalId: [
                    { text: '#', value: 'idCliente',sortable: false},
                    { text: 'NOMBRE COMPLETO', value: 'empleado',sortable: false},
                    { text: 'FECHA', value: 'fecha',sortable: false},
                    { text: 'HORA', value: 'hora',sortable: false},
                    { text: 'MOTIVO', value: 'motivo',sortable: false},
                    { text: 'TOTAL', value: 'monto', sortable: false  },    
                ],
            }
        },
        components:{
            DetalleVenta,Individual
        },
        computed: {
            formTitle () {
                return this.editedIndex === -1 ? 'DETALLE DESCUENTO DEL PERSONAL' : 'EDITAR CREDITO PERSONAL'
            },
            calcularTotalCredito:function(){
                var resultado=0.0;
                for(var i=0;i<this.descuento.length;i++){
                    resultado=resultado+(this.descuento[i].totalDescuentos);
                }
                return resultado;
            },
            calcularTotalCreditoIndividual:function(){
                var resultado=0.0;
                for(var i=0;i<this.descuentoIndividual.length;i++){
                    resultado=resultado+(this.descuentoIndividual[i].monto);
                }
                return resultado;
            },    
            calcularTotalSeleccionados:function(){
                var resultado=0.0;
                for(var i=0;i<this.selected.length;i++){
                    resultado=resultado+(this.selected[i].monto);
                }
                return resultado;
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
           this.listarDescuentos();
        },
        methods:{
            toggleAll(){
              if (this.selected.length) this.selected = []
              else this.selected = this.descuentoIndividual.slice()
            },
            since: function(d){
              return moment(d).format('L');  
            },
            volver(){
              this.verNuevo=0;
            },
            listarDescuentos(){
                let me=this;
                axios.get('api/ReporteDescuentos/ReporteDescuentoAgrupados').then(function(response){
                    me.descuento=response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            regresarIndividual(){
                this.verNuevo=1;
            },
            Individual(item){
               this.verNuevo=3;
               this.id = item.idCliente;
               this.trabajador = item.razonSocial;
            }, 
            reporteIndividual(idTra, empleado) {
                this.verNuevo=1;
                let me=this;
                me.id = idTra;
                me.trabajador = empleado;

                axios.get('api/ReporteDescuentos/DetallleReporteDescuento/'+me.id).then(function(response){
                    me.descuentoIndividual=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            enviarReportePagar(){
                let me = this;
                let enviar = [];
                var resultado=[];
                for(var i=0;i<me.selected.length;i++){
                    resultado.push({
                        'idReporte': me.selected[i].idReporte,
                        'idTrabajador': me.selected[i].idTrabajador,
                        'monto': me.selected[i].monto,
                        'motivo': me.selected[i].motivo
                    })
                }
                enviar = {
                    'cobrarReportes':resultado
                }
                if(me.selected.length == 0){
                    me.$swal('Advertencia.', 'Debe seleccionar al menos un item', 'error');
                    return;
                }else{
                    axios.put('api/ReporteDescuentos/Cobrar/', enviar).then(function(response){
                        me.$swal('Genial.', 'Se descont√≥ correctamente', 'success');
                        me.reporteIndividual(me.descuentoIndividual[0].idTrabajador, me.descuentoIndividual[0].empleado);
                        me.listarDescuentos();
                        me.verNuevo=0;
                        }).catch(function(error){
                            console.log(error);
                    });
                }
            }
        }
    }
</script>