<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>INSUMOS</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
                <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="B√∫squeda" single-line hide-details></v-text-field>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="600px">
                        <v-btn slot="activator" color="info" dark class="mb-2">Nuevo
                            <v-icon dark right>check_circle</v-icon>
                        </v-btn> 
                        <v-card>
                            <v-card-title class="primary" style="color: white;" primary-title>
                               <h3>{{ formTitle }}</H3>
                            </v-card-title>
                            <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                <v-flex xs12 sm12 md12>
                                    <v-text-field v-model="nombre" label="NOMBRE"></v-text-field>
                                </v-flex>

                                <v-flex xs6 sm16 md6>
                                    <v-select v-model="idCategoriaInsumo"
                                    :items="categoriaInsumos" label="CATEGORIA" chips>
                                    </v-select>
                                </v-flex>

                                 <v-flex xs6 sm16 md6>
                                    <v-select v-model="idUnidadMedida"
                                        :items="unidad" label="UNIDAD MEDIDA" chips>
                                    </v-select>
                                </v-flex>

                                 <v-flex xs12 sm12 md16>
                                    <v-text-field type="number" v-model="stockMinimo" label="STOCK"></v-text-field>
                                </v-flex>

                                 <v-flex xs12 sm12 md12 v-show="valida">
                                   <h3> <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h3>
                                </v-flex>

                                </v-layout>
                            </v-container>
                            </v-card-text>

                            <v-divider></v-divider>
                
                            <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="error" @click.native="close">Cancelar
                                <v-icon dark right>block</v-icon>
                            </v-btn>
                            <v-btn color="info"  @click.native="guardar">Guardar
                                 <v-icon dark right>check_circle</v-icon>
                            </v-btn>
                            </v-card-actions>
                        </v-card>
                </v-dialog>

                <v-dialog v-model="adModal" max-width="300">
                    <v-card>
                        <v-card-title class="headline info"  style="color: white;" primary-title  v-if="adAccion==0">¬øActivar Item?</v-card-title>
                        <v-card-title class="headline error" style="color: white;" primary-title  v-if="adAccion==1">¬øDesactivar Item?</v-card-title>
                        <v-card-text>
                            Estas a punto de
                            <span v-if="adAccion==0">Activar</span>
                            <span v-if="adAccion==1">Desactivar</span>
                            el item {{adNombre}}
                        </v-card-text>

                        <v-divider></v-divider>

                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="green darken-1" dark class="mb-2"   @click="activarDesactivarCerrar">Cancelar</v-btn>
                            <v-btn v-if="adAccion==0" color="info" class="mb-2"  @click="activar">Activar</v-btn>
                            <v-btn v-if="adAccion==1" color="error" dark class="mb-2" @click="desactivar">Desactivar</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
                <v-btn color="success" dark class="mb-2">
                      <export-excel
                        class   = "btn btn-default"
                        :data   = "insumos"
                        :fields = "json_fields"
                        worksheet = "Lista Insumos"
                        title="STOCK ACTUALIZADO DE INSUMOS"
                        name    = "Insumo.xls"> Excel
                     </export-excel> 
                    <v-icon dark right>cloud_download</v-icon>
                </v-btn> 
            </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="insumos"
                :search="search"
                class="elevation-1">
                <template slot="items" slot-scope="props">
                <td class="">
                    <v-icon
                    color="green darken-2"
                    small
                    class="mr-2"
                    @click="editItem(props.item)"
                    >
                    edit
                    </v-icon>

                    <template v-if="props.item.estado">
                        <v-icon
                            color="red darken-2"
                            small
                            @click="activarDesactivarMostrar(1,props.item)">
                            block
                        </v-icon>
                    </template>

                    <template v-else>
                        <v-icon
                            color="red darken-2"
                            small
                            @click="activarDesactivarMostrar(0,props.item)">
                            check
                        </v-icon>
                    </template>
                </td>
                <td>{{ props.item.nombre }}</td>
                <td>{{ props.item.categoriaInsumo }}</td>
                <td>{{ props.item.medida }}</td>
                <td>{{ props.item.stockMinimo }}</td>
                <td>
                    <div v-if="props.item.estado">
                       <span class="blue--text">Activo</span>
                    </div>
                    <div v-else>
                       <span class="red--text">Inactivo</span>
                    </div>
                </td>

                </template>
                <template slot="no-data">
                    <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    export default {
        data(){
            return {
                json_fields: {
                    'INSUMO': 'nombre',
                    'MEDIDA': 'medida',
                    'CATEGORIA': 'categoriaInsumo',
                    'STOCK': 'stockMinimo',
                },
                json_meta: [
                [
                    {
                      'key': 'charset',
                      'value': 'utf-8'
                    }
                 ]
                ],
                insumos:[],
                idUnidadMedida:'',
                idCategoriaInsumo:'',
                stockMinimo:'',
                dialog: false,
                headers: [
                    { text: 'OPCIONES', value: 'opciones', sortable: false },
                    { text: 'NOMBRE', value: 'nombre'},
                    { text: 'CATEGORIA', value: 'categoriaInsumo'},
                    { text: 'UNIDAD', value: 'medida'},
                    { text: 'STOCK', value: 'stockMinimo'},
                    { text: 'ESTADO', value: 'estado', sortable: false }
               
                ],
                search: '',
                editedIndex: -1,
                id:'',
                nombre:'',
                categoriaInsumos:[],
                unidad:[],
                valida: 0,
                validaMensaje:[],
                adModal:0,
                adAccion:0,
                adNombre:'',
                adId:'' ,
                idInsumo:'',
            }
        },
        computed: {
            formTitle () {
            return this.editedIndex === -1 ? 'NUEVO INSUMO' : 'ACTUALIZAR INSUMO'
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            this.listar();
            this.selectCat();
            this.selectUnidades();
        },
        methods:{
            listar() {
                let me=this;
                axios.get('api/Insumos/Listar').then(function(response){
                    me.insumos=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
           
            editItem (item) {
                this.idInsumo = item.idInsumo;
                this.nombre = item.nombre;
                this.stockMinimo = item.stockMinimo;
                this.idCategoriaInsumo = item.idCategoriaInsumo;
                this.idUnidadMedida= item.idUnidadMedida;
                this.editedIndex=1;
                this.dialog = true
            },
            deleteItem (item) {
                const index = this.desserts.indexOf(item)
                confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1)
            },
            close () {
                this.dialog = false;
                this.limpiar();
                this.valida=0;
            },
            limpiar(){
                this.id="",
                this.nombre=""
                this.idCategoriaInsumo=""
                this.stockMinimo=""
                this.idUnidadMedida=""
                this.editedIndex=-1;
            },
            guardar () {
                if(this.validar()){
                    return;
                }
                if (this.editedIndex > -1) {
                   let me = this;
                   axios.put('api/Insumos/Actualizar',{
                       'idInsumo': me.idInsumo,
                       'nombre': me.nombre,
                       'idCategoriaInsumo':me.idCategoriaInsumo,
                       'idUnidadMedida': me.idUnidadMedida,
                       'stockMinimo':me.stockMinimo
                   }).then(function(response){
                       me.close();
                       me.listar();
                       me.limpiar();
      
                   }).catch(function(error){
                      console.log(error);
                   });
                
                } else {
                   let me = this;
                   axios.post('api/Insumos/Crear',{
                       'nombre': me.nombre,
                       'idCategoriaInsumo':me.idCategoriaInsumo,
                       'idUnidadMedida': me.idUnidadMedida,
                       'stockMinimo':me.stockMinimo
                   }).then(function(response){
                       me.close();
                       me.listar();
                       me.limpiar();
      
                   }).catch(function(error){
                      console.log(error);
                   });
                }
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];

                if(this.nombre.length<3 || this.nombre.length>100){
                    this.validaMensaje.push("Ingrese nombre insumo.");
                }
                if(!this.idCategoriaInsumo){
                    this.validaMensaje.push("Seleccione categoria.");
                }
                if(!this.idUnidadMedida){
                    this.validaMensaje.push("Seleccione unidad medida.");
                }
                if(this.stockMinimo==0){
                    this.validaMensaje.push("Ingrese stock minimo.");
                }
                if(this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
            selectCat(){
                let me=this;
                var cat=[];
                axios.get('api/categoriaInsumos/Select').then(function(response){
                    cat=response.data;
                    cat.map(function(x){
                        me.categoriaInsumos.push({text: x.nombre,value:x.idCategoriaInsumo});
                    });
                }).catch(function(error){
                    alert(console.log(error));
                });
            },
            selectUnidades(){
                let me=this;
                var unid=[];
                axios.get('api/UnidadMedidas/Select').then(function(response){
                    unid=response.data;
                    unid.map(function(x){
                        me.unidad.push({text: x.nombreMedia,value:x.idUnidadMedida});
                    });
                }).catch(function(error){
                    alert(console.log(error));
                });
            },
            activarDesactivarMostrar(accion,item){
              this.adModal=1;
              this.adNombre= item.nombre;
              this.adId=item.idInsumo;
              
              if(accion==0){
                this.adAccion=0;
              }
              else if(accion==1){
                this.adAccion=1;
              }
              else{
                  this.adModal=0;
              }
            },
            activar(){
                let me = this;
                axios.put('api/Insumos/Activar/'+this.adId,{}).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();
                }).catch(function(error){
                    alert( console.log(error));
                });
            },
            desactivar(){
                let me = this;
                axios.put('api/Insumos/Desactivar/'+this.adId,{}).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();
                }).catch(function(error){
                    alert( console.log(error));
                });
            },
            activarDesactivarCerrar() {
                this.adModal=0;
            }
        }        
    }
</script>