router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.libre)){
      next();
  } else if (store.state.nombreUsuario && store.state.nombreUsuario.Rol== 'ADMINISTRADOR'){
    if (to.matched.some(record => record.meta.administrador)){
      next();
    }
  } else if (store.state.nombreUsuario && store.state.nombreUsuario.Rol== 'ALMACENERO'){
    if (to.matched.some(record => record.meta.almacenero)){
      next();
    }
  } else if (store.state.nombreUsuario && store.state.nombreUsuario.Rol== 'VENDEDOR'){
    if (to.matched.some(record => record.meta.vendedor)){
      next();
    }
   }else if (store.state.nombreUsuario && store.state.nombreUsuario.Rol== 'COCINA'){
    if (to.matched.some(record => record.meta.cocina)){
      next();
    }
  } else if (store.state.nombreUsuario && store.state.nombreUsuario.Rol== 'RRHH'){
      if (to.matched.some(record => record.meta.recursosHumanos)){
       next();
      }
  } else if (store.state.nombreUsuario && store.state.nombreUsuario.Rol== 'CONTABILIDAD'){
      if (to.matched.some(record => record.meta.contabilidad)) {
        next();
      }
  } else if (store.state.nombreUsuario && store.state.nombreUsuario.Rol== 'CAJERO'){
      if (to.matched.some(record => record.meta.cajero)){
        next();
      }
  } else if (store.state.nombreUsuario && store.state.nombreUsuario.Rol== 'DELIVERY'){
      if (to.matched.some(record => record.meta.delivery)){
        next();
      }
  } else if (store.state.nombreUsuario && store.state.nombreUsuario.Rol== 'SUPERVISOR'){
      if (to.matched.some(record => record.meta.supervisor)){
        next();
      }
  }else if (store.state.nombreUsuario && store.state.nombreUsuario.Rol== 'PROFORMAS'){
      if (to.matched.some(record => record.meta.proformas)){
        next();
      }
  } else{
    next({name: 'login'});
  }
})
