<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar color="white">
                <v-toolbar-title>CLIENTES</v-toolbar-title>
                <v-divider
                    class="mx-2"
                    inset
                    vertical
                ></v-divider>
                <v-spacer></v-spacer>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="850px" persistent>
                        <v-btn slot="activator" color="info" dark class="mb-2">Nuevo
                             <v-icon dark right>check_circle</v-icon>
                        </v-btn>
                        <v-card>
                            <v-card-title class="primary" style="color: white;">
                               <h3>{{formTitle}}</h3>
                            </v-card-title>
                            <v-card-text>
                             <v-container grid-list-md>
                                <v-layout wrap>
                                   <v-flex xs6 sm6 md6>
                                        <v-select label="TIPO" type="text" v-model="tipoDoc" :items="tipo" prepend-icon="assignment"></v-select>
                                   </v-flex>
                                   <v-flex xs6 sm6 md6> 
                                        <v-text-field  type="number" v-model="numeroDocumento" label="NUMERO(RUC,DNI)" prepend-icon="looks_3" @keyup.enter="consultasDocuments()"></v-text-field>
                                   </v-flex>
                                   <v-flex xs12 sm16 md12> 
                                        <v-text-field  type="text" v-model="razonSocial" label="RAZON SOCIAL"  prepend-icon="how_to_reg" ></v-text-field>
                                   </v-flex>
                                    <v-flex xs12 sm16 md12> 
                                        <v-text-field v-model="direccion"  type="text" label="DIRECCION FISCAL"  prepend-icon="location_on"></v-text-field>
                                   </v-flex>
                                   <v-flex xs12 sm16 md12> 
                                        <v-text-field  v-model="estado" disabled  type="text" label="ESTADO"  prepend-icon="assignment"></v-text-field>
                                   </v-flex>
                                    <v-flex xs6 sm6 md6>
                                        <v-text-field  type="number" v-model="telefono" label="CELULAR" prepend-icon="call" ></v-text-field>
                                    </v-flex>
                                    <v-flex xs6 sm6 md6> 
                                        <v-text-field  type="text" v-model="email" label="EMAIL" prepend-icon="contact_mail"></v-text-field>
                                   </v-flex>
                                   <v-flex xs12 sm12 md12 v-show="valida">
                                     <h3><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h3>
                                   </v-flex>
                                </v-layout>
                            </v-container>
                            </v-card-text>
                             <v-divider></v-divider>
                            <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="error" @click.native="close">Cancelar
                                <v-icon dark right>block</v-icon>
                            </v-btn>
                            <v-btn color="info" @click="guardarCliente()">Guardar
                                 <v-icon dark right>check_circle</v-icon>
                            </v-btn>
                            </v-card-actions>
                        </v-card>
                </v-dialog>
                <v-dialog v-model="adModal" max-width="300" persistent>
                    <v-card>
                        <v-card-title class="headline info"  style="color: white;"><h4>ACTUALIZAR CLIENTE</h4></v-card-title>
                        <v-card-text>
                            <h4>¬øDesea que este cliente sea considerado como un proveedor?</h4>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn  color="error" dark @click="cerrarModalCliente">Cancelar</v-btn>
                            <v-btn  color="info" dark @click="actualizarCliente()">Confirmar</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>
            <v-card>
            <v-container fluid grid-list-md v-if="verNuevo==0">
            <v-layout row wrap>
                <v-flex xs12>
                    <v-card>
                        <v-card-title class="primary" style="color: white;"><h3>ITEM CLIENTES</h3></v-card-title>
                        <v-card-text>
                            <v-flex xs12>
                                <v-text-field class="text-xs-center" v-model="search" append-icon="search" placeholder="................" label="B√∫squeda"></v-text-field>
                            </v-flex>
                            <template>
                               <v-data-table
                                :headers="headers"
                                :items="cliente"
                                :search="search"
                                class="elevation-1">
                                <template slot="items" slot-scope="props">
                                    <td >
                                        <v-icon
                                            small
                                            class="mr-2"
                                            @click="editItem(props.item)"
                                             >
                                           edit
                                        </v-icon>
                                        <v-icon
                                            v-if="props.item.idTipoPersona!=4"
                                            small
                                            class="mr-2"
                                            @click="mostrar(props.item)"
                                             >
                                           block
                                        </v-icon>
                                    </td>
                                    <td>{{ props.item.documento}}</td>
                                    <td>{{ props.item.numeroDocumento }}</td>
                                    <td>{{ props.item.razonSocial }}</td>
                                    <td>{{ props.item.direccion }}</td>
                                    <td>{{ props.item.montoCredito }}</td>
                                    <td>
                                    </td>
                                </template>
                            <template slot="no-data">
                                 <v-btn color="primary" @click="listarClientes">Resetear</v-btn>
                            </template>
                            </v-data-table>
                            </template>
                        </v-card-text>
                    </v-card>
                </v-flex>
            </v-layout>
            </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    export default {
        data(){
            return {
                habido:'',
                estado:'',
                valida: 0,
                validaMensaje:[],
                idPersona:'',
                ubigeo:[],
                search: '',
                nombre:'',
                apellido:'',
                valida:0,
                departamento:'',
                provincia:'',
                distrito:'',
                telefono:'',
                email:'',
                consultas:[],
                verNuevo:0,
                tipo:[],
                tipoDoc:'',
                numeroDocumento:'',
                cliente:[],
                razonSocial:'',
                direccion:'',
                dialog: false,
                documento:['CLIENTE DNI', 'CLIENTE RUC'],
                tabs: null,
                headers: [
                { text: 'OPCIONES', value: 'opciones', sortable: false },
                { text: 'DOCUMENTO', value: 'documento',sortable: false},
                { text: 'NUMERO DOCUMENTO', value: 'numeroDocumento', sortable: false },
                { text: 'NOMBRE COMPLETO', value: 'razonSocial', sortable: false },
                { text: 'DIRECCION', value: 'direccion', sortable: false },
                { text: 'CREDITO', value: 'montoCredito', sortable: false },
                ],
                search: '',
                editedIndex: -1,
                adModal:0,
                adAccion:0
            }
        },
        computed: {
                formTitle () {
                return this.editedIndex === -1 ? 'NUEVO CLIENTE' : 'ACTUALIZAR CLIENTE'
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () { 
            this.selectTipo();
            this.listarClientes();
        },
        methods:{
            close () {
                this.dialog = false;
                this.limpiar();
                this.valida=0;
            },
            cerrarModalCliente(){
                this.adModal=0;
            },
            editItem (item) {
                this.idPersona = item.idPersona;
                this.tipoDoc=item.idTipoDocumento;
                this.numeroDocumento=item.numeroDocumento;
                this.razonSocial=item.razonSocial;
                this.direccion=item.direccion;
                this.telefono=item.celular;
                this.email=item.email;
                this.editedIndex=1;
                this.dialog = true
            },
            nuevoCliente(){
                this.verNuevo=1;
            },
            alertDisplayDNI() {
                this.$swal('ERROR','DNI no encontrado / No tiene conexion a internet...', 'error')
            },
            alertDisplayRUC() {
                this.$swal('ERROR','RUC no encontrado / No tiene conexion a internet...', 'error')
            },
            selectTipo(){
                let me=this;
                var tipoArray=[];
                axios.get('api/TipoDocumento/Select').then(function(response){
                    tipoArray=response.data;
                    tipoArray.map(function(x){
                        me.tipo.push({text: x.descripcion,value:x.idTipoDocumneto});
                    });
                }).catch(function(error){
                    alert(console.log(error));
                });
            },
            mostrar(item){
              this.adModal=1;
              this.idPersona=item.idPersona;
            },
            actualizarCliente(){
                let me = this;
                axios.put('api/Personas/ActualizarClienteProveedor/'+ me.idPersona).then(function(response){
                    me.cerrarModalCliente();
                    me.listarClientes();
                    me.idPersona="";
                    me.montoCredito="";
                }).catch(function(error){
                    console.log(error);
                });
            },
            guardarCliente(){ 
                if(this.validar()){
                    return;
                }
                if(this.editedIndex > -1)
                {
                   let me=this;
                   axios.put('api/personas/ActualizarCliente',{
                       'idPersona': me.idPersona,
                       'celular':me.telefono,
                       'email':me.email,
                       'direccion':me.direccion,
                       'razonSocial':me.razonSocial
                   }).then(function(response){
                        me.dialog=false;
                        me.limpiar();
                        me.listarClientes();
                    }).catch(function(error){
                       console.log(error);
                   });
                }
                else
                {
                    let me=this;
                    axios.get('api/Personas/ValidarDNI/'+me.numeroDocumento).then(function(response){
                        me.$swal('ERROR','El cliente ya esta registrado..', 'error')
                    }).catch(err =>{
                        if(err.response.status==404)
                        {
                            if(this.tipoDoc==2 && this.estado!="ACTIVO"){
                                me.$swal('ADVERTENCIA.', 'Debe ser un RUC activo.', 'warning')
                                return;
                            }
                            axios.post('api/Personas/Crear',{
                                'nombre':me.nombre,
                                'apellido':me.apellido,
                                'idTipoDocumento':me.tipoDoc,
                                'razonSocial':me.razonSocial,
                                'numeroDocumento':me.numeroDocumento,
                                'direccion':me.direccion,
                                'departamento':me.departamento,
                                'provincia':me.provincia,
                                'distrito':me.distrito,
                                'telefono':me.telefono,
                                'email':me.email,
                            }).then(function(response){
                                me.dialog=false;
                                me.limpiar();
                                me.listarClientes();
                                me.valida=0;
                            }).catch(function(error){
                                console.log(error);
                            });   
                        }
                    });
                }
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];

                if(this.numeroDocumento.length<3){
                    this.validaMensaje.push("Ingrese numero documento.");
                }
                if(this.razonSocial.length<3){
                    this.validaMensaje.push("Ingrese razon social.");
                }

                if(this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
            limpiar(){
                this.numeroDocumento="";
                this.razonSocial="";
                this.direccion="";
                this.telefono="";
                this.email="";
                this.estado="";
                this.nombre="";
                this.apellido="";
                this.departamento="";
                this.provincia="";
                this.distrito="";
                this.editedIndex=-1;
            },
            listarClientes() {
                let me=this;
                axios.get('api/Personas/ListarClientes').then(function(response){
                    me.cliente=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            consultasDocuments() {
                let me=this;
                if(me.tipoDoc==1){
                    //https://ww1.essalud.gob.pe/sisep/postulante/postulante/postulante_obtenerDatosPostulante.htm?strDni=47069487
                    /* axios.get('http://192.168.1.10:8080/consultas/peru-consult-api-master/public/api/v1/dni/'+this.numeroDocumento+'?token=abcxyz').then(function(response){ */
                    axios.post('api/VentaProductos/ConsultasDocumentos/'+this.numeroDocumento).then(function(response){
                    me.consultas= response.data;
                    me.nombre   = me.consultas.datosPerson[0].nombres;
                    me.apellido = me.consultas.datosPerson[0].apellidoPaterno+' '+me.consultas.datosPerson[0].apellidoMaterno;
                    me.razonSocial = me.consultas.datosPerson[0].nombres+' '+me.consultas.datosPerson[0].apellidoPaterno+' '+me.consultas.datosPerson[0].apellidoMaterno;

                    if(me.consultas.datosPerson[0].nombres==""){
                        me.alertDisplayDNI();
                    }
                }).catch(err => {
                     me.alertDisplayDNI();
                     this.numeroDocumento="";
                });
                }
                else if(me.tipoDoc==2){
                    axios.get('http://localhost:8080/consultasdoc/peru-consult-api/public/api/v1/ruc/'+this.numeroDocumento+'?token=abcxyz').then(function(response){
                    me.consultas=response.data;
                    me.razonSocial = me.consultas.razonSocial;
                    
                    if(me.consultas.direccion=="-"){
                       me.direccion = '-';
                       me.departamento ='';
                       me.provincia ='';
                       me.distrito = '';
                    }else{
                       me.direccion = me.consultas.direccion;
                       me.departamento = me.consultas.departamento;
                       me.provincia = me.consultas.provincia;
                       me.distrito = me.consultas.distrito;
                    }
                    me.estado = me.consultas.estado;
                }).catch(err => {
                    me.alertDisplayRUC();
                    me.numeroDocumento="";
                });
              }
            }
        }        
    }
</script>