<template>
    <v-card >
        <v-container fluid grid-list-md v-if="card==0">
            <v-layout row wrap>
                <v-flex xs12>
                    <v-card>
                        <v-card-title class="error" style="color: white;"><h3>REPORTE INDIVIDUAL {{nombre}}</h3></v-card-title>                        
                            <v-card-text> 
                                 <v-data-table
                                    v-model="selected"
                                    :headers="creditosPersonalId"
                                    :items="creditoIndividual"
                                    hide-actions
                                    select-all
                                    item-key="idCredito"
                                    class="elevation-1">
                                    <template v-slot:headers="props">
                                        <tr>
                                            <th>
                                                <v-checkbox
                                                    :input-value="props.all"
                                                    :indeterminate="props.indeterminate"
                                                    primary
                                                    hide-details
                                                    @click.stop="toggleAll()"
                                                    color="error">
                                            </v-checkbox>
                                            </th>
                                            <th class="text-xs-left"
                                                v-for="header in props.headers"
                                                :key="header.text">
                                                {{ header.text }}
                                            </th> 
                                        </tr>
                                    </template>
                                    <template slot="items" slot-scope="props">
                                      <tr :active="props.selected" @click="props.selected = !props.selected">
                                            <td>
                                                <v-checkbox :input-value="props.selected"  color="error" hide-details></v-checkbox>
                                            </td>
                                            <td> 
                                                <v-icon  color="primary darken-2" small @click="detalleVenta()">check</v-icon>
                                            </td>
                                            <td>{{ props.item.idCliente}}</td>
                                            <td>{{ props.item.razonSocial}}</td>
                                            <td>{{ since(props.item.fecha) }} </td>
                                            <td>{{ props.item.hora}}</td>
                                            <td>{{ props.item.total}}</td>
                                     </tr>
                                    </template>
                                    <template slot="no-data">
                                        <span class="red--text">
                                            <h3> No hay Informacion.</h3>
                                        </span>
                                    </template>
                                </v-data-table>
                            </v-card-text>
                    </v-card>
                </v-flex>
            </v-layout>
        </v-container>
        
        <v-container v-if="card==1">
            <Detalle/>
        </v-container>
    </v-card>
    
   
</template>
<script>
import axios from 'axios'
import moment from 'moment'
import Detalle  from './DetalleVenta.vue'

export default {
    name:'Individual',
    props:{
        nombre:{
            type:String,
            default:'ANONIMO',
            required: true
        },
        valor: {
            type:Number,
            required:true
        }
    },
    components:{
        Detalle
    },
    data() {
        return {
            card:0,
            creditoIndividual:[],
            selected:[],
            creditosPersonalId: [
                    { text: 'OPCION', value: '',sortable: false},
                    { text: '#', value: 'idCliente',sortable: false},
                    { text: 'NOMBRE COMPLETO', value: 'razonSocial',sortable: false},
                    { text: 'FECHA', value: 'fecha',sortable: false},
                    { text: 'HORA', value: 'hora',sortable: false},
                    { text: 'TOTAL', value: 'hora', sortable: false  },    
            ]
        }
    },
    created(){
      this.reporteIndividual();
    },
    methods: {
        reporteIndividual() {
            let me=this;
            axios.get('api/CreditoPersonal/Mostrar/'+me.valor).then(function(response){
                me.creditoIndividual=response.data;
            }).catch(function(error){
                console.log(error);
            });
        },
        since: function(d){
              return moment(d).format('L');  
        },
        toggleAll(){
              if (this.selected.length) this.selected = []
              else this.selected = this.creditoIndividual.slice()
        },
        detalleVenta(){
            this.card=1;
        }
    }
}
</script>