<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>CATEGORIAS</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
                <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="B√∫squeda" single-line hide-details></v-text-field>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="500px">
                        <v-btn slot="activator" color="info" dark class="mb-2">Nuevo
                            <v-icon dark right>check_circle</v-icon>
                        </v-btn>
                        <v-card>
                            <v-card-title class="primary" style="color: white;" primary-title>
                               <h3>{{ formTitle }}</h3>
                            </v-card-title>
                            <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                    <v-flex xs12 sm12 md12>
                                        <v-text-field v-model="nombre" label="NOMBRE"></v-text-field>
                                    </v-flex>
                                    
                                    <v-flex xs12 sm12 md12 v-show="valida">
                                        <h3><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h3>
                                    </v-flex>
                                </v-layout>
                            </v-container>
                            </v-card-text>

                            <v-divider></v-divider>
                
                            <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="error" @click.native="close">CANCELAR
                                <v-icon dark right>block</v-icon>
                            </v-btn>
                            <v-btn color="info" @click.native="guardar">GUARDAR
                                 <v-icon dark right>check_circle</v-icon>
                            </v-btn>
                            </v-card-actions>
                        </v-card>
                </v-dialog>
                <v-dialog v-model="adModal" max-width="300">
                    <v-card>
                        <v-card-title class="headline info"  style="color: white;" primary-title  v-if="adAccion==0">¬øActivar Item?</v-card-title>
                        <v-card-title class="headline error" style="color: white;" primary-title v-if="adAccion==1">¬øDesactivar Item?</v-card-title>
                        <v-card-text>
                            Estas a punto de
                            <span v-if="adAccion==0">Activar</span>
                            <span v-if="adAccion==1">Desactivar</span>
                            el item {{adNombre}}
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="green darken-1" dark class="mb-2"   @click="activarDesactivarCerrar">CANCELAR</v-btn>
                            <v-btn v-if="adAccion==0" color="info" class="mb-2"  @click="activar">ACTIVAR</v-btn>
                            <v-btn v-if="adAccion==1" color="error" dark class="mb-2" @click="desactivar">DESACTIVAR</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
                <v-snackbar
                  :color="color"
                  v-model="snackbar"
                 :timeout="timeout">
                 {{ text }}
                <v-btn
                    text
                    flat
                    @click="snackbar = false">
                    Cerrar
                </v-btn>
                </v-snackbar>
            </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="categorias"
                :search="search"
                class="elevation-1">
                <template slot="items" slot-scope="props">
                <td class="">
                    <v-icon
                        color="green darken-2"
                        small
                        class="mr-2"
                        @click="editItem(props.item)"
                    >
                    edit
                    </v-icon>

                    <template v-if="props.item.estado">
                        <v-icon
                            color="red darken-2"
                            small
                            @click="activarDesactivarMostrar(1,props.item)">
                            block
                        </v-icon>
                    </template>
                    <template v-else>
                        <v-icon
                            color="red darken-2"
                            small
                            @click="activarDesactivarMostrar(0,props.item)">
                            check
                        </v-icon>
                    </template>
                </td>
                <td>{{ props.item.nombre }}</td>
                <td>
                    <div v-if="props.item.estado">
                       <span class="blue--text">Activo</span>
                    </div>
                    <div v-else>
                       <span class="red--text">Inactivo</span>
                    </div>
                </td>
                <td>
                     <router-link :to="'/cat/' + props.item.idCategoria" >
                            <v-icon
                            color="red darken-2"
                            small
                            >
                            visibility
                        </v-icon>
                     </router-link>  
                </td>
                </template>
                <template slot="no-data">
                    <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>
        </v-flex>
    </v-layout>
</template>
<script>
    import {misMixins} from './mixins.js'
    
    import axios from 'axios'
    
    export default {            
        data(){
            return {
                modal:false,
                mixins:[misMixins],
                categorias:[],
                color: 'success',
                snackbar: false,
                text: 'REGISTRO GUARDADO EXITOSAMENTE.',
                timeout: 2000,
                dialog: false,
                headers: [
                    { text: 'OPCIONES', value: 'opciones', sortable: false },
                    { text: 'NOMBRE', value: 'nombre'},
                    { text: 'ESTADO', value: 'estado', sortable: false },
                    { text: 'LINK', value: '', sortable: false }
               
                ],
                search: '',
                editedIndex: -1,
                id:'',
                nombre:'',
                valida: 0,
                validaMensaje:[],
                adModal:0,
                adAccion:0,
                adNombre:'',
                adId:''  
            }
        },
        computed: {
            formTitle () {
             return this.editedIndex === -1 ? 'NUEVA CATEGORIA' : 'ACTUALIZAR CATEGORIA'
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            this.listar();
        },
        methods:{
            listar() {
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/Categorias/Listar',configuracion).then(function(response){
                    me.categorias=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            ver(){
                this.modal=true;
                console.log('Click');
            },
            editItem (item) {
               this.id= item.idCategoria;
               this.nombre = item.nombre;
               this.editedIndex=1;
               this.dialog = true
            },
            deleteItem (item) {
                const index = this.desserts.indexOf(item)
                confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1)
            },
            close () {
                this.dialog = false;
                this.limpiar();
                this.valida=0;
            },
            limpiar(){
                this.id="",
                this.nombre=""
                this.editedIndex=-1;
            },
            guardar() {
                if(this.validar()){
                    return;
                }
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                if (this.editedIndex > -1) {
                   let me = this;
                   axios.put('api/Categorias/Actualizar',{
                       'idCategoria': me.id,
                       'nombre':me.nombre
                   },configuracion).then(function(response){
                        me.close();
                        me.listar();
                        me.limpiar();
                   }).catch(function(error){
                       console.log(error);
                   });
                }else {
                   let me = this;
                   axios.post('api/Categorias/Crear',{
                       'nombre':me.nombre
                   },configuracion).then(function(response){
                        me.close();
                        me.listar();
                        me.limpiar();
                        me.snackbar=true;
                   }).catch(function(error){
                        console.log(error);
                   });
                }
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];

                if(this.nombre.length<3 || this.nombre.length>100){
                    this.validaMensaje.push("Ingrese nombre categoria.");
                }
                if(this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
            activarDesactivarMostrar(accion,item){
              this.adModal   =  1;
              this.adNombre  =  item.nombre;
              this.adId      =  item.idCategoria;
              
              if(accion==0)
              {
                 this.adAccion=0;
              }
              else if(accion==1)
              {
                 this.adAccion=1;
              }
              else
              {
                  this.adModal=0;
              }
            },
            activar(){
                let me = this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.put('api/Categorias/Activar/'+this.adId,{},configuracion).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();
                }).catch(function(error){
                    alert(console.log(error));
                });
            },
            desactivar(){
                let me = this;
                axios.put('api/Categorias/Desactivar/'+this.adId).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();
                }).catch(function(error){
                    alert(console.log(error));
                });
            },
            activarDesactivarCerrar() {
                this.adModal=0;
            }
        }        
    }
</script>