<template>
<v-layout justify-center>
    <v-flex xs12>
      <v-toolbar color="white">
        <v-toolbar-title>APERTURA CAJA</v-toolbar-title>
         <v-spacer></v-spacer>
         <template>
            <v-text-field  v-model="idApertura" append-icon="search" @keyup.enter="todos(idApertura)" type="number">
            </v-text-field>
        </template>
        <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
        <template v-if="informacion.length>0">
         <v-btn color="green darken-1" @click="abrir()" dark class="mb-2" >LIQUIDAR CAJA
                <v-icon dark right>check_circle</v-icon>
        </v-btn> 
        </template>
         <v-dialog v-model="liquidacionCajaModel" max-width="1000px" persistent>
            <v-card>
              <v-card-text>
                  <v-btn @click="crearPDFLiquidacion()"><v-icon>print</v-icon></v-btn>
                            <div id="factura">
                                <header>
                                    <div id="logo">
                                        <img src="data:image/png;base64," id="imagen">
                                    </div>
                                    <div id="datos">
                                        <p id="encabezado">
                                            <b>LA CASA DE LAS ENCHILADAS</b><br>Jir√≥n Fitzcarrald 232, Iquitos-Per√∫<br>Telefono:(065) 234828<br>
                                        </p>
                                    </div>
                                    <div id="fact">
                                        <p>LIQUIDACION CAJA - CONTABILIDAD<br>
                                        </p>
                                    </div>
                                </header>
                                <br>
                                <section>
                                    <div>
                                        <table id="facliente">
                                            <tbody>
                                                <tr v-for="det in informacion" :key="det.idArqueo">
                                                    <td id="cliente">
                                                        <strong>#:</strong>{{det.idApertura}}<br>
                                                        <strong>Caja:</strong>{{det.caja}}<br>
                                                        <strong>Usuario:</strong>{{det.cajero}}<br>
                                                        <strong>Fecha Apertura:</strong>{{since(det.fechaApertura)}}<br>
                                                        <strong>Hora Cierre:</strong>{{det.horaCierre}}
                                                    </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </div>
                                </section>
                                <br>
                                <section>
                                    <div>
                                        <table id="facarticulo">
                                            <thead>
                                                <tr id="fa">
                                                    <th>CANT</th>
                                                    <th>TIPO</th>
                                                    <th>MONTO </th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <td></td>
                                                    <td style="text-align:center;">EFECTIVO</td>
                                                    <td style="text-align:center;">S/ {{tg}}</td>
                                                </tr>
                                                <tr v-for="det in personal" :key="det.pers">
                                                    <td style="text-align:center;">PERSONAL</td>
                                                    <td style="text-align:center;">{{det.nombreTrabajador}}</td>
                                                    <td style="text-align:center;">{{det.totalCreditoPersonal}}</td>
                                                </tr>

                                                <tr v-for="det in clientes" :key="det.client">
                                                    <td style="text-align:center;">CLIENTE</td>
                                                    <td style="text-align:center;">{{det.nombreTrabajador}}</td>
                                                    <td style="text-align:center;">{{det.totalCreditoPersonal}}</td>
                                                </tr>

                                                <tr v-for="det in arrayTarjetas" :key="det.idArqueo">
                                                    <td style="text-align:center;">{{det.cantidad}}</td>
                                                    <td style="text-align:center;">{{det.tipo}}</td>
                                                    <td style="text-align:center;">{{det.totalTarjeta}}</td>
                                                </tr>

                                                 <tr v-for="pagar in arrayTarjetasMixtas" :key="pagar.tipo">
                                                    <td style="text-align:center;">{{pagar.cantidad}}</td>
                                                    <td style="text-align:center;">MIXTO {{pagar.tipo}}</td>
                                                    <td style="text-align:center;">{{pagar.totalTarjetas}}</td>
                                                </tr>
                                            </tbody>
                                            <tfoot>
                                                <tr> 
                                                    <th></th>
                                                    <th>TOTAL EFECTIVO</th>
                                                    <th>S/. {{tg}}</th>
                                                </tr>
                                                <tr> 
                                                    <th></th>
                                                    <th>TOTAL CREDITOS PERSONAL</th>
                                                    <th>S/. {{Number(tcp) +Number(totalCreditoClientes)}}</th>
                                                </tr>

                                                <tr> 
                                                    <th></th>
                                                    <th>TOTAL TARJETA</th>
                                                    <th>S/. {{Number(tt) + Number(ttm)}}</th>
                                                </tr>
                                                 <tr> 
                                                    <th></th>
                                                    <th>TOTAL CAJA</th>
                                                    <th>S/. {{totalGeneral}}</th>
                                                 </tr>

                                                 <tr> 
                                                    <th></th>
                                                    <th>LIQUIDACION</th>
                                                    <th>S/. {{totalcierre}}</th>
                                                 </tr>

                                                 <tr> 
                                                    <th></th>
                                                    <th>FALTANTE</th>
                                                    <th>S/. {{faltante}}</th>
                                                 </tr>

                                            </tfoot>
                                        </table>
                                    </div>
                                </section>
                                <br>
                                <br>
                                <footer>
                                    <div id="gracias">
                                        <p><b></b></p>
                                    </div>
                                </footer>
                            </div>
                            <v-btn @click="ocultarComprobante()" color="error">CANCELAR</v-btn>                  
              </v-card-text>
            </v-card>
        </v-dialog>
      </v-toolbar>
       <v-card >
        <v-container fluid grid-list-md >
          <v-layout row wrap>
                <v-flex xs12>
                 <v-card v-if="informacion.length>0"> 
                    <v-card-title class="error" style="color: white;"><h3>INFORMACION DEL CAJERO</h3></v-card-title>
                        <v-card-text v-for="item in informacion" :key="item.idApertura">
                             <v-layout wrap>
                                <v-flex xs6 sm6 md3>
                                   <h4>CAJERO:</h4>
                                </v-flex>
                                <v-flex xs6 sm6 md3>
                                  <p>{{item.cajero}}</p>
                                </v-flex>

                                <v-flex xs6 sm6 md3>
                                   <h4>CAJA:</h4>
                                </v-flex>
                                <v-flex xs6 sm6 md3>
                                   <p>{{item.caja}}</p>
                                </v-flex>

                                <v-flex xs6 sm6 md3>
                                   <h4>FECHA APERTURA:</h4>
                                </v-flex>
                                <v-flex xs6 sm6 md3>
                                   <p>{{since(item.fechaApertura)}}</p>
                                </v-flex>

                                <v-flex xs6 sm6 md3>
                                   <h4>HORA APERTURA:</h4>
                                </v-flex>
                                <v-flex xs6 sm6 md3>
                                   <p>{{item.horaApertura}}</p>
                                </v-flex>

                                <v-flex xs6 sm6 md3>
                                   <h4>MONTO APERTURA:</h4>
                                </v-flex>
                                <v-flex xs6 sm6 md3>
                                   <p>S/. {{item.monto}}</p>
                                </v-flex>

                                <v-flex xs6 sm6 md3>
                                   <h4>TOTAL CIERRE:</h4>
                                </v-flex>
                                <v-flex xs6 sm6 md3>
                                   <p>{{totalcierre= item.totalCajaUsuario}}</p>
                                </v-flex>
                             </v-layout>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                           
                        </v-card-actions>
                 </v-card>
                </v-flex>
                <v-flex xs12 v-if="informacion.length>0">
                 <v-card>
                    <v-card-title class="primary" style="color: white;"><h3>INFORMACION DE VENTAS</h3></v-card-title>  
                    <v-card-text>
                        <v-flex>
                            <template>
                                <v-data-table :headers="efectivo" :items="efectivoSoles" hide-actions class="elevation-1">
                                    <template slot="items" slot-scope="props">
                                        <td>-</td>
                                        <td>{{props.item.descripcion}}</td>
                                        <td>S/. {{props.item.total.toFixed(2)}}</td>
                                    </template>
                                    <template slot="no-data">
                                        <span class="red--text">
                                            <h3>No hay efectivo que mostrar.</h3>
                                        </span>
                                    </template>
                                </v-data-table>
                            </template>
                             <v-flex class="text-xs-right">
                                <h2>TOTAL EFECTIVO: S/ {{totalEfectivo=(calcularTotalEfectivo).toFixed(2)}} </h2>
                            </v-flex>  
                        </v-flex>   
                        <v-flex>
                            <template>
                                <v-data-table                           
                                    :headers="cabeceraPersonal"
                                    :items="personal"
                                    class="elevation-1"  hide-actions >
                                    <template slot="items" slot-scope="props">
                                        <td>{{props.item.trabajador}}</td>
                                        <td>{{props.item.nombreTrabajador}}</td>
                                        <td>S/. {{props.item.totalCreditoPersonal.toFixed(2)}}</td>
                                    </template>
                                    <template slot="no-data">
                                        <span class="red--text">
                                            <h3>No hay credito personal.</h3>
                                        </span>
                                    </template>
                                </v-data-table> 
                                <v-flex class="text-xs-right">
                                        <h2>TOTAL CREDITO PERSONAL: S/  {{tcp=(calcularTotalCreditoPersonal).toFixed(2)}}</h2>
                                </v-flex>
                            </template>
                        </v-flex>
                        <v-flex>
                          <template>
                                <v-data-table                           
                                    :headers="cabeceraClientes"
                                    :items="clientes"
                                    class="elevation-1"  hide-actions >
                                    <template slot="items" slot-scope="props">
                                        <td>{{props.item.trabajador}}</td>
                                        <td>{{props.item.nombreTrabajador}}</td>
                                        <td>S/. {{props.item.totalCreditoPersonal.toFixed(2)}}</td>
                                    </template>
                                    <template slot="no-data">
                                        <span class="red--text">
                                            <h3>No hay credito clientes.</h3>
                                        </span>
                                    </template>
                                </v-data-table> 

                                <v-flex class="text-xs-right">
                                     <h2>TOTAL CREDITO CLIENTES: S/ {{totalCreditoClientes=(calcularTotalCreditoClientes).toFixed(2)}}</h2>
                                </v-flex>
                          </template>
                        </v-flex>
                          <v-flex>
                            <template>
                                <v-data-table :headers="tarjetas" :items="arrayTarjetas" hide-actions class="elevation-1">
                                    <template slot="items" slot-scope="props">
                                        <td>{{props.item.cantidad}}</td>
                                        <td>{{props.item.tipo}}</td>
                                        <td>S/. {{props.item.totalTarjeta.toFixed(2)}}</td>
                                    </template>
                                    <template slot="no-data">
                                        <span class="red--text">
                                            <h3>No hay tarjetas que mostrar.</h3>
                                        </span>
                                    </template>
                                </v-data-table>
                            </template>
                            <v-flex class="text-xs-right">
                              <h2>TOTAL TARJETAS: S/ {{tt=(calcularTotalTarjetas).toFixed(2)}} </h2>
                            </v-flex>  
                        </v-flex>   

                        <v-flex>
                            <template>
                                <v-data-table :headers="tarjetasMixtas" :items="arrayTarjetasMixtas" hide-actions class="elevation-1">
                                    <template slot="items" slot-scope="props">
                                        <td>{{props.item.cantidad}}</td>
                                        <td>{{props.item.tipo}}</td>
                                        <td>S/. {{props.item.totalTarjetas.toFixed(2)}}</td>
                                    </template>
                                    <template slot="no-data">
                                        <span class="red--text">
                                            <h3>No hay tarjetas mixtas que mostrar.</h3>
                                        </span>
                                    </template>
                                </v-data-table>
                            </template>
                            <v-flex class="text-xs-right">
                              <h2>TOTAL TARJETAS MIXTAS: S/ {{ttm=(calcularTotalTarjetasMixtas).toFixed(2)}}</h2>
                            </v-flex>  
                        </v-flex>  

                          <v-flex>
                                     <v-card>
                                        <v-card-title class="headline info"  style="color: white;">SALDOS GENERALES</v-card-title>
                                        <v-card-text>
                                            <h2>TOTAL CIERRE CAJERO: S/  {{totalcierre}}</h2>
                                            <h2>TOTAL GENERAL: S/  {{totalGeneral=(calcularTotalGeneral).toFixed(2)}}</h2>
                                        </v-card-text>
                                       
                                    </v-card>
                                </v-flex>
                                <v-flex class="text-xs-right" v-if="calculoFaltante>0">
                                     <h2>SOBRANTE: S/ {{faltante=(calculoFaltante).toFixed(2)}}</h2>
                                </v-flex>
                                  <v-flex class="text-xs-right" v-else>
                                     <h2>FALTANTE: S/ {{faltante=(calculoFaltante).toFixed(2)}}</h2>
                                </v-flex>
                    </v-card-text>
                 </v-card>
                </v-flex>
                
          </v-layout>
        </v-container>
       </v-card>
    </v-flex>
</v-layout>

</template>
<script>

import axios from 'axios';
import jsPDF from 'jspdf';
import html2canvas from 'html2canvas';
import moment from 'moment';

export default {
    data() {
        return {
            totalcierre:0,
            totalGeneral:0,
            faltante:0,
            totalEfectivo:0,
            totalCreditoClientes:0,
            tg:0,
            tt:0,
            ttm:0,
            tcp:0,
            idApertura:'',
            personal:[],
            informacion:[],
            arrayTarjetas:[],
            arrayTarjetasMixtas:[],
            clientes:[],
            efectivoSoles:[],
            liquidacionCajaModel:0,
            efectivo: [
                { text: '#', value: '',sortable: false },
                { text: 'TIPO MONEDA', value: '',sortable: false },
                { text: 'MONTO SOLES EFECTIVO', value: '',sortable: false  },             
            ],
              tarjetas: [
                { text: 'CANTIDAD', value: '',sortable: false },
                { text: 'TIPO TARJETA', value: '',sortable: false },
                { text: 'MONTO TARJETA CREDITO', value: '',sortable: false  },             
            ],
            tarjetasMixtas: [
                { text: 'CANTIDAD', value: '',sortable: false },
                { text: 'TIPO TARJETA', value: '',sortable: false },
                { text: 'MONTO TARJETA CREDITO MIXTAS', value: '',sortable: false  },             
            ],
            cabeceraPersonal:[
                 { text: 'TIPO', value: '',sortable: false },
                 { text: 'NOMBRE', value: '',sortable: false },
                 { text: 'TOTAL CREDITO PERSONAL', value: '',sortable: false  }
            ],
             cabeceraClientes: [
                { text: 'TIPO', value: '',sortable: false },
                { text: 'NOMBRE', value: '',sortable: false },
                { text: 'TOTAL CREDITO CLIENTES', value: '',sortable: false  },             
            ]
        }
    },
    computed: {
        calcularTotalEfectivo:function(){
            var resultado=0.0;
            for(var i=0;i<this.efectivoSoles.length;i++){
                resultado=resultado+(this.efectivoSoles[i].total);
            }
            return resultado;
        },
        calculoFaltante:function(){
            var resultado=0.0;
            resultado =  this.totalcierre - this.totalGeneral;
            return resultado;
        },
        calcularTotalTarjetas:function(){
            var resultado=0.0;
            for(var i=0;i<this.arrayTarjetas.length;i++){
                resultado=resultado+(this.arrayTarjetas[i].totalTarjeta);
            }
            return resultado;
        },
        calcularTotalTarjetasMixtas:function(){
            var resultado=0.0;
            for(var i=0;i<this.arrayTarjetasMixtas.length;i++){
                resultado=resultado+(this.arrayTarjetasMixtas[i].totalTarjetas);
            }
            return resultado;
        },
        calcularTotalCreditoPersonal:function(){
            var resultado=0.0;
            for(var i=0;i<this.personal.length;i++){
                resultado=resultado+(this.personal[i].totalCreditoPersonal);
            }
            return resultado;
        },
        calcularTotalCreditoClientes:function(){
            var resultado=0.0;
            for(var i=0;i<this.clientes.length;i++){
                resultado=resultado+(this.clientes[i].totalCreditoPersonal);
            }
            return resultado;
        },
         calcularTotalGeneral:function(){
            var res=0.0;
            res = this.calcularTotalEfectivo + this.calcularTotalTarjetas 
                                             + this.calcularTotalTarjetasMixtas 
                                             + this.calcularTotalCreditoPersonal 
                                             + this.calcularTotalCreditoClientes;
            return res;
        }
    },
    methods: {
        ocultarComprobante(){
            this.liquidacionCajaModel=0;
        },
        since: function(d){
            return moment(d).format('L');  
        },
        abrir(){
            this.liquidacionCajaModel=1;
            let me = this;
            axios.put('api/AperturaCajas/CuentaPersonal',{
                'idUsuario':me.informacion[0].idUsuario,
                'totalFaltante':me.faltante,
                'idApertura':me.informacion[0].idApertura,
                'totalLiquidacionCaja':me.totalGeneral,
            }).then(function(response){
                me.idApertura='';
                me.informacion=[];
            }).catch(function(error){
                console.log(error);
            });
        },
        crearPDFLiquidacion(){
            var quotes = document.getElementById('factura');
            html2canvas(quotes).then(function (canvas) {
                var imgData = canvas.toDataURL('image/png');
                var imgWidth = 210;
                var pageHeight = 495;
                var imgHeight = canvas.height * imgWidth / canvas.width;
                var heightLeft = imgHeight;
                var doc = new jsPDF();
                var position = 0;

                doc.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);                    
                doc.save('ReporteContabilidad.pdf');
            });
        },
        informacionUsuario(id){
            let me=this;
            axios.get('api/AperturaCajas/InformacionLiquidacionCaja/'+id).then(function(response){
                me.informacion=response.data;
            }).catch(function(error){
                console.log(error);
            });
        },
        listarEfectivo(id) {
            let me=this;
            axios.get('api/VentaProductos/TotalEfectivo/'+id).then(function(response){
                me.efectivoSoles=response.data;
            }).catch(function(error){
                console.log(error);
            });
        },
        totalGeneralEfectivo(id){
            let me=this;
            axios.get('api/VentaProductos/TotalEfectivoGeneral/'+id).then(function(response){
                me.tg=response.data;
            }).catch(function(error){
                console.log(error);
            });
        },
        listarCreditoPersonal(id){
            let me=this;
            axios.get('api/VentaProductos/ListarCreditosPersonal/'+id).then(function(response){
               me.personal=response.data;
            }).catch(function(error){
               console.log(error);
            });
        },
        listarCreditoCliente(id) {
            let me=this;
            axios.get('api/VentaProductos/CreditoClientes/'+id).then(function(response){
               me.clientes=response.data;
            }).catch(function(error){
               console.log(error);
            });
        },
        listarTarjetas(id){
            let me=this;
            axios.get('api/VentaProductos/ListarTotalTarjetas/'+id).then(function(response){
                me.arrayTarjetas=response.data;
            }).catch(function(error){
                console.log(error);
            });
        }, 
        listarMixtos(id){
            let me=this;
            axios.get('api/PagoMixtos/ListarPagoMixtoTarjetas/'+id).then(function(response){
                me.arrayTarjetasMixtas=response.data;
            }).catch(function(error){
                console.log(error);
            });
        },
        todos(id){
           if(!this.idApertura){
             this.$swal('Advertencia.', 'Ingrese codigo de apertura.', 'error')
             return;
           }
           let me=this;
           axios.get('api/AperturaCajas/InformacionLiquidacionCaja/'+id).then(function(response){
                me.informacion=response.data;
                if(me.informacion.length==0){
                     me.$swal('Advertencia.', 'No hay registros del codigo apertura.', 'error')
                     
                }else{
                    me.listarEfectivo(id);
                    me.totalGeneralEfectivo(id);
                    me.listarCreditoPersonal(id);
                    me.listarCreditoCliente(id);
                    me.listarTarjetas(id);
                    me.listarMixtos(id);
                }   
           }).catch(function(error){
                console.log(error);
           });
        }
    }
}
</script>

<style>
 #factura {
        padding: 20px;
        font-family: Arial, sans-serif;
        font-size: 16px ;
    }

    #logo {
        float: left;
        margin-left: 2%;
        margin-right: 2%;
    }
    #imagen {
        width: 100px;
    }

    #fact {
        font-size: 18px;
        font-weight: bold;
        text-align: center;
    }   

    #datos {
        float: left;
        margin-top: 0%;
        margin-left: 2%;
        margin-right: 2%;
    }

    #encabezado {
        text-align: center;
        margin-left: 10px;
        margin-right: 10px;
        font-size: 16px;
    }

    section {
        clear: left;
    }

    #cliente {
        text-align: left;
    }
    #facliente {
        width: 40%;
        border-collapse: collapse;
        border-spacing: 0;
        margin-bottom: 15px;
    }
    #fa {
        color: #FFFFFF;
        font-size: 14px;
    }
    #facarticulo {
        width: 100%;
        border-collapse: collapse;
        border-spacing: 0;
        padding: 20px;
        margin-bottom: 15px;
    }

    #facarticulo thead {
        padding: 20px;
        background: #2183E3;
        text-align: center;
        border-bottom: 1px solid #FFFFFF;
    }

    #gracias {
        text-align: center;
    }

</style>