<template>
    <v-layout justify-center>
        <v-flex xs12>
            <v-toolbar color="white">
                <v-toolbar-title>GENERAR PROFORMAS</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-divider
                    class="mx-2"
                    inset
                    vertical>
                </v-divider>
                <v-dialog v-model="nuevoCliente" max-width="750px" persistent>
                        <v-card>
                            <v-card-title class="primary" style="color: white;">
                               <h3>NUEVO CLIENTE</h3>
                            </v-card-title>
                            <v-card-text>
                             <v-container grid-list-md>
                                <v-layout wrap>
                                    <v-flex xs6 sm6 md6>
                                        <v-select label="TIPO" v-model="tipoDoc" :items="tdocumento" prepend-icon="assignment"></v-select>
                                   </v-flex>
                                   <v-flex xs6 sm6 md6> 
                                        <v-text-field  type="number" v-model="numeroDocumentoReniec" label="NUMERO(RUC,DNI)" prepend-icon="looks_3" @keyup.enter="consultasDocuments()"></v-text-field>
                                   </v-flex>
                                   <v-flex xs12 sm16 md12> 
                                        <v-text-field  type="text" v-model="razonSocialReniec" label="RAZON SOCIAL"  prepend-icon="how_to_reg" ></v-text-field>
                                   </v-flex>
                                    <v-flex xs12 sm16 md12> 
                                        <v-text-field v-model="direccionReniec"  type="text" label="DIRECCION FISCAL"  prepend-icon="location_on"></v-text-field>
                                   </v-flex>
                                   <v-flex xs12 sm16 md12> 
                                        <v-text-field  v-model="estado" disabled type="text" label="ESTADO"  prepend-icon="assignment"></v-text-field>
                                   </v-flex>
                                    <v-flex xs6 sm6 md6>
                                        <v-text-field  type="number" v-model="telefono" label="CELULAR" prepend-icon="call" ></v-text-field>
                                    </v-flex>
                                    <v-flex xs6 sm6 md6> 
                                        <v-text-field  type="text" v-model="email" label="EMAIL" prepend-icon="contact_mail"></v-text-field>
                                   </v-flex>
                                    <v-flex xs12 sm12 md12 v-show="valida">
                                        <h4><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h4>
                                    </v-flex>
                                </v-layout>
                            </v-container>
                            </v-card-text>
                             <v-divider></v-divider>
                            <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="error" @click="cancelarCrearCliente()">Cancelar
                                <v-icon dark right>block</v-icon>
                            </v-btn>
                            <v-btn color="info" @click="guardarCliente()">Guardar
                                 <v-icon dark right>check_circle</v-icon>
                            </v-btn>
                            </v-card-actions>
                        </v-card>
                </v-dialog>
                <v-dialog v-model="modalNumeroProforma" max-width="300" persistent>
                    <v-card>
                        <v-card-title class="headline info"  style="color: white;"><h4>BUSCAR PROFORMA</h4></v-card-title>
                        <v-card-text>
                            <v-flex xs12 sm12 md6 lg12 xl12>
                                <v-text-field placeholder="0" v-model="codproforma" label="COD PROFROMA" type="number">
                                </v-text-field>
                            </v-flex>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-btn  color="error" dark @click="cerrarProforma()">Cerrar</v-btn>
                            <v-btn  color="info" dark @click="buscarProforma()">Buscar</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>

                <v-dialog v-model="verClientes" max-width="1150px" persistent>
                    <v-card>
                        <v-card-title class="primary" style="color: white;"><h3>LISTA DE CLIENTES</h3></v-card-title>
                        <v-divider></v-divider>
                        <v-card-text>
                            <v-container grid-list-md>
                            <v-layout wrap>
                                <v-flex xs12 sm12 md12 lg12 xl12>
                                    <v-text-field append-icon="search" placeholder="..................." v-model="searchbuscar" class="text-xs-center buscar"  label="Busqueda">
                                    </v-text-field>
                                    <template>
                                        <v-data-table                           
                                            :headers="cabeceraClientes"
                                            :items="cliente"
                                            :search="searchbuscar"
                                            class="elevation-1">
                                            <template slot="items" slot-scope="props">
                                                <td >
                                                    <v-icon
                                                    color="red darken-2"
                                                    small
                                                    @click="clienteAdd(props.item)"
                                                    class="mr-2">
                                                    add
                                                    </v-icon>
                                                </td>
                                                <td>{{props.item.idPersona}}</td>
                                                <td>{{props.item.numeroDocumento}}</td>
                                                <td>{{props.item.razonSocial}}</td>
                                                <td>{{props.item.direccion}}</td>
                                            </template>
                                            <template slot="no-data">
                                                <span class="red--text">
                                                    <h3>No hay clientes para mostrar.</h3>
                                                </span>
                                            </template>
                                        </v-data-table> 
                                    </template>
                                </v-flex>
                            </v-layout>
                        </v-container>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-btn dark color="red darken" @click="cerrarClientes()">
                                Cerrar
                            </v-btn>
                            <v-btn dark color="primary darken" @click="abrirNuevoCliente()">
                                    Nuevo
                            </v-btn>
                        </v-card-actions>
                    </v-card>
            </v-dialog>

                <v-dialog v-model="verProductos" max-width="1250px" persistent>
                    <v-card>
                        <v-card-title class="primary" style="color: white;"><h3>LISTA DE PRODUCTOS - {{tipocliente}}</h3></v-card-title>
                        <v-divider></v-divider>
                        <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                <v-flex xs12 sm12 md12 lg12 xl12>
                                    <v-text-field append-icon="search" placeholder="..................." v-model="search" v-on:keyup="listarProductosporTipo(search)" class="text-xs-center"  label="Busqueda" >
                                    </v-text-field>
                                    <template>
                                        <v-data-table                           
                                            :headers="cabeceraProductos"
                                            :items="productos"
                                            :search="search"
                                            class="elevation-1">
                                                <template slot="items" slot-scope="props">
                                                <td>
                                                    <v-icon
                                                        color="red darken-2"
                                                        small
                                                        class="mr-2"  @click="agregarDetalle(props.item)">
                                                        add
                                                    </v-icon>
                                                </td>
                                                <td>{{props.item.codigo}}</td>
                                                <td>{{props.item.nombre}}</td>
                                                <td>{{props.item.categoria}}</td>
                                                <td>{{props.item.marca}}</td>
                                                <td>{{props.item.medida}}</td>
                                                <td>
                                                    <v-chip color="orange" dark>S/. {{props.item.precio.toFixed(2)}}</v-chip>
                                                </td>
                                                <td>
                                                    <v-chip color="primary" dark>{{props.item.stock}}</v-chip>
                                                </td>
                                            </template>
                                            <template slot="no-data">
                                                <span class="red--text">
                                                    <h3>No hay productos para mostrar.</h3>
                                                </span>
                                            </template>
                                        </v-data-table> 
                                    </template>
                                </v-flex>
                            </v-layout>
                        </v-container>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-btn dark color="red darken" @click="cerrarProductos()">
                                Cerrar
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
             <v-btn color="teal" dark @click="abrirProductos()" v-if="verNuevo==0">
                <v-icon dark >search</v-icon>BUSCAR
            </v-btn>
            <v-btn color="primary" dark  v-if="verNuevo==0" @click="abrirProforma()">
                BUSCAR PROFORMA<v-icon dark >search</v-icon>
            </v-btn>
            </v-toolbar>
            <v-card>
                <v-container fluid grid-list-md  v-if="verNuevo==0">
                    <v-layout row wrap>
                        <v-flex xs12>
                        <v-card>
                            <v-card-title class="error" style="color: white;margin-top:3px"><h3>ITEM PRODUCTOS</h3></v-card-title>
                                <v-card-text>
                                      <v-container grid-list-md class="tabs">
                                       <v-layout>
                                             <v-flex xs6>
                                                <v-select :items="tipoc"  @change="verTipo()" v-model="idTipoc" prepend-icon="assignment" label="TIPO CLIENTE">
                                                </v-select>
                                            </v-flex>
                                            <v-flex xs6>
                                                <v-text-field v-model="bcodBarras" type="number" v-on:keyup="buscarCodbarras()"  prepend-icon="looks_one" label="COD. BARRAS"></v-text-field>
                                            </v-flex>
                                       </v-layout>
                                      </v-container>
                                    <v-data-table
                                        :headers="cabeceraLista"
                                        :items="detalles"
                                        hide-actions
                                        class="elevation-1">
                                        <template  slot="items" slot-scope="props">
                                            <td class="">
                                                <v-icon
                                                color="red darken-2"
                                                small  @click="eliminarDetalle(detalles,props.item)">
                                                delete
                                                </v-icon>
                                            </td>
                                            <td>{{ props.item.nombre }}</td>
                                            <td>{{props.item.marca}}</td>
                                            <td><v-text-field type="number" v-model="props.item.cantidad"></v-text-field></td>
                                            <td><v-text-field type="number" v-model="props.item.precio"></v-text-field></td>
                                            <td><v-text-field type="number" v-model="props.item.descuento"></v-text-field></td>
                                            <td>S/. {{((props.item.cantidad * props.item.precio)-props.item.descuento).toFixed(2)}}</td>
                                        </template>
                                        <template slot="no-data">
                                            <span class="red--text">
                                                <h3> No hay productos agregados al detalle.</h3>
                                            </span>
                                        </template>
                                    </v-data-table>
                                    <v-flex class="text-xs-right">
                                        <h2>TOTAL VENTA: S/ {{subTotal=(calcularTotal).toFixed(2)}}</h2>
                                    </v-flex>
                                </v-card-text>
                            <v-divider></v-divider>
                          </v-card>
                         </v-flex>
                        <v-flex xs7>
                            <v-card>
                                <v-card-title><v-icon></v-icon><h3>INFORMACION CIERRE DE PROCESO</h3></v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-container grid-list-md>
                                            <v-layout wrap>  
                                                <v-flex>
                                                    <h3>
                                                    <v-text-field type="number" v-model="numeroDocumento" prepend-icon="looks_one" label="DNI/RUC">
                                                    </v-text-field></h3>
                                                </v-flex>
                                                <v-btn  small fab dark color="teal" @click="mostrarClientes()">
                                                        <v-icon dark>list</v-icon>
                                                </v-btn>
                                                <v-flex xs12>
                                                    <h3>
                                                        <v-text-field type="text" v-model="nombreCliente" prepend-icon="people" label="RAZON SOCIAL">
                                                        </v-text-field>
                                                    </h3>
                                                </v-flex>
                                            </v-layout>
                                        </v-container>
                                </v-card-text>
                            </v-card>
                        </v-flex>
                        <v-flex xs5>
                            <v-card>
                                <v-container fluid grid-list-lg>
                                    <v-layout row wrap>
                                        <v-flex xs6>
                                            <v-card color="info" dark>
                                                <v-card-title><h4>SUBTOTAL</h4></v-card-title>
                                                <v-card-text class="headline font-weight-bold" >
                                                     {{subtotal=(calcularTotal).toFixed(2)}} 
                                                </v-card-text>
                                            </v-card>
                                        </v-flex>

                                        <v-flex xs6>
                                            <v-card color="error" dark>
                                                <v-card-title><h4>DESCUENTO</h4></v-card-title>
                                                <v-card-text class="headline font-weight-bold" >
                                                   00.00
                                                </v-card-text>
                                            </v-card>
                                        </v-flex>
                                        <v-flex xs12>
                                            <v-card color="success" dark>
                                                <v-card-title><h4>TOTAL</h4></v-card-title>
                                                <v-card-text class="headline font-weight-bold">
                                                    {{totalVenta=((subtotal-des)).toFixed(2)}} 
                                                </v-card-text>
                                            </v-card>
                                        </v-flex>
                                    </v-layout>
                                </v-container>
                            </v-card>
                        </v-flex>
                        <v-flex xs12>
                            <v-card>
                            <v-card-text>
                            </v-card-text>
                                <v-divider></v-divider>
                            <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn color="green darken-1" dark class="mb-2" @click="pdfG()">
                                        <v-icon>assignment</v-icon>
                                        PROCESAR PROFORMA</v-btn>
                                </v-card-actions>
                            </v-card>
                    </v-flex>
                    </v-layout>
                </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>

<script>
import axios from 'axios';
var pdfMake = require ('pdfmake/build/pdfmake.js');
var pdfFonts = require ('pdfmake/build/vfs_fonts.js');
pdfMake.vfs = pdfFonts.pdfMake.vfs;
export default {
    data() {
        return {
            idProforma:0,
            modalNumeroProforma: 0,
            codproforma:'',
            consultas:[],
            correlativoProformaOk:'',
            nuevoCliente:0,
            valida: 0,
            validaMensaje:[],
            verClientes:0,
            cliente:[],
            barra:[],
            tipoDoc:'',
            telefono:'',
            email:'',
            numeroDocumentoReniec:'',
            nombreReniec:'',
            estado:'',
            direccionReniec:'',
            apellido:'',
            razonSocialReniec:'',
            departamento:'',
            provincia:'',
            distrito:'',
            bcodBarras:'',
            idTipoc:'',
            tipoc:[],
            totalVenta:0,
            numeroDocumento:0,
            idCliente:0,
            des:0,
            nombreCliente:'',
            direc: '',
            tipopago:[],
            moneda:[],
            tdocumento:[],
            idmoneda:0,
            idTipoPago:0,
            idComprobante:0,
            errorProducto:null,
            verNuevo:0,
            subTotal:0,
            correlativoActual:0,
            verProductos:0,
            search:'',
            searchbuscar:'',
            productos:[],
            listarDetalles:[],
            detalles:[],
            tipocliente:'',
            arrayFinal: [],
            pdf:'',
            correTemp: 0,
            cabeceraLista: [
                    { text: '#', value: 'opciones',sortable: false},
                    { text: 'PRODUCTO', value: 'producto',sortable: false},
                    { text: 'MARCA', value: 'marca',sortable: false},
                    { text: 'CANTIDAD', value: 'cant',sortable: false},
                    { text: 'PRECIO VENTA', value: 'precio',sortable: false},
                    { text: 'DESCUENTO', value: '',sortable: false },
                    { text: 'IMPORTE', value: 'total', sortable: false  },    
            ],
            cabeceraProductos: [
                    { text:  'SELECCIONAR', value: 'seleccionar', sortable: false },
                    { text:  'CODIGO', value: 'codigo', sortable: false },
                    { text: 'ARTICULO/PRODUCTOS', value: 'nombre',sortable: false},
                    { text: 'CATEGORIA', value: 'categoria',sortable: false },
                    { text: 'MARCA', value: 'marca',sortable: false },
                    { text: 'UNIDAD MEDIDA', value: 'medida' ,sortable: false},
                    { text: 'PRECIO VENTA', value: 'precioVenta' ,sortable: false},
                    { text: 'STOCK', value: 'stock', sortable: false  },    
            ],
            cabeceraClientes:[
                { text: 'SELECCIONAR', value: 'opcion',sortable: false},
                { text: '#', value: 'idPersona',sortable: false},
                { text: 'DOCUMENTO', value: 'numeroDocumento',sortable: false},
                { text: 'CLIENTE', value: 'razonSocial',sortable: false},
                { text: 'DIRECCION', value: 'direccion',sortable: false},
            ]
        }
    },
    created(){
       
        this.listarTipoCliente();
        this.tipocliente='PRECIO PUBLICO';
        this.numeroDocumento="00000000";
        this.idCliente=9;
        this.nombreCliente="PUBLICO GENERAL";
     
    },
    computed: {
        calcularTotal:function(){
            var resultado=0.0;
            for(var i=0;i<this.detalles.length;i++){
                resultado=resultado+((this.detalles[i].precio*this.detalles[i].cantidad)-this.detalles[i].descuento);
            }
            return resultado;
        },
        sumarDescuento: function(){
            var sumaD = 0.0;
            for(var ii=0;ii<this.detalles.length;ii++){
                sumaD= sumaD+ Number(this.detalles[ii].descuento);
            }
            return sumaD;
        }
    },
    methods: {
        listarProductos(){
           let me=this;
           axios.get('api/Productos/ListarProductosActivos').then(function(response){
               me.productos=response.data;
            }).catch(function(error){
                console.log(error);
            });
        },
        abrirProductos(){
            if(!this.idTipoc){
                return;
            }
            this.verProductos=1;
            //this.listarProductosporTipo();
        },
        cerrarProductos(){
            this.verProductos=0;
            this.productos=[];
        },
        listarPrecioPublico(buscar){
           let me=this;
           if(buscar.length==0){
               me.productos =[];
               return;
           }
           axios.get('api/Productos/ListaProductoPrecioPublico/'+buscar).then(function(response){
               me.productos=response.data;
           }).catch(function(error){
                console.log(error);
           });
        },
        listarProductosporTipo(buscar){
            if(this.idTipoc==1){    
                this.listarPrecioPublico(buscar);
            }
            else if(this.idTipoc==2){
                this.listarPrecioMayorista(buscar);
            }
            else{
                this.listarPrecioEmpresa(buscar);
            }
        },
        listarTipoDocumento(){
            let me=this;
            var tipoArray=[];
            axios.get('api/TipoDocumento/Select').then(function(response){
                tipoArray=response.data;
                tipoArray.map(function(x){
                    me.tdocumento.push({text: x.descripcion,value:x.idTipoDocumneto});
                });
            }).catch(function(error){
                console.log(error);
            });
        },
        cerrarProforma(){
            this.modalNumeroProforma=0;
        },
        buscarProforma(){
            let me=this;
            axios.get('api/Proformas/ListarProformasEdicion/'+me.codproforma).then(function(response){
                me.idProforma = response.data[0].idProforma;
                me.nombreCliente = response.data[0].cliente;
                me.numeroDocumento = response.data[0].nurDocumento;
                me.correlativoActual = response.data[0].correlativo;
                me.correTemp = 1;
                axios.get('api/Proformas/DetalleProforma/'+me.codproforma).then(function(responses){
                    me.detalles=responses.data;
                    me.modalNumeroProforma=0;
                    me.codproforma="";
                }).catch(function(error){
                    console.log(error);
                });
            }).catch(function(error){
                console.log(error);
            });
        },
        buscarCodbarras(){
            if(!this.idTipoc){
                this.$swal('ADVERTENCIA.', 'Seleccione cliente a facturar.', 'error');
                return;
            }
            else if(this.bcodBarras.length==10){
               if(this.idTipoc==1){   
                   this.buscarPrecioPublico();
                   this.barra=[];
                }
                else if(this.idTipoc==2){
                   this.buscarPrecioMayorista();
                   this.barra=[];
                }
                else{
                   this.buscarPrecioEmpresa();
                   this.barra=[];
                }   
            }
        },
        clienteAdd(item){
            this.idCliente = item.idPersona;
            this.numeroDocumento = item.numeroDocumento;
            this.nombreCliente = item.razonSocial;
            this.montoCredito = item.montoCredito;
            this.direc = item.direccion;
            this.verClientes=0;
            this.searchbuscar="";
        },
        cancelarCrearCliente(){
            let me=this;
            me.limpiarConsulta();
            me.nuevoCliente=0;       
            me.valida=0;
            me.validaMensaje=[]; 
        },
        limpiarConsulta(){
            let me=this;
            me.tipoDoc="";
            me.consultas= [];
            me.nombreReniec   = "";
            me.apellido = "";
            me.razonSocialReniec = "";
            me.estado="";
            me.departamento ='';
            me.provincia ='';
            me.distrito = '';
            me.direccionReniec = '';
            me.numeroDocumentoReniec="";   
            me.telefono="";
            me.email=""; 
        },
        alertDisplayDNI() {
            this.$swal('Cancelled', 'DNI no encontrado', 'error')
        },
        alertDisplayRUC() {
            this.$swal('Cancelled', 'RUC no encontrado', 'error')
        },
        consultasDocuments() {
            let me=this;
            if(me.tipoDoc==1){
                axios.post('api/VentaProductos/ConsultasDocumentos/'+this.numeroDocumentoReniec).then(function(response){
                me.consultas= response.data;
                me.nombreReniec   = me.consultas.datosPerson[0].nombres;
                me.apellido = me.consultas.datosPerson[0].apellidoPaterno+' '+me.consultas.datosPerson[0].apellidoMaterno;
                me.razonSocialReniec = me.consultas.datosPerson[0].nombres+' '+me.consultas.datosPerson[0].apellidoPaterno+' '+me.consultas.datosPerson[0].apellidoMaterno;

                if(me.consultas.datosPerson[0].nombres==""){
                    me.alertDisplayDNI();
                }
            }).catch(err => {
                 console.log(err)
                 me.alertDisplayDNI();
                 this.numeroDocumento="";
            });
            }
            else if(me.tipoDoc==2){
                axios.get('http://localhost:8080/consultasdoc/peru-consult-api/public/api/v1/ruc/'+this.numeroDocumentoReniec+'?token=abcxyz').then(function(response){
                me.consultas=response.data;
                me.razonSocialReniec = me.consultas.razonSocial;
                if(me.consultas.direccion=="-"){
                   me.direccionReniec = '-';
                   me.departamento ='';
                   me.provincia ='';
                   me.distrito = '';
                }else{
                   me.direccionReniec = me.consultas.direccion;
                   me.departamento = me.consultas.departamento;
                   me.provincia = me.consultas.provincia;
                   me.distrito = me.consultas.distrito;
                }
                me.estado = me.consultas.estado;
            }).catch(err => {
                console.log(err)
                me.alertDisplayRUC();
                this.numeroDocumento="";
            });
          }
        },
        buscarPrecioPublico(){
            let me=this;
            axios.get('api/Productos/BuscarCodBarrasPublico/'+me.bcodBarras).then(function(response){
               me.barra=response.data;
               me.agregarDetallescodBarras(me.barra);
            }).catch(function(error){
                console.log(error);
            });
        },
        buscarPrecioMayorista(){
            let me=this;
            axios.get('api/Productos/BuscarCodBarrasMayorista/'+me.bcodBarras).then(function(response){
               me.barra=response.data;
               me.agregarDetallescodBarras(me.barra);
            }).catch(function(error){
                console.log(error);
            });
        },
        abrirProforma(){
            this.modalNumeroProforma=1;
        },
        buscarPrecioEmpresa(){
            let me=this;
            axios.get('api/Productos/BuscarCodBarrasEmpresa/'+me.bcodBarras).then(function(response){
               me.barra=response.data;
               me.agregarDetallescodBarras(me.barra);
            }).catch(function(error){
                console.log(error);
            });
        },
        listarPrecioMayorista(buscar){
           let me=this;
           if(buscar.length==0){
               me.productos =[];
               return;
           }
           axios.get('api/Productos/ListaProductoPrecioMayorista/'+buscar).then(function(response){
               me.productos=response.data;
           }).catch(function(error){
                console.log(error);
           });
        },
        listarPrecioEmpresa(buscar){
           let me=this;
           if(buscar.length==0){
               me.productos =[];
               return;
           }
           axios.get('api/Productos/ListaProductoPrecioEmpresa/'+buscar).then(function(response){
               me.productos=response.data;
           }).catch(function(error){
                console.log(error);
           });
        },
        mostrarClientes(){
            this.verClientes=1;
            this.listarClientes();
        },
        cerrarClientes(){
            this.verClientes=0;
            this.searchbuscar="";
        },
        listarClientes() {
            let me=this;
            axios.get('api/Personas/Clientes').then(function(response){
                me.cliente=response.data;
            }).catch(function(error){
                console.log(error);
            });
        },
        agregarDetalle(data = []){
            this.detalles.push
            ({
                idDetalleProforma:0,
                idProducto:data['idProducto'],
                nombre:data['nombre'],
                model: data['model'],
                medidas:data['medida'],
                marca: data['marca'],
                descuento:0,
                idUnidadMedida:data['idUnidadMedida'],
                cantidad:1,
                precio:data['precio'],
            });
            this.cerrarProductos();
            this.search='';
        },
        agregarDetallescodBarras(item){
            this.detalles.push
            ({
                "idProducto":item[0].idProducto,
                "nombre":item[0].nombre,
                "medida":item[0].medida,
                "descuento":0,
                "idUnidadMedida":item[0].idUnidadMedida,
                "cantidad":1,
                "precio":item[0].precio
            });
            this.bcodBarras='';
        },
        verTipo(){
            if(this.idTipoc==1){
               this.tipocliente = "PRECIO PUBLICO";
            }
            else if(this.idTipoc==2)
            {
               this.tipocliente = "PRECIO MAYORISTA";
            }
            else
            {
               this.tipocliente = "PRECIO EMPRESA";
            }
        },
        listarTipoCliente(){
            let me=this;
            var TipocArray=[];
            axios.get('api/TipoClienteVentas').then(function(response){
                TipocArray=response.data;
                TipocArray.map(function(x){
                    me.tipoc.push({text: x.descripcion,value:x.idTipoClienteVenta});
                    me.idTipoc=1;
                });
            }).catch(function(error){
                console.log(error);
            });
        },
        eliminarDetalle(arr,item){
            var id = item.idDetalleProforma;
            axios.get('api/Proformas/DetalleProformaEdicion/'+id).then(function(datos){
                axios.delete('api/Proformas/EliminarDetalle/'+datos.data[0].idDetalleProforma).then(function(respuesta){
                    var i= arr.indexOf(item);
                    if (i!==-1){
                        arr.splice(i,1);
                    }
                }).catch(function(err){
                })
            }).catch(function(err){
                var i= arr.indexOf(item);
                if (i!==-1){
                    arr.splice(i,1);
                }
            })
        },
        validarConsultas(){
            this.valida=0;
            this.validaMensaje=[];
            if(this.numeroDocumentoReniec.length<3){
                this.validaMensaje.push("Ingrese numero documento.");
            }
            if(this.razonSocialReniec.length<3){
                this.validaMensaje.push("Ingrese razon social.");
            }
            if(this.validaMensaje.length){
                this.valida=1;
            }
            return this.valida;
        },
        guardarCliente () {
            if(this.validarConsultas()){
                return
            }
            if(this.tipoDoc==2 && this.estado!="ACTIVO"){
                this.$swal('ADVERTENCIA.', 'Debe ser un RUC activo.', 'error')
                return
            }
            let me = this;
            axios.get('api/Personas/ValidarDNI/'+me.numeroDocumentoReniec).then(function(response){
                me.$swal('ERROR','El cliente ya esta registrado..', 'error')
            }).catch(err =>{
                if(err.response.status==404)
                {
                    axios.post('api/Personas/Crear',{
                        'nombre':me.nombreReniec,
                        'apellido':me.apellido,
                        'idTipoDocumento':me.tipoDoc,
                        'razonSocial':me.razonSocialReniec,
                        'numeroDocumento':me.numeroDocumentoReniec,
                        'direccion':me.direccionReniec,
                        'departamento':me.departamento,
                        'provincia':me.provincia,
                        'distrito':me.distrito,
                        'telefono':me.telefono,
                        'email':me.email,
                    }).then(function(){
                        me.nuevoCliente=0;
                        me.valida=0;
                        me.limpiarConsulta();
                    }).catch(function(){
                        me.$swal('ERROR.', 'No tiene internet.', 'error')
                    }); 
                }
            });
        },
        abrirNuevoCliente(){
            this.verClientes=0;
            this.nuevoCliente=1;
            this.listarTipoDocumento();
        },
        crearArray(data = []){
            var subtotal = 0.0;
            for (let i = 0; i < data.length; i++) {
                subtotal = (Number(data[i].precio) * Number(data[i].cantidad)) - Number(data[i].descuento);
                this.arrayFinal.push({
                    IdDetalleProforma: data[i].idDetalleProforma,
                    Codigo: data[i].idProducto,
                    Medida: data[i].medidas,
                    Producto: data[i].nombre,
                    Modelo: data[i].model,
                    Marca: data[i].marca,
                    Descuento: data[i].descuento,
                    Precio: data[i].precio,
                    Cantidad: data[i].cantidad,
                    SubTotal: subtotal.toFixed(2)
                })
            }
        },
        pdfG(){
            let me = this;
            if(me.correTemp == 1){
                me.crearPdf(me.correlativoActual, me.idProforma);
                return; 
            }
            axios.get('api/Proformas/CorrelativoProforma').then(function(response){
                me.correlativoProformaOk=response.data;
                me.crearPdf(me.correlativoProformaOk, me.idProforma = 0);
            }).catch(function(error){
                console.log(error);
            });            
        },
        crearPdf(correlativo, id){
            let me = this;
            console.log(correlativo,id);
            if(me.detalles.length == 0){
                return;
            }
            var enviar = [];
            me.crearArray(me.detalles);
            for (let ii = 0; ii < me.arrayFinal.length; ii++) {
                enviar.push({
                    idDetalleProforma: me.arrayFinal[ii].IdDetalleProforma,
                    idProducto: me.arrayFinal[ii].Codigo,
                    cantidad: me.arrayFinal[ii].Cantidad,
                    precio: me.arrayFinal[ii].Precio,
                    descuento: me.arrayFinal[ii].Descuento,
                    igv: 0,
                    total: me.arrayFinal[ii].SubTotal,
                    estado: 1
                })    
            }
            function buildTableBody(data, columns){
                    var body = [];
                    body.push(columns);
                    data.forEach(function(row){
                        var dataRow = [];
                        columns.forEach(function(column) {
                            dataRow.push(row[column].toString());
                        })
                        body.push(dataRow);
                    });
                    return body;
                };
    
                function tablelleno(data, columns) {
                    return {
                    alignment:'center',
                    width:'auto',
                    fontSize: 8,
                        table: {
                            style: 'tableHeader',
                            widths:[35,70,50,250,40,45,40,60,'*'],
                            headerRows: 1,
                            body:buildTableBody(data, columns)
                        },
                        layout: 'headerLineOnly',
                    };
                }
            var fecha = new Date();
            var mestodo =["Enero","Febrero","Marzo","Abril","Mayo","Junio","Julio","Agosto",
            "Septiembre","Otubre","Noviembre","Diciembre"];
