                    pageOrientation:'landscape',
                    pageMargins: [50, 35, 50, 60],
                    content: [
                        {
                        columns:[
                            {
                            image:'data:image/jpeg;base64,/9j/4AAQSkZJRgABAQEAYABgAAD/       2wBDAAIBAQIBAQICAgICAgICAwUDAwMDAwYEBAMFBwYHBwcGBwcICQsJCAgKCAcHCg0KCgsMDAwMBwkODw0MDgsMDAz/ ',
                            alignment: 'left',
                            fit:[150,150]
                            },
                            [
                            {text: 'SELVATEC S.R.L.',alignment: 'center', fontSize: 18, bold: true, margin: [0, 3, 0, 3]},
                            {text: 'RUC 20603507399',alignment: 'center', fontSize: 11, bold: true, margin: [0, 3, 0, 3]},
                            {text: 'Calle Moore 1094/ 9 de Diciembre - Iquitos',alignment: 'center', fontSize: 10, bold: true, margin: [0, 3, 0, 3]},
                            ],
                            [
                                {text: 'PROFORMA',alignment: 'right', fontSize: 22, bold: true, margin: [0, 3, 0, 3]},
                                {text: 'N¬∞ ' +correlativo,alignment: 'right', fontSize: 15, bold: true, margin: [0, 3, 0, 3]},
                            ]
                        ],
                        },{
                            columns:[
                            {text: 'Cliente: '+ me.nombreCliente, fontSize: 9, bold: true, margin: [0, 14, 0, 5]},
                            {text: 'RUC / DNI: '+ me.numeroDocumento,alignment: 'center', fontSize: 9, bold: true, margin: [0, 14, 0, 5]},
                            ],
                        },{
                            columns:[
                            {text: 'Fecha: '+ fechahoy, fontSize: 9, bold: true, margin: [0, 5, 0, 5]},
                            {text: 'Vendedor: '+me.$store.state.nombreUsuario.NombreCompleto,alignment: 'center', fontSize: 9, bold: true, margin: [0, 5, 0, 5]},
                        ],
                        },
                        {
                            columns:[
                            {text: 'Direcci√≥n: '+ me.direc, fontSize: 9, bold: true, margin: [0, 5, 0, 15]},
                        ],
                        },
                        
                        /* { qr: 'Carlos Andres Gonzales Villanueva', fit: '50' }, */
                        tablelleno(me.arrayFinal, ['Codigo','Medida','Marca','Producto','Modelo','Cantidad','Precio','Descuento','SubTotal']),
                        {
                            columns:[
                            {text: 'Total: S/.' +this.totalVenta,alignment: 'right', fontSize: 12, bold: true, margin:[0, 8, 0, 10]}
                        ],
                        }
                        
                    ],
                    footer:{
                    margin:[40,0, 40, 10],
                    heigth:60,
                        columns:[
                        {text: 'Vendedor' +'Carlos Gonzales Villanueva',alignment: 'right', fontSize: 10, bold: true, margin:[0, 5, 0, 10]},
                        {text: 'Total: S/.' +me.totalVenta,alignment: 'right', fontSize: 12, bold: true, margin:[0, 5, 0, 10]}
                    ],
                    columns:[
                        [
                        {text: 'C & S GROUP S.R.L. ', fontSize:7, bold:true, margin:[0,2,0,2]},
                        {text:'BBVA Continental: ', fontSize:7, bold:true, margin:[0,2,0,2]},
                        {text: 'MN: 0011-0050-0100000744', fontSize:7, bold:false, margin:[0,2,0,2]},
                        {text: 'CCI: 011-050-00010000074421', fontSize:7, bold:false, margin:[0,2,0,2]}
                        ],
                        [
                        {text: 'Correos',alignment:'center', fontSize:7, bold:true, margin:[0,2,0,2]},
                        {text:'selvateciquitos@gmail.com/futuraelectric1@gmail.com',alignment:'center', fontSize:7, bold:true, margin:[0,2,0,2]},
                        {text: 'Contacto:',alignment:'center', fontSize:7, bold:false, margin:[0,2,0,2]},
                        {text: '965-764442/977-111730 ',alignment:'center', fontSize:7, bold:false, margin:[0,2,0,2]}
                        ],
                        [
                        {text: 'SELVATEC S.R.L.',alignment:'right', fontSize:7, bold:true, margin:[0,2,0,2]},
                        {text:'BBVA Continental: ',alignment:'right', fontSize:7, bold:true, margin:[0,2,0,2]},
                        {text: 'MN: 0011-0301-0201384946',alignment:'right', fontSize:7, bold:false, margin:[0,2,0,2]},
                        {text: 'ME: 0011-0301-0201384954',alignment:'right', fontSize:7, bold:false, margin:[0,2,0,2]}
                        ]
                    ]
                    }
                };
                var pdfito;
                pdfMake.createPdf(docDefinition).getBase64(function(encodedString){
                    pdfito = encodedString;
                    axios.post('api/Proformas/Crear',{
                        'idProforma':id,
                        'idCliente': me.idCliente,
                        'subtotal':me.subtotal,
                        'descuento':me.des,
                        'totalPagar':me.totalVenta,
                        'pdf': pdfito,
                        'detalleProformas':enviar
                    }).then(function(response){
                        me.$swal('CONFORME', 'Proforma Registrada N¬∞'+correlativo+' Con un total de S/.' + me.totalVenta, 'success');
                        me.pdfObjeto = pdfMake.createPdf(docDefinition).print();
                        me.detalles =[];
                        me.arrayFinal = [];
                        me.numeroDocumento="00000000";
                        me.nombreCliente="PUBLICO GENERAL";
                        me.totalVenta = 0.0;
                        me.subTotal = 0.0;
                        me.des = 0.0;
                        me.pdf = '';
                        me.direc = '';
                        me.correTemp=0;
                        me.correlativoProformaOk='';
                        
                    }).catch(function(error){
                        console.log(error);
                    });
                })
        }
    }
}
