<template>
  <v-layout justify-center>
    <v-flex xs12>
      <v-toolbar color="white" >
            <v-toolbar-title>ANULAR VENTAS</v-toolbar-title>
            <v-spacer></v-spacer>
            <template>
                <v-flex xs12 sm12 md12 lg12 xl12>
                    <v-layout row wrap class="ma">   
                        <v-flex xs5 sm5 md5 lg5 xl5>
                            <v-select :items="tipocomprobante" v-model="idComprobante" chips prepend-icon="assignment" label="TIPO DOCUMENTO">
                            </v-select>
                        </v-flex>
                        <v-flex xs5 sm5 md5 lg5 xl5>
                            <v-text-field append-icon="search" placeholder="..................." v-model="searchventa"  class="text-xs-center vs"  label="Busqueda">
                            </v-text-field>
                        </v-flex>
                        <v-flex xs2 sm2 md2 lg2 xl2>
                            <v-btn round color="primary" dark @click="listarBoleta(idComprobante, searchventa)" class="bus">Buscar</v-btn>
                        </v-flex>
                    </v-layout>
                </v-flex>
                <!-- <v-text-field v-model="search" append-icon="search" @keyup.enter="listarBoleta" type="number">
                </v-text-field> -->
            </template>
      </v-toolbar>
      <v-card>
          <v-dialog v-model="anular" max-width="450px" persistent>
            <v-card>
                <v-card-title class="error" style="color: white;" primary-title><h3> ANULAR BOLETA / FACTURA</h3></v-card-title>
                    <v-card-text>
                        <v-container grid-list-md>
                             <v-layout wrap>
                                 <v-flex text-center>
                                    <v-text-field  prepend-icon="person"  v-model="nombreUsuario" color="primary" label="USUARIO" >
                                    </v-text-field>
                                    <v-text-field  prepend-icon="lock" v-model="password" type="password" color="primary" label="PASSWORD" >
                                    </v-text-field>
                                     <v-textarea  prepend-icon="assignment" v-model="motivo" label="MOTIVO" placeholder="" row="1" auto-grow ></v-textarea>
                                     <v-flex class="red--text" v-if="error">
                                        <h4> {{error}}</h4>
                                     </v-flex>
                                </v-flex>
                            </v-layout>
                       </v-container>
                    </v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions>
                       <v-spacer></v-spacer>
                       <v-btn color="red darken-1" dark class="mb-2" @click="cerrarAnulacion()" >Cerrar</v-btn>
                       <v-btn color="info" class="mb-2" @click="anularDetalleSupervisor()">Confirmar</v-btn>
                    </v-card-actions>
            </v-card>
        </v-dialog>
        <v-container fluid grid-list-md >
          <v-layout row wrap>
                <v-flex xs12>
                 <v-card>
                    <v-card-title class="error" style="color: white;"><h3>LISTADO</h3></v-card-title>
                    <v-card-text>
                        <v-data-table
                            :headers="headers"
                            :items="listado"
                            show-select="true"
                            class="elevation-1">
                            <template slot="items" slot-scope="props">
                                <td>{{props.item.idVenta}}</td>
                                <td>
                                    {{props.item.comprobante}}
                                </td>
                                <td>{{ props.item.serie}}</td>
                                <td>{{ props.item.correlativo }}</td>
                                <td>{{ since(props.item.fecha) }}</td>
                                <td>{{ props.item.hora }}</td>
                                <td>{{ props.item.cliente }}</td>
                                <td>{{ props.item.descripcion }}</td>
                                <td>{{ props.item.numeroDoc }}</td>
                                <td>S/.{{ props.item.totalPagar }}</td>
                                <td>
                                    <v-icon  color="red darken-2" small dark @click="abrirModal(props.item.idVenta)">edit</v-icon>
                                </td>
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3> No hay comprobantes.</h3>
                                </span>
                            </template>
                        </v-data-table>
                    </v-card-text>
                   <v-divider></v-divider>
                       
                 </v-card>
               </v-flex>
          </v-layout>
        </v-container>     
      </v-card>
    </v-flex>
  </v-layout>
</template>
<script>
import axios from 'axios'
import moment from 'moment';
moment.locale('es');
var pdfMake = require ('pdfmake/build/pdfmake.js');
var pdfFonts = require ('pdfmake/build/vfs_fonts.js');
pdfMake.vfs = pdfFonts.pdfMake.vfs;

export default {
    data() {
        return {
            anular:0,
            listado:[],
            tipocomprobante: [],
            idComprobante:0,
            searchventa: '',
            idventa:'',
            headers: [
            { text: 'CODIGO', value: 'codigo',sortable: false },
            { text: 'COMPROBANTE', value: 'comprobante' },
            { text: 'SERIE', value: 'serie',sortable: false  },
            { text: 'CORRELATIVO', value: 'correlativo', sortable: false  },
            { text: 'FECHA', value: 'fecha', sortable: false  },
            { text: 'HORA', value: 'hora', sortable: false  },
            { text: 'CLIENTE', value: 'cliente', sortable: false  }, 
            { text: 'DESCRIPCION', value: 'cliente', sortable: false  },     
            { text: 'N. DOCUMENTO', value: 'documento', sortable: false  },    
            { text: 'TOTAL', value: 'total', sortable: false  },   
            { text: 'OPCIONES', value: 'opciones', sortable: false  },               
         ],
         search: '',
         nombreUsuario:'',
         password:'',
         motivo:'',
         error: null,
         pdfObjeto : null,
         id:'',
         listaTotal:[],
         usuarioP:'',
         idV: '',
         fechaP: '',
         horaP:'',
         listado1:[],
         listado2:[],
         listado3:[],
         listado4:[],
         listado5:[],
       }
    },
    mounted() {
    },
    components:{
    },
    filters: {
        uppercase: function(str){
            return str.toUpperCase()
        },
        lowercase:function(str){
            return str.toLowerCase()
        },
        capitalize: function(str){
            return str.charAt(0).toUpperCase() + str.slice(1)
        }
     },
    computed: {
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            this.selectComprobante();
        },
        methods:{
          cerrarAnulacion(){
            this.anular = 0;
            this.error=null;
          },
          abrirModal(id){
              this.anular = 1;
              this.idventa = id;
          },
          since: function(d){
              return moment(d).format('L');  
           },
          listarBoleta(id, corre) {
              this.listado =[];
              if(this.searchventa.length > 0){
                let me=this;
                axios.get('api/VentaProductos/ListaVentaAnulacion/' + id + '/' + corre).then(function(response){
                    me.listado = response.data;
                }).catch(function(error){
                    
                });
              }else{
                  this.searchventa='';
                  this.listado = [];
                  this.id ='';
              }
          },
          anularDetalleSupervisor(){
                let me = this;
                axios.post('api/AnularVentas/Crear',{
                    'nombreUsuario':me.nombreUsuario,
                    'clave':me.password,
                    'idVenta':me.idventa,
                    'motivo':me.motivo,
                }).then(function(response){
                    let id = response.data.idVenta;
                    console.log(id);
                    axios.get('api/AnularVentas/Mostrar/'+id).then(function(responses){
                        me.idV = responses.data[0].idVenta;
                        me.fechaP = responses.data[0].fecha;
                        me.fechaP = responses.data[0].hora;
                        me.usuarioP = responses.data[0].usuario;
                        me.listaTotal = [{
                            'Codigo': responses.data[0].idVenta,
                            'Comprobante': responses.data[0].comprobante,
                            'Serie': responses.data[0].serie,
                            'Correlativo': responses.data[0].correlativo,
                            'Cliente': responses.data[0].cliente,
                            'NumeroDoc': responses.data[0].dni,
                            'Total': responses.data[0].total
                        }];
                        console.log(me.listaTotal);
                        me.anular=0;
                        me.listado = [];
                        me.idventa ='';
                        me.password ='';
                        me.search='';
                        //me.pdfG();
                        me.listaTotal=[];
                        me.motivo = '';

                    }).catch(function(error){
                        console.log(error);
                    });
                    
                }).catch(err =>{
                    if (err.response.status==400){
                        this.error="No es usuario v√°lido.";
                    }else if (err.response.status==404){
                        this.error="No existe el usuario o sus datos son incorrectos.";
                     }else{
                        this.error="Ocurri√≥ un error.";
                    }
                });
            },
            pdfG(){
                function buildTableBody(data, columns){
                    var body = [];
                    body.push(columns);
                    data.forEach(function(row){
                        var dataRow = [];
                        columns.forEach(function(column) {
                            console.log(column);
                            dataRow.push(row[column].toString());
                        })
                        body.push(dataRow);
                    });
                    return body;
                };

                function tablelleno(data, columns) {
                    return {
                    alignment:'center',
                    width:'auto',
                    fontSize: 10,
                        table: {
                            style: 'filledHeader',
                            widths:[50,150,50,100,150,90,'*'],
                            headerRows: 1,
                            body:buildTableBody(data, columns)
                        },
                        styles: {
                            filledHeader: {
                                bold: true,
                                fontSize: 14,
                                color: 'black',
                                fillColor: 'black',
                                alignment: 'center'
                            }
                        }
                    };
                }
                var fecha = new Date();
                var mestodo =["Enero","Febrero","Marzo","Abril","Mayo","Junio","Julio","Agosto",
                "Septiembre","Otubre","Noviembre","Diciembre"];
                var diasemana =[
                "Domingo", "Lunes","Martes","Miercoles",
                "Jueves","Viernes","Sabado","Domingo"
                ];
                //Obtener fecha
                var fechahoy = diasemana[fecha.getDay()] + ", "+fecha.getDate() + " de " + mestodo[fecha.getMonth()]+
                " del " + fecha.getFullYear();
                var docDefinition = {
                    pageSize:'A4',
                    pageOrientation:'landscape',
                    pageMargins: [60, 35, 60, 60],
                    content: [
                        {
                        columns:[
                            {
                            image:'data:image/png;base64,',
                            fit:[100,100]
                            },
                            [
                            {text: 'La Casa de las Enchiladas',alignment: 'center', fontSize: 20, bold: true, margin: [0, 3, 0, 3]},
                            {text: 'Venta Anulada',alignment: 'center', fontSize: 15, bold: true, margin: [0, 3, 0, 3]},
                            {text: 'Calle Fitzcarrald 232 - Iquitos',alignment: 'center', fontSize: 12, bold: true, margin: [0, 3, 0, 3]},
                            {text: 'lacasadelasenchiladasiqts@gmail.com',alignment: 'center', fontSize: 12, bold: false, margin: [0, 3, 0, 3]},
                            ],
                            {
                            image:'data:image/jpeg;base64,/9j/4AAQSkZJRgABAQEAYABgAAD/2wBDAAIBAQIBAQICAgICAgICAwUDAwMDAwYEBAMFBwYHBwcGBwcICQsJCAgKCAcHCg0KCgsMDAwMBwkODw0MDgsMDAz/ ',
                            alignment: 'right',
                            fit:[100,100]
                            }
                        ]
                        },
                        {text: 'Usuario: '+ this.usuarioP, fontSize: 12, bold: true, margin: [0, 14, 0, 8]},
                        {text: 'Fecha y Hora: '+ this.fechaP+','+ this.horaP, fontSize: 12, bold: true, margin: [0, 5, 0, 8]},
                        {text: 'Cod. Venta: '+ this.idV, fontSize: 12, bold: true, margin: [0, 5, 0, 8]},
                        tablelleno(this.listaTotal, ['Codigo','Comprobante','Serie', 'Correlativo','Cliente','NumeroDoc','Total']),
                        {text: 'Motivo: '+ this.motivo, fontSize: 13, bold: true, margin: [0, 5, 0, 19]},
                    ],
                };
                var f = new Date();
                var hora = f.getHours()+"a"+f.getMinutes()+"b"+f.getSeconds();
                this.pdfObjeto = pdfMake.createPdf(docDefinition);
                this.pdfObjeto.download(this.idV+"VentaAnulada");
                this.listado =[];
            },
            selectComprobante(){
            let me=this;
            var comprobanteArray=[];
            axios.get('api/TipoComprobantes/Listar').then(function(response){
                comprobanteArray=response.data;
                comprobanteArray.map(function(x){
                    me.tipocomprobante.push({text: x.comprobante,value:x.idComprobante});
                    me.idComprobante=1;
                });
            }).catch(function(error){
                console.log(error);
            });
        },
      },
}
</script>
<style>
    .ma{
        padding-top: 3.5%;
        padding-left: 2%;
    }
</style>