<template>
     <v-card>
        <v-card-title class="primary" style="color: white;"><h3>DETALLE VENTA </h3></v-card-title>                        
        <v-card-text>
            
        </v-card-text>
    </v-card>
</template>

<script>
import axios from 'axios'

export default {
    data () {
        return {
            detalle: []
        }
    },    
    created: function() {
        EventBus.$on('detalle-venta',(p) =>{
            console.log(p)
            this.detalle.push(p)
        });
    },
    methods:{
        listarDetalleVenta(id) {
            let me=this;
            axios.get('api/VentaProductos/DetalleVenta/'+id).then(function(response){
                me.detalle=response.data;
            }).catch(function(error){
                console.log(error);
            });
        }
    }
}
</script>