<template>
  <v-layout justify-center>
    <v-flex xs12>
      <v-toolbar color="white">
        <v-toolbar-title>LISTA DE RESERVAS</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="B√∫squeda" single-line hide-details></v-text-field>
      </v-toolbar>
      <v-card>
          <v-dialog v-model="desac" max-width="400px">
                <v-card>
                    <v-card-title class="primary" style="color: white;">
                        <h3>ANULAR RESERVA</h3>
                    </v-card-title>
                    <v-card-text>
                        <span>Est√° a punto de anular una reserva, ¬øDesea continuar la operaci√≥n?</span>
                    </v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="error" @click.native="close">Cerrar
                            <v-icon dark right>block</v-icon>
                        </v-btn>
                        <v-btn color="info"  @click.native="guardar">Anular
                            <v-icon dark right>check_circle</v-icon>
                        </v-btn>
                    </v-card-actions>
                </v-card>
        </v-dialog>
          <v-dialog v-model="reservar" max-width="460px" persistent>
            <v-card>
                <v-card-title class="headline warning"  style="color: white;"><h4>ACTUALIZAR RESERVAR MESA</h4></v-card-title>
                <v-card-text>
                        <v-flex xs12 sm12 md12>
                            <v-text-field prepend-icon="person" v-model="nombresi" label="NOMBRE COMPLETO" disabled></v-text-field>
                        </v-flex>
                        <v-flex xs12 sm12 md12>
                            <v-text-field
                                prepend-icon="phone"
                                v-model="celu"
                                label="CELULAR"
                                required
                                type="number"
                                disabled
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs12 sm12 md12>
                            <v-menu
                                v-model="menu2"
                                :close-on-content-click="false"
                                :nudge-right="40"
                                lazy
                                transition="scale-transition"
                                offset-y
                                full-width
                                min-width="290px"
                                >
                                <template v-slot:activator="{ on }">
                                <v-text-field
                                    v-model="fechita"
                                    label="FECHA"
                                    prepend-icon="event"
                                    readonly
                                    v-on="on"
                                ></v-text-field>
                                </template>
                                <v-date-picker color="green lighten-1" v-model="fechita" @input="menu2 = false"></v-date-picker>
                            </v-menu>
                        </v-flex>
                        <v-flex xs12 sm12 md12>
                            
                            <v-dialog
                                    ref="dialog"
                                    v-model="modal2"
                                    :return-value.sync="hora"
                                    persistent
                                    lazy
                                    full-width
                                    width="290px"
                                >
                                <template v-slot:activator="{ on }">
                                    <v-text-field
                                    v-model="hora"
                                    label="HORA"
                                    prepend-icon="access_time"
                                    readonly
                                    v-on="on"
                                    ></v-text-field>
                                </template>
                                <v-time-picker
                                    v-if="modal2"
                                    v-model="hora"
                                    full-width>
                                <v-spacer></v-spacer>
                                    <v-btn  color="error"   @click="modal2 = false">Cancel</v-btn>
                                    <v-btn  color="primary" @click="$refs.dialog.save(hora)">OK</v-btn>
                                </v-time-picker>
                            </v-dialog>
                        </v-flex>
                        <v-flex xs12 sm12 md12>
                            <v-text-field
                                prepend-icon="people"
                                v-model="personas"
                                label="CANTIDAD PERSONAS"
                                type="number"
                                required
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs12 sm12 md12>
                            <v-textarea v-model="moReserva"  prepend-icon="assignment" label="" placeholder="MOTIVO" row="1" auto-grow disabled></v-textarea>
                        </v-flex>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="error" @click.native="closeReserva">CERRAR
                        <v-icon dark right>block</v-icon>
                    </v-btn>
                    <v-btn color="info"  @click.native="actualizarCantidad()">GUARDAR
                        <v-icon dark right>check_circle</v-icon>
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-container fluid grid-list-md >
          <v-layout row wrap>
                <v-flex xs12>
                 <v-card>
                    <v-card-title class="error" style="color: white;"><h3>RESERVAS</h3></v-card-title>
                    <v-card-text>
                        <v-data-table
                            :headers="headers"
                            :items="listado"
                            :search="search"
                            show-select="true"
                            class="elevation-1">
                            <template slot="items" slot-scope="props">
                                <td>{{ props.item.idReserva}}</td>
                                <td>{{ props.item.cliente}}</td>
                                <td>{{ props.item.mesa}}</td>
                                <td>{{ since(props.item.fecha) }}</td>
                                <td>{{ props.item.hora }}</td>
                                <td>{{ props.item.celular }}</td>
                                <td>{{ props.item.cantidad }}</td>
                                <td>{{ props.item.motivo }}</td>
                                <td>
                                    <v-icon  color="red darken-2" class="mr-2" small dark @click="actualizar(props.item)">edit</v-icon>
                                    <v-icon  color="blue darken-2" class="mr-2" small dark @click="abrirDesactivar(props.item.idReserva)">error</v-icon>
                                </td>
                            </template>
                        </v-data-table>
                    </v-card-text>
                   <v-divider></v-divider>
                 </v-card>
               </v-flex>
          </v-layout>
          <v-snackbar
            v-model="snackbar">
            {{ mensajeToast }}
            <v-btn
                color="pink"
                flat
                @click="snackbar = false">
                Cerrar
            </v-btn>
            </v-snackbar>
        </v-container>     
      </v-card>
    </v-flex>
  </v-layout>
</template>
<script>
import axios from 'axios'
import moment from 'moment';
moment.locale('es');

export default {
    data() {
        return {
            desac: 0,
            reservar:0,
            nombresi: '',
            celu: '',
            moReserva:'',
            personas:0,
            fechita: '',
            hora: '',
            listado:[],
            modal2: '',
            menu2: null,
            lista:[],
            idventa:'',
            headers: [
            { text: 'CODIGO', value: 'codigo' },
            { text: 'CLIENTE RESERVA', value: 'cliente',sortable: false },
            { text: 'MESA', value: 'mesa',sortable: false  },
            { text: 'FECHA', value: 'fecha', sortable: false  },
            { text: 'HORA', value: 'hora', sortable: false  },  
            { text: 'CELULAR', value: 'celular', sortable: false  }, 
            { text: 'CANTIDAD', value: 'personas', sortable: false  }, 
            { text: 'MOTIVO DE RESERVA', value: 'motivo', sortable: false  }, 
            { text: 'OPCIONES', value: 'opciones', sortable: false  },               
         ],
         search: '',
         error: null,
         mensajeToast:'',
         snackbar:false,
         id:'',
         idBaja: '',
       }
    },
    filters: {
        uppercase: function(str){
            return str.toUpperCase()
        },
        lowercase:function(str){
            return str.toLowerCase()
        },
        capitalize: function(str){
            return str.charAt(0).toUpperCase() + str.slice(1)
        }
     },
    computed: {
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            this.listarReservas();
        },
        methods:{
        close(){
            this.desac = 0;
            this.limpiarTodo();
            this.listarReservas();
        },
        guardar(){
            let me = this;
            axios.put('api/Reservar/Desactivar/'+ this.idBaja).then(function(response){
                me.idBaja = '';
                me.listarReservas();
                me.desac = 0;
            }).catch(function(errror){

            });
        },
        limpiarTodo(){
            this.reservar = 0;
            this.nombresi = '';
            this.id = '';
            this.celu = '';
            this.fechita = '';
            this.hora = '';
            this.personas = 0;
            this.moReserva = '';
        },
        abrirDesactivar(id){
            this.desac = 1;
            this.idBaja = id;
        },
        closeReserva(){
            this.limpiarTodo(); 
        },
        actualizar(item){
            let me = this;
            me.reservar = 1;
            me.id = item.idReserva;
            me.nombresi = item.cliente;
            me.fechita = item.fecha.substring(0,10);
            me.hora = item.hora;
            me.celu = item.celular;
            me.personas = item.cantidad;
            me.moReserva = item.motivo;
        },
        since: function(d){
            return moment(d).format('L');  
        },
        listarReservas() {
              let me = this;
              axios.get('api/Reservar/Listar').then(function(response){
                  me.listado= response.data;
              }).catch(function(error){
                  
              });
        },
        actualizarCantidad(){
            let me = this;
            let enviar = {
                'idReserva': me.id,
                'fecha': me.fechita,
                'hora': me.hora,
                'cantidad': me.personas
            }
            axios.put('api/Reservar/Actualizar',enviar).then(function(response){
                me.limpiarTodo();
                me.listarReservas();
                me.reservar = 0;
            }).catch(function(error){

            });
        }
      }
}
</script>