<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar color="white">
                <v-toolbar-title>ASIGNAR USUARIO</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
                <v-spacer></v-spacer>

                <v-dialog v-model="modalUsuario" max-width="500px">
                        <v-card>
                            <v-card-title class="primary" style="color: white;"><h3>NUEVO USUARIO</h3>
                            </v-card-title>
                             <v-card-text>
                                    <v-container grid-list-md>
                                        <v-layout wrap>
                                            <v-flex xs12 sm10 md12 lg12 xl8>
                                                <v-flex xs12>
                                                    <v-text-field  prepend-icon="looks_one" label="NOMBRE COMPLETO" v-model="nombreCompleto" disabled type="text">
                                                    </v-text-field>
                                                </v-flex>
                                                <v-flex xs12>
                                                    <v-autocomplete
                                                        v-model="idRol"
                                                        :items="roles"
                                                        prepend-icon="assignment"
                                                        label="ROL" chips
                                                        required>
                                                    </v-autocomplete>
                                                </v-flex>
                                                <v-flex xs12 sm12 md6 lg12 xl12>
                                                     <v-text-field  prepend-icon="looks_one" label="USUARIO" v-model="nombreUsuario" type="text">
                                                    </v-text-field>
                                                </v-flex>
                                                <v-flex xs12 sm12 md6 lg12 xl12>
                                                     <v-text-field  prepend-icon="looks_one" label="PASSWORD" v-model="password" type="password">
                                                    </v-text-field>
                                                </v-flex>
                                                <v-flex xs12 sm12 md12 v-show="valida">
                                                     <h3><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h3>
                                                </v-flex>
                                            </v-flex>
                                        </v-layout>
                                </v-container>
                            </v-card-text>
                            <v-divider></v-divider>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="error" @click.native="close">Cerrar
                                    <v-icon dark right>block</v-icon>
                                </v-btn>
                                <v-btn color="info" @click="crearUsuario()">Guardar
                                    <v-icon dark right>check_circle</v-icon>
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                </v-dialog>
            </v-toolbar>
            <v-card>
            <v-container fluid grid-list-md v-if="verNuevo==0">
            <v-layout row wrap>
                <v-flex xs12>
                    <v-card>
                        <v-card-title class="primary" style="color: white;"><h3>LISTADO DEL PERSONAL</h3></v-card-title>
                        <v-card-text>
                            <v-flex xs12>
                                <v-text-field class="text-xs-center" v-model="search" append-icon="search" placeholder="................" label="B√∫squeda"></v-text-field>
                            </v-flex>
                            <template>
                               <v-data-table
                                :headers="headers"
                                :items="cliente"
                                :search="search"
                                class="elevation-1">
                                <template slot="items" slot-scope="props">
                                    <td>
                                        <v-icon
                                            small
                                            color="error"
                                            class="mr-2"
                                            @click="abrirUsuario(props.item)"
                                            >
                                            account_box
                                        </v-icon>
                                    </td>
                                    <td>{{ props.item.numeroDocumento }}</td>
                                    <td>{{ props.item.nombre }} {{ props.item.apellido }} </td>
                                    <td>{{ props.item.direccion }}</td>
                                </template>
                            <template slot="no-data">
                                 <v-btn color="primary" @click="listarPersonalUsuario">Resetear</v-btn>
                            </template>
                            </v-data-table>
                            </template>
                        </v-card-text>
                    </v-card>
                </v-flex>
            </v-layout>
            </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    export default {
        data(){
            return {
                valida:0,
                validaMensaje:[],
                modalUsuario:0,
                nombreCompleto:'',
                nombreUsuario:'',
                password:'',
                idRazonSocial:'',
                idRol:'',
                roles:[],
                idArea:'',
                area:[],
                empresa:[],
                fecha: new Date().toISOString().substr(0, 10),
                idPersona:'',
                menu2: false,
                menu3: false,
                search: '',
                fecha:'',
                fechaIngreso:'',
                nombre:'',
                montoCredito:'',
                apellido:'',
                validaMensaje:[],
                valida:0,
                telefono:'',
                email:'',
                verNuevo:0,
                tipoDoc:'',
                numeroDocumento:'',
                cliente:[],
                razonSocial:'',
                direccion:'',
                dialog: false,
                documento:['CLIENTE DNI', 'CLIENTE RUC'],
                tabs: null,
                headers: [
                { text: 'OPCIONES', value: 'opciones', sortable: false },
                { text: 'NUMERO DOCUMENTO', value: 'numeroDocumento', sortable: false },
                { text: 'NOMBRE COMPLETO', value: 'razonSocial', sortable: false },
                { text: 'DIRECCION', value: 'direccion', sortable: false }
                ],
                search: '',
                editedIndex: -1,
                adModal:0,
                adAccion:0,
                sueldo:0
            }
        },
        computed: {
                formTitle () {
                return this.editedIndex === -1 ? 'NUEVO PERSONAL' : 'ACTUALIZAR PERSONAL'
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () { 
            this.listarPersonalUsuario();
        },
        methods:{
            close (){
                this.dialog = false;
                this.limpiar();
                this.valida=0;
            },
            abrirUsuario(item){
                if(item.estadoUsuario==1){
                     this.$swal('ADVERTENCIA.', item.razonSocial +' YA TIENE UNA CUENTA DE USUARIO.', 'warning');
                }else{
                   this.select();
                   this.idPersona=item.idPersona;
                   this.nombreCompleto =item.razonSocial;
                   this.modalUsuario=1;
                }
            },
            close () {
                this.modalUsuario = 0;
                this.limpiar();
            },
            limpiar(){
                this.idUsuario="";
                this.idRol="";
                this.nombreCompleto="";
                this.nombreUsuario="";
                this.password="";
            },
            cerrarModalCliente(){
                this.adModal=0;
            },
            editItem (item) {
                this.idPersona = item.idPersona;
                this.numeroDocumento=item.numeroDocumento;
                this.nombre = item.nombre;
                this.apellido = item.apellido;
                this.direccion=item.direccion;
                this.telefono=item.telefono;
                this.email=item.email;
                this.fecha = (item.fechaNacimiento).substring(0,10);
                this.idRazonSocial = item.idRazonSocial;
                this.idArea = item.idArea;
                this.sueldo = item.sueldo;
                this.fechaIngreso = (item.fechaIngreso).substring(0,10);
                this.editedIndex=1;
                this.dialog = true
            },
            nuevoCliente(){
                this.verNuevo=1;
            },
            mostrar(item){
              this.adModal=1;
              this.idPersona=item.idPersona;
              this.montoCredito=item.montoCredito
            },
            limpiar(){
                this.numeroDocumento="";
                this.razonSocial="";
                this.direccion="";
                this.telefono="";
                this.email="";
                this.fecha = '';
                this.nombre ='';
                this.apellido = '';
            },
            select(){
                let me=this;
                var RolArray=[];
                axios.get('api/Rol/Select').then(function(response){
                    RolArray=response.data;
                    RolArray.map(function(x){
                        me.roles.push({text: x.nombre,value:x.idRol});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
            crearUsuario(){
                if(this.validar()){
                    return;
                }
                let me = this;
                axios.post('api/Usuarios/Crear',{
                    'idRol':me.idRol,
                    'idPersona':me.idPersona,
                    'nombreCompleto':me.nombreCompleto,
                    'nombreUsuario':me.nombreUsuario,
                    'clave':me.password,
                    'Password':me.password
                }).then(function(response){
                    me.close();
                    me.listarPersonalUsuario();
                }).catch(function(error){
                    console.log(error);
                });
            },
            listarPersonalUsuario() {
                let me=this;
                axios.get('api/Personas/ListarPersonalUsuarios').then(function(response){
                    me.cliente=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];

                if(!this.idRol){
                    this.validaMensaje.push("Seleccione rol.");
                }
                if(this.nombreUsuario.length<3){
                    this.validaMensaje.push("Ingrese nombre usuario.");
                }
                if(this.password.length<3){
                    this.validaMensaje.push("Ingrese password.");
                }
                if(this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            }
        }        
    }
</script>