<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar color="white">
                <v-toolbar-title>PERSONAL DE SELVATEC</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="1050px" persistent>
                        <v-btn slot="activator" color="info" dark class="mb-2">Nuevo
                             <v-icon dark right>check_circle</v-icon>
                        </v-btn>
                        <v-card>
                            <v-card-title class="primary" style="color: white;">
                               <h3>{{formTitle}}</h3>
                            </v-card-title>
                            <v-card-text>
                             <v-container grid-list-md>
                                <v-layout wrap>
                                   <v-flex xs12 sm12 md16> 
                                        <v-text-field  type="number" v-model="numeroDocumento" label="INGRESE (DNI, CEDULA, OTROS)" prepend-icon="looks_3"></v-text-field>
                                   </v-flex>
                                   <v-flex xs6 sm6 md6> 
                                        <v-text-field  type="text" v-model="nombre" label="NOMBRE"  prepend-icon="how_to_reg" ></v-text-field>
                                   </v-flex>
                                   <v-flex xs12 sm16 md6> 
                                        <v-text-field  type="text" v-model="apellido" label="APELLIDO"  prepend-icon="how_to_reg" ></v-text-field>
                                   </v-flex>
                                    <v-flex xs12 sm16 md12> 
                                        <v-text-field v-model="direccion"  type="text" label="DIRECCION ACTUAL"  prepend-icon="location_on"></v-text-field>
                                   </v-flex>
                                    <v-flex xs6 sm6 md6>
                                        <v-text-field  type="number" v-model="telefono" label="CELULAR" prepend-icon="call" ></v-text-field>
                                    </v-flex>
                                    <v-flex xs6 sm6 md6> 
                                        <v-text-field  type="text" v-model="email" label="EMAIL" prepend-icon="contact_mail"></v-text-field>
                                   </v-flex>
                                   <v-flex xs6 sm6 md6>
                                        <v-select label="EMPRESA" v-model="idRazonSocial"  :items="empresa" chips type="text" prepend-icon="assignment"></v-select>
                                   </v-flex>
                                    <v-flex xs6 sm6 md6>
                                        <v-select label="AREA" type="text" v-model="idArea" :items="area" chips prepend-icon="assignment"></v-select>
                                    </v-flex>
                                    <v-flex xs6 sm6 md6>
                                        <v-menu
                                            v-model="menu2"
                                            :close-on-content-click="false"
                                            :nudge-right="40"
                                            lazy
                                            transition="scale-transition"
                                            offset-y
                                            full-width
                                            min-width="290px"
                                            >
                                            <template v-slot:activator="{ on }">
                                            <v-text-field
                                                v-model="fecha"
                                                label="FECHA NACIMIENTO"
                                                prepend-icon="event"
                                                readonly
                                                v-on="on"
                                            ></v-text-field>
                                            </template>
                                            <v-date-picker color="green lighten-1" v-model="fecha" @input="menu2 = false"></v-date-picker>
                                        </v-menu>
                                    </v-flex>

                                    <v-flex xs6 sm6 md6>
                                        <v-menu
                                            v-model="menu3"
                                            :close-on-content-click="false"
                                            :nudge-right="40"
                                            lazy
                                            transition="scale-transition"
                                            offset-y
                                            full-width
                                            min-width="290px"
                                            >
                                            <template v-slot:activator="{ on }">
                                            <v-text-field
                                                v-model="fechaIngreso"
                                                label="FECHA INGRESO"
                                                prepend-icon="event"
                                                readonly
                                                v-on="on"
                                            ></v-text-field>
                                            </template>
                                            <v-date-picker color="green lighten-1" v-model="fechaIngreso" @input="menu3 = false"></v-date-picker>
                                        </v-menu>
                                    </v-flex>
                                    <v-flex xs12 sm12 md16> 
                                        <v-text-field  type="number" v-model="sueldo" label="SUELDO" prepend-icon="looks_3"></v-text-field>
                                   </v-flex>
                                   <v-flex xs12 sm12 md12 v-show="valida">
                                     <h3><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h3>
                                   </v-flex>
                                </v-layout>
                            </v-container>
                            </v-card-text>
                             <v-divider></v-divider>
                            <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="error" @click.native="close">Cancelar
                                <v-icon dark right>block</v-icon>
                            </v-btn>
                            <v-btn color="info" @click="guardarCliente()">Guardar
                                 <v-icon dark right>check_circle</v-icon>
                            </v-btn>
                            </v-card-actions>
                        </v-card>
                </v-dialog>
                <v-dialog v-model="adModal" max-width="300">
                    <v-card>
                        <v-card-title class="headline info"  style="color: white;"><h4>ACTUALIZAR CREDITO</h4></v-card-title>
                        <v-card-text>
                            <v-flex xs12 sm12 md6 lg12 xl12>
                                <v-text-field  prepend-icon="attach_money" v-model="montoCredito" label="MONTO CREDITO" type="number">
                                </v-text-field>
                            </v-flex>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="error" dark @click="cerrarModalCliente">Cancelar</v-btn>
                            <v-btn  color="info" dark @click="actualizarCredito()">Confirmar</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>
            <v-card>
            <v-container fluid grid-list-md v-if="verNuevo==0">
            <v-layout row wrap>
                <v-flex xs12>
                    <v-card>
                        <v-card-title class="primary" style="color: white;"><h3>LISTADO DEL PERSONAL</h3></v-card-title>
                        <v-card-text>
                            <v-flex xs12>
                                <v-text-field class="text-xs-center" v-model="search" append-icon="search" placeholder="................" label="B√∫squeda"></v-text-field>
                            </v-flex>
                            <template>
                               <v-data-table
                                :headers="headers"
                                :items="cliente"
                                :search="search"
                                class="elevation-1">
                                
                                <template slot="items" slot-scope="props">
                                    <td>
                                        <v-icon
                                        small
                                        class="mr-2"
                                        @click="editItem(props.item)"
                                        >
                                        edit
                                        </v-icon>
                                        <template >
                                            <v-icon
                                            @click="mostrar(props.item)"
                                            small>
                                            block
                                            </v-icon>
                                        </template>
                                    </td>
                                    <td>{{ props.item.numeroDocumento }}</td>
                                    <td>{{ props.item.nombre }} {{ props.item.apellido }} </td>
                                    <td>{{ props.item.direccion }}</td>
                                    <td>{{ props.item.montoCredito }}</td>
                                    <td>
                                    </td>
                                </template>
                            <template slot="no-data">
                                 <v-btn color="primary" @click="listarClientes">Resetear</v-btn>
                            </template>
                            </v-data-table>
                            </template>
                        </v-card-text>
                    </v-card>
                </v-flex>
            </v-layout>
            </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    export default {
        data(){
            return {
                idRazonSocial:'',
                idArea:'',
                area:[],
                empresa:[],
                fecha: new Date().toISOString().substr(0, 10),
                idPersona:'',
                menu2: false,
                menu3: false,
                search: '',
                fecha:'',
                fechaIngreso:'',
                nombre:'',
                montoCredito:'',
                apellido:'',
                validaMensaje:[],
                valida:0,
                telefono:'',
                email:'',
                verNuevo:0,
                tipoDoc:'',
                numeroDocumento:'',
                cliente:[],
                razonSocial:'',
                direccion:'',
                dialog: false,
                documento:['CLIENTE DNI', 'CLIENTE RUC'],
                tabs: null,
                headers: [
                { text: 'OPCIONES', value: 'opciones', sortable: false },
                { text: 'NUMERO DOCUMENTO', value: 'numeroDocumento', sortable: false },
                { text: 'NOMBRE COMPLETO', value: 'razonSocial', sortable: false },
                { text: 'DIRECCION', value: 'direccion', sortable: false },
                { text: 'CREDITO', value: 'montoCredito', sortable: false },
                ],
                search: '',
                editedIndex: -1,
                adModal:0,
                adAccion:0,
                sueldo:0
            }
        },
        computed: {
                formTitle () {
                return this.editedIndex === -1 ? 'NUEVO PERSONAL' : 'ACTUALIZAR PERSONAL'
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () { 
            this.listarClientes();
            this.selectEmpresa();
            this.selectArea();
        },
        methods:{
            close (){
                this.dialog = false;
                this.limpiar();
                this.valida=0;

            },
            cerrarModalCliente(){
                this.adModal=0;
            },
            editItem (item) {
                this.idPersona = item.idPersona;
                this.numeroDocumento=item.numeroDocumento;
                this.nombre = item.nombre;
                this.apellido = item.apellido;
                this.direccion=item.direccion;
                this.telefono=item.telefono;
                this.email=item.email;
                this.fecha = (item.fechaNacimiento).substring(0,10);
                this.idRazonSocial = item.idRazonSocial;
                this.idArea = item.idArea;
                this.sueldo = item.sueldo;
                this.fechaIngreso = (item.fechaIngreso).substring(0,10);
                this.editedIndex=1;
                this.dialog = true
            },
            nuevoCliente(){
                this.verNuevo=1;
            },
            mostrar(item){
              this.adModal=1;
              this.idPersona=item.idPersona;
              this.montoCredito=item.montoCredito
            },
            actualizarCredito(){
                let me = this;
                axios.put('api/Personas/ActualizarCredito',{
                    'idPersona':me.idPersona,
                    'montoCredito':me.montoCredito,                    
                }).then(function(response){
                    me.cerrarModalCliente();
                    me.listarClientes();
                    me.idPersona="";
                    me.montoCredito="";
                }).catch(function(error){
                    console.log(error);
                });
            },
            guardarCliente () {
                if(this.validar()){
                    return
                }
                let me = this;
                if(this.editedIndex == -1){
                    axios.post('api/Personas/CrearPersonal',{
                        'nombre':me.nombre,
                        'apellido':me.apellido,
                        'numeroDocumento':me.numeroDocumento,
                        'direccion':me.direccion,
                        'telefono':me.telefono,
                        'email':me.email,
                        'fechaNacimiento': me.fecha,
                        'idRazonSocial':me.idRazonSocial,
                        'idArea':me.idArea,
                        'ingresoEmpleado':me.fechaIngreso,
                        'sueldo':me.sueldo
                    }).then(function(response){
                        me.dialog=false;
                        me.limpiar();
                        me.listarClientes();
                    }).catch(function(error){
                        console.log(error);
                    });
                }else{
                    axios.put('api/Personas/ActualizarPersonal',{
                       'idPersona': me.idPersona,
                       'numeroDocumento': me.numeroDocumento,
                       'nombre':me.nombre,
                       'apellido': me.apellido,
                       'fechaNacimiento': me.fecha,
                       'telefono': me.telefono,
                       'email':me.email,
                       'direccion':me.direccion,
                       'idRazonSocial':me.idRazonSocial,
                       'idArea':me.idArea,
                       'fechaIngreso':me.fechaIngreso,
                       'sueldo':me.sueldo
                    }).then(function(response){
                        me.dialog=false;
                        me.limpiar();
                        me.listarClientes();
                    }).catch(function(error){
                        console.log(error);
                    });
                }
            },
            limpiar(){
                this.numeroDocumento="";
                this.razonSocial="";
                this.direccion="";
                this.telefono="";
                this.email="";
                this.fecha = '';
                this.nombre ='';
                this.apellido = '';
            },
            listarClientes() {
                let me=this;
                axios.get('api/Personas/ListarPersonal').then(function(response){
                    me.cliente=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];

                if(this.numeroDocumento.length<3){
                    this.validaMensaje.push("Ingrese numero documento.");
                }
                if(this.nombre.length<3){
                    this.validaMensaje.push("Ingrese nombre.");
                }
                if(this.apellido.length<3){
                    this.validaMensaje.push("Ingrese apellido.");
                }
                if(this.direccion.length<3){
                    this.validaMensaje.push("Ingrese direccion.");
                }
                if(this.telefono.length<3){
                    this.validaMensaje.push("Ingrese telefono.");
                }
                if(!this.idRazonSocial){
                    this.validaMensaje.push("Seleccione una empresa.");
                }
                if(!this.idArea){
                    this.validaMensaje.push("Seleccione una area.");
                }
                if(this.menu2=''){
                    this.validaMensaje.push("Seleccione fecha nacimiento.");
                }
                if(this.menu3=''){
                    this.validaMensaje.push("Seleccione fecha ingreso.");
                }
                if(this.sueldo==0){
                    this.validaMensaje.push("Ingrese sueldo.");
                }
                if(this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
            selectEmpresa(){
                let me=this;
                var tipoArray=[];
                axios.get('api/RazonSocials/Select').then(function(response){
                    tipoArray=response.data;
                    tipoArray.map(function(x){
                        me.empresa.push({text: x.razon,value:x.idRazonSocial});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
            selectArea(){
                let me=this;
                var tipoArray=[];
                axios.get('api/Areas/Select').then(function(response){
                    tipoArray=response.data;
                    tipoArray.map(function(x){
                        me.area.push({text: x.descripcion,value:x.idArea});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            }
        }        
    }
</script>