<template>
  <v-layout>
    <v-flex align-start>
      <v-toolbar  color="white" >
        <v-toolbar-title v-if="verNuevo==0">COMPRAS </v-toolbar-title>
        <v-toolbar-title v-if="verNuevo==1">REGISTRAR NUEVA COMPRA </v-toolbar-title>

         <v-dialog v-model="adAnular" max-width="300" persistent>
                        <v-card>
                          <v-card-title class="headline error" style="color: white;" primary-title>ANULAR COMPRA</v-card-title>
                            <v-card-text>
                                Estas a punto de
                                <span>ANULAR <h3> {{tipoComprobante}} - {{numeroComprobante}} </h3></span>
                            </v-card-text>
                            <v-divider></v-divider>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn  color="info" class="mb-2" @click="cerrarAnularComprar" >CANCELAR</v-btn>
                                <v-btn  color="error" dark class="mb-2" @click="anularComprar()">ANULAR</v-btn>
                            </v-card-actions>
                        </v-card>
          </v-dialog>
          <v-dialog v-model="verDetalleIngreso" max-width="850px" persistent>
                    <v-card>
                        <v-card-title class="primary" style="color: white;"><h3>DETALLE DE LA COMPRA</h3></v-card-title>
                        <v-divider></v-divider>
                        <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                <v-flex xs12 sm12 md12 lg12 xl12>
                                    <template>
                                        <v-data-table                           
                                            :headers="detalleListar"
                                            :items="detallesIngreso"
                                            class="elevation-1" hide-actions>
                                                <template slot="items" slot-scope="props">
                                                <td>
                                                    <strong>{{ props.item.producto }}</strong>
                                                   
                                                </td>
                                                <td> <div class="purple darken-3 text-xs-center">
                                                       <span class="white--text">{{props.item.medida}}</span>
                                                     </div></td>
                                                <td>{{ props.item.cantidad }}</td>
                                                <td>S/ {{ props.item.precio.toFixed(2) }}</td>
                                                <td>S/ {{ props.item.total.toFixed(2) }}</td>
                                            </template>
                                            <template slot="no-data">
                                                <span class="red--text">
                                                    <h3> No hay productos agregados al detalle.</h3>
                                                </span>
                                            </template>
                                        </v-data-table> 
                                    </template>
                                </v-flex>
                            </v-layout>
                        </v-container>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-btn dark color="red darken" @click="cerrarDetalleIngreso()">
                                Cerrar
                            </v-btn>
                        </v-card-actions>
                    </v-card>
        </v-dialog>
        <v-dialog v-model="modalFlete" max-width="300">
            <v-card>
                <v-card-title class="headline info"  style="color: white;"><h4>AGREGAR FLETE</h4></v-card-title>
                <v-card-text>
                    <v-flex xs12 sm12 md6 lg12 xl12>
                        <v-text-field  prepend-icon="attach_money" v-model="flete" label="MONTO FLETE" type="number">
                        </v-text-field>
                    </v-flex>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn  color="error" dark @click="cerrarModalFlete">Cancelar</v-btn>
                    <v-btn  color="info" dark @click="actualizarflete()">Confirmar</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-spacer></v-spacer>
                <v-menu v-if="verNuevo==0"
                    v-model="menu3"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                    >
                    <template v-slot:activator="{ on }">
                    <v-text-field
                        v-model="fechaentrada"
                        label="FECHA INICIO"
                        prepend-icon="event"
                        readonly
                        v-on="on"
                    ></v-text-field>
                    </template>
                    <v-date-picker color="green lighten-1" v-model="fechaentrada" @input="menu3 = false"></v-date-picker>
                </v-menu>
                <v-menu v-if="verNuevo==0"
                    v-model="menu4"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                    >
                    <template v-slot:activator="{ on }">
                    <v-text-field
                        v-model="fechasalida"
                        label="FECHA FIN"
                        prepend-icon="event"
                        readonly
                        v-on="on"
                    ></v-text-field>
                    </template>
                    <v-date-picker color="green lighten-1" v-model="fechasalida" @input="menu4 = false"></v-date-picker>
                </v-menu>
        <v-spacer></v-spacer>

        <template v-if="verNuevo==0">
           <v-btn  color="error" dark class="mb-2"> <v-icon>check_circle</v-icon>BUSCAR</v-btn>
           <v-btn  @click="mostrarNuevo" color="primary" dark class="mb-2"> <v-icon>check_circle</v-icon>NUEVA COMPRA</v-btn>
        </template>
        <v-dialog v-model="verInsumos" max-width="1300px" persistent>
            <v-card>
                <v-card-title class="primary" style="color: white;"><h3>ARTICULOS</h3>
                </v-card-title>
                <v-card-text>
                    <v-container grid-list-md>
                        <v-layout wrap>
                               <v-flex xs12 sm12 md12 lg12 xl12>
                                  <v-text-field append-icon="search" 
                                      class="text-xs-center" v-model="texto"
                                      label="Busqueda" @click="listarProductos()">
                                </v-text-field>
                                <template>
                                    <v-data-table
                                        :headers="cabeceraInsumo"
                                        :items="productos"
                                        :search="texto"
                                        class="elevation-1">
                                        <template slot="items" slot-scope="props">
                                            <td>
                                                <v-icon
                                                  color="success darken-2"
                                                  small
                                                  class="mr-2"
                                                  @click="agregarDetalle(props.item)"
                                                  >
                                                  add
                                                </v-icon>
                                            </td>
                                            <td>{{props.item.codigo }}</td>
                                            <td>{{props.item.nombre }}</td>
                                            <td>{{props.item.categoria }}</td>
                                            <td>{{props.item.medida}}</td>
                                            <td>{{props.item.constoProducto}}</td>
                                            <td>{{props.item.stock}}</td>
                                        </template>
                                        <template slot="no-data">
                                            <span class="red--text">
                                                <h3>No hay articulos para mostrar.</h3>
                                            </span>
                                        </template>
                                    </v-data-table> 
                                </template>
                            </v-flex>
                        </v-layout>
                    </v-container>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn @click="ocultarInsumos()" dark color="red darken">
                        Cerrar
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        
        <v-btn v-if="verNuevo" color="info" @click.native="guardar">
          <v-icon>check_circle</v-icon>REGISTRAR
        </v-btn>
        <v-btn v-if="verNuevo" color="brown" dark @click="abrirModalFlete">
            <v-icon dark>search</v-icon>Agregar Flete
         </v-btn>
        </v-toolbar>

         <v-card>
            <v-container fluid grid-list-md v-if="verNuevo==0" >
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-card>
                            <v-card-title class="error" style="color: white;margin-top:3px"><h3>LISTA DE COMPRAS</h3></v-card-title>
                       
                              <v-data-table
                                :headers="headers"
                                :items="ingresos"
                                class="elevation-1"
                                >
                                <template slot="items" slot-scope="props">
                                    <td>
                                        <v-icon
                                          @click="abrirEditar(props.item)"
                                          small
                                          class="mr-2">
                                          edit
                                        </v-icon>
                                        <v-icon
                                          @click="listarDetalleIngreso(props.item.idIngreso)"
                                          small
                                          class="mr-2">
                                          visibility
                                        </v-icon>
                                        <template v-if="props.item.estado=='Aceptado'">
                                            <v-icon 
                                            small>
                                            visibility
                                            </v-icon>
                                        </template>
                                        <template v-else>
                                            <v-icon @click="abrirAnular(props.item)"
                                            small>
                                            delete
                                            </v-icon>
                                        </template>
                                    </td>
                                    <td>{{ props.item.proveedor}}</td>
                                    <td>{{ props.item.tipoComprobante }}</td>
                                    <td>{{ props.item.serieComprobante }}</td>
                                    <td>{{ props.item.numComprobante }}</td>
                                    <td>{{ since(props.item.fecha_hora)}}</td>
                                      <td>{{ (props.item.moneda)}}</td>
                                    <td>{{ props.item.total }}</td>
                                    <td>
                                        <div  class="primary darken-2 text-xs-center" v-if="props.item.estado==1">
                                          <span class="white--text">Activo</span>
                                        </div>
                                        <div  class="error darken-2 text-xs-center" v-else>
                                            <span class="white--text">Anulado</span>
                                        </div>
                                    </td>
                                </template>
                                <template slot="no-data">
                                <v-btn color="primary" @click="listarIngresos">Resetear</v-btn>
                                </template>
                      </v-data-table>
                    </v-card>
                  </v-flex>
                </v-layout>
            </v-container>
        </v-card>
      <v-card>
        <v-container fluid grid-list-md v-if="verNuevo==1">
          <v-layout row wrap>
            <v-flex xs3>
              <v-card>
                <v-card-title class="primary" style="color: white;" primary-title>
                   <h3>INFORMACION GENERAL</h3>
                </v-card-title>
                <v-card-text>
                  
                  <v-autocomplete 
                    :items="arrayIGV"
                    v-model="dataigv"
                    prepend-icon="assignment"
                    label="INCLUYE IGV" chips
                    required>
                 </v-autocomplete>

                  <v-autocomplete 
                    :items="arrayMoneda"
                    v-model="moneda"
                    prepend-icon="assignment"
                    label="MONEDA" chips
                    required>
                 </v-autocomplete>

                  <v-text-field  v-if="moneda=='DOLARES'"
                    v-model="cambio"
                    prepend-icon="money"
                    label="CAMBIO / COMPRA"
                  
                  ></v-text-field>

                  


                  <v-autocomplete
                     v-model="tipoComprobante"
                    :items="comprobantes"
                    prepend-icon="assignment"
                    label="DOCUMENTO" chips
                    required>
                 </v-autocomplete>
                 <v-text-field
                    prepend-icon="message"
                    v-model="serieNumerica"
                    label=" SERIE"
                    required
                    type="text">
                  </v-text-field>
                  <v-text-field
                    prepend-icon="looks_one"
                    v-model="numeroComprobante"
                    type="number"
                    label="NUMERO"
                    required>
                 </v-text-field>
                 <v-menu
                  v-model="menu2"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    v-model="fecha"
                    label="FECHA"
                    prepend-icon="event"
                    readonly
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker color="green lighten-1" v-model="fecha" @input="menu2 = false"></v-date-picker>
              </v-menu>

                <v-dialog
                      ref="dialog"
                      v-model="modal2"
                      :return-value.sync="hora"
                      persistent
                      lazy
                      full-width
                      width="290px"
                  >
                  <template v-slot:activator="{ on }">
                    <v-text-field
                      v-model="hora"
                      label="HORA"
                      prepend-icon="access_time"
                      readonly
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-time-picker
                    v-if="modal2"
                    v-model="hora"
                    full-width>
                   <v-spacer></v-spacer>
                    <v-btn  color="error"   @click="modal2 = false">Cancel</v-btn>
                    <v-btn  color="primary" @click="$refs.dialog.save(hora)">OK</v-btn>
                  </v-time-picker>
                </v-dialog>
                <v-autocomplete
                    v-model="idProveedor"
                    :items="proveedor" 
                    prepend-icon="people"
                    label="PROVEEDOR"
                    required >
                 </v-autocomplete>
                 <v-textarea  v-model="observacion" prepend-icon="assignment" label="" placeholder="OBSERVACION" row="1" auto-grow ></v-textarea>
                  <v-flex xs12 sm12 md12 v-show="valida">
                       <h3><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h3>
                  </v-flex>
                </v-card-text>
              </v-card>
            </v-flex>
            <v-flex xs9>
              <v-card>
                 <v-card-title class="error" style="color: white;" primary-title>
                   <h3>LISTA DE PRODUCTOS</h3>
                </v-card-title>
                  <v-flex xs2>
                        <v-btn color="teal" dark @click="mostrarInsumos()" >
                            <v-icon dark>search</v-icon>Buscar
                        </v-btn>
                  </v-flex>
                  <v-card-text>
                   <v-data-table
                    :headers="cabeceraDetalles"
                    :items="detalles"
                    hide-actions
                    class="elevation-1">
                    <template slot="items" slot-scope="props">
                        <td class="">
                            <v-icon
                              color="red darken-2"
                              @click="eliminarDetalle(detalles,props.item)"
                              small>
                              delete
                            </v-icon>
                        </td>
                        <td>{{props.item.nombre}}
                            <div class="purple darken-2 text-xs-center">
                                <span class="white--text">{{props.item.medidas}}</span>
                            </div>
                        </td>
                        <td><v-text-field type="number" v-model="props.item.cantidad"></v-text-field></td>
                        <td><v-text-field type="number" v-model="props.item.precio"></v-text-field></td>
                        <td>{{(props.item.cantidad * props.item.precio).toFixed(2)}}</td>
                        <td><v-text-field type="number" v-model="props.item.descuento"></v-text-field></td>
                        <td v-if="dataigv=='SI'"><p v-if="moneda=='SOLES'" class="pe">S/.</p> <p v-if="moneda=='DOLARES'" class="pe">$</p> {{((((props.item.cantidad * props.item.precio)*0.18)+props.item.cantidad * props.item.precio)-props.item.descuento).toFixed(2)}}</td>
                        <td v-else><p v-if="moneda=='SOLES'" class="pe">S/.</p> <p v-if="moneda=='DOLARES'" class="pe">$</p> {{((props.item.cantidad * props.item.precio)-props.item.descuento).toFixed(2)}}</td>

                      
                    </template>
                    <template slot="no-data">
                          <span class="red--text">
                              <h3> No hay articulos agregados al detalle.</h3>
                          </span>
                    </template>
                 </v-data-table>
                </v-card-text>
              </v-card>
            </v-flex>
          <v-flex xs12>
                <v-card>
                  <v-card-text>
                      <strong>Total Parcial: </strong>S/ {{totalParcial=(total-totalImpuesto).toFixed(2)}}
                      <strong>Total Neto: </strong>S/ {{total=(calcularTotal).toFixed(2)}}
                      <strong>Flete por Producto:S/. {{final =(actualizarPrecioFlete).toFixed(2)}}</strong>
                  </v-card-text>
                </v-card>
           </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </v-flex>
  </v-layout>
</template>
<script>
  import axios from 'axios'
  import moment from 'moment';
  export default {
    data () {
      return {
        adAnular:0,
        idIngreso:0,
        dataigv:'',
        arrayIGV:['SI','NO'],
        arrayMoneda:['SOLES','DOLARES'],
        cambio:0,
        moneda:'',
        valida: 0,
        validaMensaje:[],
        fecha: new Date().toISOString().substr(0, 10),
        menu2: false,
        detallesIngreso:[],
        hora: null,
        modal2: false,
        verInsumos:0,
        menu3:null,
        menu4: null,
        fechaentrada: '',
        fechasalida:'',
        idProveedor:'',
        verNuevo:0,
        idUsuario:'',
        tipoComprobante:'',
        serieNumerica:'',
        numeroComprobante:'',
        fecha:'',
        hora:'',
        observacion:'',
        impuesto:0,
        descuento:0,
        texto:'',
        productos:[],
        verDetalleIngreso:'',
        ingresos:[],
        detalles:[],
        proveedor:[],
        errorInsumo:null,
        totalParcial:0,
        totalImpuesto:0,
        descuento:0,
        modalFlete: 0,
        flete:0,
        fleteFinal: 0.0,
        final: 0.0,
        comprobantes: ['FACTURA','BOLETA','TICKET','GUIA'],
        total:0,
        headers: [
                    { text: 'OPCIONES', value: 'opciones', sortable: false },
                    { text: 'PROVEEDOR', value: 'proveedor',sortable: false  },
                    { text: 'COMPROBANTE', value: 'tipo_comprobante',sortable: false },
                    { text: 'SERIE', value: 'serie_comprobante', sortable: false  },
                    { text: 'NUMERO', value: 'num_comprobante', sortable: false  },
                    { text: 'FECHA', value: 'fecha_hora', sortable: false  },
                    { text: 'MONEDA', value: 'moneda', sortable: false  },
                    { text: 'TOTAL', value: 'total', sortable: false  },
                    { text: 'ESTADO', value: 'estado', sortable: false  }                
        ],
        cabeceraDetalles: [
                    { text: 'BORRAR', value: 'borrar', sortable: false },
                    { text: 'ARTICULO/PRODUCTOS', value: 'insumo', sortable: false },
                    { text: 'CANTIDAD', value: 'cantidad', sortable: false  },
                    { text: 'PRECIO COMPRA', value: 'precio', sortable: false  },
                    { text: 'SUBTOTAL', value: 'subtotal', sortable: false  } ,
                    /* { text: 'FLETE', value: '', sortable: false  } , */
                    { text: 'DESCUENTO', value: '', sortable: false },
                    { text: 'IMPORTE TOTAL', value: '', sortable: false  }                       
        ],
        cabeceraInsumo: [
                    {text:  'SELECCIONAR', value: 'seleccionar', sortable: false },
                    { text: 'CODIGO', value: 'codigo',sortable: false  },
                    { text: 'ARTICULO', value: 'nombre'},
                    { text: 'CATEGORIA', value: 'categoria' },
                    { text: 'UNIDAD MEDIDA', value: 'medida' },
                    { text: 'COSTO PRODUCTO', value: 'constoProducto' },
                    { text: 'STOCK', value: 'stock', sortable: false  },    
        ],
        detalleListar: [
                    { text: 'ARTICULO', value: '',sortable: false  },
                    { text: 'MEDIDA', value: '',sortable: false  },
                    { text: 'CANTIDAD', value: '',sortable: false  },
                    { text: 'PRECIO COMPRA', value: '', sortable: false  },
                    { text: 'IMPORTE', value: '', sortable: false  },                
               ],
         }
       },
       computed: {
         calcularTotal:function(){
              var resultado=0.0;

              if(this.dataigv=='SI')
              {
                  for(var i=0;i<this.detalles.length;i++)
                  {
                      resultado=resultado+((((this.detalles[i].precio*this.detalles[i].cantidad)*0.18)+(this.detalles[i].precio*this.detalles[i].cantidad))-this.detalles[i].descuento);
                  }
                  return resultado;
              }
              else
              {
                  for(var i=0;i<this.detalles.length;i++)
                  {
                    resultado=resultado+(((this.detalles[i].precio*this.detalles[i].cantidad))-this.detalles[i].descuento);
                  }
                  return resultado;
              }
          },
          calcularIgv: function(){

          },
          actualizarPrecioFlete:function(){
            this.final = 0.0;
            let cantidad = 0;
            if(this.detalles.length == 0){
              return this.fleteFinal = 0.0;
            }else{
              for (let index = 0; index < this.detalles.length; index++) {
                cantidad = cantidad + Number(this.detalles[index].cantidad);  
              }
              return this.fleteFinal = this.flete/cantidad;
            }
          }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
          this.listarIngresos();
          this.selectProveedores();
        },
        methods:{
        mostrarInsumos(){
          this.verInsumos=1;
          this.listarProductos();
        },
        since: function(d){
          return moment(d).format('L');  
        },
        mostrarNuevo(){
            this.verNuevo=1;
        },
        cerrarDetalleIngreso(){
          this.detallesIngreso=[];
          this.verDetalleIngreso=0;
        },
        ocultarInsumos(){
            this.verInsumos=0;
        },
        listarProductos(){
           let me=this;
           axios.get('api/Productos/ListarProductosActivos').then(function(response){
               me.productos=response.data;
            }).catch(function(error){
                console.log(error);
            });
        },
        actualizarflete(){
          this.modalFlete = 0;
        },
        abrirAnular(item){
            this.idIngreso         = item.idIngreso;
            this.tipoComprobante   = item.tipoComprobante;
            this.numeroComprobante = item.numComprobante;
            this.adAnular=1;
        },
        cerrarAnularComprar(){
          this.idIngreso='';
          this.tipoComprobante = '';
          this.numeroComprobante = '';
          this.adAnular=0;
        },
        guardar(){
            if(this.validar()){
              return
            }
            let me=this;
            axios.post('api/Ingresos/Crear',{
                'idIngreso':me.idIngreso,
                'idProveedor':me.idProveedor,
                'idUsuario': me.$store.state.nombreUsuario.IdUsuario,
                'tipoComprobante': me.tipoComprobante,
                'serieNumerica': me.serieNumerica,
                'numeroComprobante':me.numeroComprobante,
                'fecha': me.fecha+'T11:25:45.674Z',
                'hora':me.hora,
                'observacion':me.observacion,
                'impuesto':me.impuesto,
                'moneda':me.moneda,
                'cambio':me.cambio,
                'incluyeIGV':me.dataigv,
                'igv':0,
                'descuento':me.descuento,
                'flete':me.final,
                'fleteGeneral':me.flete,
                'total':me.total,
                'detalleIngresos':me.detalles
            }).then(function(response){
                 me.verNuevo=0;
                 me.detalles=[];
                 me.listarIngresos();
                 me.limpiar();  
            }).catch(function(error){
                console.log(error);
            });
        },
        anularComprar(){
            let me = this;
            axios.put('api/Ingresos/Anular/'+me.idIngreso).then(function(response){
                me.cerrarAnularComprar();
                me.listarIngresos();         
            }).catch(function(error){
                console.log(error);
            });
        },
        selectProveedores(){
            let me=this;
            var provedorArray=[];
            axios.get('api/Personas/Select').then(function(response){
                provedorArray=response.data;
                provedorArray.map(function(x){
                    me.proveedor.push({text: x.razonSocial,value:x.idPersona});
                });
            }).catch(function(error){
                console.log(error);
            });
        },
        encuentra(id){
          var sw=0;
          for(var i=0;i<this.detalles.length;i++){
              if (this.detalles[i].idProducto==id){
                  sw=1;
              }
          }
          return sw;
        },
        agregarDetalle(data = []){
          this.errorInsumo=null;
            if(this.dataigv=='SI'){
                 this.detalles.push(
                 {
                    idProducto:data['idProducto'],
                    nombre:data['nombre'],
                    medidas:data['medida'],
                    descuento:0,
                    idUnidadMedida:data['idUnidadMedida'],
                    cantidad:1,
                    precio:data['constoProducto'],
                    igv:data['constoProducto']*0,
                  }
               );
               }else{
                 this.detalles.push(
                {
                    idProducto:data['idProducto'],
                    nombre:data['nombre'],
                    medidas:data['medida'],
                    descuento:0,
                    idUnidadMedida:data['idUnidadMedida'],
                    cantidad:1,
                    precio:data['constoProducto'],
                    igv:data['constoProducto']*0,
                  }
               );
              }
           this.verInsumos=0;
           this.texto='';
           this.listarProductos();
        },
        validar(){
            this.valida=0;
            this.validaMensaje=[];

            if (this.serieNumerica.length==0){
                this.validaMensaje.push("Ingrese serie numerica.");
            }
            if (this.numeroComprobante.length==0){
                this.validaMensaje.push("Ingrese numero comprobante.");
            }
            if (!this.tipoComprobante){
                this.validaMensaje.push("Ingrese tipo documento.");
            }
            if (!this.fecha){
                this.validaMensaje.push("Ingrese fecha.");
            }
            if (!this.hora){
                this.validaMensaje.push("Ingrese hora.");
            }
            if (!this.idProveedor){
                this.validaMensaje.push("Ingrese proveedor.");
            }
            if(this.detalles.length==0){
                this.validaMensaje.push("No hay insumos agregados a la lista.");
            }
            if (this.validaMensaje.length){
                this.valida=1;
            }
            return this.valida;
        },
        limpiar(){
          this.serieNumerica="";
          this.numeroComprobante="";
          this.tipoComprobante="";
          this.fecha="";
          this.hora="";
          this.idProveedor="";
          this.observacion="";
          this.cambio=0;
          this.moneda = 'SOLES';
        },
        cerrarModalFlete(){
            this.modalFlete = 0;
        },
        abrirModalFlete(){
            this.modalFlete = 1;
        },
        listarIngresos() {
            let me=this;
            axios.get('api/Ingresos/Listar').then(function(response){
                me.ingresos=response.data;
            }).catch(function(error){
                console.log(error);
            });
        },
        listarDetalleIngreso(id){
          let me=this;
          axios.get('api/Ingresos/DetalleIngreso/'+id).then(function(response){
              me.detallesIngreso=response.data;
              me.verDetalleIngreso=1;
          }).catch(function(error){
              console.log(error);
          });
        },
        abrirEditar(item){
          let me=this;
          me.idIngreso = item.idIngreso;
          axios.get('api/Ingresos/DetalleIngreso/'+me.idIngreso).then(function(response){
              me.detalles=response.data;
              me.serieNumerica = item.serieComprobante;
              me.numeroComprobante = item.numComprobante;
              me.tipoComprobante = item.tipoComprobante;
              me.fecha = item.fecha_hora.substring(0,10);
              me.hora = item.hora;
              me.idProveedor = item.idProveedor;
              me.observacion = item.observacion;
              me.dataigv = item.incluyeIGV;
              me.flete = item.fleteGeneral;
              me.cambio = item.cambio;
              me.moneda = item.moneda;
              me.verNuevo=1;
          }).catch(function(error){
              console.log(error);
          });
        },
        eliminarDetalle(arr,item){
            var id = item.idDetalleIngreso;
            axios.get('api/Ingresos/DetalleIngresoID/'+id).then(function(datos){
                axios.delete('api/Ingresos/EliminarDetalle/'+datos.data[0].idDetalleIngreso).then(function(respuesta){
                    var i= arr.indexOf(item);
                    if (i!==-1){
                        arr.splice(i,1);
                    }
                }).catch(function(err){
                })
            }).catch(function(err){
                var i= arr.indexOf(item);
                if (i!==-1){
                    arr.splice(i,1);
                }
            })
        }
     }
  }
</script>

<style>
  .pe{
    padding: 0%;
    display: inline;
  }
</style>