<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>PRODUCTOS</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
                <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="B√∫squeda" single-line hide-details></v-text-field>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="800px" persistent>
                        <v-btn slot="activator" color="info" dark class="mb-2">NUEVO
                            <v-icon dark right>check_circle</v-icon>
                        </v-btn>
                        <v-card>
                            <v-card-title  class="primary" style="color: white;" primary-title>
                                <h3>{{formTitle}}</h3>
                            </v-card-title>
                            <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                <v-flex xs12 sm12 md12>
                                    <v-text-field v-model="nombre" label="NOMBRE" ></v-text-field>
                                </v-flex>
                                <v-flex xs4 sm16 md4>
                                    <v-autocomplete v-model="idCategories"
                                      :items="categorias" label="CATEGORIA" >
                                    </v-autocomplete>
                                </v-flex>
                                <v-flex xs4 sm16 md4>
                                    <v-autocomplete v-model="idUnidadMedidad"
                                    :items="unidadMedidad" label="UNIDAD MEDIDA">
                                    </v-autocomplete>
                                </v-flex>
                                <v-flex xs4 sm6 md4>
                                    <v-text-field v-model="precioBruto" type="number"  label="COSTO PRODUCTO"></v-text-field>
                                </v-flex>
                                <v-flex xs4 sm6 md4>
                                    <v-text-field v-model="precioPublico"  type="number"  label="PRECIO PUBLICO"></v-text-field>
                                </v-flex>
                                <v-flex xs4 sm6 md4>
                                    <v-text-field  type="number"  v-model="pm" label="PRECIO MAYORISTA" value="0"></v-text-field>
                                </v-flex>
                                <v-flex xs4 sm6 md4>
                                    <v-text-field   type="number"  v-model="pe" label="PRECIO EMPRESA" value="0"></v-text-field>
                                </v-flex>
                                <v-flex xs4 sm6 md4>
                                    <v-text-field  disabled type="number" v-model="margen_porcentaje"  label="MARGEN %">{{margen_porcentaje =((margen_soles*100)/precioBruto).toFixed(2)}}</v-text-field>
                                </v-flex>
                                <v-flex xs4 sm6 md4>
                                    <v-text-field  disabled v-model="margen_soles" type="number"  label="MARGEN S/">{{margen_soles = (precioPublico - precioBruto).toFixed(2)}}</v-text-field>
                                </v-flex>
                                <v-flex xs4 sm6 md4>
                                    <v-text-field   type="number" v-model="stck" label="STOCK" value="0"></v-text-field>
                                </v-flex>
                                <v-flex xs4 sm6 md4>
                                    <v-text-field  type="text" v-model="codBarras" label="COD BARRAS" value="0"></v-text-field>
                                    <barcode :value="codBarras" :options="{format:'EAN-13'}" ><h3>Generando codigo barras</h3></barcode>
                                </v-flex>
                               
                                <v-flex xs4 sm6 md4>
                                    <v-autocomplete  label="MARCA"  v-model="idMarca"
                                    :items="marca" >
                                    </v-autocomplete>
                                </v-flex>
                                <v-flex xs4 sm6 md4>
                                    <v-text-field label="MODELO" v-model="modelo"></v-text-field>
                                </v-flex>
                                <v-flex xs12 sm12 md12 v-show="valida">
                                    <h3><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h3>
                                </v-flex>
                                <v-flex xs12 sm12 md12>
                                    <label class="text-reader">
                                        <h5>Cargar Imagen</h5>
                                        <input type="file" @change="onFileChange">
                                    </label>
                                    <label class="text-reader" @click="removeImage">
                                        <h5>Quitar Imagen</h5>
                                    </label>
                                    <div v-if="!image">
                                        <v-card>
                                            <v-img :src="image" class="my-3" contain height="180"></v-img>
                                        </v-card> 
                                    </div>
                                    <div v-else>
                                        <v-card>
                                            <v-img :src="image" class="my-3" contain height="180"></v-img>
                                        </v-card>
                                    </div>
                                </v-flex>
                              </v-layout>
                            </v-container>
                            </v-card-text>
                            <v-divider></v-divider>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="error" @click.native="close">CANCELAR
                                    <v-icon dark right>block</v-icon>
                                </v-btn>
                                <v-btn color="info"  @click.native="guardar">GUARDAR
                                    <v-icon dark right>check_circle</v-icon>
                                </v-btn>
                            </v-card-actions>
                    </v-card>
                </v-dialog>
                <v-dialog v-model="adModal" max-width="300">
                        <v-card>
                            <v-card-title class="headline info"  style="color: white;" primary-title  v-if="adAccion==0">¬øActivar Item?</v-card-title>
                            <v-card-title class="headline error" style="color: white;" primary-title v-if="adAccion==1">¬øDesactivar Item?</v-card-title>
                            <v-card-text>
                                Estas a punto de
                                <span v-if="adAccion==0">Activar</span>
                                <span v-if="adAccion==1">Desactivar</span>
                                el item {{adNombre}}
                            </v-card-text>
                            <v-divider></v-divider>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="green darken-1" dark class="mb-2"   @click="activarDesactivarCerrar">CANCELAR</v-btn>
                                <v-btn v-if="adAccion==0" color="info" class="mb-2"  @click="activar">ACTIVAR</v-btn>
                                <v-btn v-if="adAccion==1" color="error" dark class="mb-2" @click="desactivar">DESACTIVAR</v-btn>
                            </v-card-actions>
                        </v-card>
             </v-dialog>
            </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="productos"
                :search="search"
                class="elevation-1">
                <template slot="items" slot-scope="props">
                <td class="">
                    <v-icon
                    color="green darken-2"
                    small
                    class="mr-2"
                    @click="editItem(props.item)"
                    >
                    edit
                    </v-icon>
                    <template v-if="props.item.estado">
                        <v-icon
                            color="red darken-2"
                            small
                            @click="activarDesactivarMostrar(1,props.item)"
                            >
                            block
                           
                        </v-icon>
                    </template>

                    <template v-else>
                        <v-icon
                            color="red darken-2"
                            small
                            @click="activarDesactivarMostrar(0,props.item)"
                            >
                            check
                        </v-icon>
                    </template>
                </td>
                <td>{{ props.item.codigo}}</td>
                 <td>{{ props.item.codigoBarras}}</td>
                <td>{{ props.item.nombre }}</td>
                <td>{{ props.item.categoria }}</td>
                <td>{{ props.item.marca }}</td>
                <td>S/. {{ props.item.constoProducto.toFixed(2) }}</td>
                <td>S/. {{ props.item.precioPublico.toFixed(2) }}</td>
                <td>S/. {{ props.item.precioMayorista.toFixed(2) }}</td>
                <td>S/. {{ props.item.precioEmpresa.toFixed(2)}}</td>
                <td> {{ props.item.codigoBarras}}</td>
                <td> {{ props.item.stock}}</td>
                <td>
                    <div v-if="props.item.estado">
                       <span class="blue--text">Activo</span>
                    </div>
                    <div v-else>
                       <span class="red--text">Inactivo</span>
                    </div>
                </td>
                </template>
                <template slot="no-data">
                    <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    import VueBarcode from 'vue-barcode';

    export default {
        data(){
            return {
                pm:0,
                pe:0,
                tp:0,
                stck:0,
                codBarras:'',
                idProducto:'',
                idProductoTransformado:false,
                productos:[],
                idUnidadMedidad:'',
                idCategories:'',
                dialog: false,
                headers: [
                { text: 'OPCIONES', value: 'opciones',sortable: false},
                { text: 'CODIGO', value: 'codigo',sortable: false},
                { text: 'CODIGO BARRAS', value: 'codigoBarras',sortable: false},
                { text: 'ARTICULO DE ELECTRICIDAD', value: 'nombre'},
                { text: 'CATEGORIA', value: 'categoria'},
                { text: 'MARCA', value: 'marca'},
                { text: 'COSTO PRODUCTO', value: 'constoProducto'},
                { text: 'PRECIO PUBLICO', value: 'precioPublico',sortable: false},
                { text: 'PRECIO MAYORISTA', value: 'precioMayorista',sortable: false},
                { text: 'PRECIO EMPRESA', value: 'precioEmpresa',sortable: false},
                { text: 'COD BARRAS', value: 'codigoBarras',sortable: false},
                { text: 'STOCK', value: 'stock',sortable: false},
                { text: 'ESTADO', value: 'estado',sortable: false},
                ],
                search: '',
                image: '',
                imgBase: '',
                imgTemporal: '',
                editedIndex: -1,
                id:'',
                nombre:'',
                precioBruto:0,
                precioPublico:0,
                categorias:[],
                unidadMedidad:[],
                valida: 0,
                validaMensaje:[],
                adModal:0,
                adAccion:0,
                adNombre:'', 
                marca:[],
                idMarca:'', 
                margen_soles:0,
                margen_porcentaje:'',
                modelo:'',
            }
        },
        components: {
            'barcode': VueBarcode
        },
        computed: {
            formTitle () {
              return this.editedIndex === -1 ? 'NUEVO PRODUCTO' : 'ACTUALIZAR PRODUCTO'
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            this.listar();
            this.selectUnidadMedida();
            this.select();
            this.selectMarca();
        },
        methods:{
            onFileChange(e) {
                var files = e.target.files || e.dataTransfer.files;
                if (!files.length)
                    return;
                this.createImage(files[0]);
                },
                createImage(file) {
                var image = new Image();
                var reader = new FileReader();
                var vm = this;

                reader.onload = (e) => {
                    vm.image = e.target.result;
                };
                reader.readAsDataURL(file);
                },
                removeImage: function (e) {
                this.image = '';
           },
           listar() {
                let me=this;
                axios.get('api/productos/Listar').then(function(response){
                   me.productos=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            select(){
                let me=this;
                var categoriaArray=[];
                axios.get('api/Categorias/Select').then(function(response){
                    categoriaArray=response.data;
                    categoriaArray.map(function(x){
                        me.categorias.push({text: x.nombre,value:x.idCategoria});
                    });
                }).catch(function(error){
                    alert(console.log(error));
                });
            },
            selectMarca(){
                let me=this;
                var marcaArray=[];
                axios.get('api/Marcas').then(function(response){
                    marcaArray=response.data;
                    marcaArray.map(function(x){
                        me.marca.push({text: x.descripcion,value:x.idMarca});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
            selectUnidadMedida(){
                let me=this;
                var produccionArray=[];
                axios.get('api/UnidadMedidas/Select').then(function(response){
                    produccionArray=response.data;
                    produccionArray.map(function(x){
                        me.unidadMedidad.push({text: x.nombreMedia,value:x.idUnidadMedida});
                    });
                }).catch(function(error){
                    alert(console.log(error));
                });
            },
            editItem (item) {
                this.idProducto = item.idProducto;
                this.nombre = item.nombre;
                this.idMarca = item.idMarca;
                this.precioBruto = item.constoProducto;
                this.precioPublico = item.precioPublico;
                this.pm = item.precioMayorista;
                this.pe = item.precioEmpresa;
                this.codBarras = item.codigoBarras;
                this.stck = item.stock;
                this.idCategories= item.idCategoria;
                this.idUnidadMedidad = item.idUnidadMedida;
                this.modelo = item.model;
                this.editedIndex=1;
                this.dialog = true;
                this.image = item.imagen;
            },
            activarDesactivarMostrar(accion,item){
              this.adModal   =  1;
              this.adNombre  =  item.nombre;
              this.adId      =  item.idProducto;
              
              if(accion==0){
                 this.adAccion=0;
              }
              else if(accion==1){
                 this.adAccion=1;
              }
              else{
                  this.adModal=0;
              }
            },
            close () {
                this.dialog = false;
                this.limpiar();
                this.valida=0;
            },
            limpiar(){
                this.idProducto="";
                this.nombre="";
                this.idCategories="";
                this.idUnidadMedidad="";
                this.precioBruto=0;
                this.precioPublico=0;
                this.pm=0;
                this.pe=0;
                this.codBarras="";
                this.stck=0;
                this.editedIndex=-1;
                this.imgTemporal = '';
                this.image = '';
            },
            activarDesactivarCerrar() {
                this.adModal=0;
            },
            guardar () {
                if(this.validar()){
                    return
                }
                if (this.editedIndex > -1){
                    let me = this;
                    axios.put('api/Productos/Actualizar',{
                        'idProducto':me.idProducto,
                        'nombre': me.nombre,
                        'idCategoria':me.idCategories,
                        'idUnidadMedida': me.idUnidadMedidad,
                        'idMarca': me.idMarca,
                        'nombre':me.nombre,
                        'precioBruto': me.precioBruto,
                        'precioPublico':me.precioPublico,
                        'precioMayorista': me.pm,
                        'precioEmpresa':me.pe,
                        'codBarras':me.codBarras,
                        'stock':me.stck,
                        'model':me.modelo,
                        'imagen':me.image
                        }).then(function(response){
                            me.close();
                            me.listar();
                        }).catch(function(error){
                            console.log(error);
                        }); 
                }
                else 
                {
                   let me = this;
                   axios.post('api/Productos/Crear',{
                       'nombre': me.nombre,
                       'idCategoria':me.idCategories,
                       'idUnidadMedida': me.idUnidadMedidad,
                       'idMarca': me.idMarca,
                       'nombre':me.nombre,
                       'precioBruto': me.precioBruto,
                       'precioPublico':me.precioPublico,
                       'precioMayorista': me.pm,
                       'precioEmpresa':me.pe,
                       'codBarras':me.codBarras,
                       'stock':me.stck,
                       'model':me.modelo,
                       'imagen':me.image
                   }).then(function(response){
                        me.close();
                        me.listar();
                   }).catch(function(error){
                        console.log(error);
                   });
                }
            },
            activar(){
                let me = this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.put('api/Productos/Activar/'+this.adId,{},configuracion).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();
                }).catch(function(error){
                    alert(console.log(error));
                });
            },
            desactivar(){
                let me = this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.put('api/Productos/Desactivar/'+this.adId,{},configuracion).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();
                }).catch(function(error){
                    alert( console.log(error));
                });
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];

                if(this.nombre.length<3 || this.nombre.length>100){
                    this.validaMensaje.push("Ingrese nombre producto.");
                }
                if (!this.idCategories){
                    this.validaMensaje.push("Seleccione una categoria.");
                }
                if (!this.idMarca){
                    this.validaMensaje.push("Seleccione una marca.");
                }
                if (!this.idUnidadMedidad){
                    this.validaMensaje.push("Seleccione unidad medida.");
                }
                if(this.precioBruto==0){
                    this.validaMensaje.push("Ingrese costo producto.");
                }
                if(this.precioPublico==0){
                    this.validaMensaje.push("Ingrese precio publico.");
                }
                if(this.codBarras.length==13){
                     this.validaMensaje.push("Ingrese minimo 10 caracteres en codigo de barras.");
                }
                if(this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            }
        }     
    }
</script>
<style>
    .text-reader {
        position: relative;
        overflow: hidden;
        display: inline-block;

        /* Fancy button style ?? */
        border: 2px solid white;
        border-radius: 0px;
        padding: 8px 12px;
        cursor: pointer;
        background-color: red;
        color: white;
    }
     .text-reader input {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        opacity: 0;
    }
    img {
        width: 30%;
        margin: auto;
        display: block;
        margin-bottom: 10px;
    }
</style>