
<template>
  <v-layout justify-center>
    <v-flex xs12>
      <v-toolbar color="white">
        <v-toolbar-side-icon></v-toolbar-side-icon>
        <v-toolbar-title>RECETAS DE PLATOS</v-toolbar-title>
        <v-spacer></v-spacer>
            <v-dialog v-model="verInsumos" max-width="650px">
                <v-card>
                    <v-card-title class="" style="color: black;"><h3>LISTA DE INSUMO</h3></v-card-title>
                    <v-divider></v-divider>
                    <v-card-text>
                        <v-container grid-list-md>
                          <v-layout wrap>
                            <v-flex xs12 sm12 md12 lg12 xl12>
                                <v-text-field append-icon="search" placeholder="..................." v-model="search" class="text-xs-center"  label="Buscar" >
                                </v-text-field>
                                <template>
                                    <v-data-table
                                        :headers="cabeceraInsumo"
                                        :items="insumo"
                                        :search="search"
                                        class="elevation-1">
                                        <template slot="items" slot-scope="props">
                                            <td class="justify-center layout px-0">
                                                 <v-icon
                                                  color="red darken-2"
                                                  small
                                                  class="mr-2">
                                                  add
                                                </v-icon>
                                            </td>
                                            <td>{{props.item.nombre}}</td>
                                            <td>
                                                <v-chip color="orange" dark>{{props.item.medida}}</v-chip>
                                            </td>
                                        </template>
                                        <template slot="no-data">
                                            <span class="red--text">
                                                <h3>No hay insumos para mostrar.</h3>
                                            </span>
                                        </template>
                                    </v-data-table> 
                                </template>
                            </v-flex>
                        </v-layout>
                    </v-container>
                    </v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions>
                        <v-btn dark color="red darken" @click="ocultarInsumo()">
                            Cerrar
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
        </v-toolbar>
        <v-card>
         <v-container fluid grid-list-md  v-if="verNuevo==0">
          <v-layout row wrap>
               <v-flex xs12>
                 <v-card>
                   <v-card-title class="primary" style="color: white;"><h3>ITEM PRODUCTOS RECETAS</h3></v-card-title>
                    <v-divider></v-divider>
                    <v-card-text>
                        <v-data-table
                            :headers="products"
                            :items="productos"
                            hide-actions
                            class="elevation-1">
                            <template slot="items" slot-scope="props">
                                <td> 
                                    <v-icon
                                         color="green darken-2"
                                         small
                                         class="mr-2"
                                         @click="mostrarReceta(props.item)"
                                         >
                                        edit
                                       </v-icon>
                                </td>
                                <td>{{props.item.nombre}}</td>
                                <td>{{ props.item.categoria }} </td>
                                <td>{{ props.item.areaProduccion}}</td>
                                <td></td>
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3> No hay productos.</h3>
                                </span>
                            </template>
                        </v-data-table>
                    </v-card-text>
                 </v-card>
               </v-flex>
          </v-layout>
         </v-container>
        </v-card>
         <v-card>
         <v-container fluid grid-list-md  v-if="verNuevo">
          <v-layout row wrap>
              <v-flex xs3>
                 <v-card>
                    <v-card-title class=""><h3>PLATO:{{prod}}</h3></v-card-title>
                     <v-divider></v-divider>
                    <v-card-text>
                    </v-card-text>
                 </v-card>
               </v-flex>
               <v-flex xs9>
                 <v-card>
                    <v-card-title class="primary" style="color: white;"><h3>ITEM INSUMOS</h3></v-card-title>
                        <v-flex xs12>
                            <v-btn color="teal" dark @click="mostrarInsumo()">
                                <v-icon dark >search</v-icon>INSUMOS/PRODUCTOS
                            </v-btn>
                             <v-btn color="primary" dark v-on:click="alertDisplay">
                                <v-icon dark>search</v-icon>REGISTRAR RECETA
                            </v-btn>
                        </v-flex>
                    <v-card-text>
                         
                    </v-card-text>
                    <v-divider></v-divider>
                 </v-card>
               </v-flex>
               <v-flex xs12>
                 <v-card>
                    <v-card-title  class="success" style="color: white;"><h3>DETALLE RECETA</h3></v-card-title>
                    <v-card-text>
                    </v-card-text>
                   <v-divider></v-divider>
                 </v-card>
               </v-flex>
          </v-layout>
         </v-container>
        </v-card>
      </v-flex>
  </v-layout>
</template>
<script>
    import axios from 'axios'
    export default {
        data(){
            return {
               editedIndex: -1,
               prod:'',
               verInsumos:0,
               idprod:'',
               insumo:[],
               search: '',
               productos:[],
               idUsuario:'',
               errorProducto:null,
               verNuevo:0,
               products: [
                    { text: 'OPCIONES', value: 'op',sortable: false  },
                    { text: 'PRODUCTO', value: 'producto' },
                    { text: 'CATEGORIA', value: 'categoria',sortable: false  },
                    { text: 'PRODUCCION', value: 'areaProduccion', sortable: false  },
                    { text: 'RECETA', value: '', sortable: false  },           
                ],
                cabeceraLista: [
                    { text: '#', value: 'opciones',sortable: false},
                    { text: 'PRODUCTO', value: 'producto'},
                    { text: 'CANT', value: 'cant' },
                    { text: 'PRECIO', value: 'precio' },
                    { text: 'TOTAL', value: 'total', sortable: false  },    
                ],
                cabeceraInsumo: [
                    { text: 'OPCION', value: 'op', sortable: false },
                    { text: 'INSUMO', value: 'nombre'},
                    { text: 'UNIDAD MEDIDA', value: 'medida' ,sortable: false },
                ],
            }
        },
        computed: {
            formTitle () {
            return this.editedIndex === -1 ? 'DETALLE PLATO' : 'EDITAR DETALLE PLATO'
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
           this.listarProductos();
           this.listarInsumo();
        },
        methods:{
            mostrarInsumo(){
              this.verInsumos=1;
            },
            ocultarInsumo(){
              this.verInsumos=0;
            },
            mostrarReceta(item){
               this.verNuevo=1;
               this.prod = item.nombre;  
            },
            listarProductos() {
                let me=this;
                axios.get('api/Productos/ListarProductoIngreso').then(function(response){
                    me.productos=response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            listarInsumo() {
                let me=this;
                axios.get('api/insumos/activos').then(function(response){
                    me.insumo=response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            alertDisplay() {
                //this.$swal('Heading', 'Apertura caja', 'OK');
                this.$swal('Cancelled', 'Cajero no aperturo caja', 'error')
            }
         }
    }
</script>