<template>
  <v-layout justify-center>
    <v-flex xs12>
      <v-toolbar color="white">
        <v-toolbar-title>RECETAS DE PLATOS</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-text-field v-if="verNuevo==0" class="text-xs-center" v-model="searching" append-icon="search" label="B√∫squeda" single-line hide-details></v-text-field>
        <v-spacer></v-spacer>
        <v-btn color="primary" v-if="verNuevo == 1" @click="regresar()">
            <v-icon>check</v-icon> 
            Regresar
            </v-btn>
            <v-dialog v-model="adModal" max-width="300" persistent>
                <v-card>
                    <v-card-title class="headline error"  style="color: white;" primary-title>Eliminar Insumo</v-card-title>
                    <v-card-text>
                        Estas a punto de
                        <span>Eliminar</span>
                        el insumo {{adNombre}}
                    </v-card-text>

                    <v-divider></v-divider>

                    <v-card-actions>
                        <v-btn color="error darken-1" dark class="mb-2"   @click="cerrarModalEliminar()">Cancelar</v-btn>
                        <v-btn color="success" class="mb-2"  @click="desactivar(idReceta)">Eliminar</v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
            <v-dialog v-model="verInsumos" max-width="650px" persistent>
                <v-card>
                    <v-card-title class="" style="color: black;"><h3>LISTA DE INSUMO</h3></v-card-title>
                    <v-divider></v-divider>
                    <v-card-text>
                        <v-container grid-list-md>
                          <v-layout wrap>
                            <v-flex xs12 sm12 md12 lg12 xl12>
                                <v-text-field append-icon="search" placeholder="..................." v-model="search" class="text-xs-center"  label="Buscar" >
                                </v-text-field>
                                <template>
                                    <v-data-table
                                        :headers="cabeceraInsumo"
                                        :items="insumo"
                                        :search="search"
                                        class="elevation-1">
                                        <template slot="items" slot-scope="props">
                                            <td >
                                                 <v-icon
                                                  color="red darken-2"
                                                  small
                                                  class="mr-2"
                                                  @click="mostrarLista(props.item)">
                                                  add
                                                </v-icon>
                                            </td>
                                            <td>{{props.item.nombre}}</td>
                                            <td>
                                                <v-chip color="orange" dark>{{props.item.medida}}</v-chip>
                                            </td>
                                        </template>
                                        <template slot="no-data">
                                            <span class="red--text">
                                                <h3>No hay insumos para mostrar.</h3>
                                            </span>
                                        </template>
                                    </v-data-table> 
                                </template>
                            </v-flex>
                        </v-layout>
                    </v-container>
                    </v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions>
                        <v-btn dark color="red darken" @click="ocultarInsumo()">
                            Cerrar
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
        </v-toolbar>
        <v-card>
         <v-container fluid grid-list-md  v-if="verNuevo==0">
          <v-layout row wrap>
               <v-flex xs12>
                 <v-card>
                   <v-card-title class="error" style="color: white;"><h3>ITEM PRODUCTOS RECETAS</h3></v-card-title>
                    <v-divider></v-divider>
                    <v-card-text>
                        <v-data-table
                            :headers="products"
                            :items="productos"
                            :search="searching"
                            class="elevation-1">
                            <template slot="items" slot-scope="props">
                                <td> 
                                    <v-icon
                                         color="blue darken-2"
                                         small
                                         class="mr-2"
                                         @click="mostrarReceta(props.item)"
                                         >
                                        visibility
                                       </v-icon>
                                </td>
                                <td>{{props.item.nombre}}</td>
                                <td>{{ props.item.categoria }} </td>
                                <td>{{ props.item.areaProduccion}}</td>
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3> No hay productos.</h3>
                                </span>
                            </template>
                        </v-data-table>
                    </v-card-text>
                 </v-card>
               </v-flex>
          </v-layout>
         </v-container>
        </v-card>
         <v-card>
         <v-container fluid grid-list-md  v-if="verNuevo">
          <v-layout row wrap>
              <v-flex xs3>
                 <v-card>
                    <v-card-title class="" color="success">
                        <h3>PLATO:{{prod}}</h3>
                        <br>
                        <br>
                        <h3>CATEGORIA:{{categoria}}</h3>
                        <br>
                        <br>
                        <h3>AREA:{{area}}</h3>
                     </v-card-title>
                     <v-divider></v-divider>
                    <v-card-text>
                    </v-card-text>
                 </v-card>
               </v-flex>
               <v-flex xs9>
                 <v-card>
                    <v-card-title class="success" style="color: white;"><h3>ITEM INSUMOS</h3></v-card-title>
                        <v-flex xs12>
                            <v-btn color="teal" dark @click="mostrarInsumo()">
                                <v-icon dark >search</v-icon>INSUMOS
                            </v-btn>
                             <v-btn color="primary" dark v-on:click="alertDisplay">
                                <v-icon dark>search</v-icon>REGISTRAR
                            </v-btn>
                        </v-flex>
                    <v-card-text>
                         <v-data-table
                            :headers="cabeceraPrevia"
                            :items="listaPrevia"
                            hide-actions
                            class="elevation-1">
                            <template  slot="items" slot-scope="props">
                                 <td class="">
                                    <v-icon
                                    color="red darken-2"
                                    @click="eliminarDetalle(props.item)"
                                    small>
                                    delete
                                    </v-icon>
                                 </td>
                                 <td>
                                    {{ props.item.nombre }}
                                </td>
                                <td><v-text-field type="number" v-model="props.item.Cantidad"></v-text-field></td>
                                <td>{{  props.item.unidad }}</td>
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3> No hay productos agregados al detalle.</h3>
                                </span>
                            </template>
                         </v-data-table>
                    </v-card-text>
                    <v-divider></v-divider>
                 </v-card>
               </v-flex>
               <v-flex xs12>
                 <v-card>
                    <v-card-title  class="error" style="color: white;"><h3>DETALLE RECETA</h3></v-card-title>
                    <v-card-text>
                        <v-data-table
                            :headers="cabeceraDetalle"
                            :items="detalles"
                            hide-actions
                            class="elevation-1">
                            <template  slot="items" slot-scope="props">
                                 <td>{{ props.item.idInsumo}}</td>
                                 <td>
                                    {{ props.item.insumos }}
                                </td>
                                <td>{{ props.item.cantidad}}</td>
                                <td>{{  props.item.medidas }}</td>
                                <td>
                                    <v-icon
                                    color="red darken-2"
                                    @click="abrirModalEliminar(props.item)"
                                    small>
                                    delete
                                    </v-icon>
                                 </td>
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3> No hay productos agregados al detalle.</h3>
                                </span>
                            </template>
                         </v-data-table>
                    </v-card-text>
                   <v-divider></v-divider>
                 </v-card>
               </v-flex>
          </v-layout>
         </v-container>
        </v-card>
      </v-flex>
  </v-layout>
</template>
<script>
    import axios from 'axios'
    export default {
        data(){
            return {
               editedIndex: -1,
               prod:'',
               verInsumos:0,
               idprod:'',
               insumo:[],
               search: '',
               productos:[],
               idUsuario:'',
               errorProducto:null,
               verNuevo:0,
               searching: '',
               listaPrevia:[],
               detalles:[],
               categoria: '',
               area: '',
               adModal: 0,
               adNombre: '',
               idReceta: '',
               products: [
                    { text: 'OPCIONES', value: 'op',sortable: false  },
                    { text: 'PRODUCTO', value: 'producto' },
                    { text: 'CATEGORIA', value: 'categoria',sortable: false  },
                    { text: 'PRODUCCION', value: 'areaProduccion', sortable: false  }    
                ],
                cabeceraLista: [
                    { text: '#', value: 'opciones',sortable: false},
                    { text: 'PRODUCTO', value: 'producto'},
                    { text: 'CANTIDAD', value: 'cant' },
                    { text: 'UNIDAD', value: 'medida' }, 
                ],
                cabeceraInsumo: [
                    { text: 'OPCION', value: 'op', sortable: false },
                    { text: 'INSUMO', value: 'nombre'},
                    { text: 'UNIDAD MEDIDA', value: 'medida' ,sortable: false },
                ],
                cabeceraPrevia: [
                    { text: '#', value: 'opciones',sortable: false},
                    { text: 'PRODUCTO', value: 'producto'},
                    { text: 'CANTIDAD', value: 'cant' },
                    { text: 'UNIDAD', value: 'medida' }, 
                ],
                cabeceraDetalle: [
                    { text: '#', value: 'opciones',sortable: false},
                    { text: 'INSUMO', value: 'insumo'},
                    { text: 'CANT', value: 'cant' },
                    { text: 'UNIDAD', value: 'medida' }, 
                    { text: 'ACCIONES', value: ''}
                ],
            }
        },
        computed: {
            formTitle () {
            return this.editedIndex === -1 ? 'DETALLE PLATO' : 'EDITAR DETALLE PLATO'
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
           this.listarProductos();
           this.listarInsumo();
        },
        methods:{
            cerrarModalEliminar(item){
                this.adModal = 0;
                this.listarDetalle();
                this.adNombre = '';
                this.idReceta = '';
            },
            abrirModalEliminar(item){
                this.adModal = 1;
                this.adNombre = item.insumos;
                this.idReceta = item.idReceta;
            },
            mostrarInsumo(){
              this.verInsumos=1;
            },
            ocultarInsumo(){
              this.verInsumos=0;
            },
            mostrarReceta(item){
               this.verNuevo=1;
               this.prod = item.nombre;
               this.idprod = item.idProducto;
               this.area = item.areaProduccion;
               this.categoria = item.categoria;
               this.listarDetalle(item.idProducto);
            },
            listarProductos() {
                let me=this;
                axios.get('api/Productos/PlatosTransformados').then(function(response){
                    me.productos=response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            listarInsumo() {
                let me=this;
                axios.get('api/insumos/activos').then(function(response){
                    me.insumo=response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            regresar(){
                this.verNuevo = 0;
                this.idprod = '';
            },
            alertDisplay() {
                if( this.listaPrevia.length == 0){
                    this.$swal('Cancelled', 'La lista esta vac√≠a', 'error')
                }else{
                    let listas = [];
                    let enviar = [];
                    for (let index = 0; index < this.listaPrevia.length; index++) {
                        if(this.listaPrevia[index].Cantidad == 0 | this.listaPrevia[index].Cantidad == '0.0'| this.listaPrevia[index].Cantidad == ''){
                            this.$swal('Cancelled', 'No debe ingresar un valor igual a 0', 'error');
                            return;
                        }else{
                            listas.push({
                                'idProducto':this.listaPrevia[index].idProducto,
                                'idInsumo': this.listaPrevia[index].idInsumo,
                                'cantidad': this.listaPrevia[index].Cantidad,
                                'idUnidadMedida': this.listaPrevia[index].idMedida
                            });
                        }
                    }
                    enviar = {
                            'recetas': listas
                    };
                    let me = this;
                    axios.post('api/Recetas/RecetasPlatos', enviar).then(function(succes){
                        me.listarDetalle();
                        me.listaPrevia = [];
                        me.listas = [];
                    }).catch(function(err){
                    })
                }
            },
            mostrarLista(item){
                this.verInsumos = 0;
                this.search='';
                this.listaPrevia.push({
                    'idProducto': this.idprod,
                    'nombre': item.nombre,
                    'unidad': item.medida,
                    'Cantidad': 0.0,
                    'idInsumo': item.idInsumo,
                    'idMedida': item.idUnidadMedida
                });
            },
            eliminarDetalle(item){
                var i= this.listaPrevia.indexOf(item);
                if (i!==-1){
                    this.listaPrevia.splice(i,1);
                }
            },
            listarDetalle(){
                let me = this;
                axios.get('api/Recetas/RecetaPlatosProductos/'+me.idprod).then(function(ok){
                    me.detalles = ok.data;
                })
            },
            desactivar(id){
                let me = this;
                axios.put('api/Recetas/Desactivar/'+id).then(function(ok){
                    me.adModal = 0;
                    me.listarDetalle();
                    me.$swal('Correcto', 'Eliminado Correctamente', 'success');
                }).catch(function(err){
                })
            }
         }
    }
</script>