<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>USUARIOS</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
                <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="B√∫squeda" single-line hide-details></v-text-field>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="500px">
                        <v-card>
                            <v-card-title class="primary" style="color: white;"><h3>{{ formTitle }}</h3>
                            </v-card-title>
                             <v-card-text>
                                    <v-container grid-list-md>
                                        <v-layout wrap>
                                            <v-flex xs12 sm10 md12 lg12 xl8>
                                                <v-flex xs12>
                                                    <v-text-field  prepend-icon="looks_one" label="NOMBRE COMPLETO" v-model="nombreCompleto" type="text">
                                                    </v-text-field>
                                                </v-flex>
                                                <v-flex xs12>
                                                    <v-autocomplete
                                                        v-model="idRol"
                                                        :items="roles"
                                                        prepend-icon="assignment"
                                                        label="ROL" chips
                                                        required>
                                                    </v-autocomplete>
                                                </v-flex>
                                                <v-flex xs12 sm12 md6 lg12 xl12>
                                                     <v-text-field  prepend-icon="looks_one" label="USUARIO" v-model="nombreUsuario" type="text">
                                                    </v-text-field>
                                                </v-flex>
                                                <v-flex xs12 sm12 md6 lg12 xl12>
                                                     <v-text-field  prepend-icon="looks_one" label="PASSWORD" v-model="password" type="password">
                                                    </v-text-field>
                                                </v-flex>
                                            </v-flex>
                                        </v-layout>
                                </v-container>
                            </v-card-text>

                            <v-divider></v-divider>
                
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="error" @click.native="close">Cerrar
                                    <v-icon dark right>block</v-icon>
                                </v-btn>
                                <v-btn color="info" @click.native="guardar">Guardar
                                    <v-icon dark right>check_circle</v-icon>
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                </v-dialog>

                <v-dialog v-model="adModal" max-width="300">
                    <v-card>
                        <v-card-title class="headline info"  style="color: white;" primary-title  v-if="adAccion==0">¬øActivar Item?</v-card-title>
                        <v-card-title class="headline error" style="color: white;" primary-title  v-if="adAccion==1">¬øDesactivar Item?</v-card-title>
                        <v-card-text>
                            Estas a punto de
                            <span v-if="adAccion==0">Activar</span>
                            <span v-if="adAccion==1">Desactivar</span>
                            el item {{nombreCompleto}}
                        </v-card-text>

                        <v-divider></v-divider>

                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="green darken-1" dark class="mb-2"   @click="activarDesactivarCerrar">Cancelar</v-btn>
                            <v-btn v-if="adAccion==0" color="info" class="mb-2"  @click="activar">Activar</v-btn>
                            <v-btn v-if="adAccion==1" color="error" dark class="mb-2" @click="desactivar">Desactivar</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="usuarios"
                :search="search"
                class="elevation-1">
                <template slot="items" slot-scope="props">
                <td class="">
                    <v-icon
                    color="green darken-2"
                    small
                    class="mr-2"
                    @click="editItem(props.item)"
                    >
                    edit
                    </v-icon>

                    <template v-if="props.item.estado">
                        <v-icon
                            color="red darken-2"
                            small
                            @click="activarDesactivarMostrar(1,props.item)">
                            block
                        </v-icon>
                    </template>

                    <template v-else>
                        <v-icon
                            color="red darken-2"
                            small
                            @click="activarDesactivarMostrar(0,props.item)">
                            check
                        </v-icon>
                    </template>
                </td>
                <td>{{ props.item.nombreCompleto }}</td>
                <td>{{ props.item.nombreUsuario }}</td>
                <td>{{ props.item.rol }}</td>
                <td>
                    <div v-if="props.item.estado">
                       <span class="blue--text">Activo</span>
                    </div>
                    <div v-else>
                       <span class="red--text">Inactivo</span>
                    </div>
                </td>

                </template>
                <template slot="no-data">
                    <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    export default {
        data(){
            return {
                usuarios:[],
                dialog: false,
                headers: [
                    { text: 'OPCIONES', value: 'opciones', sortable: false },
                    { text: 'NOMBRE', value: 'nombreCompleto'},
                    { text: 'USUARIO', value: 'nombreUsuario', sortable: false },
                    { text: 'ROL', value: 'rol', sortable: false },
                    { text: 'ESTADO', value: 'estado', sortable: false }
                ],
                search: '',
                editedIndex: -1,
                ActPassword:false,
                idUsuario:'',
                idRol:'',
                nombreCompleto:'',
                nombreUsuario:'',
                clave:'',
                password:'',
                passwordAnt:'',
                roles:[],
                valida: 0,
                validaMensaje:[],
                adModal:0,
                adAccion:0,
            }
        },
        computed: {
            formTitle () {
             return this.editedIndex === -1 ? 'NUEVO USUARIO' : 'ACTUALIZAR USUARIO'
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            this.listar();
            this.select();
        },
        methods:{
            listar() {
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/usuarios/Listar',configuracion).then(function(response){
                    me.usuarios=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            editItem (item) {
                this.idUsuario = item.idUsuario;
                this.idRol = item.idRol;
                this.nombreCompleto = item.nombreCompleto;
                this.nombreUsuario = item.nombreUsuario
                this.clave = item.clave;
                this.password=item.password_Hash;
                this.passwordAnt=item.password_Hash;
                this.editedIndex=1;
                this.dialog = true
            },
            deleteItem (item) {
                const index = this.desserts.indexOf(item)
                confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1)
            },
            close () {
                this.dialog = false;
                this.limpiar();
            },
            limpiar(){
                this.idUsuario="";
                this.idRol="";
                this.nombreCompleto="";
                this.nombreUsuario="";
                this.password="";
                this.passwordAnt="";
                this.ActPassword=false;
                this.editedIndex=-1;
            },
            guardar () {
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                if (this.editedIndex > -1) {
                   let me = this;
                   if (me.password!=me.passwordAnt){
                       me.ActPassword=true;
                   }
                   axios.put('api/Usuarios/Actualizar',{
                       'idUsuario':me.idUsuario,
                       'idRol':me.idRol,
                       'nombreCompleto':me.nombreCompleto,
                       'nombreUsuario':me.nombreUsuario,
                       'clave':me.password,
                       'password':me.password,
                       'act_password':me.ActPassword                        
                   },configuracion).then(function(response){
                       me.close();
                       me.listar();
                       me.limpiar();
                   }).catch(function(error){
                      console.log(error);
                   });
                } else {
                   let me = this;
                   axios.post('api/Usuarios/Crear',{
                       'idRol':me.idRol,
                       'nombreCompleto':me.nombreCompleto,
                       'nombreUsuario':me.nombreUsuario,
                       'clave':me.password,
                       'Password':me.password
                   },configuracion).then(function(response){
                       me.close();
                       me.listar();
                   }).catch(function(error){
                      console.log(error);
                   });
                }
            },
            activarDesactivarCerrar() {
                this.adModal=0;
            },  
            select(){
                let me=this;
                var RolArray=[];
                axios.get('api/Rol/Select').then(function(response){
                    RolArray=response.data;
                    RolArray.map(function(x){
                        me.roles.push({text: x.nombre,value:x.idRol});
                    });
                }).catch(function(error){
                });
            },
            activarDesactivarMostrar(accion,item){
                this.adModal=1;
                this.nombreCompleto=item.nombreCompleto;
                this.idUsuario=item.idUsuario;                
                if (accion==0){
                    this.adAccion=0;
                }
                else if (accion==1){
                    this.adAccion=1;
                }
                else{
                    this.adModal=0;
                }
            },
            activar(){
                let me=this;
                axios.put('api/Usuarios/Activar/'+this.idUsuario,{}).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.nombreCompleto="";
                    me.idUsuario="";
                    me.listar();                       
                }).catch(function(error){
                    console.log(error);
                });
            },
            desactivar(){
                let me=this;
                axios.put('api/Usuarios/Desactivar/'+this.idUsuario,{}).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.nombreCompleto="";
                    me.idUsuario="";
                    me.listar();                       
                }).catch(function(error){
                    console.log(error);
                });
            }
        }        
    }
</script>