<template>
  <v-layout justify-center>
    <v-flex xs12>
      <v-toolbar color="white">
        <v-toolbar-title>CREDITO PERSONAL</v-toolbar-title>
        <v-spacer></v-spacer>
        <template v-if="verNuevo==0">
              <v-btn color="success"> 
                    <export-excel
                    class   = "btn btn-default"
                    :data   = "creditos"
                    :fields = "json_fields"
                    worksheet = "Lista Creditos Personal"
                    title="CREDITOS PERSONAL CASA DE LAS ENCHILADAS"
                    name    = "credito.xls"> Excel
                    </export-excel> 
                <v-icon dark right>cloud_download</v-icon>
             </v-btn>
        </template>
        <template v-if="verNuevo==1">
            <v-btn color="error" @click="cobrarCreditos()">
                    <v-icon>check_circle</v-icon>Cobrar
             </v-btn>
            <v-btn color="info" @click="volver()">
                <v-icon>check_circle</v-icon>REGRESAR
            </v-btn>
             
        </template>
        <template v-if="verNuevo==2">
              <v-btn color="info" @click="regresarIndividual()">
                    <v-icon>check_circle</v-icon>REGRESAR
             </v-btn>
        </template>
        <template v-if="verNuevo==3">
            <v-btn color="info" @click="volver()">
                    <v-icon>check_circle</v-icon>REGRESAR
            </v-btn>
        </template>
        <template v-if="verNuevo==1">
              <v-btn color="success"> 
                    <export-excel
                    class   = "btn btn-default"
                    :data   = "creditoIndividual"
                    :fields = "json_fields2"
                    worksheet = "Reporte Individual"
                    title="CREDITOS PERSONAL INDIVIDUAL CASA DE LAS ENCHILADAS"
                    name    = "creditoindividual.xls"> Excel
                    </export-excel> 
                <v-icon dark right>cloud_download</v-icon>
             </v-btn>
        </template>       
        </v-toolbar>
        <v-card>
         <v-container fluid grid-list-md  v-if="verNuevo==0">
          <v-layout row wrap>
               <v-flex xs12>
                 <v-card>
                   <v-card-title class="primary" style="color: white;"><h3>LISTA DE CREDITO PERSONAL</h3></v-card-title>                        
                    <v-container>
                        <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="B√∫squeda" single-line hide-details></v-text-field>
                    </v-container>
                    <v-card-text>
                        <v-data-table
                            :headers="creditosPersonal"
                            :items="creditos"
                            hide-actions
                            :search="search"
                            class="elevation-1">
                            <template slot="items" slot-scope="props">
                                <td> 
                                    <v-icon
                                         color="green darken-2"
                                         small
                                         class="mr-2"
                                         @click="reporteIndividual(props.item)"
                                         >
                                        edit
                                    </v-icon>
                                    <v-icon
                                         color="red darken-2"
                                         small
                                         class="mr-2"
                                         @click="Individual(props.item)"
                                         >
                                        check
                                    </v-icon>
                                </td>
                                <td>{{ props.item.razonSocial}}</td>
                                <td>{{ props.item.creditoDisponible }} </td>
                                <td>{{ props.item.totalConsumo}}</td>
                               
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3> No hay productos.</h3>
                                </span>
                            </template>
                        </v-data-table>
                         <v-flex class="text-xs-left">
                            <h2>TOTAL CREDITO: S/  {{totalCredito=(calcularTotalCredito).toFixed(2)}} </h2>
                        </v-flex>
                    </v-card-text>
                 </v-card>
               </v-flex>
          </v-layout>
         </v-container>
        </v-card>
        <v-card>
         <v-container fluid grid-list-md  v-if="verNuevo==1">
          <v-layout row wrap>
                <v-flex xs12>
                 <v-card>
                   <v-card-title class="primary" style="color: white;"><h3>REPORTE INDIVIDUAL {{trabajador}}</h3></v-card-title>                        
                    <v-card-text>
                        <v-data-table
                            v-model="selected"
                            :headers="creditosPersonalId"
                            :items="creditoIndividual"
                            hide-actions
                            select-all
                            :search="search"
                            item-key="idCredito"
                            class="elevation-1">
                             <template v-slot:headers="props">
                                <tr>
                                    <th>
                                        <v-checkbox
                                            :input-value="props.all"
                                            :indeterminate="props.indeterminate"
                                            primary
                                            hide-details
                                            color="error"
                                            @click.stop="toggleAll"
                                           >
                                      </v-checkbox>
                                    </th>
                                    <th class="text-xs-left"
                                        v-for="header in props.headers"
                                        :key="header.text">
                                        {{ header.text }}
                                    </th> 
                                </tr>
                             </template>
                            <template slot="items" slot-scope="props">
                                <tr :active="props.selected" @click="props.selected = !props.selected">
                                <td>
                                    <v-checkbox :input-value="props.selected"  color="error" hide-details></v-checkbox>
                                </td>
                                <td> 
                                    <v-icon  color="primary darken-2" small @click="venta(props.item)">check</v-icon>
                                </td>
                                <td>{{props.item.idCliente}}</td>
                                <td>{{props.item.razonSocial}}</td>
                                <td>{{ since(props.item.fecha) }} </td>
                                <td>{{ props.item.hora}}</td>
                                <td>{{ props.item.total}}</td>
                             </tr>
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3> No hay Informacion.</h3>
                                </span>
                            </template>
                        </v-data-table>
                    </v-card-text>
                 </v-card>
               </v-flex>
                <v-flex xs12>
                    <v-card>
                        <v-card-text>
                            Total Check Seleccionado:S/   <strong> {{totalSelec=(calcularTotalSeleccionados).toFixed(2)}} </strong>
                            Total Consumo Personal:S/     <strong> {{totalCreditoIndividual=(calcularTotalCreditoIndividual).toFixed(2)}}  </strong>
                        </v-card-text>
                    </v-card>
                </v-flex>
          </v-layout>
         </v-container>
        </v-card>
        <v-card>
         <v-container fluid grid-list-md  v-if="verNuevo==2">
          <v-layout row wrap>
            <v-flex xs12>
                  <v-card>
                    <v-card-title class="primary" style="color: white;"><h3>DETALLE VENTA </h3></v-card-title>                        
                    <v-card-text>
                         <v-data-table
                            :headers="creditosDetalleVenta"
                            :items="detalle"
                            hide-actions
                            class="elevation-1">
                            <template slot="items" slot-scope="props">
                                <td>{{ props.item.cantidad}}</td>
                                <td>{{ props.item.producto }} </td>
                                <td>{{ props.item.monto}}</td>
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3> No hay detalles.</h3>
                                </span>
                            </template>
                        </v-data-table>
                         <v-flex class="text-xs-left">
                            <h2>TOTAL VENTA: S/  </h2>
                        </v-flex>
                    </v-card-text>
                </v-card>
            </v-flex>
          </v-layout>
         </v-container>
        </v-card>
        <Individual :nombre=trabajador :valor=id v-if="verNuevo==3"/>
      </v-flex>
  </v-layout>
</template>
<script>
    import axios from 'axios'
    import DetalleVenta  from './DetalleVenta.vue'
    import Individual  from './Individuales/IndividualPersonal.vue'
    import EventBus from '../../main'
    import moment from 'moment';

    export default {
        data(){
            return {
               json_fields: {
                    'NOMBRE COMPLETO': 'razonSocial',
                    'CREDITO DISPONIBLE': 'creditoDisponible',
                    'TOTAL CONSUMO': 'totalConsumo',
                },
                json_meta: [
                [
                    {
                      'key': 'charset',
                      'value': 'utf-8'
                    }
                 ]
               ],
                json_fields2: {
                    'NOMBRE COMPLETO': 'razonSocial',
                    'FECHA': 'fecha',
                    'HORA': 'hora',
                    'TOTAL': 'total',
                },
                json_meta2: [
                [
                    {
                      'key': 'charset',
                      'value': 'utf-8'
                    }
                 ]
               ],
               idVenta:'',
               detalle:[],
               totalCredito:0,
               totalSelec:0,
               totalCreditoIndividual:0,
               editedIndex: -1,
               trabajador:'',
               verInsumos:0,
               idprod:'',
               creditoIndividual:[],
               search: '',
               creditos:[],
               selected:[],
               id:0,
               errorProducto:null,
               verNuevo:0,
               creditosPersonal: [
                    { text: 'OPCIONES', value: 'op',sortable: false  },
                    { text: 'NOMBRE COMPLETO', value: 'razonSocial',sortable: false },
                    { text: 'CREDITO DISPONIBLE', value: 'creditoDisponible',sortable: false  },
                    { text: 'TOTAL CONSUMO', value: 'totalConsumo', sortable: false  },         
                ],
                creditosDetalleVenta: [
                    { text: 'CANTIDAD', value: 'cantidad',sortable: false },
                    { text: 'PRODUCTO', value: 'producto',sortable: false  },
                    { text: 'TOTAL', value: 'monto', sortable: false  },         
                ],
                creditosPersonalId: [
                    { text: 'OPCION', value: '',sortable: false},
                    { text: '#', value: 'idCliente',sortable: false},
                    { text: 'NOMBRE COMPLETO', value: 'razonSocial',sortable: false},
                    { text: 'FECHA', value: 'fecha',sortable: false},
                    { text: 'HORA', value: 'hora',sortable: false},
                    { text: 'TOTAL', value: 'hora', sortable: false  },    
                ],
            }
        },
        components:{
            DetalleVenta,Individual
        },
        computed: {
            formTitle () {
                return this.editedIndex === -1 ? 'DETALLE CREDITOS PERSONAL' : 'EDITAR CREDITO PERSONAL'
            },
            calcularTotalCredito:function(){
                var resultado=0.0;
                for(var i=0;i<this.creditos.length;i++){
                    resultado=resultado+(this.creditos[i].totalConsumo);
                }
                return resultado;
            },
            calcularTotalCreditoIndividual:function(){
                var resultado=0.0;
                for(var i=0;i<this.creditoIndividual.length;i++){
                    resultado=resultado+(this.creditoIndividual[i].total);
                }
                return resultado;
            },    
            calcularTotalSeleccionados:function(){
                var resultado=0.0;
                for(var i=0;i<this.selected.length;i++){
                    resultado=resultado+(this.selected[i].total);
                }
                return resultado;
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
           this.listarCreditos();
        },
        methods:{
            toggleAll(){
              if (this.selected.length) this.selected = []
              else this.selected = this.creditoIndividual.slice()
            },
            since: function(d){
              return moment(d).format('L');  
            },
            volver(){
              this.verNuevo=0;
            },
            listarCreditos() {
                let me=this;
                axios.get('api/CreditoPersonal/ListarCreditosPersonal').then(function(response){
                    me.creditos=response.data;
                }).catch(function(error){
                   console.log(error);
                });
            },
            regresarIndividual(){
                this.verNuevo=1;
            },
            venta:function(item){
                this.idVenta = item.idVenta;
                this.verNuevo=2;
                this.listarDetalleVenta(this.idVenta);
            },
            cobrarCreditos(){
                let me = this;
                axios.put('api/CreditoPersonal/Cobrar',{
                    'creditoPersonalHelpers': me.selected
                }).then(function(response){
                   me.listarCreditos();
                   me.verNuevo=0;
                }).catch(function(error){
                    console.log(error);
                });
            },
            listarDetalleVenta(id) {
                let me=this;
                axios.get('api/VentaProductos/DetalleVenta/'+id).then(function(response){
                    me.detalle=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },  
            Individual(item){
               this.verNuevo=3;
               this.id = item.idCliente;
               this.trabajador = item.razonSocial;
            }, 
            reporteIndividual(item) {
                this.verNuevo=1;
                let me=this;
                me.id = item.idCliente;
                me.trabajador = item.razonSocial;

                axios.get('api/CreditoPersonal/Mostrar/'+me.id).then(function(response){
                    me.creditoIndividual=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            }
        }
    }
</script>