<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>MARCAS DE PRODUCTOS</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
                <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="B√∫squeda" single-line hide-details></v-text-field>
                <v-spacer></v-spacer>
                
                <v-dialog v-model="dialog" max-width="500px">
                        <v-btn slot="activator" color="info" dark class="mb-2">Nuevo
                            <v-icon dark right>check_circle</v-icon>
                        </v-btn>
                        <v-card>
                            <v-card-title class="primary" style="color: white;">
                               <h3>{{ formTitle }}</h3>
                            </v-card-title>
                            <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                <v-flex xs12 sm12 md12>
                                    <v-text-field v-model="descripcion" label="NOMBRE"></v-text-field>
                                </v-flex>
                                 <v-flex xs12 sm12 md12 v-show="valida">
                                    <h3><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h3>
                                </v-flex>

                                </v-layout>
                            </v-container>
                            </v-card-text>
                            <v-divider></v-divider>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="error" @click.native="close">CANCELAR
                                    <v-icon dark right>block</v-icon>
                                </v-btn>
                                <v-btn color="info"  @click.native="guardar">GUARDAR
                                    <v-icon dark right>check_circle</v-icon>
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                </v-dialog>

            <v-dialog v-model="adModal" max-width="300">
                <v-card>
                    <v-card-title class="headline info"  style="color: white;" primary-title v-if="adAccion==0">¬øActivar Item?</v-card-title>
                    <v-card-title class="headline error" style="color: white;" v-if="adAccion==1">¬øDesactivar Item?</v-card-title>
                    <v-card-text>
                        Estas a punto de
                        <span v-if="adAccion==0">Activar</span>
                        <span v-if="adAccion==1">Desactivar</span>
                        el item {{adNombre}}
                    </v-card-text>

                    <v-divider></v-divider>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="green darken-1" dark class="mb-2" @click="activarDesactivarCerrar">Cancelar</v-btn>
                        <v-btn v-if="adAccion==0" color="info"  class="mb-2" @click="activar">Activar</v-btn>
                        <v-btn v-if="adAccion==1" color="error" class="mb-2" @click="desactivar">Desactivar</v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>


            </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="marcas"
                :search="search"
                class="elevation-1">
                <template slot="items" slot-scope="props">
                <td> <v-icon
                    color="green darken-2"
                    small
                    class="mr-2"
                    @click="editItem(props.item)"
                    >
                    edit
                    </v-icon></td>
                <td>{{ props.item.idMarca }}</td>
                <td>{{ props.item.descripcion }}</td>
                
                </template>
                <template slot="no-data">
                    <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    export default {
        data(){
            return {
                marcas:[],
                dialog: false,
                headers: [
                { text: 'OPCIONES', value: '',sortable: false},
                { text: '#', value: 'idMarca',sortable: false},
                { text: 'MARCA', value: 'descripcion', sortable: false },               
                ],
                search: '',
                editedIndex: -1,
                id:'',
                descripcion:'',
                valida: 0,
                validaMensaje:[],
                adModal:0,
                adAccion:0,
                adNombre:'',
                adId:''  
            }
        },
        computed: {
            formTitle () {
            return this.editedIndex === -1 ? 'NUEVO MARCA' : 'ACTUALIZAR MARCA'
            }
        },

        watch: {
            dialog (val) {
            val || this.close()
            }
        },
       created () {
            this.listar();
        },
        methods:{
            listar() {
                let me=this;
                axios.get('api/Marcas').then(function(response){
                   me.marcas=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            sonido(){
                console.log('Will it play here?? lol')
                console.log('Play outside of ');
                var audio = new Audio('https://www.futura-electric.com/img/audio.mp3') // path to file
                audio.play() 
            },
            editItem (item) {
                this.id= item.idMarca;
                this.descripcion = item.descripcion;
                this.editedIndex=1;
                this.dialog = true
            },
            deleteItem (item) {
                const index = this.desserts.indexOf(item)
                confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1)
            },
            close () {
                this.dialog = false;
                this.limpiar();
                this.valida=0;
            },
            limpiar(){
                this.id="",
                this.descripcion=""
                this.editedIndex=-1;
            },
            guardar () {
               console.log(this.id);

                if(this.validar()){
                    return;
                }
                if (this.editedIndex > -1) {
                   //CODIGO EDITAR
                   let me = this;
                   axios.put('api/marcas/'+me.id,{
                       'idMarca': me.id,
                       'descripcion':me.descripcion
                   }).then(function(response){
                       me.close();
                       me.listar();
                       me.limpiar();
                   }).catch(function(error){
                      console.log(error);
                   });
                } else {
                   //CODIGO GUARDAR
                   let me = this;
                   axios.post('api/marcas',{
                       'descripcion':me.descripcion
                   }).then(function(response){
                       me.close();
                       me.listar();
                       me.limpiar();
                       //me.sonido();
                   }).catch(function(error){
                      console.log(error);
                   });
                }
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];

                if(this.descripcion.length<1 || this.descripcion.length>100){
                    this.validaMensaje.push("Ingrese nombre salon.");
                }
                if(this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
            activarDesactivarMostrar(accion,item){
              this.adModal=1;
              this.adNombre= item.nombre;
              this.adId=item.idSalon;
              
              if(accion==0){
                this.adAccion=0;
              }
              else if(accion==1){
                this.adAccion=1;
              }
              else{
                  this.adModal=0;
              }
            },
            activar(){
                let me = this;
                axios.put('api/Salones/Activar/'+this.adId,{}).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();
                }).catch(function(error){
                    alert( console.log(error));
                });
            },
            desactivar(){
                let me = this;
                axios.put('api/Salones/Desactivar/'+this.adId,{}).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();
                }).catch(function(error){
                    alert( console.log(error));
                   
                });
            },
            activarDesactivarCerrar() {
                this.adModal=0;
            }
        }        
    }
</script>