<template>
  <v-layout justify-center>
    <v-flex xs12>
      <v-toolbar color="white" >
        <v-toolbar-title>DOCUMENTOS APROBADOS</v-toolbar-title>
        <v-spacer></v-spacer>
            <v-menu
                v-model="menu1"
                :close-on-content-click="false"
                :nudge-right="40"
                lazy
                transition="scale-transition"
                offset-y
                full-width
                min-width="290px"
                >
                <template v-slot:activator="{ on }">
                <v-text-field
                    v-model="fecha"
                    label="FECHA INICIO"
                    prepend-icon="event"
                    readonly
                    v-on="on"
                ></v-text-field>
                </template>
                <v-date-picker color="green lighten-1" v-model="fecha" @input="menu1 = false"></v-date-picker>
            </v-menu>
            <v-menu
                    v-model="menu2"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                    >
                    <template v-slot:activator="{ on }">
                    <v-text-field
                        v-model="fechasalida"
                        label="FECHA FIN"
                        prepend-icon="event"
                        readonly
                        v-on="on"
                    ></v-text-field>
                    </template>
                    <v-date-picker color="green lighten-1" v-model="fechasalida" @input="menu2 = false"></v-date-picker>
                </v-menu>
            <v-spacer></v-spacer>
            <v-btn color="info" @click="listarBoletaAnuladas"><v-icon>search</v-icon></v-btn>
            <v-spacer></v-spacer>
            <v-btn color="success" dark class="mb-2">
                    <export-excel
                    class   = "btn btn-default"
                    :data   = "listado"
                    :fields = "json_fields"
                    worksheet = "Lista Boletas Aprobadas"
                    title="Boletas Aprobadas"
                    name    = "BoletasAprobadas.xls"> Excel
                    </export-excel> 
                <v-icon dark right>cloud_download</v-icon>
            </v-btn> 
      </v-toolbar>
      <v-card>
        <v-container fluid grid-list-md >
          <v-layout row wrap>
                <v-flex xs12>
                 <v-card>
                    <v-card-title class="error" style="color: white;"><h3>Listado</h3></v-card-title>
                    <v-card-text>
                        <v-data-table
                            :headers="headers"
                            :items="listado"
                            show-select="true"
                            class="elevation-1">
                            <template slot="items" slot-scope="props">
                                <td>{{props.item.idAnulacion}}</td>
                                <td>{{props.item.cliente}}</td>
                                <td>{{props.item.dni}}</td>
                                <td>{{props.item.comprobante}} </td>
                                <td>{{props.item.serie}}</td>
                                <td>{{props.item.correlativo }}</td>
                                <td>{{since(props.item.fecha) }}</td>
                                <td>{{props.item.hora }}</td>
                                <td>{{props.item.motivo}}</td>
                                <td>S/ {{props.item.total}}</td>
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3> Debe Buscar previamente.</h3>
                                </span>
                            </template>
                        </v-data-table>
                    </v-card-text>
                   <v-divider></v-divider>
                 </v-card>
               </v-flex>
          </v-layout>
        </v-container>     
      </v-card>
    </v-flex>
  </v-layout>
</template>
<script>
import axios from 'axios'
import moment from 'moment';
moment.locale('es');

export default {
    data() {
        return {
            listado:[],
            idventa:'',
            headers: [
            { text: 'CODIGO', value: 'codigo',sortable: false },
            { text: 'CLIENTE COMPROBANTE', value: 'cliente', sortable: false  },     
            { text: 'N. DOCUMENTO', value: 'documento', sortable: false  }, 
            { text: 'COMPROBANTE', value: 'comprobante' },
            { text: 'SERIE', value: 'serie',sortable: false  },
            { text: 'CORRELATIVO', value: 'correlativo', sortable: false  },
            { text: 'FECHA', value: 'fecha', sortable: false  },
            { text: 'HORA', value: 'hora', sortable: false  }, 
            { text: 'MOTIVO DE ANULACION', value: 'motivo', sortable: false  },    
            { text: 'TOTAL', value: 'total', sortable: false  }               
         ],
         error: null,
         mensajeToast:'',
         snackbar:false,
         id:'',
         fecha: '',
         menu1: null,
         menu2: null,
         fechasalida:'',
         json_fields: {
            'CODIGO': 'idVenta',
            'CLIENTE': 'cliente',
            'NDOCUMENTO': 'dni',
            'COMPROBANTE': 'comprobante',
            'SERIE': 'serie',
            'CORRELATIVO': 'correlativo',
            'FECHA': 'fecha',
            'HORA': 'hora',
            'MOTIVO': 'motivo',
            'TOTAL': 'total',
        },
       }
    },
    components:{
    },
    filters: {
        uppercase: function(str){
            return str.toUpperCase()
        },
        lowercase:function(str){
            return str.toLowerCase()
        },
        capitalize: function(str){
            return str.charAt(0).toUpperCase() + str.slice(1)
        }
     },
    computed: {
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
        },
        methods:{
          since: function(d){
                return moment(d).format('L');  
          },
          cerrarAnulacion(){
            this.anular = 0;
          },
          listarBoletaAnuladas() {
              let me = this;
              if(me.fecha =='' || me.fechasalida == ''){
                  this.$swal('Advertencia.', 'Debe selecionar las fechas', 'error')
              }else{
                  axios.get('api/AnularVentas/AnuladasAceptadas/'+me.fecha +'/'+me.fechasalida).then(function(response){
                      me.listado= response.data;
                      me.fecha = '';
                      me.fechasalida = '';
                  }).catch(function(error){
                      
                  });
              }
          }
      }
}
</script>