<template>
    <v-dialog v-model="modal" max-width="500px" persistent>
        <v-card>
            <v-card-title class="primary" style="color: white;" primary-title><h1>DETALLE CATEGORIA</h1>
            </v-card-title>
            <v-card-text>
            <v-container grid-list-md>
                <h1>{{categorias.idCategoria}}</h1>
                <h2>{{categorias.nombre}}</h2>
            </v-container>
            </v-card-text>
            <v-divider></v-divider>
            <v-spacer></v-spacer>
            <v-btn color="error" dark class="mb-2" @click="cerrar()">CANCELAR</v-btn>
        </v-card>
    </v-dialog>
</template>

<script>
    import axios from 'axios'
    
    export default {        
        data(){
            return {
                categorias:[],
                modal: false,
                headers: [
                    { text: 'ID', value: '', sortable: false },
                    { text: 'NOMBRE', value: 'nombre'},
                    { text: 'ESTADO', value: 'estado', sortable: false },
                ]
            }
        },
        computed: {
            formTitle () {
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            this.listar();
            this.modal = true;
        },
        methods:{
            listar() {
                let me=this;
                var ide = this.$route.params.id;
                axios.get('api/Categorias/Mostrar/'+ide).then(function(response){
                    me.categorias=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            cerrar(){
                this.modal=false;
                this.$router.push('/categorias');
            }

        }        
    }
</script>